<template>
  <div>
    <div>
      <q-btn
        icon="fas fa-chevron-circle-left"
        label="Voltar"
        class="btns"
        text-color="white"
        color="negative"
        @click="voltar()"
      />
      <q-btn
        icon="fas fa-print"
        label="Imprimir"
        class="btns"
        text-color="white"
        color="primary"
        @click="imprime()"
      />
    </div>
    <div id="content">
      <div class="row col-12 no-wrap">
        <div class="col-md-1 col-sm-3 col-xs-3 margin-cabecalhos">
          <img :src="clienteAlfameta.LogoEmpresa" alt="logo" class="logo-responsiva" />
        </div>
        <div class="col-md-11 col-sm-9 col-xs-9 margin-cabecalhos texto-cabecalho">
          <div>{{clienteAlfameta.Nome}}</div>
          <div>{{clienteAlfameta.Endereco}} - {{clienteAlfameta.Contato}}</div>
          <div>{{clienteAlfameta.CidadeEstado}} - {{clienteAlfameta.CEP}}</div>
          <div>CNPJ: {{clienteAlfameta.Cnpj}}</div>
        </div>
      </div>
      <div>
        <div>Produto: {{produto}}</div>
        <div>Ordem de servio: {{codOrdemServicoClassificacao}}</div>
      </div>
      <div>
        <table>
          <thead v-if="codProduto == 1">
            <tr>
              <th>Data</th>
              <th>Laudo</th>
              <th>Placa</th>
              <th>Peso(kg)</th>
              <th>Umidade</th>
              <th>Impurezas</th>
              <th>Ardidos</th>
              <th>Queimados</th>
              <th>Picados</th>
              <th>Esverdeados</th>
              <th>Fermentados</th>
              <th>Quebrados</th>
              <th>Total</th>
            </tr>
          </thead>
          <tbody v-if="codProduto == 1">
            <tr v-for="item in itensOs" :key="item.CodItemOrdemServicoClassificacao">
              <td>{{item.DataClassificacao}}</td>
              <td>{{item.NumeroLaudo}}</td>
              <td>{{item.PlacaCavalo}}</td>
              <td>{{item.PesoLiquido}}</td>
              <td>{{item.Umidade}}</td>
              <td>{{item.Impureza}}</td>
              <td>{{item.Ardidos}}</td>
              <td>{{item.Queimados}}</td>
              <td>{{item.Picados}}</td>
              <td>{{item.Esverdeados}}</td>
              <td>{{item.FermentadoChochoImaturoGerminado}}</td>
              <td>{{item.PartidoQuebradoAmassado}}</td>
              <td>{{item.TotalAvariado}}</td>
            </tr>
          </tbody>
          <thead v-if="codProduto == 2">
            <tr>
              <th>Data</th>
              <th>Laudo</th>
              <th>Placa</th>
              <th>Peso(kg)</th>
              <th>Umidade</th>
              <th>Impurezas</th>
              <th>Ardidos e Queimados</th>
              <th>Fermentados</th>
              <th>Partidos</th>
              <th>Total</th>
            </tr>
          </thead>
          <tbody v-if="codProduto == 2">
            <tr v-for="item in itensOs" :key="item.CodItemOrdemServicoClassificacao">
              <td>{{item.DataClassificacao}}</td>
              <td>{{item.NumeroLaudo}}</td>
              <td>{{item.PlacaCavalo}}</td>
              <td>{{item.PesoLiquido}}</td>
              <td>{{item.Umidade}}</td>
              <td>{{item.Impureza}}</td>
              <td>{{item.ArdidosEQueimados}}</td>
              <td>{{item.FermentadoChochoImaturoGerminado}}</td>
              <td>{{item.PartidoQuebradoAmassado}}</td>
              <td>{{item.TotalAvariado}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <div>
      <q-btn
        icon="fas fa-chevron-circle-left"
        label="Voltar"
        class="btns"
        text-color="white"
        color="negative"
        @click="voltar()"
      />
      <q-btn
        icon="fas fa-print"
        label="Imprimir"
        class="btns"
        text-color="white"
        color="primary"
        @click="imprime()"
      />
    </div>
  </div>
</template>

<script>
import { detectaCliente } from '../utils/DetectaCliente';
//import { detectaCliente } from "../utils/DetectaCliente.js";
export default {
  props: ["itens"],
  name: "relatorioEmbarque",
  data() {
    return {
      itensOs: [],
      codProduto: "",
      produto: "",
      nomeEmpresa: "",
      enderecoEmpresa: "",
      email: "",
      telefoneECelular: "",
      cnpj: "",
      inscrestadual: "",
      cep: "",
      cidadeEstado: "",
      logo: "",
      codOrdemServicoClassificacao: "",
      clienteAlfameta: {}
    };
  },
  methods: {
    voltar() {
      this.$router.push("areacliente/principal");
    },
    imprime() {
      window.print();
    },
  },
  mounted() {
    this.itensOs = this.$props.itens;
    this.codProduto = this.itensOs[0].CodProduto;
    this.codOrdemServicoClassificacao = this.itensOs[0].CodOrdemServicoClassificacao;
    detectaCliente().then(result => this.clienteAlfameta = result);
    if (this.codProduto == 1) {
      this.produto = "Soja";
    } else {
      this.produto = "Milho";
    }
  },
};
</script>

<style scoped>
table {
  border-collapse: collapse;
  margin-top: 10px;
}
@media print {
  .page-break {
    page-break-before: always;
  }
}
tr td {
  page-break-inside: avoid;
}
table,
th,
td {
  border: 1px solid black;
  font-size: 12px;
  text-align: center;
}
th,
td {
  padding: 4px;
}
.titulo-tabela {
  background-color: lightgray;
}
.logo-responsiva {
  width: 100%;
}
.margin-cabecalhos {
  margin: 5px;
}
.texto-cabecalho {
  font-size: 11px;
}
.btns {
  margin: 10px;
  font-size: 15px;
}
#content {
  margin: 15px;
}
@media print {
  .btns {
    display: none;
  }
  #content {
    margin: 0px;
  }
}
</style>