<template>
  <q-layout>
    <div class="responsivo">
      <div class="centraliza-titulo margin-titulo">Lotes da O.S</div>
      <div>
        <q-table
          :data="lotesConab"
          :columns="colunasTabela"
          row-key="CodLote"
          :loading="statusCarregamentoTabela"
          separator="cell"
          dense
          :filter="termoBusca"
          :rows-per-page-options="opcoesLinhasPorPagina"
          :selected="linhaSelecionada"
          :pagination.sync="padraoLinhasPorPagina"
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>

          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td key="NumeroLote" :props="props" class="tamanho-coluna-id">{{ props.row.NumeroLote }}</q-td>
              <q-td key="NomeMunicipio" :props="props">{{ props.row.NomeMunicipio }}</q-td>
              <q-td key="ATR" :props="props">{{ props.row.ATR }}</q-td>
              <q-td key="Safra" :props="props">{{ props.row.Safra }}</q-td>
              <q-td key="PesoLiquido" :props="props">{{ Number(props.row.PesoLiquido).toLocaleString("pt-BR")}}</q-td>
              <q-td key="TotalClassificado" :props="props">{{ Number(props.row.TotalClassificado).toLocaleString("pt-BR")}}</q-td>
              <q-td key="SaldoRestante" :props="props">{{ Number(props.row.PesoSaldoRestante).toLocaleString("pt-BR")}}</q-td>
              <q-td key="acoes" :props="props" style="width:15px">
                <q-btn
                  icon="fas fa-file-alt"
                  dense
                  :props="props"
                  flat
                  color="positive"
                  @click="abrirModalClassificacaoConab(props.row)"
                >
                  <q-tooltip>Emitir Certificado</q-tooltip>
                </q-btn>
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  v-show="administrador"
                  @click="alterarLote(props.row)"
                >
                  <q-tooltip>Editar lote</q-tooltip>
                </q-btn>
                <q-btn
                  icon="ion-trash"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-show="administrador"
                  @click="desativarLote(props.row.CodLote)"
                >
                  <q-tooltip>Remover lote</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizarLote(props.row)"
                >
                  <q-tooltip>Visualizar lote</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
          <template slot="top-right">
            <div class="offset-6 col-12">
              <q-btn
                color="primary"
                icon="add"
                label="Cadastrar novo lote"
                @click="abrirModalcadastroNovoLote()"
                v-show="administrador"
              />
            </div>
          </template>
        </q-table>
      </div>
    </div>
    <modal-classificacao-conab ref="modalClassificacaoConab"/>
    <modal-lote ref="modalLoteConab" @atualizaTabela="carregaTabela" />
  </q-layout>
</template>


<script>
import { get, requestDeleteComCabecalho } from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
import ModalLote from "./ModalLote.vue";
import ModalClassificacaoConab from './ModalNovaClassificacaoConab.vue';
export default {
  name: "gradeLotesConab",
  components: {
    ModalLote,
    ModalClassificacaoConab
  },
  data() {
    return {
      padraoLinhasPorPagina: { rowsPerPage: 10 },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      exibe: false,
      abreModal: false,
      termoBusca: "",
      usuarios: [],
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "NumeroLote",
          label: "Lote",
          field: "NumeroLote",
          sortable: true,
          align: "center"
        },
        {
          name: "NomeMunicipio",
          label: "Destino",
          field: "NomeMunicipio",
          align: "left",
        },
        {
          name: "ATR",
          label: "A.T.R",
          field: "ATR",
          align: "left",
        },
        {
          name: "Safra",
          label: "Safra",
          field: "Safra",
          align: "left",
        },
        {
          name: "PesoLiquido",
          label: "Peso Liquido (kg)",
          field: "PesoLiquido",
          sortable: true,
          align: "left",
        },
        {
          name: "TotalClassificado",
          label: "Total Classificado (kg)",
          field: "TotalClassificado",
          sortable: true,
          align: "left",
        },
        {
          name: "SaldoRestante",
          label: "Saldo Restante (kg)",
          field: "PesoSaldoRestante",
          sortable: true,
          align: "left",
        },
        { name: "acoes", label: "Aes", field: "acoes", align:"center"},
      ],
      //Array com os dados da tabela
      lotesConab: [],
      linhaSelecionada: [],
      administrador: false,
      supervisor: false,
      ordemServicoClassificacaoConab: {}
    };
  },
  methods: {
    abreTabela(dadosOs) {
      this.ordemServicoClassificacaoConab = dadosOs;
      this.carregaTabela();
    },
    carregaTabela() {
      get(
        `loteconab?CodOrdemServicoClassificacaoConab=${this.ordemServicoClassificacaoConab.CodOrdemServicoClassificacaoConab}`
      )
        .then((result) => {
          this.lotesConab = JSON.parse(result);
          this.statusCarregamentoTabela = false;
        })
        .catch(() => {
          this.statusCarregamentoTabela = false;
        });
    },
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    abrirModalcadastroNovoLote() {
      this.$refs.modalLoteConab.abreModal(
        this.ordemServicoClassificacaoConab.CodOrdemServicoClassificacaoConab
      );
    },
    abrirModalClassificacaoConab(dadosLote) {
      this.$refs.modalClassificacaoConab.abreModalCertificado(this.ordemServicoClassificacaoConab, dadosLote);
    },
    fechaTabela() {
      this.exibe = false;
    },
    visualizarLote(dadosJustificativa) {
      this.$refs.modalLoteConab.abreModalVisualizacao(dadosJustificativa);
    },
    desativarLote(codLote) {
      this.$q
        .dialog({
          title: "Excluir Lote",
          message: "Deseja realmente excluir o lote selecionado?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          requestDeleteComCabecalho(`loteconab?CodLote=${codLote}`, "CodLote", codLote)
            .then((result) => {
              const resposta = JSON.parse(result);
              if (resposta.Status == true) {
                this.$q.notify({
                  message: resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    alterarLote(dadosJustificativa) {
      this.$refs.modalLoteConab.abreModalAlteracao(dadosJustificativa);
    },
  },
  mounted() {
    this.verificaAdministrador();
  },
};
</script>


<style scoped>
.q-layout {
  min-height: auto;
}
.centraliza-titulo {
  text-align: center;
}
.margin-titulo {
  margin-top: 10px;
  margin-bottom: 10px;
}
tr:nth-child(even) {
  background-color: lightgray;
}
.responsivo {
  width: 98%;
  margin: 10px;
}
</style>
