import{getCookie} from "../../js/Cookies.js";
import {post} from "./ConexaoApiCliente.js";
export function verificaSeEstaLogado() {
  const cliente = window.location.pathname;
  if (
    getCookie("tkCliente") != null ||
    getCookie("tkCliente") != ""
  ) {
    
    const Token = {}
    let Resposta = {}
    Token.token = getCookie("tkCliente");
    post("auth", Token)
      .then(result => {
        Resposta = JSON.parse(result);
        //Se houver qualquer problema de autenticao, leva o usurio at a tela de login
        if (Resposta.Autenticado != true) {
          window.location.href = cliente + "#/areacliente";
        }
      })
      .catch(err => {
        Resposta = JSON.parse(err);
        window.location.href = cliente + "#/areacliente";
      });
  } else {
    window.location.href = cliente + "#/areacliente";
  }
}
