<template>
  <q-modal
    v-model="exibe"
    minimized
    :content-css="{width: '50vw', minHeight: '45vh'}"
    no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn
          style="padding:0px; margin:0"
          flat
          round
          dense
          icon="close"
          @click="confirmarCancelamentoDeCadastroDeAcesso"
        />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>
      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{tituloModal}}</h6>
      </q-toolbar>
      <div class="layout-padding">
        <div class="row">
          <div class="col-md-12 col-sm-12 col-xs-12 margin-form">
            <q-input
              v-model="termoBuscaCliente"
              placeholder="Escolha um Cliente"
              float-label="Escolha um Cliente"
              :disable="modoVisualizacao == true"
              @focus="validaCamposForm"
            >
              <q-autocomplete
                :static-data="{field:'label', list:clientes}"
                @selected="clienteSelecionado"
                :max-results="maximoResultadoAutoSelectClientes"
              />
            </q-input>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-input
              v-model="cnpj"
              placeholder="CNPJ (Esse sera o usurio)"
              float-label="CNPJ  (Esse sera o usurio)"
              :disable="modoVisualizacao == true"
              @focus="validaCamposForm"
            />
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-input
              v-model="senha"
              placeholder="Senha"
              float-label="Senha"
              type="password"
              :disable="modoVisualizacao == true"
              @focus="validaCamposForm"
            />
          </div>
          <div class="col-12 margin-form">
            <q-checkbox
              v-model="primeiroLogin"
              val="true"
              label="O usurio deve alterar sua senha no prximo login"
            />
          </div>
          <div class="row justify-center col-12" style="margin-top:20px;">
            <q-btn
              color="primary"
              label="Salvar"
              @click="comportamentoBotaoSalvar()"
              v-show="modoVisualizacao != true"
            />
          </div>

          <div class="row items-center col-12 q-mt-md">
            <div class="row col-10 q-ml-sm justify-end">
              <q-input readonly ref="areacliente" v-model="linkAreaCliente" style="width: 100%" />
            </div>

            <q-btn @click="copiarLinkAreaCliente" icon="fa fa-copy" round size="xs" color="primary">
              <q-tooltip>Copiar rea do Cliente</q-tooltip>
            </q-btn>

          </div>

        </div>
        <div style="margin-top:10px">
          <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
        </div>
      </div>
    </q-modal-layout>
  </q-modal>
</template>
<script>
import { get, post } from "../components/js/ConexaoApi.js";
export default {
  name: "modalAcessos",
  components: {},
  data() {
    return {
      linkAreaCliente: "",
      tituloModal: "",
      termoBuscaCliente: "",
      codclienteSelecionado: "",
      modoVisualizacao: false,
      exibe: false,
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      alteraInsere: "",
      cnpj: "",
      senha: "",
      nome: "",
      primeiroLogin: true,
      id: "",
      clientes: [],
      email: "",
      maximoResultadoAutoSelectClientes: 20,
    };
  },
  methods: {
    verificaAreaCliente() {
      var link = window.location.href
      var areaCliente = link.replace('clientes', 'areacliente')
      this.linkAreaCliente = areaCliente
    },
    copiarLinkAreaCliente() {
      const inp = this.$refs.areacliente

      inp.select()

      try {
        document.execCommand("copy")
        this.$q.notify({
          message: 'Copiado para URL',
          type: 'positive',
          timeout: 1500
        })
      } catch (e) {
        this.$q.notify({
          message: 'Erro ao copiar link',
          type: 'negative',
          timeout: 1500
        })
      }
    },
    abreModal() {
      this.exibe = true;
      this.tituloModal = "Novo acesso";
      this.modoVisualizacao = false;
      this.alteraInsere = 1;
    },
    carregaCampos(dadosAcesso) {
      this.id = dadosAcesso.id;
      this.codclienteSelecionado = dadosAcesso.CodCliente;
      this.termoBuscaCliente = dadosAcesso.Nome;
      this.cnpj = dadosAcesso.CNPJ;
      this.primeiroLogin = dadosAcesso.PrimeiroAcesso == 'S';
    },
    abreModalVisualizacao(dadosUsuario) {
      this.tituloModal = "Visualizando usurio";
      this.modoVisualizacao = true;
      this.exibe = true;
      this.carregaCampos(dadosUsuario);
    },
    abreModalAlteracao(dadosUsuario) {
      this.alteraInsere = 2;
      this.tituloModal = "Alterar acesso";
      this.modoVisualizacao = false;
      this.exibe = true;
      this.carregaCampos(dadosUsuario);
    },
    fechaModal() {
      this.exibe = false;
      this.senha = "";
      this.nome = "";
      this.id = "";
      this.cnpj = "";
      this.mensagemRetorno = "";
      this.codclienteSelecionado = "";
      this.termoBuscaCliente = "";
      this.primeiroLogin = false;
    },
    confirmarCancelamentoDeCadastroDeAcesso() {
      if (this.modoVisualizacao != true) {
        this.$q
          .dialog({
            title: "Cancelar cadastro de acesso",
            message:
              "Deseja realmente cancelar o cadastro de acesso desse cliente?",
            color: "negative",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => {
            this.fechaModal();
          })
          .catch(() => {});
      } else {
        this.fechaModal();
      }
    },
    validaCamposFormNaHoraDeSalvar() {
      if (this.codclienteSelecionado === "") {
        this.$q.dialog({
          title: "Selecione um cliente",
          message: "Antes de salvar, por favor, selecione um cliente!",
          ok: "Sim",
        });
        return false;
      } else if (this.senha === "") {
        this.$q.dialog({
          title: "Informe uma senha",
          message: "Antes de salvar, por favor, informe uma senha!",
          ok: "Sim",
        });
        return false;
      } else {
        return true;
      }
    },
    salvaAcesso() {
      const cadastroCorreto = this.validaCamposFormNaHoraDeSalvar();
      if (cadastroCorreto) {
        this.alteraInsere = 1;
        const Acesso = {};
        Acesso.Senha = this.senha;
        Acesso.CNPJ = this.cnpj;
        Acesso.CodClienteSistema = this.codclienteSelecionado;
        Acesso.Nome = this.termoBuscaCliente;
        if (this.primeiroLogin == true) {
          Acesso.PrimeiroAcesso = "S";
        } else {
          Acesso.PrimeiroAcesso = "N";
        }
        // eslint-disable-next-line no-console
        post("novousuariocliente", Acesso)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.exibe = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
      }
    },
    validaCamposForm() {
      if (this.codclienteSelecionado === "") {
        this.termoBuscaCliente = "";
      }
    },
    alteraAcesso() {
      const cadastroCorreto = this.validaCamposFormNaHoraDeSalvar();
      if (cadastroCorreto) {
        this.exibeMensagemRetorno = false;
        const Acesso = {}
        Acesso.Senha = this.senha;
        Acesso.CNPJ = this.cnpj;
        Acesso.id = this.id;
        Acesso.CodClienteSistema = this.codclienteSelecionado;
        Acesso.Nome = this.termoBuscaCliente;
        if (this.primeiroLogin == true) {
          Acesso.PrimeiroAcesso = "S";
        } else {
          Acesso.PrimeiroAcesso = "N";
        }
        // eslint-disable-next-line no-console
        post("atualizaacesso", Acesso)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.exibe = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
      }
    },
    comportamentoBotaoSalvar() {
      if (this.alteraInsere == 1) {
        return this.salvaAcesso();
      } else {
        return this.alteraAcesso();
      }
    },
    clienteSelecionado(cliente) {
      this.termoBuscaCliente = cliente.label;
      this.codclienteSelecionado = cliente.value;
      this.cnpj = cliente.sublabel;
      this.email = cliente.Email;
    },
    carregaClientesSemAcesso() {
      get("listaclientessemacesso")
        .then((result) => {
          let jsonClientes = result;
          jsonClientes = jsonClientes.replace(/"CodCliente":/g, '"value":');
          jsonClientes = jsonClientes.replace(/"Nome":/g, '"label":');
          jsonClientes = jsonClientes.replace(/"CnpjCpf":/g, '"sublabel":');
          this.clientes = JSON.parse(jsonClientes);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
  },
  mounted() {
    this.verificaAreaCliente()
    this.carregaClientesSemAcesso();
  },
};
</script>

<style scoped>
.tira-margin-pading {
  padding: 0;
  margin: 0;
}
.margin-form {
  padding: 5px;
}
@media screen and (max-width: 768px) {
  .margin-form {
    padding: 0px;
  }
  .lagura-modal {
    width: 70px;
  }
}
</style>
