<template>
  <q-layout v-show="exibe">
    <div class="responsivo">
      <div class="centraliza-titulo margin-titulo">Justificativas de no embarque</div>
      <div>
        <q-table
          :data="justificativas"
          :columns="colunasTabela"
          row-key="CodOrdemServicoClassificacao"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          :rows-per-page-options="opcoesLinhasPorPagina"
          :selected="linhaSelecionada"
          :pagination.sync="padraoLinhasPorPagina"
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>

          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td key="id" :props="props" class="tamanho-coluna-id">{{ props.row.id }}</q-td>
              <q-td key="NomeClassificador" :props="props">{{ props.row.NomeClassificador }}</q-td>
              <q-td key="Justificativa" :props="props">{{ props.row.Justificativa }}</q-td>
              <q-td key="Observacao" :props="props">{{ props.row.Observacao }}</q-td>
              <q-td key="DataJustificativa" :props="props">{{ props.row.DataJustificativa }}</q-td>
              <q-td key="acoes" :props="props" style="width:15px">
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  v-show="administrador || supervisor"
                  @click="alteraJustificativa(props.row)"
                >
                  <q-tooltip>Editar justificativa</q-tooltip>
                </q-btn>
                <q-btn
                  icon="ion-trash"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-show="administrador || supervisor"
                  @click="desativaJustificativa(props.row.id)"
                >
                  <q-tooltip>Remover justificativa</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizaJustificativa(props.row)"
                >
                  <q-tooltip>Visualizar justificativa</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
          <template slot="top-right">
            <div class="row margin-form">
              <q-btn
                  color="red"
                  icon="events"
                  label="Justificar - vrias datas"

                  @click="abreModalJustificativa(true)"
                  v-show="administrador || supervisor">
              </q-btn>


            </div>
              <div class="row margin-form">
                <q-btn
                    color="primary"
                    icon="far fa-calendar-times"
                    label="Justificar - data"
                    @click="abreModalJustificativa()"
                    v-show="administrador || supervisor"
                />
              </div>
          </template>
        </q-table>
      </div>
    </div>
    <ModalJustificativa ref="modalJustificativa" @atualizaTabela="carregaTabela" />
  </q-layout>
</template>


<script>
import { getComCabecalho } from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
import ModalJustificativa from "@/components/ModalJustificativa";
export default {
  name: "gradeJustificativas",
  components: {
    ModalJustificativa,
  },
  data() {
    return {
      padraoLinhasPorPagina: { rowsPerPage: 10 },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      exibe: false,
      abreModal: false,
      termoBusca: "",
      usuarios: [],
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "id",
          label: "ID",
          field: "id",
          sortable: true,
          align: "center",
          style: "width: 75px",
        },
        {
          name: "NomeClassificador",
          label: "Classificador",
          sortable: true,
          field: "NomeClassificador",
          align: "left",
        },
        {
          name: "Justificativa",
          label: "Justificativa",
          field: "Justificativa",
          align: "left",
          sortable: true,
        },
        {
          name: "Observacao",
          label: "Observao",
          field: "Observacao",
          sortable: true,
          align: "center",
        },
        {
          name: "DataJustificativa",
          label: "Data",
          field: "DataJustificativa",
          sortable: true,
          align: "center",
        },
        { name: "acoes", label: "Aes", field: "acoes" },
      ],
      //Array com os dados da tabela
      justificativas: [],
      linhaSelecionada: [],
      administrador: false,
      supervisor: false,
      codOrdemServicoClassificacao: "",
    };
  },
  methods: {
    carregaTabela() {
      getComCabecalho(
        "justificativas",
        "CodOs",
        this.codOrdemServicoClassificacao
      )
        .then((result) => {
          this.justificativas = JSON.parse(result);
          this.statusCarregamentoTabela = false;
        })
        .catch(() => {
          this.statusCarregamentoTabela = false;
        });
    },
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    abreModalJustificativa(variasDatas=false) {
      if (variasDatas){
        this.$refs.modalJustificativa.abreModal(
            this.codOrdemServicoClassificacao,
            true
        );
      }else {
        this.$refs.modalJustificativa.abreModal(
            this.codOrdemServicoClassificacao
        );
      }
    },
    fechaTabela() {
      this.exibe = false;
    },
    visualizaJustificativa(dadosJustificativa) {
      this.$refs.modalJustificativa.abreModalVisualizacao(dadosJustificativa);
    },
    pegaCodOs(codOS) {
      this.exibe = true;
      this.codOrdemServicoClassificacao = codOS;
      this.carregaTabela();
    },
    desativaJustificativa(idJustificativa) {
      this.$q
        .dialog({
          title: "Excluir justificativa",
          message: "Deseja realmente excluir a justificativa selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho("deletajustificativa", "id", idJustificativa)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    alteraJustificativa(dadosJustificativa) {
      this.$refs.modalJustificativa.abreModalAlteracao(dadosJustificativa);
    },
  },
  mounted() {
    this.verificaAdministrador();
  },
};
</script>


<style scoped>
.q-layout {
  min-height: auto;
}
.centraliza-titulo {
  text-align: center;
}
.margin-titulo {
  margin-top: 10px;
  margin-bottom: 10px;
}
tr:nth-child(even) {
  background-color: lightgray;
}
.responsivo {
  width: 98%;
  margin: 10px;
}
</style>
