<template>
  <q-layout>
    <div class="responsivo">
      <div class="centraliza-titulo margin-titulo">Classificaes realizadas</div>
      <div>
        <q-table
          :data="itensOrdemServico"
          :columns="colunasTabela"
          row-key="NumeroLaudo"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          :selected="linhaSelecionada"
          :pagination.sync="padraoLinhasPorPagina"
          :rows-per-page-options="opcoesLinhasPorPagina"
          table-class="css-tabela"
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>
          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td
                key="DataClassificacao"
                :props="props"
                style="width:25px"
              >{{ new Date(props.row.DataClassificacao).toLocaleDateString('pt-BR') }}</q-td>
              <q-td key="NumeroLote" :props="props">{{ props.row.NumeroLote}}</q-td>
              <q-td key="NumeroLaudo" :props="props">{{ props.row.NumeroLaudo}}</q-td>
              <q-td key="PlacaCavalo" :props="props">{{ props.row.PlacaCavalo }}</q-td>
              <q-td key="PesoLiquido" :props="props">{{adicionaMilhar(props.row.PesoLiquido) }}KG</q-td>
              <q-td key="NomeClassificador" :props="props">{{ props.row.NomeClassificador }}</q-td>
              <q-td key="acoes" :props="props">
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizaClassificacao(props.row)"
                >
                  <q-tooltip>Visualizar dados da classificao {{ props.row.NumeroLaudo }}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  v-show="administrador"
                  @click="corrigirPesoClassificacao(props.row)"
                >
                  <q-tooltip>Editar classificao {{ props.row.NumeroLaudo }}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="ion-trash"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-show="administrador"
                  @click="desativaClassificacao(props.row.CodItemOrdemServicoClassificacaoConab)"
                >
                  <q-tooltip>Remover classificao {{ props.row.NumeroLaudo }}</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
        </q-table>
      </div>
    </div>
    <ModalNovaClassificacao ref="modalNovaClassificacao" @atualizaTabela="carregaItensDaOs(codOrdemServicoClassificacao)" />
    <QDialogCustomizado ref="qDialogCustomizado" @atualizaTabela="carregaItensDaOs(codOrdemServicoClassificacao)" />
  </q-layout>
</template>


<script>
import { getComCabecalho, post } from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
import ModalNovaClassificacao from "@/components/ModalNovaClassificacao";
import { detectaCliente } from "../components/utils/DetectaCliente.js";
import QDialogCustomizado from "./QDialogCustomizado";
export default {
  name: "gradeItensOS",
  components: {
    ModalNovaClassificacao,
    QDialogCustomizado,
  },
  data() {
    return {
      codClienteAlfameta: "",
      termoBusca: "",
      padraoLinhasPorPagina: { rowsPerPage: 10 },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      codOrdemServicoClassificacao: "",
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "DataClassificacao",
          label: "Data",
          field: "DataClassificacao",
          sortable: true,
          align: "center",
        },
        {
          name: "NumeroLote",
          label: "N Lote",
          field: "NumeroLote",
          align: "center",
          sortable: true,
        },
        {
          name: "NumeroLaudo",
          label: "N Laudo",
          field: "NumeroLaudo",
          align: "center",
          sortable: true,
        },
        {
          name: "PlacaCavalo",
          label: "Placa",
          field: "PlacaCavalo",
          sortable: true,
          align: "center",
        },
        {
          name: "PesoLiquido",
          label: "Peso Liquido",
          field: "PesoLiquido",
          sortable: true,
          align: "center",
        },
        {
          name: "NomeClassificador",
          label: "Classificador",
          field: "NomeClassificador",
          align: "center",
          sortable: true,
        },
        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
          sortable: false,
          align: "center",
        },
      ],
      //Array com os dados da tabela
      itensOrdemServico: [],
      linhaSelecionada: [],
      administrador: false,
      urlQrCode: "",
    };
  },
  methods: {
    visualizaClassificacao(dadosOs) {
      let routeData = this.$router.resolve(`/conab/formulario/${dadosOs.NumeroLaudo}`);
      window.open(routeData.href, "_blank");
    },
    corrigirPesoClassificacao(dadosOs) {
      this.$q
        .dialog({
          title: "Alterar o peso da classificao",
          message: "Informe o peso correto da classificao",
          prompt: {
            model: "",
            type: "text", // optional
          },
          cancel: true,
          color: "secondary",
        })
        .then((data) => {
          const Classificacao = {}
          Classificacao.CodItemOrdemServicoClassificacaoConab = dadosOs.CodItemOrdemServicoClassificacaoConab;
          Classificacao.PesoLiquido = data;
          Classificacao.CodUsuarioAlteracao = getCookie('id');
          post("corrigeclassificacaoconab", Classificacao)
            .then((result) => {
              const resposta = JSON.parse(result);
              if (resposta.Status == true) {
                this.$q.notify({
                  type: "positive",
                  message: resposta.msg,
                });
                this.$emit("atualizaTabelaOs");
                this.carregaItensDaOs(this.codOrdemServicoClassificacao);
              } else {
                this.$q.notify({
                  type: "negative",
                  message: resposta.msg,
                });
              }
            })
            .catch(() => {
              this.$q.notify({
                type: "negative",
                message:
                  "Falha ao alterar peso liquido da classificao, por favor, contate o suporte",
              });
            });
        }).catch(() => {});
    },
    adicionaMilhar(numero) {
      return Number(numero).toLocaleString("pt-BR");
    },
    desativaClassificacao(codItemOrdemServicoClassificacaoConab) {
      this.$q
        .dialog({
          title: "Desativar classificao",
          message: "Deseja realmente desativar a classificao selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho(
            "desativaclassificacaoconab",
            "CodItemOrdemServicoClassificacaoConab",
            codItemOrdemServicoClassificacaoConab
          )
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaItensDaOs(this.codOrdemServicoClassificacao);
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    carregaItensDaOs(codOrdemServicoClassificacaoConab){
      this.codOrdemServicoClassificacao = codOrdemServicoClassificacaoConab;
      getComCabecalho("retornaitensconab","CodOrdemServicoClassificacaoConab", codOrdemServicoClassificacaoConab)
        .then((result) => {
          this.itensOrdemServico = JSON.parse(result);
          this.statusCarregamentoTabela = false;
        })
        .catch(() => {
          this.statusCarregamentoTabela = false;
        });
      this.$emit("atualizaPesosOs");
    },
    abreModalCadastro() {
      this.$refs.modalOrdemServico.abreModal();
    },
    verificaAdministrador() {
      const admin = getCookie("su");
      if (admin != 0) {
        this.administrador = true;
      }
    },
    pegaUrlQrCode() {
      detectaCliente()
        .then((result) => {
          this.urlQrCode = result.UrlQRCode;
        })
        .catch(() => {
          // console.error(err);
        });
    },
    abrePdf(uuid) {
      window.open(this.urlQrCode + uuid, "_blank");
      // this.$router.push('/impresso/' + uuid) // Para desenvolvimento
    },
  },
  beforeMount() {
    this.verificaAdministrador();
  },
  created() {
    this.pegaUrlQrCode();
  },
};
</script>

<style scoped>
.q-layout {
  min-height: 0;
  margin-bottom: 95px;
}
.centraliza-titulo {
  text-align: center;
}
.margin-titulo {
  margin-top: 10px;
  margin-bottom: 10px;
}
.titulo-dependentes {
  font-size: 14px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.tamanho-coluna-id {
  width: 5px;
}
.responsivo {
  width: 98%;
  margin: 10px;
}
.distancia-topo {
  margin-top: 15px;
}
.nome-usuario {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.q-table tbody td {
  height: 15px;
  font-weight: 400;
  font-size: 10px;
}

.q-table th,
.q-table td {
  white-space: nowrap;
  height: 1px;
}
.q-table-dense .q-table th,
.q-table-dense .q-table td {
  padding: 0px 1px;
}
/*.css-tabela {
  table-layout: fixed;
  width: 100%;
}
.css-tabela tr > *:nth-child(1) {
  width: 65px;
}
.css-tabela tr > *:nth-child(2) {
  width: 120px;
}
.css-tabela tr > *:nth-child(3) {
  width: 300px;
}
.css-tabela tr > *:nth-child(4) {
