<template>
  <q-modal
    v-model="exibe"
    minimized
    :content-css="{width: '100vw', minHeight: '95vh'}"
    no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn style="padding:0px; margin:0" flat round dense icon="close" v-close-overlay />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>
      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{tituloModal}}</h6>
      </q-toolbar>
      <div class="layout-padding">
        <div class="row">
          <div class="col-12 centraliza-titulo">Laudo de classificao</div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="pesoLiquido"
                placeholder="Peso Lquido"
                float-label="Peso Lquido"
                type="number"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="placaCavalo"
                placeholder="Placa Cavalo"
                float-label="Placa Cavalo"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="placa1"
                placeholder="Placa 01"
                float-label="Placa 01"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="placa2"
                placeholder="Placa 02"
                float-label="Placa 02"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="transportadora"
                placeholder="Transportadora"
                float-label="Transportadora"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="pesoAmostraTrabalhada"
                placeholder="Peso Amostra"
                float-label="Peso Amostra"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 2" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-select
                filled
                v-model="tipoProdutoMilhoSelecionado"
                float-label="Teste Aflatoxina"
                filter
                :options="tipoProdutoMilho"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div v-else class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-select
                filled
                v-model="tipoProdutoSojaSelecionado"
                float-label="Soja Intacta"
                filter
                :options="tipoProdutoSoja"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 2" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-select
              filled
              v-model="tipoTesteOGMSelecionado"
              float-label="Teste OGM"
              filter
              :options="tipoTesteOGM"
              :disable="modoVisualizacao == true"
            />
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="umidade"
                placeholder="Umidade (14%)"
                float-label="Umidade (14%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="impureza"
                placeholder="Materiais estranhos e impurezas (1%)"
                float-label="Materiais estranhos e impurezas (1%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                  v-model="mofados"
                  placeholder="Mofados (6%)"
                  float-label="Mofados (6%)"
                  :disable="modoVisualizacao == true"
                  type="number"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 2" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="ardidosEQueimados"
                placeholder="Ardidos e Queimados (1%)"
                float-label="Ardidos e Queimados (1%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div v-else class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="queimados"
                placeholder="Queimados (1%)"
                float-label="Queimados (1%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 2" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="fermentados"
                placeholder="Fermentados, chochos, imaturos, germinados"
                float-label="Ferment/Chocho/Imat/Germinado"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div v-else class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="ardidos"
                placeholder="Ardidos (4%)"
                float-label="Ardidos (4%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 2" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="total"
                placeholder="Total (5%)"
                float-label="Total (5%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div v-else class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="fermentados"
                placeholder="Fermentados, chochos, imaturos, germinados"
                float-label="Ferment/Chocho/Imat/Germinado"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 2" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="carunchados"
                placeholder="Carunchados (2%)"
                float-label="Carunchados (2%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-else>
            <q-field>
              <q-input
                v-model="picados"
                placeholder="Picados (8%)"
                float-label="Picados (8%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 2" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="partidos"
                placeholder="Partidos/quebrados (3%)"
                float-label="Partidos/quebrados (3%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div v-else class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="total"
                placeholder="Total (8%)"
                float-label="Total (8%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="partidos"
                placeholder="Partidos, Quebrados, Amassados (30%)"
                float-label="Partidos, Quebrados, Amassados (30%)"
                :disable="modoVisualizacao == true"
                type="number"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-if="codProduto == 1">
            <q-field>
              <q-input
                v-model="esverdeados"
                placeholder="Esverdeados (8%)"
                float-label="Esverdeados (8%)"
                :disable="modoVisualizacao == true"
                type="tel"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-else>
            <q-field>
              <q-input
                v-model="observacao"
                placeholder="Observao"
                float-label="Observao"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="nomeMotorista"
                placeholder="Nome do Motorista"
                float-label="Nome do Motorista"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="ordemCarga"
                placeholder="Ordem de Carga"
                float-label="Ordem de Carga"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-if="codProduto == 1">
            <q-field>
              <q-input
                v-model="cnpj"
                placeholder="CNPJ Participante"
                float-label="CNPJ Participante"
                :disable="modoVisualizacao == true || (codProduto == 1 && tipoProdutoSojaSelecionado != 8)"
              />
            </q-field>
          </div>
          <!-- 
            Aguardar Liberao
          -->
          <ContainerArquivos
            ref="containerArquivos"
            class="col-12"
            v-show="clienteAlfameta.ModuloArquivos === true"
          />
        </div>
      </div>
    </q-modal-layout>
  </q-modal>
</template> 

<script>
import { getCookie } from "../../js/Cookies.js";
import { detectaCliente } from "../../utils/DetectaCliente.js";
import ContainerArquivos from "@/components/AreaCliente/components/ContainerArquivos.vue";
export default {
  name: "modalClassificacao",
  components: {
    ContainerArquivos,
  },
  data() {
    return {
      cnpj: "",
      numeroLaudo: "",
      dataClassificacao: "",
      ordemCarga: "",
      codOrdemServicoClassificacao: "",
      observacao: "",
      observacaoInterna: "",
      arquivo: "",
      nomeMotorista: "",
      umidade: "",
      impureza: "",
      ardidosEQueimados: "",
      queimados: "",
      fermentados: "",
      ardidos: "",
      mofados: "",
      partidos: "",
      esverdeados: "",
      picados: "",
      carunchados: "",
      total: "",
      transportadora: "",
      tituloModal: "",
      pesoAmostraTrabalhada: "",
      modoVisualizacao: true,
      tipoProdutoMilho: [
        { value: 1, label: "Convencional" },
        { value: 2, label: "Transgnico" },
        { value: 3, label: "Aflatoxina - Positivo" },
        { value: 4, label: "Aflatoxina - Negativo" },
        { value: 5, label: "No testado" },
      ],
      tipoProdutoSojaSelecionado: "",
      tipoProdutoSoja: [
        { value: 7, label: "Intacta Declarada" },
        { value: 8, label: "Participante" },
        { value: 9, label: "Testada Positiva" },
        { value: 10, label: "Testada Negativa" },
        { value: 11, label: "No Testada" },
      ],
      tipoTesteOGMSelecionado: "",
      tipoTesteOGM: [
        { value: 1, label: "No testado" },
        { value: 2, label: "Positivo" },
        { value: 3, label: "Negativo" },
      ],
      tipoProdutoMilhoSelecionado: "",
      codItemServicoClassificacao: "",
      todosDadosCarregados: false,
      exibe: false,
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      pesoLiquido: "",
      placaCavalo: "",
      placa1: "",
      placa2: "",
      codProduto: "",
      codMunicipio: "",
      codClassificador: "",
      codCliente: "",
      tokenClienteLogado: "",
      clienteAlfameta: null
    };
  },
  methods: {
    carregaCampos(dadosOs) {
      this.codCliente = getCookie("codcliente");
      this.codOrdemServicoClassificacao = dadosOs.CodOrdemServicoClassificacao;
      this.codItemServicoClassificacao =
        dadosOs.CodItemOrdemServicoClassificacao;
      this.codProduto = dadosOs.CodProduto;
      this.pesoLiquido = dadosOs.PesoLiquido;
      this.placaCavalo = dadosOs.PlacaCavalo;
      this.numeroLaudo = dadosOs.NumeroLaudo;
      this.placa1 = dadosOs.Placa1;
      this.placa2 = dadosOs.Placa2;
      this.ordemCarga = dadosOs.OrdemCarga;
      this.observacao = dadosOs.Observacao;
      this.codMunicipio = dadosOs.CodMunicipio;
      this.observacaoInterna = dadosOs.ObservacaoInterna;
      this.nomeMotorista = dadosOs.NomeMotorista;
      this.transportadora = dadosOs.Transportadora;
      this.impureza = dadosOs.Impureza;
      this.partidos = dadosOs.PartidoQuebradoAmassado;
      this.fermentados = dadosOs.FermentadoChochoImaturoGerminado;
      this.umidade = dadosOs.Umidade;
      this.pesoAmostraTrabalhada = dadosOs.PesoAmostraTrabalhada;
      this.tipoTesteOGMSelecionado = dadosOs.CodTesteOGM;
      if (this.codProduto == 1) {
        this.tipoProdutoSojaSelecionado = dadosOs.TipoProduto;
        this.cnpj = dadosOs.CNPJ;
        this.ardidos = dadosOs.Ardidos;
        this.queimados = dadosOs.Queimados;
        this.picados = dadosOs.Picados;
        this.esverdeados = dadosOs.Esverdeados;
      } else {
        this.tipoProdutoMilhoSelecionado = dadosOs.TipoProduto;
        this.ardidosEQueimados = dadosOs.ArdidosEQueimados;
        this.carunchados = dadosOs.Carunchados;
      }
      this.total = dadosOs.TotalAvariado;
      const admin = getCookie("su");
      if (admin != 0) {
        this.codClassificador = dadosOs.CodClassificador;
      } else {
        this.codClassificador = getCookie("id");
      }
      dadosOs.CodCliente = this.codCliente;
      dadosOs.EnviadoPor = "CLI";
      this.$refs.containerArquivos.setaInformacaoOs(dadosOs);
      this.$refs.containerArquivos.listaArquivos();
    },
    limpaCampos() {
      this.pesoLiquido = "";
      this.placaCavalo = "";
      this.placa1 = "";
      this.placa2 = "";
      this.codProduto = "";
      this.codMunicipio = "";
      this.codClassificador = "";
      this.tipoProdutoSojaSelecionado = "";
      this.cnpj = "";
      this.tipoProdutoMilhoSelecionado = "";
      this.dataClassificacao = "";
      this.ordemCarga = "";
      this.codOrdemServicoClassificacao = "";
      this.observacao = "";
      this.observacaoInterna = "";
      this.nomeMotorista = "";
      this.umidade = "";
      this.impureza = "";
      this.ardidosEQueimados = "";
      this.queimados = "";
      this.fermentados = "";
      this.ardidos = "";
      this.partidos = "";
      this.esverdeados = "";
      this.picados = "";
      this.carunchados = "";
      this.total = "";
      this.transportadora = "";
      this.tituloModal = "";
      this.pesoAmostraTrabalhada = "";
    },
    abreModalVisualizacao(dadosOs) {
      this.tituloModal = "Visualizando classificao";
      this.exibe = true;
      this.carregaCampos(dadosOs);
      this.modoVisualizacao = true;
    },
    fechaModal() {
      this.$refs.containerArquivos.limpaUploader();
      this.limpaCampos();
      this.limpaMensagensErro();
    },
    detectaCliente() {
      detectaCliente()
        .then((result) => {
          this.clienteAlfameta = result;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
  },
  mounted() {
    this.tokenClienteLogado = getCookie("tkCliente");
    this.detectaCliente();
  },
};
</script>

<style scoped>
.tira-margin-pading {
  padding: 0;
  margin: 0;
}
.margin-form {
  padding: 5px;
}
.distancia-borda-form {
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px;
}
.centraliza-titulo {
  margin-top: -25px;
  text-align: center;
  font-size: 20px;
}
@media screen and (max-width: 768px) {
  .margin-form {
    padding: 0px;
  }
  .lagura-modal {
    width: 100vh;
  }
}
</style>
