<template>
  <q-layout>
    <cabecalho />
    <q-page-container>
      <div class="container">
        <tabela-ordem-servico-classificacao-conab ref="gradeTabelaOrdemServicoClassificacaoConabAbertas" nomeTabela="O.S Conab abertas" @atualizaTabelaFinalizada="atualizaTabelaFinalizada"/>
        <tabela-ordem-servico-classificacao-conab v-if="administrador" ref="gradeTabelaOrdemServicoClassificacaoConabFinalizadas" nomeTabela="O.S Conab Finalizadas" visualizaFinalizados @atualizaTabelaReaberta="atualizaGradeOs"/>
      </div>
    </q-page-container>
  </q-layout>
</template>

<script>
import Cabecalho from '../components/Cabecalho.vue'
import { getCookie } from '../components/js/Cookies';
import TabelaOrdemServicoClassificacaoConab from '../components/TabelaOrdemServicoClassificacaoConab.vue'
export default {
  components: {
    TabelaOrdemServicoClassificacaoConab,
    Cabecalho
  },
  data(){
    return{
      administrador: false,
      supervisor: false
    }
  },
  methods:{
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    atualizaTabelaFinalizada(){
      this.$refs.gradeTabelaOrdemServicoClassificacaoConabFinalizadas.carregaTabelaApenasFinalizadas();
    },
    atualizaGradeOs(){
      this.$refs.gradeTabelaOrdemServicoClassificacaoConabAbertas.carregaTabela();
    }
  },
  beforeMount(){
    this.verificaAdministrador();
  }
};
</script>

