<template>
  <div class="container">
    <div class="row col-12 justify-center esconde-botao-impressao" v-show="layoutCelular">
      <div class="distancia-esquerda-logo">
        <img :src="logo" class="logo-download" />
      </div>
    </div>
    <div class="row" id="gavilon" v-show="exibeLayout">
      <div class="col-12 row">
        <div class="col-3 distancia-esquerda-logo">
          <img :src="logo" class="logo" />
        </div>
        <div class="col-8">
          <div class="nome-empresa">CLODUALDO MARTINS DOS SANTOS - ME</div>
          <div class="informacoes-empresa">
            CNPJ: 10.594.667/0001-59
            <br />Rua das Amricas, 515-A - Centro - Sorriso - MT
            <br />Telefone: (66) 3544-8059 Celular: (66) 99982-9202
          </div>
        </div>
      </div>
      <div class="col-12 row margin-secoes-padrao">
        <div class="offset-3 laudo-classificacao col-6">LAUDO DE CLASSIFICAO</div>
        <div class="numero-laudo">{{numeroLaudo}}</div>
      </div>
      <div class="col-12 row margin-secoes-padrao distancia-margem-esquerda-padrao">
        <div class="col-12 distancia-interna-campos">CLIENTE: {{nomeCliente}}</div>
        <div class="col-12 distancia-interna-campos">LOCAL EMBARQUE: {{localEmbarque}}</div>
        <div class="col-12 distancia-interna-campos">DESTINO: {{nomeDestino}} - {{UFDestino}}</div>
        <div class="col-12 margin-secoes-padrao">DESCRIO DO ITEM: {{nomeProduto}}</div>
      </div>
      <div
        class="col-11 distancia-margem-esquerda-padrao margin-secoes-padrao"
        v-if="codProduto == 1"
      >
        <table cellspacing="0">
          <thead>
            <tr>
              <th colspan="2">Placa Caminho: {{placaCavalo}}</th>
              <th colspan="2">Transportadora: {{transportadora}}</th>
              <th colspan="2">Peso Lquido: {{pesoLiquido}}</th>
            </tr>
            <tr>
              <th>Local</th>
              <th>N</th>
              <th>Descrio</th>
              <th>Resultado</th>
              <th>Exame</th>
              <th>Classificador</th>
            </tr>
          </thead>
          <tbody v-for="itens in estruturaTabelaSoja" :key="itens.id">
            <td>{{itens.local}}</td>
            <td>{{itens.n}}</td>
            <td>{{itens.descricao}}</td>
            <td>{{itens.resultado}}</td>
            <td>{{itens.exame}}</td>
            <td>{{itens.classificador}}</td>
          </tbody>
        </table>
        <div class="row col-12">
          <div class="row col-12">
            <div class="col-2 borda-esquerda borda-inferior" style="text-align:center">Monsato</div>
            <div class="borda-esquerda borda-inferior borda-direita">
              <q-checkbox v-model="intactaDeclarada" val="true" :disable="true" />
            </div>
            <div class="col-3 borda-inferior borda-direita">Intacta Declarada</div>
          </div>
          <div class="offset-2 col-12 row">
            <div class="borda-esquerda borda-inferior borda-direita">
              <q-checkbox v-model="testeNegativoSoja" val="true" :disable="true" />
            </div>
            <div class="col-3 borda-inferior borda-direita">Testada Negativa</div>
          </div>
          <div class="offset-2 col-12 row">
            <div class="borda-esquerda borda-inferior borda-direita">
              <q-checkbox v-model="testePositivoSoja" val="true" :disable="true" />
            </div>
            <div class="col-3 borda-inferior borda-direita">Testada Positiva</div>
          </div>
          <div class="offset-2 col-12 row">
            <div class="borda-esquerda borda-inferior borda-direita">
              <q-checkbox v-model="participante" val="true" :disable="true" />
            </div>
            <div class="col-3 borda-inferior borda-direita">Participante</div>
            <div
              class="col-5 borda-inferior borda-direita borda-superior"
            >CNPJ Participante: {{cnpj}}</div>
          </div>
          <div class="offset-2 col-12 row">
            <div class="borda-esquerda borda-inferior borda-direita">
              <q-checkbox v-model="naoTestadaSoja" val="true" :disable="true" />
            </div>
            <div class="col-3 borda-inferior borda-direita">No Testada</div>
          </div>
        </div>
      </div>
      <div class="col-11 distancia-margem-esquerda-padrao margin-secoes-padrao" v-else>
        <table cellspacing="0">
          <thead>
            <tr>
              <th colspan="2">Placa Caminho: {{placaCavalo}}</th>
              <th colspan="2">Transportadora: {{transportadora}}</th>
              <th colspan="2">Peso Lquido: {{pesoLiquido}}</th>
            </tr>
            <tr>
              <th>Local</th>
              <th>N</th>
              <th>Descrio</th>
              <th>Resultado</th>
              <th>Exame</th>
              <th>Classificador</th>
            </tr>
          </thead>
          <tbody v-for="itens in estruturaTabelaMilho" :key="itens.id">
            <td>{{itens.local}}</td>
            <td>{{itens.n}}</td>
            <td>{{itens.descricao}}</td>
            <td>{{itens.resultado}}</td>
            <td>{{itens.exame}}</td>
            <td>{{itens.classificador}}</td>
          </tbody>
        </table>
        <div class="row col-12">
          <div class="row col-12">
            <div
              class="col-2 borda-esquerda borda-inferior"
              style="text-align:center"
            >Teste Aflatoxina</div>
            <div class="borda-esquerda borda-direita borda-inferior">
              <q-checkbox v-model="testeNegativoMilho" val="true" :disable="true" />
            </div>
            <div class="col-3 borda-inferior borda-direita">Teste Negativo</div>
          </div>
          <div class="offset-2 col-12 row">
            <div class="borda-direita borda-inferior borda-esquerda">
              <q-checkbox v-model="testePositivoMilho" val="true" :disable="true" />
            </div>
            <div class="col-3 borda-inferior borda-direita">Teste Positivo</div>
          </div>
          <div class="offset-2 col-12 row">
            <div class="borda-direita borda-esquerda borda-inferior">
              <q-checkbox v-model="testeNaoRealizadoMilho" val="true" :disable="true" />
            </div>
            <div class="col-3 borda-inferior borda-direita">Teste No Realizado</div>
          </div>
        </div>
      </div>
      <div class="col-12 row distancia-margem-esquerda-padrao margin-secoes-padrao">
        <div class="col-3">Data: {{dataClassificacao}}</div>
        <div class="offset-1 col-6">Nome Completo Classificador: {{nomeClassificador}}</div>
      </div>
      <div
        class="col-12 margin-interna-campos fonte-url-classificacao distancia-margem-esquerda-padrao margin-secoes-padrao"
      >
        Classificao disponivel online em:
        <a :href="urlClassificacao">{{urlClassificacao}}</a>
      </div>
    </div>
    <div
      class="text-center distancia-botoes-acao fonte-explicacao-download esconde-botao-impressao"
    >Faa o download em PDF de todos os dados da classificao {{numeroLaudo}} clicando no boto abaixo.</div>
    <div class="row col-12 justify-center">
      <q-btn
        icon="fas fa-file-pdf"
        label="Baixar PDF"
        @click="downloadPDF()"
        class="distancia-botoes esconde-botao-impressao cor-pdf col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
      <q-btn
        icon="fas fa-print"
        color="primary"
        label="Imprimir"
        @click="imprimirPagina()"
        class="distancia-botoes esconde-botao-impressao col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
    </div>
  </div>
</template>
<script>
// eslint-disable-next-line no-unused-vars
import jsPDF from "jspdf";
// eslint-disable-next-line no-unused-vars
import html2canvas from "html2canvas";
import html2pdf from "js-html2pdf";

export default {
  props: { dadosClassificacao: { type: Array } },
  data() {
    return {
      exibeLayout: false,
      dataClassificacao: "",
      intactaDeclarada: false,
      participante: false,
      cnpj: "",
      logo: require("../assets/logo-exata.png"),
      nomeCliente: "",
      localEmbarque: "",
      nomeDestino: "",
      nomeProduto: "",
      UFDestino: "",
      placaCavalo: "",
      transportadora: "",
      pesoLiquido: "",
      ardidos: "%",
      total: "",
      esverdeados: "",
      impureza: "",
      querbrados: "",
      queimados: "",
      umidade: "",
      picados: "",
      nomeClassificador: "",
      ardidosEQueimados: "",
      carunchados: "",
      codProduto: "",
      testeNegativoMilho: false,
      testePositivoMilho: false,
      testeNaoRealizadoMilho: false,
      municipioDestino: "",
      numeroLaudo: "",
      tipoProduto: "",
      testeNegativoSoja: false,
      testePositivoSoja: false,
      naoTestadaSoja: false,
      urlClassificacao: "",
      layoutCelular: false,
    };
  },
  methods: {
    carregaDados(dadosClassificacao) {
      this.dataClassificacao = dadosClassificacao.dataClassificacao;
      this.nomeCliente = dadosClassificacao.NomeCliente;
      this.localEmbarque = dadosClassificacao.LocalEmbarque;
      this.destino = dadosClassificacao.MunicipioDestino;
      this.placaCavalo = dadosClassificacao.PlacaCavalo;
      this.transportadora = dadosClassificacao.Transportadora;
      this.pesoLiquido = dadosClassificacao.PesoLiquido;
      this.codProduto = dadosClassificacao.CodProduto;
      this.nomeDestino = dadosClassificacao.MunicipioDestino;
      this.UFDestino = dadosClassificacao.UFDestino;
      this.total = dadosClassificacao.TotalAvariado;
      this.impureza = dadosClassificacao.Impureza;
      this.querbrados = dadosClassificacao.PartidoQuebradoAmassado;
      this.umidade = dadosClassificacao.Umidade;
      this.nomeClassificador = dadosClassificacao.nome;
      this.dataClassificacao = dadosClassificacao.DataClassificacao;
      this.numeroLaudo = dadosClassificacao.NumeroLaudo;
      this.tipoProduto = dadosClassificacao.TipoProduto;
      if (this.codProduto == 1) {
        this.nomeProduto = "SOJA EM GROS";
        this.ardidos = dadosClassificacao.Ardidos;
        this.esverdeados = dadosClassificacao.Esverdeados;
        this.queimados = dadosClassificacao.Queimados;
        this.picados = dadosClassificacao.Picados;
        if (this.tipoProduto == 7) {
          this.intactaDeclarada = true;
        } else {
          this.intactaDeclarada = false;
        }
        if (this.tipoProduto == 8) {
          this.participante = true;
          this.cnpj = dadosClassificacao.CNPJ;
        } else {
          this.cnpj = "";
          this.participante = false;
        }
        if (this.tipoProduto == 9) {
          this.testePositivoSoja = true;
        } else {
          this.testePositivoSoja = false;
        }
        if (this.tipoProduto == 10) {
          this.testeNegativoSoja = true;
        } else {
          this.testeNegativoSoja = false;
        }
        if (this.tipoProduto == 11) {
          this.naoTestadaSoja = true;
        } else {
          this.naoTestadaSoja = false;
        }
      } else {
        this.nomeProduto = "MILHO EM GROS";
        this.carunchados = dadosClassificacao.Carunchados;
        this.ardidosEQueimados = dadosClassificacao.ArdidosEQueimados;

        if (this.tipoProduto == 4) {
          this.testeNegativoMilho = true;
        } else {
          this.testeNegativoMilho = false;
        }
        if (this.tipoProduto == 3) {
          this.testePositivoMilho = true;
        } else {
          this.testePositivoMilho = false;
        }
        if (this.tipoProduto == 5) {
          this.testeNaoRealizadoMilho = true;
        } else {
          this.testeNaoRealizadoMilho = false;
        }
      }
    },
    downloadPDF() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = true;
      }
      setTimeout(() => {
        const elementoASerImprimido = document.getElementById("gavilon");
        const options = {
          filename: "Classificacao" + this.numeroLaudo + ".pdf",
          margin: 0,
          enableLinks: true,
          image: "png",
        };
        const exporter = new html2pdf(elementoASerImprimido, options);
        exporter
          .getPdf(true)
          .then(() => {
            if (this.$q.platform.is.mobile) {
              this.exibeLayout = false;
            }
          })
          .catch(() => {
            if (this.$q.platform.is.mobile) {
              this.exibeLayout = false;
            }
          });
      }, 700);
    },
    imprimirPagina() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = true;
      }
      setTimeout(() => {
        window.print();
        if (this.$q.platform.is.mobile) {
          setTimeout(() => {
            this.exibeLayout = false;
          }, 1000);
        }
      }, 1500);
    },
    detectaPlataforma() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = false;
        this.layoutCelular = true;
      } else {
        this.layoutCelular = false;
        this.exibeLayout = true;
      }
    },
  },
  beforeMount() {
    this.urlClassificacao = window.location.href;
    this.detectaPlataforma();
  },
  computed: {
    estruturaTabelaSoja() {
      return [
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Ardidos Soja",
          resultado: this.ardidos,
          exame: 1,
          classificador: this.nomeClassificador,
          id: 1,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Avariados Totais Soja",
          resultado: this.total,
          exame: 2,
          classificador: this.nomeClassificador,
          id: 2,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Esverdeados Soja",
          resultado: this.esverdeados,
          exame: 3,
          classificador: this.nomeClassificador,
          id: 3,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Impureza Soja",
          resultado: this.impureza,
          exame: 4,
          classificador: this.nomeClassificador,
          id: 4,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Quebrados Soja",
          resultado: this.querbrados,
          exame: 5,
          classificador: this.nomeClassificador,
          id: 5,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Queimados Soja",
          resultado: this.queimados,
          exame: 6,
          classificador: this.nomeClassificador,
          id: 6,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Umidade Soja",
          resultado: this.umidade,
          exame: 7,
          classificador: this.nomeClassificador,
          id: 7,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Picados Soja",
          resultado: this.picados,
          exame: "",
          classificador: this.nomeClassificador,
          id: 8,
        },
      ];
    },
    estruturaTabelaMilho() {
      return [
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Ardidos Milho",
          resultado: this.ardidosEQueimados,
          exame: 8,
          classificador: this.nomeClassificador,
          id: 1,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Avariados Milho",
          resultado: this.total,
          exame: 9,
          classificador: this.nomeClassificador,
          id: 2,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Impureza Milho",
          resultado: this.impureza,
          exame: 10,
          classificador: this.nomeClassificador,
          id: 4,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Quebrados Milho",
          resultado: this.querbrados,
          exame: 11,
          classificador: this.nomeClassificador,
          id: 5,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Umidade Milho",
          resultado: this.umidade,
          exame: 12,
          classificador: this.nomeClassificador,
          id: 7,
        },
        {
          local: this.nomeDestino,
          n: 1,
          descricao: "Carunchados Milho",
          resultado: this.carunchados,
          exame: 13,
          classificador: this.nomeClassificador,
          id: 8,
        },
      ];
    },
  },
};
</script>
<style scoped>
.fonte-explicacao-download {
  font-size: 22px;
  margin-bottom: 10px;
}
.cor-pdf {
  background-color: #ff0000;
  color: white;
}
.borda-superior {
  border-top: 1px solid black;
}
.borda-inferior {
  border-bottom: 1px solid black;
}
.borda-direita {
  border-right: 1px solid black;
}
.borda-esquerda {
  border-left: 1px solid black;
}
.tamanho-botao {
  height: 100px;
}
.fonte-botao {
  font-size: 28px;
}
@media only screen {
  .container {
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 30px;
  }
}
@media only print {
  .esconde-botao-impressao {
    display: none;
  }
}
.distancia-botoes-acao {
  margin-top: 35px;
}
.fonte-url-classificacao {
  font-family: sans-serif;
  font-size: 12px;
}
.distancia-botoes {
  margin: 5px;
  border-radius: 5px;
}
.fonte-botoes {
  font-family: Roboto;
  font-size: 16px;
}
.checkbox {
  scale: 1.2;
}
.borda {
  border: 1px solid black;
}
.nome-empresa {
  margin-top: 15px;
  font-size: 22px;
  text-transform: capitalize;
  text-align: center;
  font-weight: bold;
}
.informacoes-empresa {
  text-align: center;
}
.distancia-esquerda-logo {
  margin-left: 25px;
}
.logo-download {
  width: 35vw;
  height: auto;
}
.logo {
  width: 100%;
  margin-top: 35px;
  height: auto;
}
.numero-laudo {
  color: red;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 20px;
  margin-top: 0px;
}
.laudo-classificacao {
  text-align: center;
  font-weight: 900;
  font-size: 22px;
  padding: 0;
}
.margin-secoes-padrao {
  margin-top: 15px;
}
.distancia-margem-esquerda-padrao {
  margin-left: 25px;
}
.distancia-interna-campos {
  margin: 2px;
}
th {
  border: 1px solid black;
  font-size: 12px;
  padding: 5px;
}
td {
  border: 1px solid black;
  font-size: 18px;
  padding: 5px;
  text-align: center;
}
.teste {
  background-color: Red;
}
@media screen and (max-width: 1024px) {
  .logo-download {
    width: 100vw;
    height: auto;
    padding-right: 25px;
  }
}
</style>


