<template>
  <q-modal
    v-model="exibe"
    minimized
    class="modalLayout"
    :content-css="{minWidth:'50vw', minHeight: '30vh'}"
    no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn style="padding: 0px margin: 0" flat dense round icon="close" @click="cancelarView" />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>

      <q-toolbar slot="header" class="tituloDoModal">
        <h6 class="headerTitle">{{tituloModal}}</h6>
      </q-toolbar>

      <div class="row col-md- col-sm-12 col-xs-12 Campos">
        <div :class="classeCSS">
          <div class="row col-md-12">
            <q-field
              class="col-md-2 col-xs-12 margin-form uploaderImagem"
              style="align-items: center"
              v-if="!modoLeitura"
            >
              <form enctype="multipart/form-data">
                <input
                  type="file"
                  ref="inputFile"
                  accept="image/*"
                  style="display: none"
                  @change="carregarArquivoSelecionado"
                />
                <q-btn
                  color="primary"
                  style="width: 100% height: 15%"
                  icon="insert_photo"
                  label="Enviar Foto"
                  @click="arquivoSelecionado"
                />
              </form>
            </q-field>

            <q-field class="campoVisualiza col-md-3 col-xs-12 margin-form">
              <q-datetime
                v-model="data"
                placeholder="Data"
                float-label="Data"
                filled
                modal
                format-model="string"
                format="DD/MM/YYYY"
                :readonly="modoLeitura"
                :error="erroDespesa"
              />
            </q-field>

            <q-field class="col-md-3 col-xs-12 campoVisualiza margin-form">
              <q-select
                filled
                v-model="tipoDespesaSelecionada"
                float-label="Tipo da Despesa"
                :options="opcoesTipoDespesa"
                :readonly="modoLeitura"
                :error="erroDespesa"
              />
            </q-field>

            <q-field class="col-md-4 col-xs-12 campoVisualiza margin-form">
              <q-select
                v-model="classificadorSelecionado"
                filled
                float-label="Nome Classificador"
                :options="classificador"
                :readonly="modoLeitura"
                :error="erroDespesa"
              />
            </q-field>

            <q-field class="col-md-2 col-xs-12 campoVisualiza margin-form">
              <q-input
                v-model="valorDespesa"
                float-label="Valor"
                :readonly="modoLeitura"
                :type="modoLeitura === true ? 'text' : 'number'"
                :decimals="2"
                :min="0"
                :error="erroDespesa"
              ></q-input>
            </q-field>

            <q-field class="col-md-2 col-xs-12 campoVisualiza margin-form" v-if="!modoLeitura">
              <q-select
                multiple
                v-model="osSelecionadas"
                filled
                separator
                style="overflow: hidden text-overflow: ellipsis white-space: pre"
                float-label="O.S Vinculada"
                @input="val => osSelecionadas = val === null ? [] : val"
                :options="ordemServicoClassificacao"
                :readonly="modoLeitura"
                :error="erroDespesa"
              />
            </q-field>
            <q-field class="col-md-2 col-xs-12 campoVisualiza margin-form" v-else>
              <q-input
                v-model="osvinculadas"
                type="text"
                label="O.S Vinculadas"
                float-label="O.S Vinculadas"
                :readonly="modoLeitura"
                :error="erroDespesa"
              />
            </q-field>

            <q-field
              :class="modoLeitura === true ? 'campoVisualiza col-md-7 col-xs-12 margin-form' : 'campoVisualiza col-md-8 col-xs-12 margin-form'"
            >
              <q-input
                v-model="movcontafinanceiraclassificador.historicoDespesa"
                float-label="Histrico"
                :readonly="modoLeitura"
                :error="erroDespesa"
              ></q-input>
            </q-field>

            <q-field class="col-md-3 col-xs-12 campoVisualiza margin-form" v-if="modoLeitura">
              <q-input
                v-model="movcontafinanceiraclassificador.statusDespesa"
                float-label="Status"
                readonly
                :error="erroDespesa"
              ></q-input>
            </q-field>
          </div>
        </div>
      </div>

      <div class="row col-12 q-mb-md" style="justify-content: center">
        <q-btn
          @click="verDespesa"
          label="Visualizar Imagem da Despesa"
          class="btn_visualizarImagem"
          color="primary"
          v-if="modoLeitura"
        />
        <q-btn
          @click="salvarNovaDespesa"
          class="q-ml-md"
          color="primary"
          label="Salvar"
          v-if="!modoLeitura"
        />
        <q-btn @click="fechaModal" class="q-ml-md" color="red" label="Fechar" />
      </div>
    </q-modal-layout>
  </q-modal>
</template>

<script>
import {
  postFormData,
  get,
  getComCabecalho,
  post,
} from "../components/js/ConexaoApi.js";

export default {
  name: "visualizarDespesa",
  data() {
    return {
      arquivo: [],
      classificadorSelecionado: "",
      classificador: [],
      osSelecionadas: [],
      osvinculadas: "",
      ordemServicoClassificacao: [],
      data: "",
      exibe: false,
      modoLeitura: false,
      erroDespesa: false,
      modoVisualizacao: false,
      valorDespesa: Number,
      movcontafinanceiraclassificador: [
        { codigoDaDespesa: "" },
        { nomeTipoDespesa: "" },
        { historicoDespesa: "" },
        { statusDespesa: "" },
        { motivoRejeicao: "" },
        { imagem: "" },
        { nomeClassificador: "" },
      ],
      opcoesTipoDespesa: [
        { value: 1, label: "Alimentao" },
        { value: 2, label: "Estacionamento" },
        { value: 3, label: "Hospedagem" },
        { value: 4, label: "Txi" },
        { value: 5, label: "Passagem" },
        { value: 6, label: "Pedgio" },
        { value: 7, label: "Combustvel" },
        { value: 8, label: "Outro" },
      ],
      tipoDespesaSelecionada: [],
      classeCSS: "",
      tituloModal: "",
    };
  },
  methods: {
    visualizaDespesa(movcontafinanceiraclassificador) {
      this.classeCSS = "col-xs-12 col-md-12 margin-form espacamento";
      this.exibe = true;
      this.modoVisualizacao = true;
      this.modoLeitura = true;
      this.tituloModal = "Visualizando Despesas";
      this.carregarCampos(movcontafinanceiraclassificador);
    },
    limparCampos() {
      this.erroDespesa = false;
      this.tipoDespesaSelecionada = "";
      this.movcontafinanceiraclassificador.codigoDaDespesa = "";
      this.movcontafinanceiraclassificador.historicoDespesa = "";
      this.valorDespesa = "";
      this.movcontafinanceiraclassificador.motivoRejeicao = "";
      this.movcontafinanceiraclassificador.imagem = "";
      this.osSelecionadas = [];
      this.data = "";
      this.classificadorSelecionado = "";
    },
    cancelarView() {
      if (this.modoVisualizacao != false) {
        this.$q
          .dialog({
            title: "Ateno",
            message: "Deseja sair da Tela de Visualizao ?",
            color: "negative",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => this.fecharView())
          .catch(() => {});
      } else {
        this.fecharView();
      }
    },
    fecharView() {
      this.exibe = false;
      this.modoVisualizacao = false;
    },
    fechaModal() {
      this.fecharView();
    },
    insereNovaDespesa() {
      this.limparCampos();
      this.classeCSS = "col-xs-12 col-md-12 margin-form";
      this.tituloModal = "Incluso de Nova Despesa";
      this.exibe = true;
      this.modoLeitura = false;
      this.erroDespesa = false;
    },
    carregarCampos(movcontafinanceiraclassificador) {
      this.limparCampos();
      this.movcontafinanceiraclassificador.codigoDaDespesa =
        movcontafinanceiraclassificador.CodDespesa;
      this.movcontafinanceiraclassificador.historicoDespesa =
        movcontafinanceiraclassificador.Historico;

      // https://developer.mozilla.org/pt-BR/docs/Web/JavaScript/Reference/Global_Objects/Number/toLocaleString
      this.valorDespesa = Number(
        movcontafinanceiraclassificador.Valor.replace(/,/g, ".")
      ).toLocaleString("pt-BR", {
        minimumFractionDigits: 2,
        currency: "BRL",
        style: "currency",
      });

      this.classificadorSelecionado =
        movcontafinanceiraclassificador.CodClassificador;
      this.movcontafinanceiraclassificador.motivoRejeicao =
        movcontafinanceiraclassificador.MotivoRecusa;

      this.movcontafinanceiraclassificador.imagem =
        movcontafinanceiraclassificador.UrlImagem;

      this.tipoDespesaSelecionada = parseInt(
        movcontafinanceiraclassificador.CodTipoDespesa
      );
      this.osvinculadas = movcontafinanceiraclassificador.OsVinculadas;
      this.data = movcontafinanceiraclassificador.DataLancamento;
      if (movcontafinanceiraclassificador.Status == "A") {
        this.movcontafinanceiraclassificador.statusDespesa = "Despesa Aceita";
      } else if (movcontafinanceiraclassificador.Status == "R") {
        this.movcontafinanceiraclassificador.statusDespesa =
          "Despesa Rejeitada";
      } else {
        this.movcontafinanceiraclassificador.statusDespesa =
          "Esperando resposta";
      }
    },
    verDespesa() {
      window.open(this.movcontafinanceiraclassificador.imagem);
    },
    arquivoSelecionado() {
      this.$refs.inputFile.click();
    },
    carregarArquivoSelecionado(evento) {
      this.arquivo = evento.target.files[0];
    },
    salvarNovaDespesa() {
      if (this.valorDespesa === undefined || Number(this.valorDespesa) <= 0) {
        this.erroDespesa = true;
        this.$q.notify({
          message: "Por favor, informe um valor vlido para a despesa",
          color: "negative",
        });
      }
      let moedaBrasileira = 0;
      if (this.valorDespesa !== undefined) {
        moedaBrasileira = this.valorDespesa.toString().replace(/,/g, ".");
        moedaBrasileira = Number(moedaBrasileira).toFixed(2);
      }
      const enviar = new FormData();
      enviar.append(
        "Historico",
        this.movcontafinanceiraclassificador.historicoDespesa
      );
      enviar.append("Valor", moedaBrasileira);
      enviar.append("CodTipoDespesa", this.tipoDespesaSelecionada);
      enviar.append("CodClassificador", this.classificadorSelecionado);
      enviar.append("DataLancamento", this.data);
      enviar.append("file", this.arquivo);
      enviar.append("Status", 'A');
      enviar.append("EnviaApp", 'S');

      postFormData("adicionadespesa", enviar)
        .then((result) => {
          this.$q.notify({
            message: "Despesa criada com sucesso!",
            color: "green",
          });
          const codMovContaFinanceiraClassificador = JSON.parse(result)
            .CodMovContaFinanceiraClassificador;

          const vinculos = [];
          this.osSelecionadas.forEach((v) => {
            const vinculo = {};
            vinculo.CodOrdemServicoClassificacao = v;
            vinculo.CodMovContaFinanceiraClassificador = codMovContaFinanceiraClassificador;
            vinculos.push(vinculo);
          });
          post("vinculadespesaos", vinculos)
            .then(() => {
              setTimeout(() => {
                this.fecharView();
              }, 2000);
              this.$emit("atualizaTabela");
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              console.error(err);
            });
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
          const resposta = JSON.parse(err);
          this.$q.notify({
            message: resposta.msg,
            color: "red",
          });
        });
      // }
    },
    carregaDados() {
      // Carrega os classificadores
      get("classificadores")
        .then((resultado) => {
          resultado = resultado.replace(/"id":/g, '"value":');
          resultado = resultado.replace(/"nome":/g, '"label":');
          this.classificador = JSON.parse(resultado);
        })
        .catch((erro) => {
          // eslint-disable-next-line no-console
          console.error(erro);
        });
      getComCabecalho("osdespesa", "administrador", 1)
        .then((resultado) => {
          this.ordemServicoClassificacao = JSON.parse(resultado);
        })
        .catch((erro) => {
          // eslint-disable-next-line no-console
          console.error(erro);
        });
    },
  },
  mounted() {
    this.carregaDados();
  },
};
</script>

<style scoped>
.headerTitle {
  padding: 0;
  margin: 0;
}

.margin-form {
  padding: 5px;
}

@media screen and (max-width: 575px) {
  .btn_visualizarImagem {
    margin-bottom: 5px;
  }

  .imagemModal img {
    margin-top: 10px;
    width: 100%;
    margin-left: 5px;
    border-radius: 5px;
    border: 2px solid #1b1f5f;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  .Campos {
    flex-direction: column;
    width: 95%;
    padding: 0;
    margin: 0;
  }

  .campoVisualiza {
    margin-bottom: 10px;
  }
}

@media screen and (min-width: 576px) {
  .margin-form {
    padding: 5px;
  }
  .imagemModal img {
    height: 48vh;
    width: 24vw;
    margin-left: 6px;
    margin-top: 10px;
    box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
    border: 2px solid #1b1f5f;
  }

  .campoVisualiza {
    margin-bottom: 20px;
  }

  .uploaderImagem {
    margin-top: 0px;
  }
}

@media screen and (min-width: 1400px) {
  .imagemModal img {
    height: 33vh;
    width: 17vw;
    margin-left: 50px;
    margin-top: 5px;
    box-shadow: 0 4px 6px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
    border: 2px solid #1b1f5f;
  }

  .uploaderImagem {
    margin-top: 0px;
  }
}
</style>