<template>
  <q-layout-header>
    <q-toolbar color="primary">
      <q-toolbar-title>clienteAlfameta.Nome</q-toolbar-title>
      <q-btn @click="drawer = !drawer" flat round icon="menu"/>
    </q-toolbar>
    <q-layout-drawer v-model="drawer" side="right" :overlay="true" behavior="mobile">
      <q-list-header class="tamanho-font-menu">Menu</q-list-header>
      <q-item
        v-for="opcoes in opcoesMenu"
        :key="opcoes.id"
        :inset-separator="true"
        >
        <q-btn flat @click="opcoes.click" dense>
          <q-item-side :icon="opcoes.icone" />
          <q-item-main :label="opcoes.label" />
        </q-btn>
      </q-item>
    </q-layout-drawer>
  </q-layout-header>
</template>

<script>
import {verificaSeEstaLogado} from "../js/VerificaSeEstaLogado.js";
import {deleteCookie} from "../../js/Cookies.js";
import {get} from "../js/ConexaoApiCliente.js";
import {detectaCliente} from '../../utils/DetectaCliente.js'
export default {
  data() {
    return {
      nome: "",
      clienteAlfameta: null,
      administrador: false,
      drawer: true,
      opcoesMenu: [
        {
          icone: 'description',
          label: 'Minhas solicitaces',
          click: () => this.abreSolicitacoes(),
          id: 2
        },
        {
          icone: "mdi-clipboard-text-outline",
          label: "Ordens de servio",
          click: () => this.abreTelaInicialAreaCliente(),
          id: 1
        },
        {
          icone: "mdi-exit-to-app",
          label: "Sair",
          click: () => this.fazLogout(),
          id: 3
        }
      ]
    };
  },
  methods: {
    fazLogout() {
      this.$q
        .dialog({
          title: "Confirmao de saida",
          message: "Deseja realmente sair?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top"
        })
        .then(() => {
          get("logoff");
          deleteCookie("tkCliente");
          deleteCookie("cnpj");
          deleteCookie("email");
          deleteCookie("codcliente");
          deleteCookie("layout");
          deleteCookie("nomeCliente");
          this.$router.push("/areacliente");
        })
        .catch(() => {});
    },
    selecionaCliente() {
      detectaCliente().then((result) => {
        this.clienteAlfameta = result;
      }).catch((err) => {
        // eslint-disable-next-line no-console
        console.error(err);
      });
    },
    verificaSeEstaLogado() {
      verificaSeEstaLogado();
    },
    abreSolicitacoes(){
      this.$router.push("/areacliente/solicitacoes");
    },
    abreTelaInicialAreaCliente(){
      this.$router.push("/areacliente/principal");
    }
  },
  created() {
    this.selecionaCliente();
    this.verificaSeEstaLogado();
  }
};
</script>


<style scoped>
.tamanho-font-menu {
  font-size: 22px;
}
.altura-btn {
  height: 100%;
}
.centralizado {
  justify-content: center;
}
.logo-responsiva {
  width: 190px;
  height: auto;
}
.bg {
  background-color: blue;
}
</style>
