<template>
  <q-modal
      v-model="exibe"
      minimized
      :content-css="{width: '55vw', minHeight: '45vh'}"
      no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn
            style="padding:0px; margin:0"
            flat
            round
            dense
            icon="close"
            @click="confirmarCancelamentoDeCadastroDeOs"
        />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>
      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{ tituloModal }}</h6>
      </q-toolbar>
      <div class="layout-padding">
        <div class="row">
          <div class="col-md-12 col-sm-12 col-xs-12 margin-form">
            <q-input
                v-model="termoBuscaClassificador"
                placeholder="Escolha um classificador"
                float-label="Escolha um classificador"
                :disable="modoVisualizacao == true"
            >
              <q-autocomplete
                  :static-data="{field:'label', list:classificadores}"
                  @selected="classificadorSelecionado"
              />
            </q-input>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-input
                v-model="motivo"
                placeholder="Motivo"
                float-label="Motivo"
                :disable="modoVisualizacao == true"
            />
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-input
                v-model="observacao"
                placeholder="Observao"
                float-label="Observao"
                :disable="modoVisualizacao == true"
            />
          </div>
          <div class="col-12 margin-form" v-show="!this.variasDatas">
            <q-datetime
                v-model="data"
                placeholder="Data"
                float-label="Data"
                type="date"
                :max="hoje"
                format-model="string"
                format="DD/MM/YYYY"
                :disable="modoVisualizacao == true"
            />
          </div>
          <div class="col-md-2 col-sm-12 col-xs-12 margin-form" v-show="this.variasDatas">
            <q-btn
                @click="$refs.calendario.show()"
                flat
                align="left"
                size="11px"
                :disable="modoVisualizacao == true">
              <div class="row">
                <div class="col-md-4">
                  <q-icon name="event" size="30px"/>
                </div>
                <div class="col-md-4 margin-form">
                  <p>Adicionar data</p>
                </div>
              </div>
            </q-btn>
          </div>
          <div class="col-md-10 col-sm-12 col-xs-12 margin-form" v-show="this.variasDatas">
            <q-chips-input
                v-model="datas"
                float-label=""
                hide-underline
                @keydown="(e) => {e.preventDefault()}"
                @blur="validaDatas"
                :disable="modoVisualizacao == true"
                :readonly="this.datas.length == 0"/>
          </div>
          <q-modal ref="calendario">
            <q-datetime-picker
                value=""
                @change="addData"
                :max="hoje"
                type="date"
                format-model="string"
            >
              <q-btn
                  flat
                  icon="close"
                  label="Fechar"
                  @click="$refs.calendario.hide()"/>
            </q-datetime-picker>
          </q-modal>
          <!--<q-input v-model="data"   type="date"   :disable="modoVisualizacao == true"/>-->
          <div class="col-12" style="margin-top:10px;">
            <q-btn
                color="primary"
                label="Salvar"
                @click="() => {variasDatas?confirmarLancamentoVariasJustificativas():comportamentoBotaoSalvar()}"
                v-show="modoVisualizacao != true"
            />
          </div>
        </div>
        <div style="margin-top:10px">
          <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
        </div>
      </div>
    </q-modal-layout>
  </q-modal>
</template>

<script>
import {get, post} from "../components/js/ConexaoApi.js";

export default {
  name: "modalJustificativas",
  components: {},
  data() {
    return {
      hoje: this.getDataAtual(),
      idJustificativa: "",
      tituloModal: "",
      motivo: "",
      observacao: "",
      data: "",
      datas: [],
      variasDatas: false,
      termoBuscaClassificador: "",
      codClassificadorSelecionado: "",
      modoVisualizacao: false,
      exibe: false,
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      alteraInsere: "",
      classificadores: [],
      codOrdemServicoClassificacao: "",
    };
  },
  methods: {
    confirmarLancamentoVariasJustificativas() {
      this.$q
          .dialog({
            title: "Lanamento de justificativa",
            message: "Deseja realmente inserir " + this.datas.length + " justificativa" + (this.datas.length > 1 ? "s" : "") + "?",
            color: "negative",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => {
            this.comportamentoBotaoSalvar();
          })
          .catch(() => {
          });
    },
    getDataAtual() {
      let date = new Date();
      date.setDate(date.getDate() + 1);
      let day = (date.getDate()) > 9 ? (date.getDate()) : '0' + (date.getDate());
      let month = (date.getMonth() + 1) > 9 ? (date.getMonth() + 1) : '0' + (date.getMonth() + 1);
      let year = date.getFullYear();
      let formatedDate = year + '-' + (month) + '-' + (day) + 'T00:00:00.000Z';
      // eslint-disable-next-line no-console
      console.log(formatedDate);
      return formatedDate;
    },
    validaDatas() {
      this.datas.forEach(d => {
        let dia = d.substring(0, 2);
        let mes = d.substring(3, 5);
        let ano = d.substring(6, 10);
        let dataFormatada = mes + "/" + dia + "/" + ano;
        let date = new Date(dataFormatada);
        if (isNaN(date.getDate()) || !d.includes("/")) {
          this.datas.pop(d)
        }
      })
    },
    addData(val) {
      if (!val) {
        return
      }

      let date = new Date(val)

      val = (date.getDate() > 9 ? date.getDate() : '0' + date.getDate()) + '/' +
          ((date.getMonth() + 1) > 9 ? (date.getMonth() + 1) : '0' + (date.getMonth() + 1)) + '/' + date.getFullYear()

      // deselecting ability
      if (this.datas.find(v => v.includes(val))) {
        let newDates = []
        this.datas.forEach(v => {
          if (v.includes(val)) {
            return
          }
          newDates.push(v)
        })

        newDates.length >= 0 && (this.datas = newDates)
        return
      }
      if (this.alteraInsere == 1) {
        this.datas.push(val)
      } else {
        this.datas.splice(0)
        this.datas.push(val)
        this.data = val
      }
      // this.$refs.qDateProxy.hide()
    },
    abreModal(codOs, variasdatas = false) {
      this.variasDatas = variasdatas
      this.exibe = true;
      this.tituloModal = "Justificar no embarque";
      this.modoVisualizacao = false;
      this.alteraInsere = 1;
      this.codOrdemServicoClassificacao = codOs;
    },
    carregaCampos(dadosJustificativa) {
      this.motivo = dadosJustificativa.Justificativa;
      this.observacao = dadosJustificativa.Observacao;
      this.codClassificadorSelecionado = dadosJustificativa.CodClassificador;
      this.termoBuscaClassificador = dadosJustificativa.NomeClassificador;
      this.idJustificativa = dadosJustificativa.id;
      //Para exibir corretamente a data no componente, eu preciso antes transforma-la
      //para o formato americano (mes/dia/ano)
      const date = dadosJustificativa.DataJustificativa;
      const dia = date.substring(0, 2);
      const mes = date.substring(3, 5);
      const ano = date.substring(6, 10);
      const dataFormatada = mes + "/" + dia + "/" + ano;
      this.datas.push(dataFormatada);
      this.data = dataFormatada;
    },
    abreModalVisualizacao(dadosJustificativa) {
      this.variasDatas = false;
      this.tituloModal = "Visualizando Justificativa";
      this.modoVisualizacao = true;
      this.exibe = true;
      this.carregaCampos(dadosJustificativa);
    },
    abreModalAlteracao(dadosJustificativa) {
      this.variasDatas = false;
      this.alteraInsere = 2;
      this.tituloModal = "Alterao de Justificativa";
      this.modoVisualizacao = false;
      this.exibe = true;
      this.carregaCampos(dadosJustificativa);
    },
    fechaModal() {
      this.data = "";
      this.datas = [];
      this.motivo = "";
      this.observacao = "";
      this.codClassificadorSelecionado = "";
      this.termoBuscaClassificador = "";
      this.exibe = false;
    },
    confirmarCancelamentoDeCadastroDeOs() {
      if (this.modoVisualizacao != true) {
        this.$q
            .dialog({
              title: "Cancelar lanamento de justificativa",
              message: "Deseja realmente cancelar olanamento de justificativa?",
              color: "negative",
              ok: "Sim",
              cancel: "No",
            })
            .then(() => {
              this.fechaModal();
            })
            .catch(() => {
            });
      } else {
        this.fechaModal();
      }
    },
    classificadorSelecionado(classificador) {
      this.termoBuscaClassificador = classificador.label;
      this.codClassificadorSelecionado = classificador.value;
    },
    salvaJustificativa() {
      const Justificativa = {}
      Justificativa.Justificativa = this.motivo;
      Justificativa.Observacao = this.observacao;
      Justificativa.CodClassificador = this.codClassificadorSelecionado;
      Justificativa.DataJustificativa = this.data;
      Justificativa.CodOrdemServicoClassificacao = this.codOrdemServicoClassificacao;
      post("justifica", Justificativa)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.exibe = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
    },
    alteraJustificativa() {
      this.exibeMensagemRetorno = false;
      const Justificativa = {}
      Justificativa.id = this.idJustificativa;
      Justificativa.Justificativa = this.motivo;
      Justificativa.Observacao = this.observacao;
      Justificativa.CodClassificador = this.codClassificadorSelecionado;
      Justificativa.DataJustificativa = this.data;
      post("alterajustificativa", Justificativa)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.exibe = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
    },
    comportamentoBotaoSalvar() {
      let ret = ''
      if (this.codClassificadorSelecionado === "") {
        this.mensagemRetorno = 'Necessrio preenchimento do classificador';
        this.tipoMensagem = "negative";
        this.exibeMensagemRetorno = true;
        setTimeout(() => {
          this.exibeMensagemRetorno = false;
        }, 2000)
        return '';
      } else if ((!this.variasDatas && this.data === "") || (this.variasDatas && this.datas === [])) {
        this.mensagemRetorno = 'Necessrio preenchimento da data da justificativa';
        this.tipoMensagem = "negative";
        this.exibeMensagemRetorno = true;
        setTimeout(() => {
          this.exibeMensagemRetorno = false;
        }, 2000)
        return '';
      }
      if (this.variasDatas) {
        this.datas.forEach(v => {
          let dia = v.substring(0, 2);
          let mes = v.substring(3, 5);
          let ano = v.substring(6, 10);
          let dataFormatada = mes + "/" + dia + "/" + ano;
          this.data = dataFormatada
          if (this.alteraInsere == 1) {
            ret = this.salvaJustificativa()
          } else {
            ret = this.alteraJustificativa()
          }
        })
        return ret
      } else {
        if (this.alteraInsere == 1) {
          return this.salvaJustificativa()
        } else {
          return this.alteraJustificativa()
        }
      }
    },
    buscaClassificador(terms, done) {
      setTimeout(() => {
        done(
            // eslint-disable-next-line no-undef
            filter(this.termoBuscaCliente, {
              field: "label",
              list: this.clientes,
            })
        );
      }, 100);
    },
    carregaClassificadores() {
      get("classificadores")
          .then((result) => {
            let jsonClassificadores = result;
            jsonClassificadores = jsonClassificadores.replace(
                /"id":/g,
                '"value":'
            );
            jsonClassificadores = jsonClassificadores.replace(
                /"nome":/g,
                '"label":'
            );
            this.classificadores = JSON.parse(jsonClassificadores);
          })
          .catch(() => {
          });
    },
  },
  mounted() {
    this.carregaClassificadores();
  },
};
</script>

<style>
.tira-margin-pading {
  padding: 0;
  margin: 0;
}

.margin-form {
  padding: 5px;
}

@media screen and (max-width: 768px) {
  .margin-form {
    padding: 0px;
  }

  .lagura-modal {
    width: 70px;
  }
}
</style>
