<template>
  <div class="col-12 flex flex-center" style="padding: 5px">
    <div class="container col-12" align="center" v-show="exibirLayout" id="layoutPadrao">
      <!------------------- Bloco referente ao Cabealho do Laudo ------------->
      <div class="col-md-12 col-xs-12 row cabecalho">
        <div class="col-md-4 col-xs-12 borda">
          <img :src="logoEmpresa" alt="logoEmpresa" class="logoEmpresa" style="padding: 2px" />
        </div>

        <div class="col-md-8 col-xs-12" style="border-left: 1px solid">
          <div class="col-md-6" style="border-bottom: 1px solid">
            <div class="col-12" style="font-size: 1.5rem">{{nomeEmpresa.toUpperCase()}}</div>
            <div>{{enderecoEmpresa}} - {{cidadeEstadoEmpresa}}</div>
            <div class="q-mb-xs">Telefone: {{telefoneEmpresa}}</div>
          </div>

          <div class="col-md-6 row">
            <div class="col-4" style="border-right: 1px solid">
              <div align="left" style="margin: 5px">
                <strong>Nmero do Laudo:</strong>
              </div>
              <div class="col-12" style="font-size: 1rem; color: red">{{numeroLaudo}}</div>
            </div>
            <div class="col-4" style="border-right: 1px solid">
              <div align="left" style="margin: 5px">
                <strong>Nmero da O.S:</strong>
              </div>
              <div class="col-12" style="font-size: 1rem;">{{codOSAno}}</div>
            </div>
            <div class="col-4">
              <div align="left" style="margin: 5px">
                <strong>Data Classificao:</strong>
              </div>
              <div
                  class="col-12"
                  style="font-size: 1rem;"
              >{{dataClassificacao}} s {{horaClassificacao}}</div>
            </div>
          </div>
        </div>
      </div>
      <!-----------------------------  Final do Bloco ------------------------->

      <!-----------------------------  Ttulo ------------------------->
      <div class="col-12" style="border-bottom: 1px solid">
        <div style="font-size: 1.6rem; margin: 5px">
          <strong>LAUDO DE CLASSIFICAO</strong>
        </div>
      </div>
      <!-----------------------------  Final do Bloco ------------------------->

      <!-----------------------------  Bloco informaes do Cliente e da O.S ------------------------->
      <div class="col-12 row" style="border-bottom: 1px solid">
        <div class="col-6" style="border-right: 1px solid; padding: 5px">
          <div class="q-mb-xs" align="center" style="font-size: 1.1rem">Dados da O.S</div>
          <div class="q-mb-xs" align="left">
            <b>N ITEM O.S:</b>
            {{itemOS}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>CLIENTE:</b>
            {{nomeCliente}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>ORIGEM:</b>
            {{municipioOrigem}}/{{UFOrigem}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>DESTINO:</b>
            {{municipioDestino}}/{{UFDestino}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>IDENT. VECULO/ PLACA:</b>
            {{placaVeiculo}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>TRANSPORTADORA:</b>
            {{transportadora}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>ORDEM CARGA:</b>
            {{ordemCarga}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>LOCAL DE EMBARQUE:</b>
            {{localEmbarque}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>TIPO PRODUTO:</b>
            {{tipoDoProduto}}
          </div>
          <div class="q-mb-xs" align="left">
            <b>MOTORISTA:</b>
            {{nomeMotorista}}
          </div>
          <div align="left">
            <b>N - LACRE:</b>
            {{numeroLacre}}
          </div>
        </div>

        <!-----------------------------  Dados da Classificao SOJA ------------------------->
        <div
            class="col-6"
            style="padding: 5px; overflow: hidden; white-space: pre-wrap"
            align="left"
            v-if="codProduto == '1'"
        >
          <div class="q-mb-xs" align="center" style="font-size: 1.1rem">Dados da Classificao</div>

          <div class="col-12 row">
            <div class="col-10 q-mb-xs classificacao">
              <b>UMIDADE:</b>
            </div>
            <div class="col-2" align="right">{{umidade}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-xs classificacao">
              <b>IMPUREZAS:</b>
            </div>
            <div class="col-2" align="right">{{impureza}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-xs classificacao">
              <b>PARTIDOS/QUEBRADOS:</b>
            </div>
            <div class="col-2" align="right">{{partidoQuebrado}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-xs classificacao">
              <b>ARDIDOS:</b>
            </div>
            <div class="col-2" align="right">{{ardidos}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-xs classificacao">
              <b>PICADOS:</b>
            </div>
            <div class="col-2" align="right">{{picados}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-xs classificacao">
              <b>QUEIMADOS:</b>
            </div>
            <div class="col-2" align="right">{{queimados}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-xs classificacao">
              <b>MOFADOS:</b>
            </div>
            <div class="col-2" align="right">{{mofados}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-xs classificacao">
              <b>ESVERDEADOS:</b>
            </div>
            <div class="col-2" align="right">{{esverdiados}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 classificacao">
              <b>FERMENTADOS:</b>
            </div>
            <div class="col-2" align="right">{{fermentados}}%</div>
          </div>
          <div
              class="q-mb-xs"
              style="font-size: 0.7rem"
              align="left"
          >Fermentados, Chochos, Imaturos, Germinados</div>

          <div class="col-12 row">
            <div class="col-10 classificacao">
              <b>* TOTAL AVARIADOS:*</b>
            </div>
            <div class="col-2" align="right">{{avariados}}%</div>
          </div>
          <div
              class="q-mb-xs"
              style="font-size: 0.7rem"
              align="left"
          >Soma de Ardidos, Chochos, Fermentados, Germinados, Danificados, Queimados e Mofados</div>
        </div>
        <!-----------------------------  Final do Bloco ------------------------->

        <!-----------------------------  Dados da Classificao MILHO ------------------------->
        <div
            class="col-6"
            style="padding: 5px; overflow: hidden; white-space: pre-wrap"
            align="left"
            v-if="codProduto == '2'"
        >
          <div class="q-mb-xs" align="center" style="font-size: 1.1rem">Dados da Classificao</div>

          <div class="col-12 row">
            <div class="col-10 q-mb-sm classificacao">
              <b>UMIDADE:</b>
            </div>
            <div class="col-2" align="right">{{umidade}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-sm classificacao">
              <b>ARDIDOS/QUEIMADOS:</b>
            </div>
            <div class="col-2" align="right">{{ardidos}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 q-mb-sm classificacao">
              <b>CARUNCHADOS:</b>
            </div>
            <div class="col-2" align="right">{{carunchados}}%</div>
          </div>

          <div class="col-12 row">
            <div class="col-10 classificacao">
              <b>FERMENTADOS:</b>
            </div>
            <div class="col-2" align="right">{{fermentados}}%</div>
          </div>
          <div
              class="q-mb-xs"
              style="font-size: 0.7rem"
              align="left"
          >Fermentados, Chochos, Imaturos, Germinados</div>

          <div class="col-12 row">
            <div class="col-10 classificacao">
              <b>QUEBRADOS:</b>
            </div>
            <div class="col-2" align="right">{{partidoQuebrado}}%</div>
          </div>
          <div
              class="q-mb-xs"
              style="font-size: 0.7rem"
              align="left"
          >Partidos, Quebrados ou Amassados</div>

          <div class="col-12 row">
            <div class="col-10 classificacao">
              <b>IMPUREZAS:</b>
            </div>
            <div class="col-2" align="right">{{impureza}}%</div>
          </div>
          <div
              class="q-mb-xs"
              style="font-size: 0.7rem"
              align="left"
          >Mterias Estranhas ou Impurezas</div>

          <div class="col-12 row">
            <div class="col-10 classificacao">
              <b>* TOTAL AVARIADOS:*</b>
            </div>
            <div class="col-2" align="right">{{avariados}}%</div>
          </div>
          <div class="q-mb-xs" style="font-size: 0.7rem" align="left">Soma de Ardidos e Carunchados</div>
        </div>
        <!-----------------------------  Final do Bloco ------------------------->
      </div>
      <!-----------------------------  Final do Bloco ------------------------->

      <!-----------------------------  Dados da Classificao SORGO ------------------------->
      <div
          class="col-6"
          style="padding: 5px; overflow: hidden; white-space: pre-wrap"
          align="left"
          v-if="codProduto === '4'"
      >
        <div class="q-mb-xs" align="center" style="font-size: 1.1rem">Dados da Classificao</div>

        <div class="col-12 row">
          <div class="col-10 q-mb-sm classificacao">
            <b>UMIDADE:</b>
          </div>
          <div class="col-2" align="right">{{umidade}}%</div>
        </div>

        <div class="col-12 row">
          <div class="col-10 classificacao">
            <b>IMPUREZAS:</b>
          </div>
          <div class="col-2" align="right">{{impureza}}%</div>
        </div>
        <div
            class="q-mb-xs"
            style="font-size: 0.7rem"
            align="left"
        >Mterias Estranhas ou Impurezas</div>


      </div>

      <!-----------------------------  Produto ------------------------->
      <div class="col-12" style="border-bottom: 1px solid">
        <div class="col-12 row" style="padding: 5px; justify-content: space-between">
          <div style="margin-top: 1px">
            <b>PRODUTO:</b>
          </div>
          <div>
            MILHO:
            <q-checkbox style="margin-bottom: 3px" v-model="milho" readonly val="true" />
          </div>
          <div>
            SOJA:
            <q-checkbox style="margin-bottom: 3px" v-model="soja" readonly val="true" />
          </div>
          <div>
            SORGO:
            <q-checkbox style="margin-bottom: 3px" v-model="sorgo" readonly val="true" />
          </div>
          <div>
            MILHETO:
            <q-checkbox style="margin-bottom: 3px" v-model="milheto" readonly val="true" />
          </div>
          <div>
            FARELO:
            <q-checkbox style="margin-bottom: 3px" v-model="farelo" readonly val="true" />
          </div>
        </div>
      </div>
      <!-----------------------------  Final do Bloco ------------------------->

      <!-----------------------------  PESO ------------------------->
      <div class="col-12 blocoProduto row" style="border-bottom: 1px solid">
        <div class="row" style="padding: 5px">
          <div class="blocoA">
            <strong>BRUTO:</strong>
          </div>
          <div style="font-size: 1.5rem;">{{pesoBruto}}</div>
          <div class="blocoB">Kg</div>
        </div>
        <div class="row" style="padding: 5px">
          <div class="blocoA">
            <strong>TARA:</strong>
          </div>
          <div style="font-size: 1.5rem;">{{pesoTara}}</div>
          <div class="blocoB">Kg</div>
        </div>
        <div class="row" style="padding: 5px">
          <div class="blocoA">
            <strong>LQUIDO:</strong>
          </div>
          <div style="font-size: 1.5rem;">{{pesoLiquido}}</div>
          <div class="blocoB">Kg</div>
        </div>
      </div>
      <!-----------------------------  Final do Bloco ------------------------->

      <!-----------------------------  Observao ------------------------->
      <div class="col-12 observacao" style="border-bottom: 1px solid">
        <div style="text-align: start;">
          <strong>OBS:</strong>
          {{observacao}}
        </div>
      </div>
      <!-----------------------------  Final do Bloco ------------------------->

      <!-----------------------------  Assinatura ------------------------->
      <div class="q-mt-xs col-12 row" style="font-size: 1rem">
        <div class="col-12" style="padding: 10px" align="center">
          <div style="margin-top: 10px">{{nomeClassificador.toUpperCase()}}</div>
          <div class="bordaPrint" style="border-bottom: 1px solid;"></div>
          <div style="font-weight: bold;">NOME DO CLASSIFICADOR</div>
        </div>

        <!-- <div class="col-3" style="padding: 10px 20px 0px 20px; text-align: center">
                <div style="margin-top: 10px">{{dataClassificacao}}</div>
                <div style="border-bottom: 1px solid"></div>
                <div style="font-weight: bold;">DATA</div>
            </div>

            <div class="col-3" style="padding: 10px 20px 0px 20px; text-align: center">
                <div style="margin-top: 10px">{{horaClassificacao}}</div>
                <div style="border-bottom: 1px solid"></div>
                <div style="font-weight: bold;">HORRIO</div>
        </div>-->
      </div>
      <!-----------------------------  Final do Bloco ------------------------->
      <div class="centraliza-texto col-12 distancia-botoes-acao">
        <qrcode-vue :value="urlClassificacao" :size="200" level="H"/>
      </div>
    </div>
    <div class="col-12 botoes" style="width: 100%" align="center">
      <q-btn
          @click="baixarPDF()"
          icon="fas fa-file-pdf"
          class="col-md-6 col-xs-12 botao"
          color="red"
          label="Baixar PDF"
          size="20px"
      />
      <q-btn
          @click="imprimirDocumento()"
          icon="fas fa-print"
          class="col-md-6 col-xs-12 botao"
          color="green"
          label="Imprimir"
          size="20px"
      />
    </div>
  </div>
</template>

<script>
// import jsPDF from "jspdf";
// import html2canvas from "html2canvas";
import html2pdf from "js-html2pdf";
import { detectaCliente } from "./utils/DetectaCliente";
import { tipoProduto } from "./utils/TipoProduto";
import { getComCabecalho } from "./js/ConexaoApi.js";
import QrcodeVue from 'qrcode.vue';

export default {
  name: "LaudoPadrao",
  data() {
    return {
      exibirLayout: true,
      milho: false,
      soja: false,
      sorgo: false,
      farelo: false,
      milheto: false,
      numeroLaudo: "",
      picados: "",
      numNF: "",
      codOSAno: "",
      observacao: "",
      codProduto: "",
      codCliente: "",
      nomeProduto: "",
      nomeEmpresa: "",
      itemOS: "",
      enderecoEmpresa: "",
      telefoneEmpresa: "",
      cnpjEmpresa: "",
      cepEmpresa: "",
      cidadeEstadoEmpresa: "",
      logoEmpresa: "",
      nomeClassificador: "",
      dataClassificacao: "",
      horaClassificacao: "",
      data: "",
      nomeCliente: "",
      pesoLiquido: "",
      pesoBruto: "",
      pesoTara: "",
      nomeMotorista: "",
      fermentados: "",
      placaVeiculo: "",
      ordemCarga: "",
      numeroLacre: "",
      municipioOrigem: "",
      UFOrigem: "",
      municipioDestino: "",
      UFDestino: "",
      umidade: "",
      impureza: "",
      mofados: "",
      ardidos: "",
      queimados: "",
      outrosDefeitos: "",
      avariados: "",
      carunchados: "",
      esverdiados: "",
      partidoQuebrado: "",
      transportadora: "",
      localEmbarque: "",
      codTipoProduto: "",
      tipoDoProduto: "",
      urlClassificacao: "",
    };
  },
  methods: {
    carregarDados() {
      let uuid = this.$route.params.u;
      if (uuid == "" || uuid == null || uuid == undefined) {
        this.$router.push("/");
      }

      getComCabecalho("carregadadosqrcode", "uuid", uuid)
          .then((result) => {
            const resposta = JSON.parse(result);
            this.nomeCliente = resposta[0].NomeCliente;
            this.numeroLaudo = resposta[0].NumeroLaudo;
            this.codOSAno = resposta[0].CodOrdemServicoClassificacaoAno;
            this.UFOrigem = resposta[0].UFOrigem;
            this.numNF = resposta[0].NumNF;
            this.nomeMotorista = resposta[0].NomeMotorista;
            this.municipioOrigem = resposta[0].MunicipioOrigem;
            this.UFDestino = resposta[0].UFDestino;
            this.municipioDestino = resposta[0].MunicipioDestino;
            this.numeroLacre = resposta[0].NumeroLacre;
            this.placaVeiculo = resposta[0].PlacaCavalo;
            this.observacao = resposta[0].Observacao;
            this.nomeClassificador = resposta[0].nome;
            this.umidade = resposta[0].Umidade;
            this.picados = resposta[0].Picados;
            this.fermentados = resposta[0].FermentadoChochoImaturoGerminado;
            this.impureza = resposta[0].Impureza;
            this.mofados = resposta[0].Mofados;
            this.ardidos = resposta[0].Ardidos;
            this.queimados = resposta[0].Queimados;
            this.outrosDefeitos = resposta[0].FermentadoChochoImaturoGerminado;
            this.avariados = resposta[0].TotalAvariado;
            this.carunchados = resposta[0].Carunchados;
            this.esverdiados = resposta[0].Esverdeados;
            this.partidoQuebrado = resposta[0].PartidoQuebradoAmassado;
            this.transportadora = resposta[0].Transportadora;
            this.localEmbarque = resposta[0].LocalEmbarque;
            this.ordemCarga = resposta[0].OrdemCarga;
            this.codProduto = resposta[0].CodProduto;
            this.codTipoProduto = resposta[0].TipoProduto;
            this.tipoDoProduto = tipoProduto(this.codTipoProduto).toUpperCase();
            this.itemOS = resposta[0].CodItemOrdemServicoClassificacao;
            this.pesoLiquido = Number(parseFloat(resposta[0].PesoLiquido));
            this.pesoTara = Number(parseFloat(resposta[0].PesoTara));
            if (Number.isNaN(this.pesoTara)) {
              this.pesoTara = 0;
            }
            this.pesoBruto = parseFloat(resposta[0].PesoBruto);
            if (Number.isNaN(this.pesoBruto)) {
              this.pesoBruto = 0;
            }

            // eslint-disable-next-line no-console
            console.log(this.codProduto);
            const date = resposta[0].DataClassificacao;
            const dia = date.substring(0, 2);
            const mes = date.substring(3, 5);
            const ano = date.substring(6, 10);
            this.dataClassificacao = dia + "/" + mes + "/" + ano;
            this.horaClassificacao = date.substring(11, 19);

            if (this.codProduto == 1) {
              this.soja = true;
            } else if (this.codProduto == 2) {
              this.milho = true;
            } else if (this.codProduto == 3) {
              this.farelo = true;
            } else if (this.codProduto == 4) {
              this.sorgo = true;
            } else if (this.codProduto == 5){
              this.milheto = true;
            } else {
              this.milheto = false;
              this.milho = false;
              this.soja = false;
              this.sorgo = false;
              this.farelo = false;
            }
          })
          .catch(() => {});
    },
    imprimirDocumento() {
      if (this.$q.platform.is.mobile) {
        this.exibirLayout = true;
      }

      setTimeout(() => {
        window.print();
        if (this.$q.platform.is.mobile) {
          setTimeout(() => {
            this.exibirLayout = false;
          }, 1000);
        }
      }, 1500);
    },
    baixarPDF() {
      if (this.$q.platform.is.mobile) {
        this.exibirLayout = true;
      }

      setTimeout(() => {
        const elementoASerImprimido = document.getElementById("layoutPadrao");
        const opt = {
          filename: "Classificacao" + this.numeroLaudo + ".pdf",
          margin: 8,
          enableLinks: true,
          imagem: "png",
          html2canvas: {
            scale: 5,
            logging: true,
            dpi: 192,
            letterRendering: true,
          },
          jsPDF: {
            unit: "mm",
            format: "a4",
            orientation: "portrait",
          },
        };

        const exportar = new html2pdf(elementoASerImprimido, opt);
        exportar
            .getPdf(true)
            .then(() => {
              if (this.$q.platform.is.mobile) {
                this.exibirLayout = false;
              }
            })
            .catch(() => {
              if (this.$q.platform.is.mobile) {
                this.exibirLayout = false;
              }
            });
      });
    },
  },
  components:{
    QrcodeVue
  },
  async mounted() {
    this.urlClassificacao = window.location.href;
    this.carregarDados();
    const clienteAlfameta = await detectaCliente();
    this.nomeEmpresa = clienteAlfameta.Nome;
    this.enderecoEmpresa = clienteAlfameta.Endereco;
    this.telefoneEmpresa = clienteAlfameta.Contato;
    this.cnpjEmpresa = clienteAlfameta.Cnpj;
    this.cepEmpresa = clienteAlfameta.CEP;
    this.cidadeEstadoEmpresa = clienteAlfameta.CidadeEstado;
    this.logoEmpresa = clienteAlfameta.LogoEmpresaSrc ? clienteAlfameta.LogoEmpresaSrc : clienteAlfameta.LogoEmpresa;
    this.email = clienteAlfameta.Email;
  },
};
</script>

<style scoped>
.container {
  border: 1px solid #000;
  border-radius: 10px;
  width: 98%;
}


.logoEmpresa {
  height: 100%;
  width: 60%;
}

.cabecalho {
  align-items: center;
  height: 100%;
  border-bottom: 1px solid;
}

.blocoProduto {
  justify-content: space-between;
}

.blocoProduto .blocoA {
  margin-right: 8px;
}

.blocoProduto .blocoB {
  align-self: flex-end;
  margin-left: 3px;
  margin-bottom: 2px;
}

.cabecalho blocoB {
  display: flex;
  flex-direction: column;
}

.botao {
  width: 150px;
}

.bordaPrint {
  width: 30%;
}

.classificacao {
  overflow: hidden;
  white-space: nowrap;
}

.classificacao:after {
  content: "....................................................................................................";
}

.observacao {
  font-size: 1rem;
  padding: 5px;
  overflow: auto;
  white-space: pre-wrap;
  height: auto;
}

@media print {
  .botoes {
    display: none !important;
  }

  .borda {
    border-bottom: 1px solid;
    border-right: none !important;
  }

  .logoEmpresa {
    width: 50%;
    height: 30%;
