<template>
  <q-layout>
    <Cabecalho />
    <q-page-container>
      <div class="col-12 q-mt-md q-mb-md text-centralizado">Controle de Despesas</div>
      <Despesas @carregaDados="atualizaTabela" />
    </q-page-container>
  </q-layout>
</template>

<script>
import Cabecalho from "@/components/Cabecalho.vue";
import Despesas from "../components/TabelaDespesas.vue";
import { getCookie } from "../components/js/Cookies.js";
import { get } from "../components/js/ConexaoApi";

export default {
  components: {
    Cabecalho,
    Despesas,
  },
  data() {
    return {
      administrador: false,
      supervisor: false,
    };
  },
  methods: {
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    atualizaTabela() {
      get("despesas")
        .then((res) => {
          this.dados = JSON.parse(res);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
  },
  created() {
    this.verificaAdministrador();
  },
};
</script>

<style>
.text-centralizado {
  text-align: center;
  font-size: 25px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #1b1f5f;
}
</style>