      this.certificadoHomogeneizacao = 'S'
      this.abreModal(dadosOs)
      setTimeout(() => {
        this.codLote = dadosLote.CodLote
      }, 2000);
    },
    abreModal(dadosOs) {
      this.alteraInsere = 1;
      this.exibe = true;
      const admin = getCookie("su");
      if (admin != 0) {
        this.codClassificador = dadosOs.CodClassificador;
      } else {
        this.codClassificador = getCookie("id");
      }
      this.responsavelColeta = getCookie('nome')
      this.codProduto = dadosOs.CodProduto;
      this.codOrdemServicoClassificacaoConab =
        dadosOs.CodOrdemServicoClassificacaoConab;
      this.codMunicipio = dadosOs.CodMunicipio;
      this.tituloModal = "Nova classificao";
      this.modoVisualizacao = false;
      this.limpaMensagensErro();
      this.carregaLotes();
      this.carregaCamposDaOs(dadosOs);
      switch (this.codProduto) {
        case 1:
          this.descriminacao = "SOJA";
          break;
        case 2:
          this.descriminacao = "MILHO";
          break;
      }
    },
    carregaLotes() {
      get(
        `loteconab?CodOrdemServicoClassificacaoConab=${this.codOrdemServicoClassificacaoConab}`
      ).then((result) => {
        result = result.replace(/"CodLote":/g, '"value":');
        result = result.replace(/"NumeroLote":/g, '"label":');
        result = result.replace(/"ATR":/g, '"sublabel":');
        result = result.replace(/"NomeMunicipio":/g, '"stamp":');
        this.lotesDaOs = JSON.parse(result);
      });
    },
    carregaGruposConab() {
      get("gruposconab")
        .then((result) => {
          let jsonGruposConab = result;
          jsonGruposConab = jsonGruposConab.replace(/"CodGrupo":/g, '"value":');
          jsonGruposConab = jsonGruposConab.replace(/"Nome":/g, '"label":');
          this.gruposConab = JSON.parse(jsonGruposConab);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    carregaClassesConab() {
      get("classesconab")
        .then((result) => {
          let jsonClassesConab = result;
          jsonClassesConab = jsonClassesConab.replace(
            /"CodClasse":/g,
            '"value":'
          );
          jsonClassesConab = jsonClassesConab.replace(/"Nome":/g, '"label":');
          this.classesConab = JSON.parse(jsonClassesConab);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    carregaCamposDaOs(dadosOs) {
      this.marca = dadosOs.Marca;
      this.codGrupo = dadosOs.CodGrupo;
      this.codClasse = dadosOs.CodClasse;
      this.tipo = dadosOs.Tipo;
    },
    carregaCampos(dadosOs) {
      this.limpaMensagensErro();
      this.codOrdemServicoClassificacaoConab =
        dadosOs.codOrdemServicoClassificacaoConab;
      this.codItemServicoClassificacao =
        dadosOs.CodItemOrdemServicoClassificacao;
      this.codProduto = dadosOs.CodProduto;
      this.pesoLiquido = dadosOs.PesoLiquido;
      this.placaCavalo = dadosOs.PlacaCavalo;
      this.numeroLaudo = dadosOs.NumeroLaudo;
      this.observacao = dadosOs.Observacao;
      this.codMunicipio = dadosOs.CodMunicipio;
      this.impureza = dadosOs.Impureza;
      this.numNF = dadosOs.NumNF;
      this.fermentados = dadosOs.FermentadoChochoImaturoGerminado;
      this.umidade = dadosOs.Umidade;
      this.pesoAmostra = dadosOs.PesoAmostra;
