    },
    //Continuar a validao aqui
    validaCamposClassificacao() {
      return new Promise((resolve) => {
        this.erroLote = false;
        this.erroDescriminacao = false;
        this.erroMarca = false;
        this.erroPesoLiquido = false;
        this.erroNaturezaOperacao = false;
        this.erroPlacaCavalo = false;
        this.erroGrupo = false;
        this.erroClasse = false;
        this.erroTipo = false;
        this.erroAparelho = false;
        this.erroPesoAmostra = false;
        this.erroUmidade = false;
        this.erroImpureza = false;
        this.erroQuebrados = false;
        this.erroCarunchados = false;
        this.erroSadios = false;
        this.erroArdidos = false;
        this.erroMofados = false;
        this.erroFermentados = false;
        this.erroGerminados = false;
        this.erroGessados = false;
        this.erroChochos = false;
        this.erroTotal = false;
        this.erroObservacao = false;
        if (this.codLote === undefined || this.codLote === "") {
          this.erroLote = true;
        }
        if (this.descriminacao === undefined || this.descriminacao === "") {
          this.erroDescriminacao = true;
        }
        if (this.marca === undefined || this.marca === "") {
          this.erroMarca = true;
        }
        if (this.pesoLiquido === undefined || this.pesoLiquido === "") {
          this.erroPesoLiquido = true;
        }
        if (
          this.naturezaOperacao === undefined ||
          this.naturezaOperacao === ""
        ) {
          this.erroNaturezaOperacao = true;
        }
        if(this.certificadoHomogeneizacao == 'N'){
          if (this.placaCavalo === undefined || this.placaCavalo === "") {
            this.erroPlacaCavalo = true;
          }
        }
        
        if (this.codGrupo === undefined || this.codGrupo === "") {
          this.erroGrupo = true;
        }
        if (this.codClasse === undefined || this.codClasse === "") {
          this.erroClasse = true;
        }
        if (this.tipo === undefined || this.tipo === "") {
          this.erroTipo = true;
        }
        if (this.aparelho === undefined || this.aparelho === "") {
          this.erroAparelho = true;
        }
        if (
          this.pesoAmostra === undefined ||
          this.pesoAmostra === ""
        ) {
          this.erroPesoAmostra = true;
        }
        if (this.umidade === undefined || this.umidade === "") {
          this.erroUmidade = true;
          this.erroUmidadeMsg = "Por favor, informe a umidade";
        } else {
          this.erroUmidadeMsg = "";
        }
        if (this.umidade > 13 && this.observacao === ''){
          this.erroUmidade = true;
          this.erroUmidadeMsg = "Umidade fora do padro, por favor, informe uma observao"
          this.erroObservacao = true;
        }
        if (this.quebrados > 3 && this.observacao === ''){
          this.erroUmidade = true;
          this.erroQuebradosMsg = "Quebrados fora do padro, por favor, informe uma observao"
          this.erroObservacao = true;
        }
        if (this.impureza === undefined || this.impureza === "") {
          this.erroImpureza = true;
        }
        if (this.quebrados === undefined || this.quebrados === "") {
          this.erroQuebrados = true;
        }
        if (this.carunchados === undefined || this.carunchados === "") {
          this.erroCarunchados = true;
        }
        if (this.sadios === undefined || this.sadios === "") {
          this.erroSadios = true;
        }
        if (this.ardidos === undefined || this.ardidos === "") {
          this.erroArdidos = true;
        }
        if (this.mofados === undefined || this.mofados === "") {
          this.erroMofados = true;
        }
        if (this.fermentados === undefined || this.fermentados === "") {
          this.erroFermentados = true;
        }
        if (this.germinados === undefined || this.germinados === "") {
          this.erroGerminados = true;
        }
        if (this.gessados === undefined || this.gessados === "") {
          this.erroGessados = true;
        }
        if (this.chochos === undefined || this.chochos === "") {
          this.erroChochos = true;
        }
        if (this.total === undefined || this.total === "") {
          this.erroTotal = true;
        }
        if ((this.erroUmidade === true || this.erroQuebrados === true) && this.observacao === '') {
          this.erroObservacao = true;
        }
        if (
          this.erroLote == false &&
          this.erroDescriminacao == false &&
          this.erroMarca == false &&
          this.erroPesoLiquido == false &&
          this.erroNaturezaOperacao == false &&
          this.erroPlacaCavalo == false &&
          this.erroGrupo == false &&
          this.erroClasse == false &&
          this.erroTipo == false &&
          this.erroAparelho == false &&
          this.erroPesoAmostra == false &&
          this.erroUmidade == false &&
          this.erroImpureza == false &&
          this.erroQuebrados == false &&
          this.erroCarunchados == false &&
          this.erroSadios == false &&
          this.erroArdidos == false &&
          this.erroMofados == false &&
          this.erroFermentados == false &&
          this.erroGerminados == false &&
          this.erroGessados == false &&
          this.erroChochos == false &&
          this.erroTotal == false &&
          this.erroObservacao == false &&
          this.erroTotal == false
        ) {
          resolve(true);
        } else {
          resolve(false);
        }
      });
    },
    limpaMensagensErro() {
      //Limpa as variaveis de erro
      this.erroPesoLiquido = false;
