    abreModalAlteracao(dadosOs) {
      this.alteraInsere = 2;
      this.tituloModal = "Alterao de classificao";
      this.modoVisualizacao = false;
      this.exibe = true;
      this.carregaCampos(dadosOs);
    },
    fechaModal() {
      this.limpaCampos();
      this.limpaMensagensErro();
    },
    async formataNumeros() {
      this.proteina = await this.removeVirgulaNumero(this.proteina);
      this.umidade = await this.removeVirgulaNumero(this.umidade);
      this.impureza = await this.removeVirgulaNumero(this.impureza);
      this.ardidosEQueimados = await this.removeVirgulaNumero(
        this.ardidosEQueimados
      );
      this.queimados = await this.removeVirgulaNumero(this.queimados);
      this.partidos = await this.removeVirgulaNumero(this.partidos);
      this.esverdeados = await this.removeVirgulaNumero(this.esverdeados);
      this.picados = await this.removeVirgulaNumero(this.picados);
      this.carunchados = await this.removeVirgulaNumero(this.carunchados);
      this.total = await this.removeVirgulaNumero(this.total);
    },
    async removeVirgulaNumero(numero) {
      return new Promise((resolve) => {
        try {
          if (numero !== undefined) {
            numero = numero.toString();
            if (numero !== "") {
              numero = numero.replace(/,/g, ".");
            }
          }
          resolve(numero);
        } catch (e) {
          resolve(numero);
        }
      });
    },
    async verificaLancamentoDuplicado() {
      return new Promise(async (resolve) => {
        const cabecalhos = new Map();
        cabecalhos.set("PlacaCavalo", this.placaCavalo);
        cabecalhos.set("Data", await ConverteDataHorarioFormatoBancoDeDados(this.dataClassificacao))
        getComVariosCabecalhos("verificalancamentoduplicado", cabecalhos)
          .then((result) => {
            const resposta = JSON.parse(result);
            if (resposta.Status == true) {
              resolve(true);
            } else {
              this.$q
                .dialog({
                  title: "Confirmao de lanamento",
                  message: resposta.msg,
                  color: "negative",
                  ok: "Sim",
                  cancel: "No",
                  position: "top",
                })
                .then(() => {
                  resolve(true);
                })
                .catch(() => {
                  resolve(false);
                });
            }
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            console.error(err);
            resolve(false);
          });
      });
    },
    async salvarItemOrdemClassificacao() {
      await this.formataNumeros();
      if (await this.validaCamposClassificacao()) {
        if (await this.verificaLancamentoDuplicado()) {
          const itemOrdemServicoClassificacaoConab = {};
          criaUUID()
            .then(async (result) => {
              itemOrdemServicoClassificacaoConab.CertificadoHomogeneizacao = this.certificadoHomogeneizacao
              if(this.certificadoHomogeneizacao == 'S'){
                itemOrdemServicoClassificacaoConab.PlacaCavalo = 'Certificado de homogeneizao'
              }else{
                itemOrdemServicoClassificacaoConab.PlacaCavalo = this.placaCavalo;
              }
              itemOrdemServicoClassificacaoConab.UUID = result;
              itemOrdemServicoClassificacaoConab.CodLote = this.codLote;
              itemOrdemServicoClassificacaoConab.CodProduto = this.codProduto;
              itemOrdemServicoClassificacaoConab.Marca = this.marca;
              itemOrdemServicoClassificacaoConab.PesoLiquido = this.pesoLiquido;
              itemOrdemServicoClassificacaoConab.ResponsavelColeta = this.responsavelColeta;
              itemOrdemServicoClassificacaoConab.NumeroAmostra = this.numeroAmostra;
              itemOrdemServicoClassificacaoConab.NaturezaOperacao = this.naturezaOperacao;
              itemOrdemServicoClassificacaoConab.NumNF = this.numNF;
              
              itemOrdemServicoClassificacaoConab.TempoAtendimento = this.tempoAtendimento;
              itemOrdemServicoClassificacaoConab.CodGrupo = this.codGrupo;
              itemOrdemServicoClassificacaoConab.CodClasse = this.codClasse;
              itemOrdemServicoClassificacaoConab.Tipo = this.tipo;
              itemOrdemServicoClassificacaoConab.PesoAmostra = this.pesoAmostra.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Umidade = this.umidade.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.ImpurezaGramas = this.impurezaGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Impureza = this.impureza.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.QuebradosGramas = this.quebradoGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Quebrados = this.quebrados.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.CarunchadosGramas = this.carunchadosGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Carunchados = this.carunchados.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.SadiosGramas = this.sadiosGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Sadios = this.sadios.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.ArdidosGramas = this.ardidosGrama.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Ardidos = this.ardidos.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.MofadosGramas = this.mofadosGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Mofados = this.mofados.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.FermentadosGramas = this.fermentadosGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Fermentados = this.fermentados.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.GerminadosGramas = this.germinadosGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Germinados = this.germinados.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.GessadosGramas = this.gessadosGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Gessados = this.gessados.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.ChochosGramas = this.chochosGramas.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Chochos = this.chochos.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Total = this.total.toString().replace(/,/g, ".");
              itemOrdemServicoClassificacaoConab.Observacao = this.observacao;
              itemOrdemServicoClassificacaoConab.Mapa = this.mapa;
              itemOrdemServicoClassificacaoConab.LocalDataClassificacao = this.localDataClassificacao;
              itemOrdemServicoClassificacaoConab.DataClassificacao = new Date();
              itemOrdemServicoClassificacaoConab.CodClassificador = this.codClassificador;
              itemOrdemServicoClassificacaoConab.Aparelho = this.aparelho;
              itemOrdemServicoClassificacaoConab.CodOrdemServicoClassificacaoConab = this.codOrdemServicoClassificacaoConab;
              post("salvaclassificacaoconab", itemOrdemServicoClassificacaoConab)
                .then((result) => {
                  const resposta = JSON.parse(result);
                  if (resposta.Status == true) {
                    this.mensagemRetorno = resposta.msg;
                    this.exibeMensagemRetorno = true;
                    this.tipoMensagem = "positive";
                    setTimeout(() => {
                      this.mensagemRetorno = "";
                      this.tipoMensagem = "positive";
                      this.exibeMensagemRetorno = false;
                      this.fechaModal();
                    }, 2000);
                    this.visualizaClassificacao(resposta.NumeroLaudo);
                  } else {
                    this.mensagemRetorno = resposta.msg;
                    this.exibeMensagemRetorno = true;
                    this.tipoMensagem = "negative";
                  }
                })
                .catch((err) => {
                  const Erro = JSON.parse(err);
                  this.mensagemRetorno = Erro.msg;
                  this.exibeMensagemRetorno = true;
                  this.tipoMensagem = "negative";
                  // eslint-disable-next-line no-console
                  console.error(Erro.Erro);
                });
            })
            .catch(() => {});
        }
      }
    },
    confirmarCancelamentoDeCadastroDeClassificacao() {
      if (this.modoVisualizacao != true) {
        this.$q
          .dialog({
            title: "Cancelar lanamento de classificao",
            message: "Deseja realmente cancelar o lanamento da classificao?",
            color: "negative",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => {
            this.fechaModal();
          })
          .catch(() => {});
      } else {
        this.fechaModal();
      }
    },
    visualizaClassificacao(numeroLaudo) {
      let routeData = this.$router.resolve(`/conab/formulario/${numeroLaudo}`);
      window.open(routeData.href, "_blank");
    },
    comportamentoBotaoSalvar() {
      if (this.alteraInsere == 1) {
        return this.salvarItemOrdemClassificacao();
      } else {
        return this.alteraItemOrdemClassificacao();
      }
    },
    detectaCliente() {
      detectaCliente()
        .then((result) => {
          this.codClienteAlfameta = result;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    calculaPorcentagemApartirDaGrama(g) {
      let gramas = String(g);
      gramas = gramas.replace(",", ".");
      return Number((gramas * 100) / this.pesoAmostra).toFixed(2);
    },
    calculaTotal() {
      this.total =
        Number(this.ardidos) +
        Number(this.mofados) +
        Number(this.fermentados) +
        Number(this.germinados) +
        Number(this.gessados) +
        Number(this.chochos);
      this.total = Number(this.total).toFixed(2);
    },
    validaUmidade(){
      if (this.umidade !== undefined) {
        const umidade = Number(this.umidade);
        if (umidade > 13 && this.observacao === '') {
          this.erroUmidade = true;
          this.erroUmidadeMsg = "Umidade fora do padro";
          this.erroObservacao = true;
        } else {
          this.erroUmidade = false;
          this.erroUmidadeMsg = "";
          if (this.erroQuebrados === false && this.erroUmidade === false) {
            this.erroObservacao = false;
          }
        }
      }
    },
    validaQuebrados(){
      if (this.quebrados !== undefined) {
        const quebrados = Number(this.quebrados);
        if (quebrados > 3 && this.observacao === '') {
          this.erroQuebrados = true;
          this.erroQuebradosMsg = "Gros quebrados fora do padro";
          this.erroObservacao = true;
        } else {
          this.erroQuebradoGramas = false;
          this.erroQuebrados = false;
          this.erroQuebradosMsg = "";
          if (this.erroQuebrados === false && this.erroUmidade === false) {
            this.erroObservacao = false;
          }
        }
      }
    }
  },
  beforeMount() {
    this.detectaCliente();
    this.carregaGruposConab();
    this.carregaClassesConab();
  },
  watch: {
    codLote: function () {
      const loteSelecionado = this.lotesDaOs.filter(
        (l) => l.value === this.codLote
      )[0];
      this.codMunicipio = loteSelecionado.CodMunicipio;
    },
    impurezaGramas: function () {
      this.impureza = this.calculaPorcentagemApartirDaGrama(
        this.impurezaGramas
      );
    },
    quebradoGramas: function () {
      this.quebrados = this.calculaPorcentagemApartirDaGrama(
        this.quebradoGramas
      );
    },
    carunchadosGramas: function () {
      this.carunchados = this.calculaPorcentagemApartirDaGrama(
        this.carunchadosGramas
      );
    },
    sadiosGramas: function () {
      this.sadios = this.calculaPorcentagemApartirDaGrama(this.sadiosGramas);
    },
    ardidosGrama: function () {
      this.ardidos = this.calculaPorcentagemApartirDaGrama(this.ardidosGrama);
      this.calculaTotal();
    },
    mofadosGramas: function () {
      this.mofados = this.calculaPorcentagemApartirDaGrama(this.mofadosGramas);
      this.calculaTotal();
    },
    fermentadosGramas: function () {
      this.fermentados = this.calculaPorcentagemApartirDaGrama(
        this.fermentadosGramas
      );
      this.calculaTotal();
    },
    germinadosGramas: function () {
      this.germinados = this.calculaPorcentagemApartirDaGrama(
        this.germinadosGramas
      );
      this.calculaTotal();
    },
    gessadosGramas: function () {
      this.gessados = this.calculaPorcentagemApartirDaGrama(
        this.gessadosGramas
      );
      this.calculaTotal();
    },
    chochosGramas: function () {
      this.chochos = this.calculaPorcentagemApartirDaGrama(this.chochosGramas);
      this.calculaTotal();
    },
    umidade: function () {
      this.validaUmidade();
    },
    quebrados: function () {
      this.validaQuebrados();
    },
    observacao: function () {
      this.validaUmidade();
      this.validaQuebrados();
    }
  },
};
</script>

