import { getCookie } from "./Cookies.js";
import { post } from "./ConexaoApi.js"
export function verificaAdministrador() {
    return new Promise((resolve, reject) => {
        let Administrador = {}
        const Token = {}
        Token.token = getCookie('tk');
        //Verifica se o usuario  administrador ou no para poder liberar os menus de administrador
        post('authadm', Token).then((result) => {
            Administrador = JSON.parse(result);
            if (Administrador.superuser == true) {
                resolve(true);
            } else {
                reject(false);
            }
        }).catch(() => {
            reject(false);
        });
    });
}

