import Vue from "vue";
import Router from "vue-router";
import Login from "./views/Login.vue";
import Home from "./views/Home.vue";
import OS from "./views/OrdemServico";
import Classificadores from "./views/Classificadores";
import impressao from "./views/ImpressoPadraoExata";
import Justificativa from "./views/Justificativas";
import LoginAreaCliente from "./views/LoginAreaCliente";
import AreaCliente from "./views/AreaCliente";
import ClientesComAcesso from "./views/Clientes";
import Ajuda from "./views/ManualAjuda";
import glycine from "@/components/LayoutImpressaoGlycinemax";
import positivagro from "@/components/LayoutImpressaoPositivagro";
import GranQuality from '../src/components/LayoutImpressaoGranQuality.vue';
import impressoClasgraos from '@/components/LayoutImpressaoClasGraos.vue';
import AgroService from './components/LayoutImpressaoAgroService.vue';
import FGLAgro from '@/components/LayoutImpressaoFGLAgro.vue';
import VHS from '@/components/LayoutImpressaoVHS.vue';
import Futura from '@/components/LayoutImpressaoPadraoFutura';
import Agronorte from '@/components/LayoutImpressaoAgroNorte.vue';
import Focograo from '@/components/LayoutImpressaoPadraoFocoGrao.vue';
import LayouyPadrao from './components/LayoutImpressaoPadrao.vue'
import Relatorio from '../src/components/Relatorios/RelatorioEmbarque.vue'
import SolicitacoesAreaCliente from './views/SolicitacoesAreaCliente.vue'
import Solicitacoes from './views/Solicitacoes.vue'
import Despesa from './views/Despesa.vue'
import Conab from './views/Conab.vue'
import FormularioConab from './components/ModalFormularioConab.vue'
import PoliticaPrivacidade from './components/LayoutPoliticaPrivacidade.vue'

Vue.use(Router);

//export default new Router({
const router = new Router({
  //mode: 'history',
  //base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "Login",
      component: Login,
      meta: { title: "Alfameta Sistemas - Login" }
    },
    {
      path: "/home",
      name: "Home",
      component: Home,
      meta: { title: "Alfameta Sistemas - Frente WEB" }
    },
    {
      path: "/os",
      name: "OrdemServico",
      component: OS,
      meta: { title: "Alfameta Sistemas - Ordens de servio" }
    },
    {
      path: "/classificadores",
      name: "Classificadores",
      component: Classificadores,
      meta: { title: "Alfameta Sistemas - Classificadores" }
    },
    {
      path: "/justificativas",
      name: "JustificativaNaoEmbarque",
      component: Justificativa,
      meta: { title: "Alfameta Sistemas - Justificativas" }
    },
    //Quando houver um parametro na URL, ele entrar na rota abaixo
    {
      path: "/impresso/:c",
      name: "QRCodeExata",
      component: impressao,
      meta: { title: "Alfameta Sistemas - Classificaes" }
    },
    //Caso no haja parametro, ele entra nessa e ao entrar nessa, ele  jogado para a tela de Login
    {
      path: "/impresso",
      name: "QRCodeExataSemUUID",
      component: impressao,
      meta: { title: "Alfameta Sistemas - Classificaes" }
    },
    {
      path: "/areacliente",
      name: "LoginAreaDoUsurio",
      component: LoginAreaCliente,
      meta: { title: "Area Cliente - Login" }
    },
    {
      path: "/areacliente/principal",
      component: AreaCliente,
      name: "areaPrincipalCliente",
      meta: { title: "Area do cliente - Principal" }
    },
    {
      path: "/clientes",
      component: ClientesComAcesso,
      name: "ClientesComAcesso",
      meta: { title: "Clientes com acesso" }
    },
    {
      path: "/ajuda",
      component: Ajuda,
      name: "AlfametaAjuda",
      meta: { title: "Alfameta - Ajuda" }
    },
    {
      path: "/qrcode/:u",
      component: glycine,
      name: "QRCodeGlycinemax",
      meta: { title: "Glycinemax Classificaes - Dados da classificao" }
    },

    {
      path: "/impressoagroservice/:u",
      component: AgroService,
      name: "QRCodeAgroService",
      meta: { title: "AgroService - Classificaes/Testes" }
    },

    {
      path: "/layoutpadrao/:u",
      component: LayouyPadrao,
      name: 'LayoutPadrao',
      meta: { title: 'Layout Padrao Alfameta Sistemas' }
    },



    {
      path: "/qrcode",
      component: AreaCliente,
      name: "QRCodeGlycinemaxSemUUID",
      meta: { title: "Area do cliente - Principal" }
    },
    {
      path: "/positiva/:u",
      component: positivagro,
      name: "QRCodePositivagro",
      meta: { title: "Positivagro Classificaes - Dados da classificao" }
    },
    {
      path: "/positiva",
      component: AreaCliente,
      name: "QRCodePositivagroSemUUID",
      meta: { title: "Area do cliente - Principal" }
    },
    {
      path: "/fgl/:u",
      component: FGLAgro,
      name: "QRCodeFGLAgro",
      meta: { title: "FGL Agro Servios Agrcolas - Dados da classificao" }
    },
    {
      path: "/fgl",
      component: AreaCliente,
      name: "QRCodeFGLAgroSemUUID",
      meta: { title: "Area do cliente - Principal" }
    },
    {
      path:'/relatorio',
      component: Relatorio,
      name:'RelatorioEmbarque',
      meta:{title : "Relatrio de embarque"},
      props:true
    },
    {
      path: "/impclasgraos/:u",
      component: impressoClasgraos,
      name: "QRCodeClasgraos",
      meta: { title: "Clasgros Classificaes - Dados da classificao" }
    },
    {
      path: "/impclasgraos",
      component: AreaCliente,
      name: "QRCodeClasgraosSemUUID",
      meta: { title: "Area do cliente - Principal" }
    },
    {
      path: "/areacliente/solicitacoes",
      component: SolicitacoesAreaCliente,
      name: "viewSolicitacoesAreaCliente",
      meta: { title: "Minhas solicitaes" }
    },
    {
      path: "/solicitacoes",
      component: Solicitacoes,
      name: "viewSolicitacoes",
      meta: { title: "Solicitaes" }
    },
    {
      path: "/qrcode/:u",
      component: VHS,
      name: "QRCodeVHS",
      meta: {title: "VHS classificaes e anlise de gros"}
    },

    {
    path: "/qrcode",
    component: AreaCliente,
    name: "QRCodeVHSSemUUID",
    meta: { title: "Area do cliente - Principal" }
    },


    {
      path: "/elite",
      component: AreaCliente,
      name: "QRCodeEliteSemUUID",
      meta: { title: "Area do cliente - Principal" }
    },

    {
      path: "/impressofutura/:u",
      component: Futura,
      name: "QRCodeFutura",
      meta: {title: "Futura anlise e classificao vegetal"}
    },

    {
      path: "/impressofocograo/:u",
      component: Focograo,
      name: "QRCodeFutura",
      meta: {title: "Foco Gro Classificaes LTDA"}
    },

    {
      path: "/impressoagronorte/:u",
      component: Agronorte,
      name: "QRCodeAgroNorte",
      meta: {title: "Agro Norte Classificao de Cereais LTDA"}
    },

    {
      path: "/qrcode",
      component: AreaCliente,
      name: "QRCodeFuturaSemUUID",
      meta: { title: "Area do cliente - Principal" }
    },

    {
      path: "/qrcode/:u",
      component: Agronorte,
      name: "QRCodeElite",
      meta: {title: "Agro Norte classificao de cereais"}
    },

    {
      path: "/qrcode/:u",
      component: Focograo,
      name: "QRCodeFoco",
      meta: {title: "Foco Gro classificao LTDA"}
    },

    {
      path: "/qrcode",
      component: AreaCliente,
      name: "QRCodeEliteSemUUID",
      meta: { title: "Area do cliente - Principal" }
    },

    {
      name: 'despesa',
      component: Despesa,
      path: '/despesa',
      meta: {
          title: 'Despesas'
      }
    },
    {
      name: 'conab',
      component: Conab,
      path: '/conab',
      meta: {
          title: 'Conab'
      },
    },
    {
      name: 'formularioConab',
      component: FormularioConab,
      path: '/conab/formulario',
      meta: {
          title: 'Conab'
      },
    },
    {
      name: 'formularioConab',
      component: FormularioConab,
      path: '/conab/formulario/:u',
      meta: {
          title: 'Conab'
      },
    },
    {
      name: 'impressoGranQuality',
      component: GranQuality,
      path: '/impressogranquality/:u',
      meta: {
          title: 'GranQuality Classificacoes'
      }
    },
    {
      name: 'classificacaoGranQuality',
      component: GranQuality,
      path: '/classificacaogranquality/:u',
      meta: {
        title: 'GranQuality Classificacoes'
      }
    },
    {
      name: 'impressoGranQualitySemUUID',
      component: AreaCliente,
      path: '/impressogranquality',
      meta: {
          title: 'GranQuality Classificacoes'
      }
    },
    {
      name: 'politicaPrivacidade',
      component: PoliticaPrivacidade,
      path: '/privacidade',
        meta: {
            title: 'Politica de Privacidade'
        }
    }
  ]
});

router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  next();
});

export default router;
