<template>
  <q-modal
    v-model="exibe"
    minimized
    :content-css="{width: '100vw', minWidth:'95vw', minHeight: '75vh'}"
    no-backdrop-dismis
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn
          style="padding:0px; margin:0"
          flat
          round
          dense
          icon="close"
          @click="confirmarCancelamentoDeCadastroDeOs"
        />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>

      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{tituloModal}}</h6>
      </q-toolbar>

      <div class="layout-padding">
        <div class="row">
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroCliente" error-label="Por favor, informe o cliente">
              <q-search
                v-model="termoBuscaCliente"
                clearable
                placeholder="Escolha um cliente"
                float-label="Escolha um cliente"
                :disable="modoVisualizacao == true"
                :error="erroCliente"
                @focus="todosCamposPreenchidos"
              >
                <q-autocomplete
                  @search="buscaCliente"
                  @selected="clienteSelecionado"
                  :max-results="15"
                />
              </q-search>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroClassificador" error-label="Por favor, informe um classificador">
              <q-input
                v-model="termoBuscaClassificador"
                placeholder="Escolha um classificador"
                float-label="Escolha um classificador"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              >
                <q-autocomplete
                  :static-data="{field:'label', list:classificadores}"
                  @selected="classificadorSelecionado"
                />
              </q-input>
            </q-field>
          </div>

          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroProduto"
              error-label="Por favor, informe o produto a ser classificado"
            >
              <q-select
                filled
                v-model="produtoSelecionado"
                float-label="Produto"
                :options="produtos"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroLocalEmbarque"
              error-label="Por favor, informe o local de embarque"
            >
              <q-search
                v-model="termoBuscaEmbarque"
                placeholder="Escolha o local de embarque"
                float-label="Escolha o local de embarque"
                :disable="modoVisualizacao == true"
                clearable
                @focus="todosCamposPreenchidos"
              >
                <q-autocomplete
                  @search="buscaLocalEmbarque"
                  @selected="embarqueSelecionado"
                  :max-results="15"
                />
              </q-search>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="numeroLeilao"
                placeholder="Numero do Leilo"
                float-label="Numero do Leilo"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>

          <div class="col-md-3 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroGrupo" error-label="Por favor, informe o grupo do produto">
              <q-select
                filled
                v-model="codGrupoSelecionado"
                float-label="Grupo"
                :options="gruposConab"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>
          <div class="col-md-3 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroClasse" error-label="Por favor, informe a classe do produto">
              <q-select
                filled
                v-model="codClasseSelecionada"
                float-label="Classe"
                :options="classesConab"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>
          <div class="col-md-3 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroTipo" error-label="Por favor, informe o tipo do produto">
              <q-input
                v-model="tipo"
                placeholder="Tipo"
                float-label="Tipo"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>
          <div class="col-md-3 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="marca"
                placeholder="Marca"
                float-label="Marca"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>
          <div class="col-md-3 col-sm-12 col-xs-12 margin-form">
            <q-input
              v-model="valorServico"
              placeholder="Valor Servio / Ton"
              float-label="Valor Servio / Ton"
              onkeypress="return event.charCode >= 48 && event.charCode <= 57 || (event.charCode == 44 || event.charCode == 46)"
              :disable="modoVisualizacao == true"
              @focus="todosCamposPreenchidos"
            />
          </div>
          <div class="col-md-3 col-sm-12 col-xs-12 margin-form">
            <q-input
              v-model="outrosValores"
              placeholder="Outros Valores / Ton"
              float-label="Outros Valores / Ton"
              onkeypress="return event.charCode >= 48 && event.charCode <= 57 || (event.charCode == 44 || event.charCode == 46)"
              :disable="modoVisualizacao == true"
              @focus="todosCamposPreenchidos"
            />
          </div>
          <div class="col-12" style="margin-top:10px;">
            <q-btn
              color="primary"
              label="Salvar OS"
              @click="comportamentoBotaoSalvar()"
              v-show="modoVisualizacao != true"
            />
          </div>
        </div>
        <div style="margin-top:10px">
          <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
        </div>
        <div class="col-12" v-show="modoVisualizacao">
          <tabela-item-ordem-servico-classificacao-conab ref="gradeItemOs"/>
        </div>
        <div class="col-12" v-show="modoVisualizacao">
          <tabela-lote-conab ref="gradeLoteConab" />
        </div>
      </div>

    </q-modal-layout>
  </q-modal>
</template>

<script>
import { detectaCliente } from "../components/utils/DetectaCliente.js";
import { get, post, put } from "../components/js/ConexaoApi.js";
import TabelaLoteConab from './TabelaLoteConab.vue'
import TabelaItemOrdemServicoClassificacaoConab from '../components/TabelaItemOrdemServicoClassificacaoConab.vue'


export default {
  name: "modalVisualizaAbreOS",
  props: ["visualizando"],
  components: {
    TabelaItemOrdemServicoClassificacaoConab,
    TabelaLoteConab
  },
  data() {
    return {
      //Variaveis de validao
      erroCliente: false,
      erroDestino: false,
      erroClassificador: false,
      erroProduto: false,
      erroPesoLiquido: false,
      erroLocalEmbarque: false,
      erroClasse: false,
      erroTipo: false,
      erroGrupo: false,
      codClienteAlfameta: 1,
      outrosValores: "",
      valorServico: "",
      tituloModal: "",
      modoVisualizacao: false,
      todosDadosCarregados: false,
      exibe: false,
      termoBuscaCliente: "",
      clientes: [],
      codClienteSelecionado: "",
      codEmbarqueSelecionado: "",
      codClassificadorSelecionado: "",
      termoBuscaEmbarque: "",
      locaisEmbarque: [],
      termoBuscaClassificador: "",
      classificadores: [],
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      produtos: [],
      produtoSelecionado: "",
      gruposConab: [],
      classesConab: [],
      numeroLeilao: "",
      marca: "",
      alteraInsere: "",
      codGrupoSelecionado: "",
      termoBuscaClasse: "",
      codClasseSelecionada: "",
      termoBuscaClasseConab: "",
      termoBuscaGrupoConab: "",
      tipo: "",
    };
  },
  methods: {
    abreModal() {
      this.limpaErrosCadastro();
      this.exibe = true;
      this.tituloModal = "Nova OS";
      this.modoVisualizacao = false;
      this.alteraInsere = 1;
    },
    carregaCampos(dadosOs) {
      this.limpaErrosCadastro();
      this.alteraInsere;
      this.produtoSelecionado = dadosOs.CodProduto;
      this.termoBuscaEmbarque = dadosOs.NomeLocalArmazenagem;
      this.codEmbarqueSelecionado = dadosOs.CodLocalArmazenagem;
      this.termoBuscaClassificador = dadosOs.NomeClassificador;
      this.codClienteSelecionado = dadosOs.CodCliente;
      this.termoBuscaCliente = dadosOs.NomeCliente;
      this.codClassificadorSelecionado = dadosOs.CodClassificador;
      this.outrosValores = dadosOs.OutrosValores;
      this.CodOrdemServicoClassificacaoConab =
        dadosOs.CodOrdemServicoClassificacaoConab;
      this.valorServico = dadosOs.ValorServico;
      this.codGrupoSelecionado = dadosOs.CodGrupoConab;
      this.codClasseSelecionada = dadosOs.CodClasseConab;
      this.tipo = dadosOs.Tipo;
      this.marca = dadosOs.Marca;
      this.numeroLeilao = dadosOs.NumeroLeilao;
      this.codGrupoSelecionado = dadosOs.CodGrupo;
      this.codClasseSelecionada = dadosOs.CodClasse;
    },
    abreModalVisualizacao(dadosOs) {
      this.limpaErrosCadastro();
      this.tituloModal = "Visualizando OS";
      this.modoVisualizacao = true;
      this.exibe = true;
      this.carregaCampos(dadosOs);
      this.$refs.gradeItemOs.carregaItensDaOs(this.CodOrdemServicoClassificacaoConab);
      this.$refs.gradeLoteConab.abreTabela(dadosOs);
    },
    abreModalAlteracao(dadosOs) {
      this.limpaErrosCadastro();
      this.alteraInsere = 2;
      this.tituloModal = "Alterao OS";
      this.modoVisualizacao = false;
      this.exibe = true;
      this.carregaCampos(dadosOs);
      this.$refs.gradeItemOs.carregaItensDaOs(this.CodOrdemServicoClassificacaoConab);
      this.$refs.gradeLoteConab.abreTabela(dadosOs);
    },
    fechaModal() {
      this.limpaErrosCadastro();
      this.abriuAtravesDeSolicitacao = false;
      this.exibe = false;

      this.numeroLeilao = "";
      this.termoBuscaCliente = "";
      this.codClienteSelecionado = "";
      this.codClassificadorSelecionado = "";
      this.termoBuscaClassificador = "";
      this.termoBuscaEmbarque = "";
      this.codEmbarqueSelecionado = "";
      this.produtoSelecionado = "";
      this.outrosValores = "";
      this.valorServico = "";
      this.codGrupoSelecionado = "";
      this.termoBuscaGrupoConab = "";
      this.codClasseSelecionada = "";
      this.termoBuscaClasse = "";
      this.safra = "";
      this.tipo = "";
      this.marca = "";
    },
    todosCamposPreenchidos() {
      return new Promise((resolve) => {
        if (this.codClienteSelecionado == "" || this.termoBuscaCliente == "") {
          this.erroCliente = true;
        } else {
          this.erroCliente = false;
        }
        if (
          this.codClassificadorSelecionado == "" ||
          this.termoBuscaClassificador == ""
        ) {
          this.erroClassificador = true;
        } else {
          this.erroClassificador = false;
        }
        if (this.produtoSelecionado == "") {
          this.erroProduto = true;
        } else {
          this.erroProduto = false;
        }
        if (
          this.codEmbarqueSelecionado == "" ||
          this.termoBuscaEmbarque == ""
        ) {
          this.erroLocalEmbarque = true;
        } else {
          this.erroLocalEmbarque = false;
        }
        if (this.codGrupoSelecionado == "") {
          this.erroGrupo = true;
        } else {
          this.erroGrupo = false;
        }
        if (this.codClasseSelecionada == "") {
          this.erroClasse = true;
        } else {
          this.erroClasse = false;
        }
        if (this.tipo == "") {
          this.erroTipo = true;
        } else {
          this.erroTipo = false;
        }
        if (
          this.erroCliente == false &&
          this.erroClassificador == false &&
          this.erroProduto == false &&
          this.erroLocalEmbarque == false &&
          this.erroGrupo == false &&
          this.erroClasse == false &&
          this.erroTipo == false
        ) {
          resolve(true);
        } else {
          resolve(false);
        }
      });
    },
    converteKgParaTon() {
      this.toneladas = this.pesoLiquido / 1000;
    },
    converteTonParaKg() {
      this.pesoLiquido = this.toneladas * 1000;
    },
    confirmarCancelamentoDeCadastroDeOs() {
      if (this.modoVisualizacao != true) {
        this.$q
          .dialog({
            title: "Cancelar lanamento de OS",
            message: "Deseja realmente cancelar o lanamento da OS?",
            color: "negative",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => {
            this.fechaModal();
          })
          .catch(() => {});
      } else {
        this.fechaModal();
      }
    },
    limpaErrosCadastro() {
      this.erroCliente = false;
      this.erroDestino = false;
      this.erroClassificador = false;
      this.erroProduto = false;
      this.erroPesoLiquido = false;
      this.erroLocalEmbarque = false;
    },
    carregaDados() {
      if (this.$props.visualizando == false) {
        let clientesCarregados = false;
        let locaisCarregados = false;
        let classificadoresCarregados = false;
        let produtosCarregados = false;
        if (this.todosDadosCarregados != true) {
          //Carrega os clientes
          get("clientes")
            .then((result) => {
              let jsonClientes = result;
              jsonClientes = jsonClientes.replace(/"CodCliente":/g, '"value":');
              jsonClientes = jsonClientes.replace(/"Nome":/g, '"label":');
              jsonClientes = jsonClientes.replace(/"CnpjCpf":/g, '"sublabel":');
              jsonClientes = jsonClientes.replace(
                /"NomeMunicipio":/g,
                '"stamp":'
              );
              this.clientes = JSON.parse(jsonClientes);
              clientesCarregados = true;
              // eslint-disable-next-line no-console
            })
            .catch(() => {
              // eslint-disable-next-line no-console
              console.error("Falha ao carregar clientes");
            });
          //Carrega os locais de embarque
          get("locais")
            .then((result) => {
              let jsonLocais = result;
              jsonLocais = jsonLocais.replace(
                /"CodLocalArmazenagem":/g,
                '"value":'
              );
              jsonLocais = jsonLocais.replace(/"Nome":/g, '"label":');
              jsonLocais = jsonLocais.replace(/"cnpjcpf":/g, '"sublabel":');
              jsonLocais = jsonLocais.replace(/"NomeMunicipio":/g, '"stamp":');
              this.locaisEmbarque = JSON.parse(jsonLocais);
              locaisCarregados = true;
              // eslint-disable-next-line no-console
            })
            .catch(() => {
              // eslint-disable-next-line no-console
              console.error("Falha ao carregar locais de embarque");
            });
          //Carrega os classificadores
          get("classificadores")
            .then((result) => {
              result = result.replace(/"id":/g, '"value":');
              result = result.replace(/"nome":/g, '"label":');
              this.classificadores = JSON.parse(result);
              classificadoresCarregados = true;
              // eslint-disable-next-line no-console
            })
            .catch(() => {
              // eslint-disable-next-line no-console
              console.error("Falha ao carregar classificadores");
            });
          //Carrega os municipios de destino

          get("produto")
            .then((result) => {
              let jsonProdutos = result;
              jsonProdutos = jsonProdutos.replace(/"CodProduto":/g, '"value":');
              jsonProdutos = jsonProdutos.replace(/"Nome":/g, '"label":');
              this.produtos = JSON.parse(jsonProdutos);
              produtosCarregados = true;
              // eslint-disable-next-line no-console
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              console.error(err);
            });
          get("gruposconab")
            .then((result) => {
              let jsonGruposConab = result;
              jsonGruposConab = jsonGruposConab.replace(
                /"CodGrupo":/g,
                '"value":'
              );
              jsonGruposConab = jsonGruposConab.replace(/"Nome":/g, '"label":');
              this.gruposConab = JSON.parse(jsonGruposConab);
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              console.error(err);
            });
          get("classesconab")
            .then((result) => {
              let jsonClassesConab = result;
              jsonClassesConab = jsonClassesConab.replace(
                /"CodClasse":/g,
                '"value":'
              );
              jsonClassesConab = jsonClassesConab.replace(
                /"Nome":/g,
                '"label":'
              );
              this.classesConab = JSON.parse(jsonClassesConab);
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              console.error(err);
            });
          if (
            clientesCarregados == true &&
            locaisCarregados == true &&
            classificadoresCarregados == true &&
            produtosCarregados == true
          ) {
            this.todosDadosCarregados = true;
          } else {
            this.todosDadosCarregados = false;
          }
        }
      }
    },
    buscaCliente(terms, done) {
      get(`clientessite?filter=${this.termoBuscaCliente}`)
        .then((result) => {
          let jsonClientes = result;
          jsonClientes = jsonClientes.replace(/"CodCliente":/g, '"value":');
          jsonClientes = jsonClientes.replace(/"Nome":/g, '"label":');
          jsonClientes = jsonClientes.replace(/"CnpjCpf":/g, '"sublabel":');
          jsonClientes = jsonClientes.replace(/"NomeMunicipio":/g, '"stamp":');
          const clientes = JSON.parse(jsonClientes);
          done(clientes);
        })
        .catch(() => {
          done([]);
        });
    },
    buscaLocalEmbarque(terms, done) {
      get(`locaissite?filter=${this.termoBuscaEmbarque}`)
        .then((result) => {
          let jsonLocais = result;
          jsonLocais = jsonLocais.replace(
            /"CodLocalArmazenagem":/g,
            '"value":'
          );
          jsonLocais = jsonLocais.replace(/"Nome":/g, '"label":');
          jsonLocais = jsonLocais.replace(/"cnpjcpf":/g, '"sublabel":');
          jsonLocais = jsonLocais.replace(/"NomeMunicipio":/g, '"stamp":');
          // jsonLocais = jsonLocais.replace(/\"RazaoSocial\":/g, '"a":')
          this.locaisEmbarque = JSON.parse(jsonLocais);
          this.locaisEmbarque.forEach((local) => {
            local.label =
              local.RazaoSocial !== null
                ? local.label + " - " + local.RazaoSocial
                : local.label;
          });
          done(this.locaisEmbarque);
        })
        .catch(() => {
          done([]);
        });
    },
    async salvaOS() {
      const todosCamposValidados = await this.todosCamposPreenchidos();
      if (todosCamposValidados) {
        const OS = {};
        OS.CodCliente = this.codClienteSelecionado;
        OS.CodClassificador = this.codClassificadorSelecionado;
        OS.CodProduto = this.produtoSelecionado;
        OS.NumeroLeilao = this.numeroLeilao;
        OS.CodLocalArmazenagem = this.codEmbarqueSelecionado;
        if (this.outrosValores !== null && this.outrosValores !== "") {
          OS.OutrosValores = this.outrosValores.replace(/,/g, ".");
        } else {
          OS.OutrosValores = null;
        }
        if (this.valorServico !== null && this.valorServico !== "") {
          OS.ValorServico = this.valorServico.replace(/,/g, ".");
        } else {
          OS.ValorServico = null;
        }
        OS.CodGrupo = this.codGrupoSelecionado;
        OS.CodClasse = this.codClasseSelecionada;
        OS.TipoConab = this.tipo;
        OS.Tipo = this.tipo;
        post("ordemservicoconab", OS)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              if (this.abriuAtravesDeSolicitacao == true) {
                this.informaOsAberta(Resposta.CodOS);
              }
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
      }
    },
    atualizaTabelaOs() {
      this.$emit("atualizaTabela");
    },
    async informaOsAberta(CodOs) {
      const OS = {}
      OS.CodSolicitacao = this.codsolicitacao;
      OS.CodOrdemServicoClassificacaoConab = CodOs;
      post("osabertasolicitacao", OS)
        .then(() => {
          this.$emit("atualizaSolicitacao");
        })
        .catch(() => {});
    },
    async alteraOs() {
      const todosCamposPreenchidos = await this.todosCamposPreenchidos();
      if (todosCamposPreenchidos) {
        this.exibeMensagemRetorno = false;
        const os = {}
        os.NumeroContrato = this.numeroLeilao;
        os.CodOrdemServicoClassificacaoConab = this.CodOrdemServicoClassificacaoConab;
        os.CodCliente = this.codClienteSelecionado;
        os.CodClassificador = this.codClassificadorSelecionado;
        os.CodProduto = this.produtoSelecionado;
        os.PesoLiquido = this.pesoLiquido;
        os.CodLocalArmazenagem = this.codEmbarqueSelecionado;
        os.OutrosValores = this.outrosValores;
        os.ValorServico = this.valorServico;
        os.CodGrupo = this.codGrupoSelecionado;
        os.CodClasse = this.codClasseSelecionada;
        os.Tipo = this.tipo;
        this.TipoOrdemServico = "C";
        // console.log(JSON.stringify(os));
        put("ordemservicoconab", os)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
      }
    },
    buscaClassificador(terms, done) {
      setTimeout(() => {
        done(
          // eslint-disable-next-line no-undef
          filter(this.termoBuscaClassificador, {
            field: "label",
            list: this.classificadores,
          })
        );
      }, 100);
    },
    selecionaClienteApartirSolicitacao(codCliente) {
      this.codClienteSelecionado = codCliente;
      this.termoBuscaCliente = this.clientes.find(
        (x) => x.value == codCliente
      ).label;
    },
    clienteSelecionado(cliente) {
      this.termoBuscaCliente = cliente.label;
      this.codClienteSelecionado = cliente.value;
    },
    grupoSelecionado(grupo) {
      this.termoBuscaGrupoConab = grupo.label;
      this.codGrupoSelecionado = grupo.value;
    },
    classeSelecionada(classe) {
      this.termoBuscaClasse = classe.label;
      this.codClasseSelecionada = classe.value;
    },
    embarqueSelecionado(embarque) {
      this.termoBuscaEmbarque = embarque.label;
      this.codEmbarqueSelecionado = embarque.value;
    },
    classificadorSelecionado(classificador) {
      this.termoBuscaClassificador = classificador.label;
      this.codClassificadorSelecionado = classificador.value;
    },
    comportamentoBotaoSalvar() {
      if (this.alteraInsere == 1) {
        return this.salvaOS();
      } else {
        return this.alteraOs();
      }
    },
    selecionaCliente() {
      detectaCliente()
        .then((result) => {
          this.codClienteAlfameta = result;
        })
        .catch(() => {
          this.codClienteAlfameta = 0;
        });
    },
  },
  mounted() {
    this.carregaDados();
    this.selecionaCliente();
  },
  watch: {
    exibe: function () {
      if (this.exibe == false) {
        this.fechaModal();
      }
    },
  },
};
</script>

