export function ConverteDataHorarioFormatoBancoDeDados(data){
    return new Promise(resolve =>{
        let dataFormatada = "";
        let dia = "";
        let mes = "";
        let ano = "";
        let hora = "";
        let minuto = "";
        let segundo = "";
        //Se nenhuma data tiver sido informada, retornar a data e hora atuais
        if(data == null || data === ""){
            const date = new Date();
            dia = date.getDate()
            mes = date.getMonth()+1;
            ano = date.getFullYear();
            hora = date.getHours();
            minuto = date.getMinutes();
            segundo = date.getSeconds();
            if(dia < 10){
              dia = '0'+dia;
            }
            if(mes < 10){
              mes = '0'+mes;
            }
            if(hora < 10){
              hora = '0'+hora;
            }
            if(minuto < 10){
              minuto = '0'+minuto;
            }
            if(segundo < 10){
              segundo = '0'+segundo;
            }
            dataFormatada = ano+'-'+mes+'-'+dia+' '+hora+':'+minuto+':'+segundo;
            //Se no, ir formatar a data informada
          }else{
            ano = data.substring(0,4);
            mes = data.substring(5,7);
            dia = data.substring(8,10);
            hora = data.substring(11,13);
            minuto = data.substring(14,16);
            data = ano+"-"+mes+"-"+dia+" "+hora+":"+minuto+":00"
            dataFormatada = data;
          }
          resolve(dataFormatada);
    });
}
