<template>
  <div class="caixa-login">
    <div>
      <div class="centraliza-titulo">
        <img :src="logo" class="logo-responsiva" />
      </div>
      <q-field icon="fas fa-user">
        <q-input
          v-model="usuario"
          type="text"
          float-label="Usurio"
          placeholder="Informe seu usurio"
          @keyup.enter="$refs.campoSenha.focus"
        />
      </q-field>
      <q-field icon="fas fa-key">
        <q-input
          ref="campoSenha"
          v-model="senha"
          type="password"
          float-label="Senha"
          placeholder="Informe sua senha"
          @keyup.enter="fazLogin()"
        />
      </q-field>
      <q-btn class="logo-responsiva btn-login" color="primary" label="Entrar" @click="fazLogin" />
    </div>
    <div style="margin-top:5px">
      <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
    </div>
    <div class="jusitifica-centro tamanho-fonte-versao">
      <div>{{versao}}</div>
      <div>{{dataVersao}}</div>
    </div>
    <ModalPrimeiroLogin
      ref="modalPrimeiraSenha"
      @senhaAlterada="senhaAlterada"
      usuarioOuCliente="usuario"
    />
  </div>
</template>

<script>
import { setCookie } from "../components/js/Cookies.js";
import { post } from "../components/js/ConexaoApi.js";
import { detectaCliente } from "../components/utils/DetectaCliente.js";
import ModalPrimeiroLogin from "../components/ModalPrimeiroLogin";
export default {
  components: {
    ModalPrimeiroLogin,
  },
  name: "Login",
  data() {
    return {
      erroUsuario: false,
      erroSenha: false,
      logo: "",
      usuario: "",
      senha: "",
      versao: "2.7.0",
      dataVersao: "25/07/2023",
      tipoMensagem: "positive",
      mensagemRetorno: "",
      exibeMensagemRetorno: "",
    };
  },
  methods: {
    fazLogin() {
      const Usuario = {}
      Usuario.Login = this.usuario.toLowerCase();
      Usuario.Senha = this.senha;
      post("auth/login/", Usuario)
        .then((result) => {
          const Resposta = JSON.parse(result);
          if (Resposta.Status) {
            if (Resposta.PrimeiroLogin === "S") {
              setCookie("id", Resposta.CodUsuario);
              setCookie("tk", Resposta.token_acesso, 7);
              this.$refs.modalPrimeiraSenha.abreModal(this.fazLogin);
            } else {
              this.exibeMensagemRetorno = true;
              this.tipoMensagem = "positive";
              this.mensagemRetorno = "Entrando";
              setCookie("tk", Resposta.token_acesso, 7);
              setCookie("id", Resposta.CodUsuario);
              setCookie("nome", Resposta.NomeUsuario);
              setCookie("su", Resposta.Superuser);
              setCookie("sup", Resposta.Supervisor);
              setCookie(
                "codClassificadorSistema",
                Resposta.CodUsuarioClassificadorSistema
              );
              this.$router.push("/os");
            }
          }
        })
        .catch((err) => {
          const Erro = JSON.parse(err);
          this.exibeMensagemRetorno = true;
          this.tipoMensagem = "negative";
          this.mensagemRetorno = Erro.Erro;
        });
    },
    senhaAlterada(novaSenha) {
      this.senha = novaSenha;
    },
    detectaCliente() {
      detectaCliente()
        .then((result) => {
          this.logo = result.LogoEmpresa;
        })
        .catch(() => {
          this.logo =
            "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/logotransparente.png";
        });
    },
  },
  beforeMount() {
    this.detectaCliente();
  },
};
</script>

<style scoped>
.caixa-login {
  border: 1px solid black;
  position: relative;
  width: 315px;
  height: auto;
  display: table;
  margin: 0 auto;
  margin-top: 90px;
  border-radius: 4px;
  padding: 8px;
}

.logo-responsiva {
  margin-top: 5px;
  width: 100%;
  height: auto;
}

.centraliza-titulo {
  text-align: center;
}

.distancia-inputs {
  margin-top: 5px;
  border-radius: 4px;
}
.btn-login {
  margin-top: 15px;
  height: 50px;
}
html,
body {
  width: 100%;
  height: 100%;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  background-color: white;
}
.jusitifica-centro {
  margin-top: 15px;
  text-align: center;
  margin-bottom: 0px;
}
.tamanho-fonte-versao {
  font-size: 12px;
  font-family: monospace;
}
</style>

