<template>
  <q-modal
    v-model="exibe"
    minimized
    :content-css="{width: '85vw', minHeight: '75vh'}"
    no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn style="padding:0px; margin:0" flat round dense v-close-overlay icon="close" />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>
      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">Visualizando OS</h6>
      </q-toolbar>
      <div class="layout-padding">
        <div class="row">
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="termoBuscaCliente"
                placeholder="Escolha um cliente"
                float-label="Escolha um cliente"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="termoBuscaDestino"
                placeholder="Escolha o destino"
                float-label="Escolha o destino"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="termoBuscaClassificador"
                placeholder="Escolha um classificador"
                float-label="Escolha um classificador"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="produtoSelecionado"
                type="text"
                float-label="Produto"
                placeholder="Produto"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="contrato"
                placeholder="Contrato"
                float-label="Contrato"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="pesoLiquido"
                placeholder="Peso Lquido"
                float-label="Peso Lquido"
                type="number"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-select
                filled
                v-model="cadenciaSelecionada"
                float-label="Possui cadencia"
                :options="opcoesCadencia"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="pesoCadencia"
                placeholder="Peso Cadncia"
                float-label="Peso Cadncia"
                type="number"
                :disable="cadenciaSelecionada == 'N' || modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="statusSelecionado"
                type="text"
                float-label="Status"
                placeholder="Status"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="termoBuscaEmbarque"
                placeholder="Escolha o local de embarque"
                float-label="Escolha o local de embarque"
                :disable="modoVisualizacao == true"
              ></q-input>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="filial"
                placeholder="Filial"
                float-label="Filial"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="totalEmbarcado"
                placeholder="Total Embarcado (Tons)"
                float-label="Total Embarcado (Tons)"
                disable
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="totalCargas"
                placeholder="Quantidade de cargas"
                float-label="Quantidade de cargas"
                disable
              />
            </q-field>
          </div>
          <!--<div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-show="codClienteAlfameta == 2">
            <q-input v-model="valorServico" placeholder="Valor Servio / Ton" float-label="Valor Servio / Ton"  :disable="modoVisualizacao == true" />
          </div>-->
        </div>
      </div>
      <TabelaClassificacoes ref="gradeItemOs" />
    </q-modal-layout>
  </q-modal>
</template>

<script>
import TabelaClassificacoes from "@/components/AreaCliente/components/TabelaClassificacoesCliente.vue";
import { getComCabecalho } from "../js/ConexaoApiCliente.js";
import { detectaCliente } from "../../utils/DetectaCliente.js";
export default {
  name: "modalVisualizaAbreOS",
  components: {
    TabelaClassificacoes,
  },
  data() {
    return {
      //codClienteAlfameta = 1 = Exata
      //codClienteAlfameta = 2  Glycine
      codClienteAlfameta: 1,
      valorServico: "",
      CodOrdemServicoClassificacao: "",
      modoVisualizacao: false,
      exibe: false,
      totalEmbarcado: "",
      totalCargas: "",
      termoBuscaCliente: "",
      codClienteSelecionado: "",
      codEmbarqueSelecionado: "",
      codMunicipioSelecionado: "",
      codClassificadorSelecionado: "",
      termoBuscaEmbarque: "",
      termoBuscaClassificador: "",
      termoBuscaDestino: "",
      produtoSelecionado: "",
      contrato: "",
      pesoLiquido: "",
      filial: "",
      pesoCadencia: "",
      opcoesCadencia: [
        { value: "S", label: "Sim" },
        { value: "N", label: "No" },
      ],
      cadenciaSelecionada: "N",
      statusSelecionado: "A",
      alteraInsere: "",
    };
  },
  methods: {
    carregaCampos(dadosOs, funcaoBotaoSalvar) {
      if (dadosOs.CodProduto == "1") {
        this.produtoSelecionado = "Soja em Gros";
      } else {
        this.produtoSelecionado = "Milho em Gros";
      }
      this.totalEmbarcado = dadosOs.TotalEmbarcado;
      this.totalCargas = dadosOs.TotalCarga;
      this.alteraInsere = funcaoBotaoSalvar;
      this.termoBuscaCliente = dadosOs.nomecliente;
      this.termoBuscaDestino = dadosOs.NomeMunicipio;
      this.termoBuscaEmbarque = dadosOs.nomelocalarmazenagem;
      this.termoBuscaClassificador = dadosOs.nomeclassificador;
      this.codClienteSelecionado = dadosOs.CodCliente;
      this.codEmbarqueSelecionado = dadosOs.CodLocalArmazenagem;
      this.codMunicipioSelecionado = dadosOs.CodMunicipio;
      this.codClassificadorSelecionado = dadosOs.CodClassificador;
      this.contrato = dadosOs.NumeroContrato;
      this.pesoLiquido = dadosOs.PesoLiquido;
      this.pesoCadencia = dadosOs.PesoCadencia;
      if (dadosOs.Status == "A") {
        this.statusSelecionado = "Aberto";
      } else {
        this.statusSelecionado = "Fechado";
      }
      this.cadenciaSelecionada = dadosOs.Cadencia;
      this.filial = dadosOs.Filial;
      this.CodOrdemServicoClassificacao = dadosOs.CodOrdemServicoClassificacao;
      this.carregaTotais();
    },
    abreModalVisualizacao(dadosOs) {
      this.modoVisualizacao = true;
      this.exibe = true;
      this.carregaCampos(dadosOs);
      this.$refs.gradeItemOs.pegaCodOs(this.CodOrdemServicoClassificacao);
    },
    carregaTotais() {
      getComCabecalho(
        "totaisembarque",
        "CodOrdemServicoClassificacao",
        this.CodOrdemServicoClassificacao
      )
        .then((result) => {
          const Resposta = JSON.parse(result);
          this.totalEmbarcado = Resposta.TotalEmbarcado / 1000 + "t";
          //A linha abaixo serve para quando for montar em KGs, ele coloca virgula para separar
          //this.totalEmbarcado = Math.max(0, ).toFixed(0).replace(/(?=(?:\d{3})+$)(?!^)/g, ',');
          this.totalCargas = Resposta.TotalCarga;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    fechaModal() {
      this.exibe = false;
      this.termoBuscaClassificador = "";
      this.contrato = "";
      this.statusSelecionado = "A";
      this.cadenciaSelecionada = "N";
      this.termoBuscaDestino = "";
      (this.termoBuscaCliente = ""), (this.termoBuscaEmbarque = "");
      this.produtoSelecionado = "";
      this.filial = "";
      this.pesoCadencia = "";
      this.cadenciaSelecionada = "";
      this.codClienteSelecionado = "";
      this.codEmbarqueSelecionado = "";
      this.codMunicipioSelecionado = "";
      this.codClassificadorSelecionado = "";
      this.valorServico = "";
    },
    selecionaCliente() {
      detectaCliente()
        .then((result) => {
          this.codClienteAlfameta = result.CodCliente;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
  },
  beforeMount() {
    this.selecionaCliente();
  },
};
</script>

<style scoped>
.tira-margin-pading {
  padding: 0;
  margin: 0;
}
.margin-form {
  padding: 5px;
}
@media screen and (max-width: 768px) {
  .margin-form {
    padding: 0px;
  }
  .lagura-modal {
    width: 70px;
  }
}
</style>
