<template>
  <div>
    <div class="row container" id="content">
      <div class="caixa row col-md-12 col-sm-12 margin-bottom">
        <div class="col-md-4 col-sm-4">
          <img :src="logo" class="tamanho-logo" />
        </div>
        <div class="col-md-8 col-sm-8 row contatos">
          <div class="col-md-12 col-sm-12">(64) 99985-2935 / 99906-3189</div>
          <div class="col-md-12 col-sm-12">(66) 99987-4969</div>
        </div>
      </div>
      <div class="centraliza-texto laudo-classificacao col-12 margin-bottom">
        Laudo de classificao
        <span class="numero-laudo">{{numeroLaudo}}</span>
      </div>
      <div class="col-12 row">
        <div class="col-12">Cliente: {{nomeCliente}}</div>
        <div class="col-12">Local de embarque: {{localEmbarque}}</div>
        <div class="col-12">Destino:{{municipioDestino}}</div>
        <div class="row col-12">
          <div class="col-4">PLACA CAM.: {{placaCavalo}}</div>
          <div class="col-4">PL. 1 VAGO.: {{placa1}}</div>
          <div class="col-4">PL. 2 VAGO.: {{placa2}}</div>
        </div>
        <div class="col-12">Produto: {{nomeProduto}}</div>
      </div>
      <div class="col-12 row margin-bottom">
        <div class="col-6">Peso: {{pesoLiquido}} Kg</div>
        <div class="col-6">Nota Fiscal: {{numNf}}</div>
      </div>
      <div class="col-12 row margin-bottom">Tipo do produto: {{tipoProduto}}</div>
      <div class="col-12 margin-bottom"  v-if="codProduto == 1 || codProduto == 2 ">
        <dl>
          <dt>Umidade:</dt>
          <dd>{{umidade}}%</dd>
          <dt>Materias Estranhas e/ou Impurezas:</dt>
          <dd>{{impureza}}%</dd>
          <dt>Esverdeados:</dt>
          <dd>{{esverdeados}}%</dd>
          <dt>Partidos/Quebrados:</dt>
          <dd>{{quebrados}}%</dd>
          <dt>Ardidos:</dt>
          <dd>{{ardidos}}%</dd>
          <dt>Queimados</dt>
          <dd>{{queimados}}%</dd>
          <dt>Picados:</dt>
          <dd>{{picados}}%</dd>
          <dt>Fermentados:</dt>
          <dd>{{fermentados}}%</dd>
          <dt>Total Avariado:</dt>
          <dd>{{total}}%</dd>
        </dl>
      </div>
      <div class="col-12 margin-bottom"  v-if ="codProduto == 3">
        <dl>
          <dt>Umidade:</dt>
          <dd>{{umidade}}%</dd>
          <dt>Proteina:</dt>
          <dd>{{proteina}}%</dd>
        </dl>
      </div>
      <div class="col-12 margin-bottom"  v-if ="codProduto == 4">
        <dl>
          <dt>Umidade:</dt>
          <dd>{{umidade}}%</dd>
          <dt>Materias Estranhas e/ou Impurezas:</dt>
          <dd>{{impureza}}%</dd>
        </dl>
      </div>
      <div class="row col-12 margin-bottom">
        <div class="col-3">
          GMO:
          <q-checkbox v-model="convencional" label="Convencional" disable />
        </div>
        <div class="col-3">
          <q-checkbox v-model="transgenico" label="Transgnica" disable />
        </div>
        <div class="col-3">
          <q-checkbox v-model="naoRealizado" label="No Realizada" disable />
        </div>
      </div>
      <div class="row col-12">
        <div class="col-6">
          Carregamento: Inspecionou
          <q-checkbox class="checkbox" v-model="vistoriadoSim" label="S" disable />
          <q-checkbox class="checkbox" v-model="vistoriadoNao" label="N" disable />
        </div>
        <div class="col-6">
          Carga Lacrada:
          <q-checkbox class="checkbox" v-model="cargaLacrada" label="S" disable />
          <q-checkbox class="checkbox" v-model="cargaNaoLacrada" label="N" disable />
          <br />
          N Lacre: {{numeroLacre}}
        </div>
      </div>
      <div class="col-12">Data: {{data}}</div>
      <div class="col-12 margin-obs">Obs: {{observacao}}</div>
      <div class="col-12 row assinaturas">
        <div class="col-6">Motorista: {{nomeMotorista}}</div>
        <div class="col-6">Classificador: {{nomeClassificador}}</div>
      </div>
    </div>
    <div
      class="text-center distancia-botoes-acao fonte-explicacao-download esconde-botao-impressao"
    >Faa o download em PDF de todos os dados da classificao {{numeroLaudo}} clicando no boto abaixo.</div>
    <div class="row col-12 justify-center distancia-botoes-acao">
      <q-btn
        icon="fas fa-file-pdf"
        label="Baixar PDF"
        @click="downloadPDF()"
        class="distancia-botoes esconde-botao-impressao cor-pdf col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
      <q-btn
        icon="fas fa-print"
        color="primary"
        label="Imprimir"
        @click="imprimirPagina()"
        class="distancia-botoes esconde-botao-impressao col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
    </div>
  </div>
</template>

<script>
import { getComCabecalho } from "./js/ConexaoApi.js";
// eslint-disable-next-line no-unused-vars
import jsPDF from "jspdf";
// eslint-disable-next-line no-unused-vars
import html2canvas from "html2canvas";
import html2pdf from "js-html2pdf";
export default {
  data() {
    return {
      // logo: 'https://alfameta-cdn.s3-sa-east-1.amazonaws.com/GranQualityLogo.png',
      logo: require("../assets/GranQualityLogo.png"),
      monsanto: true,
      intactaNegativa: false,
      intactaPositiva: false,
      codProduto: 1,
      nomeCliente: "",
      municipioOrigem: "",
      localEmbarque: "",
      placaCavalo: "",
      placa1: "",
      placa2: "",
      transportadora: "",
      pesoTrabalhado: "",
      pesoLiquido: "",
      nomeProduto: "",
      umidade: "",
      impureza: "",
      queimados: "",
      ardidos: "",
      fermentados: "",
      picados: "",
      total: "",
      esverdeados: "",
      quebrados: "",
      ardidosEQueimados: "",
      numeroContrato: "",
      carunchados: "",
      aflatoxinaNegativo: false,
      aflatoxinaPositivo: false,
      sojaNegativo: false,
      sojaPositivo: false,
      sojaDeclarado: false,
      observacao: "",
      data: "",
      nomeClassificador: "",
      numeroLaudo: "",
      ufOrigem: "",
      numeroLacre: "",
      ufDestino: "",
      tipoProduto: "",
      municipioDestino: "",
      urlClassificacao: "",
      exibeLayout: false,
      layoutCelular: false,
      vistoriado: 0,
      nomeMotorista: "",
      proteina: "",
      numNf: "",
      codOrdemServicoClassificacao: "",
      size: 200,
      vistoriadoNao: false,
      vistoriadoSim: false,
      cargaNaoLacrada: false,
      cargaLacrada: false,
      naoRealizado: false,
      transgenico: false,
      convencional: false,
      codTesteOGM: 0,
      tiposProduto: [
        { value: 1, label: "Convencional" },
        { value: 2, label: "Transgenico" },
        { value: 3, label: "Positivo" },
        { value: 4, label: "Negativo" },
        { value: 5, label: "No testado" },
        { value: 7, label: "Intacta Declarada" },
        { value: 8, label: "Participante Monsanto" },
        { value: 9, label: "Testada Positiva" },
        { value: 10, label: "Testada Negativa" },
        { value: 11, label: "Nao testada" },
        { value: 12, label: "RR2" },
      ],
    };
  },
  methods: {
    carregaDadosClassificacao() {
      let uuid = this.$route.params.u;
      if (uuid == "" || uuid == null || uuid == undefined) {
        this.$router.push("/");
      }
      getComCabecalho("carregadadosqrcode", "uuid", uuid)
        .then((result) => {
          const Resposta = JSON.parse(result);
          this.nomeCliente = Resposta[0].NomeCliente;
          this.numeroLaudo = Resposta[0].NumeroLaudo;
          this.localEmbarque = Resposta[0].LocalEmbarque;
          this.municipioDestino = Resposta[0].MunicipioDestino;
          this.pesoTrabalhado = Resposta[0].PesoAmostraTrabalhada;
          this.pesoLiquido = Resposta[0].PesoLiquido;
          this.codOrdemServicoClassificacao =
            Resposta[0].CodOrdemServicoClassificacao;
          this.ufDestino = Resposta[0].UFDestino;
          this.vistoriado = Resposta[0].Vistoria;
          this.ufOrigem = Resposta[0].UFOrigem;
          this.umidade = Resposta[0].Umidade;
          this.numeroLacre = Resposta[0].NumeroLacre;
          this.codProduto = Resposta[0].CodProduto;
          this.impureza = Resposta[0].Impureza;
          this.proteina = Resposta[0].Proteina;
          this.esverdeados = Resposta[0].Esverdeados;
          this.ardidos = Resposta[0].Ardidos;
          this.queimados = Resposta[0].Queimados;
          this.picados = Resposta[0].Picados;
          this.fermentados = Resposta[0].FermentadoChochoImaturoGerminado;
          this.total = Resposta[0].TotalAvariado;
          this.transportadora = Resposta[0].Transportadora;
          this.placaCavalo = Resposta[0].PlacaCavalo;
          this.placa1 = Resposta[0].Placa1;
          this.placa2 = Resposta[0].Placa2;
          this.observacao = Resposta[0].Observacao;
          this.data = Resposta[0].DataClassificacao;
          this.nomeClassificador = Resposta[0].nome;
          this.numeroContrato = Resposta[0].NumeroContrato;
          this.municipioOrigem = Resposta[0].MunicipioOrigem;
          this.nomeMotorista = Resposta[0].NomeMotorista;
          this.numNf = Resposta[0].NumNF;
          this.codTesteOGM = Resposta[0].CodTesteOGM;
          const tipoProduto = parseInt(Resposta[0].TipoProduto);
          this.tipoProduto = this.tiposProduto.find(
            (t) => t.value == tipoProduto
          ).label;
          if (this.codProduto == 1) {
            this.esverdeados = Resposta[0].Esverdeados;
            this.queimados = Resposta[0].Queimados;
            this.ardidos = Resposta[0].Ardidos;
            this.picados = Resposta[0].Picados;
            this.nomeProduto = "Soja";
          } else {
            this.ardidosEQueimados = Resposta[0].ArdidosEQueimados;
            this.carunchados = Resposta[0].Carunchados;
            this.nomeProduto = "Milho";
          }
          this.quebrados = Resposta[0].PartidoQuebradoAmassado;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    //Gera o PDF e inicia o Download
    downloadPDF() {
      this.exibeLayout = true;
      setTimeout(() => {
        const elementoASerImprimido = document.getElementById("content");
        const options = {
          filename: "Classificacao" + this.numeroLaudo + ".pdf",
          margin: 10,
          enableLinks: true,
          image: "png",
        };
        const exporter = new html2pdf(elementoASerImprimido, options);
        exporter
          .getPdf(true)
          .then(() => {})
          .catch(() => {});
      }, 500);
    },
    imprimirPagina() {
      this.exibeLayout = true;
      setTimeout(() => {
        window.print();
      }, 1500);

      setTimeout(() => {
        this.exibeLayout = false;
      }, 3000);
    },
    detectaPlataforma() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = false;
        this.layoutCelular = true;
      } else {
        this.layoutCelular = false;
        this.exibeLayout = true;
      }
    },
  },
  mounted() {
    this.urlClassificacao = window.location.href;
    this.carregaDadosClassificacao();
  },
};
</script>

<style scoped>
.centraliza-div {
  margin: 0 auto;
}
.assinaturas {
  margin-top: 50px;
}
.margin-obs {
  margin-top: 25px;
}
.caixa {
  border: 1px solid black;
}
.contatos {
  text-align: center;
  color: #00704c;
  font-size: 20px;
  font-weight: bold;
  display: inline;
  padding-top: 25px;
  padding-left: 20px;
}
.teste {
  background-color: red;
}
.placas {
  display: flex;
}
.checkbox {
  margin-left: 10px;
}
.dataelaudo {
  text-align: center;
  font-size: 20px;
}
.pontilhado {
  border-bottom: 1px dotted black;
}
.numero-laudo {
  color: red;
}
img {
  padding: 5px;
}
.tamanho-logo {
  width: 95%;
}
.laudo-classificacao {
  font-size: 20px;
}
.centraliza-texto {
  text-align: center;
}
.margin-bottom {
  margin-bottom: 15px;
}
.fonte-explicacao-download {
  font-size: 22px;
  margin-bottom: 10px;
}
.tamanho-botao {
  height: 100px;
}
.fonte-botao {
  font-size: 28px;
}
.cor-pdf {
  background-color: #ff0000;
  color: white;
}
.distancia-botoes-acao {
  margin-top: 35px;
}
.fonte-url-classificacao {
  font-family: sans-serif;
  font-size: 12px;
}
.distancia-botoes {
  margin: 5px;
  border-radius: 5px;
}
.fonte-botoes {
  font-family: Roboto;
  font-size: 16px;
}
dl {
  width: 700px;
}
dt {
  float: left;
  width: 600px;
  overflow: hidden;
  white-space: nowrap;
}
dd {
  float: left;
  width: 60px;
  overflow: hidden;
  text-align: right;
}

dt:after {
  content: " .........................................................................................................................................................................";
}
@media only print {
  .contatos {
    font-size: 15px;
    padding-right: 10px;
  }
  .centraliza-texto {
    text-align: center;
  }
  .teste {
    background-color: red;
  }
  .dataelaudo {
    text-align: center;
    font-size: 20px;
  }
  img {
    width: 80%;
  }
  .esconde-botao-impressao {
    display: none;
  }
}
@media only screen {
  .container {
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 20px;
  }
}
</style>