<template>
  <div class="col-12" align="center">
    <div class="containerPrincipal" style="width: 95%" id="AgroService">
      <div class="col-12 row blocoInicio">
        <div class="col-9 bordaPadrao" style="height: 30%">
          <img class="Logo" :src="logo" alt="AgroService" />
        </div>

        <div class="col-3 bordaPadrao text-center">
          <div style="color: #f00" class="NumeroLaudo"><div class="col-12">MAPA/TO 000661-0</div> {{numeroLaudo}}</div>
        </div>

        <div class="q-mt-xs titulo text-center col-12 flex flex-center">
          <div>LAUDO DE CLASSIFICAO</div>
        </div>
      </div>

      <!-- Aqui comea as informaes do Primeiro bloco -->
      <div class="q-mt-xs col-12 bordaPadrao primeiroBloco row">
        <div class="row col-12">
          <strong style="margin-right: 5px">CLIENTE:</strong>
          <div style="position: relative; overflow: hidden">
            <!-- <div style="position: absolute" class="linha"></div> -->
            <div>{{nomeCliente}}</div>
          </div>
        </div>

        <div class="row q-mt-sm col-12">
          <strong style="margin-right: 5px">FORNECEDOR:</strong>
          <div style="position: relative; overflow: hidden">
            <!-- <div style="position: absolute" class="linha"></div> -->
            <div>{{nomeFornecedor}}</div>
          </div>
        </div>

        <div class="row q-mt-sm col-12">
          <strong style="margin-right: 5px">ORIGEM / MUNICPIO:</strong>
          <div style="position: relative; overflow: hidden">
            <!-- <div style="position: absolute" class="linha"></div> -->
            <div>{{municipioOrigem}}</div>
          </div>
        </div>

        <div class="row q-mt-sm col-12">
          <strong style="margin-right: 5px">DESTINO:</strong>
          <div style="position: relative; overflow: hidden">
            <!-- <div style="position: absolute" class="linha"></div> -->
            <div>{{municipioDestino}}/{{ufDestino}}</div>
          </div>
        </div>

        <div class="row q-mt-sm col-12">
          <strong style="margin-right: 5px">IDENT. VECULO/VAGO:</strong>
          <div style="position: relative; overflow: hidden">
            <!-- <div style="position: absolute" class="linha"></div> -->
            <div>{{placaCavalo}}</div>
          </div>
        </div>

        <div class="row q-mt-sm col-12">
          <strong style="margin-right: 5px">NOTA FISCAL:</strong>
          <div style="position: relative; overflow: hidden">
            <!-- <div style="position: absolute" class="linha"></div> -->
            <div>{{numeroNotaFiscal}}</div>
          </div>
        </div>

        <div class="row q-mt-sm col-12">
          <strong style="margin-right: 5px">N LACRE:</strong>
          <div style="position: relative; overflow: hidden">
            <!-- <div style="position: absolute" class="linha"></div> -->
            <div>{{numeroLacre}}</div>
          </div>
        </div>
      </div>
      <!-- ------------- Final do bloco ------------------>

      <!-- Aqui comea as informaes do Segundo bloco -->
      <div class="q-mt-xs col-12 bordaPadrao segundoBloco row">
        <div class="row" style="padding: 5px">
          <div class="blocoA">
            <strong>BRUTO:</strong>
          </div>
          <div style="font-size: 1.5rem;">{{pesoBruto}}</div>
          <div class="blocoB">Kg</div>
        </div>
        <div class="row" style="padding: 5px">
          <div class="blocoA">
            <strong>TARA:</strong>
          </div>
          <div style="font-size: 1.5rem;">{{pesoTara}}</div>
          <div class="blocoB">Kg</div>
        </div>
        <div class="row" style="padding: 5px">
          <div class="blocoA">
            <strong>LQUIDO:</strong>
          </div>
          <div style="font-size: 1.5rem;">{{pesoLiquido}}</div>
          <div class="blocoB">Kg</div>
        </div>
      </div>

      <!-- Aqui comea as informaes do Segundo bloco -->
      <div class="q-mt-xs col-12 bordaPadrao especificacao row" style="padding: 5px">
        <div class="col-12 q-mb-xs blocoA">ESPECIFICAO:</div>
        <div class="col-12 row" style="justify-content: flex-end">
          <div>MILHO:</div>
          <q-checkbox v-model="milho" readonly val="true" />
          <div style="margin-left: 100px">SOJA:</div>
          <q-checkbox v-model="soja" readonly val="true" />
          <!-- <div style="margin-left: 100px">SORGO: </div><q-checkbox v-model="sorgo" readonly val="true"/> -->
        </div>
      </div>
      <!-- ------------- Final do bloco de Especificao ------------------>

      <!-- ------------- Final do bloco ------------------>
      <!-- ___________________________ -->

      <!-- Aqui comea as informaes do Terceiro bloco -->
      <div class="q-mt-xs col-12 bordaPadrao terceiroBloco">
        <div>
          <strong>UMIDADE:</strong>
          _________________ {{umidade}} %
        </div>
        <div class="q-mt-sm">
          <strong>MAT. ESTRANHO E/OU IMPUREZA:</strong>
          _________________ {{impureza}} %
        </div>
        <div class="q-mt-sm">
          <strong>ARDIDOS:</strong>
          _________________ {{ardidos}} %
        </div>
        <div class="q-mt-sm">
          <strong>PICADOS:</strong>
          _________________ {{picados}} %
        </div>
        <div class="q-mt-sm">
          <strong>QUEIMADOS:</strong>
          _________________ {{queimados}} %
        </div>
        <div class="q-mt-sm">
          <strong>OUTROS DEFEITOS:</strong>
          _________________ {{outrosDefeitos}} %
        </div>
        <div class="q-mt-sm">
          <strong>AVARIADOS TOTAIS:</strong>
          _________________ {{avariados}} %
        </div>
        <div class="q-mt-sm">
          <strong>CARUNCHADOS:</strong>
          _________________ {{carunchados}} %
        </div>
        <div class="q-mt-sm">
          <strong>GROS VERDES:</strong>
          _________________ {{graosVerdes}} %
        </div>
        <div class="q-mt-sm">
          <strong>PARTIDOS, QUEBRADOS:</strong>
          _________________ {{partidosQuebrados}} %
        </div>
      </div>
      <!-- ------------- Final do bloco ------------------>

      <!-- Aqui comea as informaes do Quarto bloco -->
      <div class="q-mt-xs col-md-12 col-xs-12 bordaPadrao quartoBloco row">
        <div>
          <b>{{tipoDoProduto.toUpperCase()}}</b>
          : {{tipagem}}
        </div>
      </div>
      <!-- ------------- Final do bloco ------------------>

      <!-- Aqui comea as informaes do Quinto bloco -->
      <div class="q-mt-xs col-12 bordaPadrao quintoBloco">
        <div style="text-align: start;">
          <strong>OBS:</strong>
          {{observacao}}
        </div>
      </div>
      <!-- ------------- Final do bloco ------------------>

      <!-- Aqui comea as informaes do Sexto bloco -->
      <div class="q-mt-xs col-12 sextoBloco row">
        <div class="col-7 bordaPadrao" style="padding: 10px; text-align: center">
          <div style="margin-top: 10px">{{nomeClassificador}}</div>
          <div style="border: 1px solid #000"></div>
          <div style="font-weight: bold;">NOME DO CLASSIFICADOR</div>
        </div>
        <div class="col-5 bordaPadrao" style="padding: 10px 20px 0px 20px; text-align: center">
          <div style="margin-top: 10px">{{data}}</div>
          <div style="border: 1px solid #000"></div>
          <div style="font-weight: bold;">DATA</div>
        </div>
      </div>
      <!-- ------------- Final do bloco ------------------>
    </div>

    <!-- ------------------ Botes --------------------- -->
    <div class="q-mb-md botoes" style="margin-top: 5px; margin-left: 30px;">
      <q-btn
        @click="baixarPDF()"
        icon="fas fa-file-pdf"
        class="botao"
        color="red"
        label="Baixar PDF"
        size="30px"
      />
      <q-btn
        @click="imprimirDocumento()"
        icon="fas fa-print"
        class="botao"
        color="green"
        label="Imprimir"
        size="30px"
      />
    </div>
  </div>
</template>

<script>
// import jsPDF from "jspdf";
// import html2canvas from "html2canvas";
import html2pdf from "js-html2pdf";
import { tipoProduto } from "./utils/TipoProduto";
import { getComCabecalho } from "./js/ConexaoApi.js";

export default {
  data() {
    return {
      exibeLayout: false,
      ogmPositivo: false,
      ogmNegativo: false,
      milho: false,
      logo: require("../assets/logoAgroService.png"),
      soja: false,
      sorgo: false,
      tipoDoProduto: "",
      codOS: "",
      codProduto: "",
      codTipoProduto: "",
      tipagem: "",
      nomeClassificador: "",
      data: "",
      nomeCliente: "",
      nomeFornecedor: "",
      municipioOrigem: "",
      pesoLiquido: "",
      pesoTara: "",
      numeroNotaFiscal: "",
      pesoBruto: "",
      ufOrigem: "",
      municipioDestino: "",
      ufDestino: "",
      placaCavalo: "",
      numeroLacre: "",
      numeroLaudo: "",
      produto: "",
      umidade: "",
      impureza: "",
      ardidos: "",
      picados: "",
      queimados: "",
      partidosQuebrados: "",
      outrosDefeitos: "",
      avariados: "",
      carunchados: "",
      graosVerdes: "",
      observacao: "",
    };
  },
  methods: {
    carregaDadosLaudo() {
      let uuid = this.$route.params.u;
      if (uuid == "" || uuid == null || uuid == undefined) {
        this.$router.push("/");
      }

      getComCabecalho("carregadadosqrcode", "uuid", uuid)
        .then((result) => {
          const resposta = JSON.parse(result);
          this.numeroLaudo = resposta[0].NumeroLaudo;
          this.umidade = resposta[0].Umidade;
          this.impureza = resposta[0].Impureza;
          this.ardidos = resposta[0].Ardidos;
          this.picados = resposta[0].Picados;
          this.queimados = resposta[0].Queimados;
          this.avariados = resposta[0].TotalAvariado;
          this.outrosDefeitos = resposta[0].FermentadoChochoImaturoGerminado;
          this.carunchados = resposta[0].Carunchados;
          this.partidosQuebrados = resposta[0].PartidoQuebradoAmassado;
          this.codProduto = resposta[0].CodProduto;
          this.codTesteOGM = resposta[0].CodTesteOGM;
          this.nomeClassificador = resposta[0].nome.toUpperCase();
          this.graosVerdes = resposta[0].Esverdeados;
          this.nomeCliente = resposta[0].NomeCliente;
          this.nomeFornecedor = resposta[0].LocalEmbarque;
          this.municipioOrigem = resposta[0].MunicipioOrigem;
          this.ufOrigem = resposta[0].UFOrigem;
          this.municipioDestino = resposta[0].MunicipioDestino;
          this.ufDestino = resposta[0].UFDestino;
          this.numeroLacre = resposta[0].NumeroLacre;
          this.placaCavalo = resposta[0].PlacaCavalo;
          this.observacao = resposta[0].Observacao;
          this.numeroNotaFiscal = resposta[0].NumNF;
          this.codTipoProduto = resposta[0].TipoProduto;
          this.tipagem = tipoProduto(this.codTipoProduto);
          this.pesoLiquido = Number(parseFloat(resposta[0].PesoLiquido));
          this.pesoTara = Number(parseFloat(resposta[0].PesoTara));
          if (Number.isNaN(this.pesoTara)) {
            this.pesoTara = 0;
          }
          this.pesoBruto = parseFloat(resposta[0].PesoBruto);
          if (Number.isNaN(this.pesoBruto)) {
            this.pesoBruto = 0;
          }
          const date = resposta[0].DataClassificacao;
          const dia = date.substring(0, 2);
          const mes = date.substring(3, 5);
          const ano = date.substring(6, 10);
          this.data = dia + "/" + mes + "/" + ano;

          if (this.codProduto == 1) {
            this.soja = true;
          } else if (this.codProduto == 2) {
            this.milho = true;
          } else if (this.codProduto == 4) {
            this.sorgo = true;
          } else {
            this.milho = false;
            this.soja = false;
            this.sorgo = false;
          }

          if (this.codProduto == 1) {
            this.tipoDoProduto = "Tipo do Produto";
          } else {
            this.tipoDoProduto = "Aflatoxina";
          }
        })
        .catch(() => {
        });
    },
    baixarPDF() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = true;
      }
      setTimeout(() => {
        const elementoASerImprimido = document.getElementById("AgroService");
        const options = {
          filename: "Classificacao" + this.numeroLaudo + ".pdf",
          margin: 8,
          enableLinks: true,
          imagem: "png",
          html2canvas: {
            scale: 5,
            logging: true,
            dpi: 192,
            letterRendering: true,
          },
          jsPDF: {
            unit: "mm",
            format: "a4",
            orientation: "portrait",
          },
        };

        const exporter = new html2pdf(elementoASerImprimido, options);
        exporter
          .getPdf(true)
          .then(() => {
            if (this.$q.platform.is.mobile) {
              this.exibeLayout = false;
            }
          })
          .catch(() => {
            if (this.$q.platform.is.mobile) {
              this.exibeLayout = false;
            }
          });
      }, 700);
    },
    detectaPlataforma() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = false;
        this.layoutCelular = true;
      } else {
        this.layoutCelular = false;
        this.exibeLayout = true;
      }
    },
    imprimirDocumento() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = true;
      }
      setTimeout(() => {
        window.print();
        if (this.$q.platform.is.mobile) {
          setTimeout(() => {
            this.exibeLayout = false;
          }, 1000);
        }
      }, 1500);
    },
  },
  mounted() {
    this.carregaDadosLaudo();
    this.detectaPlataforma();
  },
};
</script>

<style>
.containerPrincipal {
  width: 80vw;
}
.Logo {
  padding: 5px;
  margin-top: 12px;
  height: 75%;
  width: 100%;
}

.containerPrincipal {
  padding: 5px;
}

.bordaPadrao {
  border: 2px solid #000;
  border-radius: 10px;
}

.titulo {
  font-size: 2.8rem;
  font-weight: bold;
  border: 2px solid #000;
  border-radius: 10px;
}

.divisor {
  border: 1px solid #000;
  justify-self: flex-end;
}

.primeiroBloco {
  padding: 5px;
  font-size: 1rem;
  text-align: start;
}

.segundoBloco {
  font-size: 1rem;
  justify-content: space-between;
}

.segundoBloco .blocoA {
  /* font-weight: bold; */
  margin-right: 8px;
}

.segundoBloco .blocoB {
  /* font-weight: bold; */
  align-self: flex-end;
  margin-left: 3px;
  margin-bottom: 2px;
}

.especificacao {
  font-weight: bold;
  font-size: 1rem;
}

.especificacao .blocoA {
  text-align: start;
}

.especificacao blocoB {
  align-self: flex-end;
  justify-self: end;
}

.terceiroBloco {
  padding: 5px;
  font-size: 1rem;
  text-align: end;
}

.quartoBloco {
  justify-content: space-between;
  font-size: 1rem;
  padding: 5px;
}

.quintoBloco {
  font-size: 1rem;
  padding: 5px;
  overflow: auto;
  white-space: pre-wrap;
  height: auto;
}

.sextoBloco {
  font-size: 1rem;
}

.NumeroLaudo {
  font-size: 1.5rem;
  margin-top: 70px;
}

@media print {
  .botoes * {
    display: none !important;
  }
}

@media only screen and (max-width: 599px) {
  .NumeroLaudo {
    text-align: center;
    font-size: 0.9rem;
    padding-top: 12px;
    margin-top: 0px;
  }

  .botao {
    margin: 10px;
    width: 70%;
    height: 30%;
  }

  .botoes {
    margin-right: 30px;
  }

  .titulo {
    font-size: 1.8rem;
  }

  .primeiroBloco,
  .segundoBloco,
  .especificacao,
  .terceiroBloco,
  .quartoBloco,
  .quintoBloco,
  .sextoBloco {
    font-size: 1rem;
  }
}

.botao {
  margin: 10px;
}
</style>