<template>
  <q-modal
    v-model="exibe"
    minimized
    :content-css="{width: '60vw', minWidth:'40vw', minHeight: '55vh'}"
    no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn
          style="padding:0px; margin:0"
          flat
          round
          dense
          icon="close"
          @click="confirmarCancelamentoDeCadastroDeOs"
        />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>
      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{tituloModal}}</h6>
      </q-toolbar>

      <div class="layout-padding">
        <div class="row">
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroDestino" error-label="Por favor, informe o destino">
              <q-input
                v-model="destino"
                placeholder="Informe o(s) destino(os)"
                float-label="Informe o(s) destino(os)"
                :disable="modoVisualizacao == true"
              ></q-input>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroProduto"
              error-label="Por favor, informe o produto a ser classificado"
            >
              <q-select
                filled
                v-model="produtoSelecionado"
                float-label="Produto"
                filter
                :options="produtos"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="numeroContrato"
                placeholder="Contrato"
                float-label="Contrato"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-3 col-sm-6 col-xs-6 margin-form">
            <q-field :error="erroPesoLiquido" error-label="Por favor, informe o peso lquido">
              <q-input
                v-model="pesoLiquido"
                placeholder="Peso Lquido(kg)"
                float-label="Peso Lquido(kg)"
                type="number"
                :disable="modoVisualizacao == true"
                @keyup="converteKgParaTon(false)"
              />
            </q-field>
          </div>
          <div class="col-md-3 col-sm-6 col-xs-6 margin-form">
            <q-field :error="erroPesoLiquido" error-label="Por favor, informe o peso lquido">
              <q-input
                v-model="toneladas"
                placeholder="Peso Lquido(Tons)"
                float-label="Peso Lquido(Tons)"
                type="number"
                :disable="modoVisualizacao == true"
                @keyup="converteTonParaKg(false)"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroLocalEmbarque"
              error-label="Por favor, informe o local de embarque"
            >
              <q-input
                v-model="localEmbarque"
                placeholder="Informe o local de embarque"
                float-label="Informe o local de embarque"
                :disable="modoVisualizacao == true"
              ></q-input>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="responsavel"
                placeholder="Responsavel"
                float-label="Responsavel"
                :disable="modoVisualizacao == true"
              ></q-input>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="filial"
                placeholder="Filial de faturamento"
                float-label="Filial de faturamento"
                :disable="modoVisualizacao == true"
              ></q-input>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-select
                filled
                v-if="produtoSelecionado == 1"
                v-model="aflatoxinaTipoProduto"
                float-label="Tipo de Produto"
                :options="opcoesTipoProdutoSoja"
                :disable="modoVisualizacao == true"
              />
              <q-select
                filled
                v-if="produtoSelecionado == 2"
                v-model="aflatoxinaTipoProduto"
                float-label="Realiza teste aflatoxina?"
                :options="opcoesTesteAflatoxina"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-12" style="margin-top:10px;">
            <q-btn
              color="primary"
              :label="tituloModal"
              @click="comportamentoBotaoSalvar()"
              v-show="modoVisualizacao != true || alteraInsere == 1 || alteraInsere == 2"
            />
          </div>
        </div>
        <div style="margin-top:10px">
          <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
        </div>
      </div>
    </q-modal-layout>
  </q-modal>
</template>

<script>
import { get, post } from "./js/ConexaoApi";
import { Solicitacao } from "../model/Solicitacao.js";
export default {
  name: "modalSolicitacaoOs",
  data() {
    return {
      /**********/
      erroLocalEmbarque: false,
      erroPesoLiquido: false,
      erroProduto: false,
      erroDestino: false,
      /**********/
      exibe: false,
      modoVisualizacao: false,
      exibeMensagemRetorno: false,
      produtosCarregados: false,
      tipoMensagem: "negative",
      mensagemRetorno: "",
      tituloModal: "",
      numeroContrato: "",
      destino: "",
      localEmbarque: "",
      produtoSelecionado: "",
      aflatoxinaTipoProduto: "",
      pesoLiquido: "",
      toneladas: "",
      filial: "",
      responsavel: "",
      tituloBotaoSalvar: "",
      codSolicitacao: "",
      alteraInsere: 1,
      produtos: [],
      opcoesTipoProdutoSoja: [
        { value: 8, label: "ARMAZEM PARTICIPANTE" },
        { value: 12, label: "CONVENCIONAL" },
        { value: 7, label: "DECLARADO INTACTA" },
        { value: 13, label: "REALIZAR TESTE" },
      ],
      opcoesTesteAflatoxina: [
        { value: 1, label: "SIM" },
        { value: 2, label: "NO" },
      ],
      codCliente: "",
    };
  },
  methods: {
    abreFechaModal() {
      this.exibe = !this.exibe;
      this.alteraInsere = 1;
      this.tituloModal = "Adicionar nova solicitao";
    },
    abreModalAlteracao(solicitacao) {
      this.alteraInsere = 2;
      this.exibe = !this.exibe;
      this.tituloModal = "Alterar Solicitao";
      this.carregaCampos(solicitacao);
    },
    abreModalVisualizacao(solicitacao) {
      this.alteraInsere = 3;
      this.modoVisualizacao = true;
      this.exibe = !this.exibe;
      this.tituloModal = "Visualizar Solicitao";
      this.carregaCampos(solicitacao);
    },
    carregaCampos(solicitacao) {
      this.destino = solicitacao.Destino;
      this.produtoSelecionado = solicitacao.CodProduto;
      this.pesoLiquido = solicitacao.PesoLiquido;
      this.filial = solicitacao.Filial;
      this.localEmbarque = solicitacao.LocalEmbarque;
      this.responsavel = solicitacao.Responsavel;
      this.CodCliente = solicitacao.CodCliente;
      this.codSolicitacao = solicitacao.CodSolicitacao;
      this.numeroContrato = solicitacao.NumeroContrato;
      this.aflatoxinaTipoProduto = solicitacao.AflatoxinaTipoProduto;
      this.codCliente = solicitacao.CodCliente;
    },
    confirmarCancelamentoDeCadastroDeOs() {
      if (this.modoVisualizacao != true) {
        this.$q
          .dialog({
            title: "Cancelar lanamento solicitao",
            message:
              "Deseja realmente cancelar o lanamento dessa solicitao?",
            color: "negative",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => {
            this.fechaModal();
          })
          .catch(() => {});
      } else {
        this.fechaModal();
      }
    },
    fechaModal() {
      this.limpaErrosCadastro();
      this.exibe = false;
      this.numeroContrato = "";
      this.destino = "";
      this.localEmbarque = "";
      this.produtoSelecionado = "";
      this.pesoLiquido = "";
      this.responsavel = "";
      this.toneladas = "";
      this.modoVisualizacao = false;
      this.aflatoxinaTipoProduto = "";
      this.codCliente = "";
    },
    limpaErrosCadastro() {
      this.erroCliente = false;
      this.erroDestino = false;
      this.erroClassificador = false;
      this.erroProduto = false;
      this.erroPesoLiquido = false;
      this.erroPesoCadencia = false;
      this.erroLocalEmbarque = false;
    },
    carregaDados() {
      if (this.produtosCarregados != true) {
        get("produto")
          .then((result) => {
            let jsonProdutos = result;
            jsonProdutos = jsonProdutos.replace(/"CodProduto":/g, '"value":');
            jsonProdutos = jsonProdutos.replace(/"Nome":/g, '"label":');
            this.produtos = JSON.parse(jsonProdutos);
            this.produtosCarregados = true;
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            console.error(err);
          });
      }
    },
    converteKgParaTon(calculandoCadencia) {
      if (calculandoCadencia) {
        this.toneladasCadencia = this.pesoCadencia / 1000;
      } else {
        this.toneladas = this.pesoLiquido / 1000;
      }
    },
    converteTonParaKg(calculandoCadencia) {
      if (calculandoCadencia) {
        this.pesoCadencia = this.toneladasCadencia * 1000;
      } else {
        this.pesoLiquido = this.toneladas * 1000;
      }
    },
    comportamentoBotaoSalvar() {
      if (this.alteraInsere == 1) {
        return this.salvarSolicitacao();
      } else {
        return this.alteraSolicitacao();
      }
    },
    alteraSolicitacao() {
      const solicitacao = new Solicitacao();
      solicitacao.CodCliente = this.codCliente;
      solicitacao.CodSolicitacao = this.codSolicitacao;
      solicitacao.CodProduto = this.produtoSelecionado;
      solicitacao.PesoLiquido = this.pesoLiquido;
      solicitacao.Destino = this.destino;
      solicitacao.LocalEmbarque = this.localEmbarque;
      solicitacao.Responsavel = this.responsavel;
      solicitacao.NumeroContrato = this.numeroContrato;
      solicitacao.Filial = this.Filial;
      solicitacao.AflatoxinaTipoProduto = this.aflatoxinaTipoProduto;
      post("atualizasolicitacao", solicitacao)
        .then((result) => {
          const Resposta = JSON.parse(result);
          this.mensagemRetorno = Resposta.msg;
          if (Resposta.Status == true) {
            this.tipoMensagem = "positive";
            this.exibeMensagemRetorno = true;
            this.$emit("atualizou");
            setTimeout(() => {
              this.mensagemRetorno = "";
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = false;
              this.fechaModal();
            }, 2000);
          } else {
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
          }
        })
        .catch(() => {
          this.mensagemRetorno =
            "Falha ao abrir solicitao, por favor, contate o suporte";
          this.tipoMensagem = "negative";
          this.exibeMensagemRetorno = true;
        });
    },
  },
  mounted() {
    this.carregaDados();
  },
  watch: {
    toneladas: function () {
      this.converteTonParaKg(false);
    },
    pesoLiquido: function () {
      this.converteKgParaTon(false);
    },
    exibe: function () {
      if (this.exibe == false) {
        this.fechaModal();
      }
    },
  },
};
</script>

