<template>
  <div class="row col-12">
    <div class="col-4">
      <q-select
        :options="tiposArquivos"
        filter
        clearable
        v-model="tipoArquivoSelecionado"
        placeholder="Selecione o tipo do arquivo"
        stack-label="Selecione o tipo do arquivo"
        popup-max-height="100"
        style="margin-bottom: 10px"
      />
    </div>
    <div class="offset-8"></div>
    <div class="col-12 margin-form drag-area" id="drag-area" dropzone @drop="onDrop($event)" @dragover.prevent @dragenter.prevent>
      <div>{{arquivoCarregado === true ? 'Arquivo selecionado: ' + nomeArquivoSelecionado : 'Arraste um arquivo para ca, ou ento...'}}</div>
      <q-uploader
        v-show="false"
        url="http://localhost:4444/upload"
        color="teal"
        flat
        ref="fileUploader"
        bordered
        style="max-width: 300px"
        id="selectFile"
        @add="carregaArquivoSelecionado"
      />
      <!-- <input ref="fileInput" id="selectFile" type="file" accept=".jpeg, .jpg, .png, .pdf, .txt, .doc, .docx, .xls, .xlxs, .xml, .csv" style="display: none" @change="carregaArquivoSelecionado($event)"> -->
      <q-btn color="primary" label="Selecione um arquivo" icon="attach_file" @click="abreSelecaoDeArquivos"  style="height:50px"/>
      
    </div>
    <div class="div-botoes-comando">
      <q-btn v-show="arquivoCarregado" color="negative" label="Remover arquivo selecionado" icon="delete" @click="resetFilePicker" class="btn-commando"/>
      <q-btn v-show="arquivoCarregado" align="center" color="positive" icon="check" label="Enviar arquivo" @click="enviaArquivo" class="btn-commando"/>
    </div>
    
  </div>
</template>

<script>
import { postFormDataComCabecalhosCliente } from '../components/AreaCliente/js/ConexaoApiCliente.js';
import { postFormDataComCabecalhos } from '../components/js/ConexaoApi.js';
import { getCookie } from '../components/js/Cookies.js';
import { detectaCliente } from '../components/utils/DetectaCliente.js';
const enviadoPor = {CLIENTE : 'CLI', CLASSIFICADOR : 'CLA'};
export default {
  data() {
    return {
      tiposArquivos: [
        {value : 1, label : 'Documentos do motorista'},
        {value : 2, label : 'Laudo de classificao'},
        {value : 3, label : 'Nota Fiscal'},
        {value : 4, label : 'Ordem de carga'},
        {value : 5, label : 'Ticket de balana'},
        {value : 6, label : 'Ticket de pesagem'},
        {value : 7, label : 'Comprovante de agendamento'},
        {value : 8, label : 'CT-e'},
        {value : 9, label : 'Contrato de transporte'},
        {value : 10, label : 'MDF-e'},
        {value : 11, label : 'NF-e'},
        {value : 12, label : 'Comprovante de pedagio'},
        {value : 13, label : 'Fita Teste'},
        {value : 14, label : 'Checklist'},
        {value : 15, label : 'Romaneio'},
        {value : 16, label : 'Vale Pedagio'},
        {value : 17, label : 'Declarao do motorista'},
      ],
      clienteAlfameta: null,
      codCliente: '',
      codClassificador: '',
      codItemOrdemServicoClassificacao: '',
      codOrdemServicoClassificacao: '',
      numeroLaudo: '',
      enviadoPor: '',
      urlUpload: '',
      arquivoCarregado: false,
      tipoArquivoSelecionado: null,
      documentoSelecionado: null,
      nomeArquivoSelecionado: '',
      arquivoSelecionado: null
    }
  },
  methods: {
    enviaArquivo(){
      if(this.tipoArquivoSelecionado === null){
        this.$q
        .dialog({
          title: "Tipo do arquivo",
          message: "Por favor, informe o tipo do arquivo que voc esta enviando",
          color: "negative",
          ok: "OK",
          position: "top",
        })
        .then(() => {
        })
        .catch(() => {});
        return;
      }
      const formData = new FormData();
      formData.append("file", this.arquivoSelecionado);
      let cabecalhos = new Map();
      cabecalhos.set("CodCliente", this.codCliente);
      cabecalhos.set("CodClassificador", this.codClassificador);
      cabecalhos.set("CodItemOrdemServicoClassificacao", this.codItemOrdemServicoClassificacao);
      cabecalhos.set("CodOrdemServicoClassificacao", this.codOrdemServicoClassificacao);
      cabecalhos.set("EnviadoPor", this.enviadoPor);
      cabecalhos.set("NumeroLaudo", this.numeroLaudo);
      cabecalhos.set("codTipoDocumento", this.tipoArquivoSelecionado);
      if (this.enviadoPor === enviadoPor.CLASSIFICADOR){
        postFormDataComCabecalhos('enviaarquivos3', formData, cabecalhos, this.token).then(()=> {
          this.$q.notify({
            message: `${this.tiposArquivos.filter(t => t.value === this.tipoArquivoSelecionado)[0].label} enviado com sucesso!`,
            timeout: 3000,
            type: "positive",
            color: "postive",
            textColor: "white",
            position: "top",
          });
          this.resetFilePicker();
          this.$emit("arquivoEnviado");
        }).catch(err => {
          // eslint-disable-next-line no-console
          console.error(err);
          this.$q.notify({
            message: `Falha ao enviar ${this.tiposArquivos.filter(t => t.value === this.tipoArquivoSelecionado)[0].label}. Por favor, contate o suporte`,
            timeout: 3000,
            type: "negative",
            color: "negative",
            textColor: "white",
            position: "top",
          });
        })
      }else {
        postFormDataComCabecalhosCliente('enviaarquivos3', formData, cabecalhos, this.token).then(() => {
          this.$q.notify({
            message: `${this.tiposArquivos.filter(t => t.value === this.tipoArquivoSelecionado)[0].label} Enviado com sucesso!`,
            timeout: 3000,
            type: "positive",
            color: "postive",
            textColor: "white",
            position: "top",
          });
          this.$emit("arquivoEnviado");
          this.resetFilePicker();
        }).catch(err => {
          // eslint-disable-next-line no-console
          console.error(err);
          this.$q.notify({
            message: `Falha ao enviar ${this.tiposArquivos.filter(t => t.value === this.tipoArquivoSelecionado)[0].label}. Por favor, contate o suporte`,
            timeout: 3000,
            type: "negative",
            color: "negative",
            textColor: "white",
            position: "top",
          });
        })
      }
    },
    async setaInformacaoOs(dadosItem) {
      this.clienteAlfameta = await detectaCliente()
      if (getCookie("codcliente") !== "") {
        this.codCliente = getCookie("codcliente");
        this.enviadoPor = enviadoPor.CLIENTE;
      } else {
        this.codClassificador = getCookie("id");
        this.enviadoPor = enviadoPor.CLASSIFICADOR;
      }
      this.codItemOrdemServicoClassificacao = dadosItem.CodItemOrdemServicoClassificacao;
      this.codOrdemServicoClassificacao = dadosItem.CodOrdemServicoClassificacao;
      this.numeroLaudo = dadosItem.NumeroLaudo;
      this.enviadoPor = dadosItem.EnviadoPor;
    },
    carregaArquivoSelecionado(){
      this.arquivoSelecionado = this.$refs.fileUploader.queue[0];
      this.nomeArquivoSelecionado = this.arquivoSelecionado.name;
      this.arquivoCarregado = true;
    },
    abreSelecaoDeArquivos(){
      this.$refs.fileUploader.pick()
    },
    resetFilePicker(){
      this.tipoArquivoSelecionado = null;
      this.arquivoSelecionado = null;
      this.arquivoCarregado = false;
      this.nomeArquivoSelecionado = ''
      this.$refs.fileUploader.reset()
    },
    onDrop(event){
      event.stopPropagation();
      event.preventDefault();
      this.arquivoSelecionado = event.dataTransfer.files[0];
      this.nomeArquivoSelecionado = this.arquivoSelecionado.name;
      this.arquivoCarregado = true;
    },
    filtroArquivos(files) {
      const MAX_FILE_SIZE = 5 * 1024 * 1024; //5MB
      return files.filter((file) => {
        return file.size <= MAX_FILE_SIZE;
      });
    },
    
  },
};
</script>

<style scoped>
.drag-area{
  border: 1px solid black;
  background-color: lightgray;
  text-align: center;
  height: 125px;
  padding-top: 25px;
}
.btn-selecionar-arquivo{
  border: 1px solid black;
  background-color: #1B1F5F;
  color: white;
  height: 50px;
}
.div-botoes-comando{
  width: 100%;
  display: flex;
  justify-content: center;
}
.btn-commando{
  margin: 5px;
  height: 50px;
}
</style>