const protocolo = location.protocol;
const url = protocolo + "//alfameta.ddns.net";
let porta = "";
import { detectaCliente } from '../utils/DetectaCliente.js'
import { getCookie } from "../js/Cookies.js";

function setaPortaCorreta() {
  return new Promise(resolve => {
    detectaCliente().then((result) => {
      porta = result.PortaAPI;
    });
    resolve();
  });
}
setaPortaCorreta();

//Exata
//A porta oficial  8080

//Glycine
//A porta oficial  8082

//Positivagro
//A porta oficial  8084

//Clasgros
//A porta oficial  8086

//Testes
//A porta oficial de testes  8081


export function post(rota, objeto) {
  return new Promise(async (resolve, reject) => {
    if (porta == null || porta == "") {
      await setaPortaCorreta();
    }
    const request = new XMLHttpRequest();
    const json = JSON.stringify(objeto);
    const token = getCookie("tk");

    if (protocolo == "http:") {
      request.open("POST", url + ":" + porta + "/api/" + rota, true);
    }
    else {
      request.open("POST", url + "/api/public/api/" + rota, true);
    }

    request.setRequestHeader("Content-Type", "Application/json");
    request.setRequestHeader("Authorization", "bearer " + token);


    try {
      request.send(json);
      request.onreadystatechange = function () {
        if (request.readyState == request.DONE) {
          if (request.status == 200) {
            resolve(request.response);
          } else {
            reject(request.response);
          }
        }
      };
    }
    catch (err) {
      reject(err);
    }
  });
}


