<template>
  <q-layout>
    <ImpressoPadrao v-show="layoutEmail == 'E'" ref="layoutPadraoExata" />
    <ImpressoGavilon v-show="layoutEmail == 'G'" ref="layoutPadraoGavilon" />
  </q-layout>
</template>

<script>
import ImpressoPadrao from "@/components/LayoutImpressaoPadraoExata.vue";
import ImpressoGavilon from "@/components/LayoutImpressaoGavilonExata.vue";
import { getComCabecalho } from "../components/js/ConexaoApi.js";
export default {
  components: {
    ImpressoPadrao,
    ImpressoGavilon,
  },
  data() {
    return {
      layoutEmail: "",
      dados: "",
    };
  },
  methods: {
    carregaDadosClassificacao() {
      let uuid = this.$route.params.c;
      if (uuid == "" || uuid == null || uuid == undefined) {
        this.$router.push("/");
      }
      getComCabecalho("carregadadosqrcode", "uuid", uuid)
        .then((result) => {
          const Resposta = JSON.parse(result);
          this.dados = Resposta[0];
          this.layoutEmail = Resposta[0].LayoutEmail;
          if (this.layoutEmail == "E") {
            this.$refs.layoutPadraoExata.carregaDados(this.dados);
          } else {
            this.$refs.layoutPadraoGavilon.carregaDados(this.dados);
          }
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
  },
  beforeMount() {
    this.carregaDadosClassificacao();
  },
};
</script>

