        <img :src="logoGlycine" crossorigin="Anonymous" class="logo-download" />
      </div>
    </div>
    <div class="container cor-fundo" id="glycinemax" v-show="exibeLayout">
      <div class="row no-wrap">
        <div class="row col-8 cor-fundo-divs margin-divs no-wrap borda">
          <img :src="logoGlycine" class="logo" />
        </div>
        <div class="row col-4 cor-fundo-divs borda margin-divs efeito-quadro-laudo-acompanhamento">
          <div
            class="col-12 fonte-tamanho-titulo-laudo borda-bottom padding-campos"
          >LAUDO DE ACOMPANHAMENTO DE EMBARQUE</div>
          <div class="col-12 borda-bottom padding-campos">N OS.:{{codOrdemServicoClassificacao}}</div>
          <div class="col-12 borda-bottom padding-campos">N NF.:</div>
          <div class="col-12 borda-bottom padding-campos">Peso Lquido.:{{pesoLiquido}} KG</div>
          <div class="col-12 numero-laudo padding-campos">{{numeroLaudo}}</div>
        </div>
      </div>
      <div
        class="row col-12 cor-fundo-divs borda margin-divs margin-direita-alinhamento fonte-tamanho-padrao"
      >
        <div class="row col-12 borda-bottom">
          <div class="col-6 padding-campos">EMPRESA.: {{nomeCliente}}</div>
          <div class="col-6 padding-campos">Filial.: {{filial}}</div>
        </div>
        <div class="row col-12 borda-bottom">
          <div class="col-6 padding-campos">MUNICIPIO ORIGEM.: {{municipioOrigem}}</div>
          <div class="col-6 padding-campos">LOCAL DE EMBARQUE.: {{nomeLocalEmbarque}}</div>
        </div>
        <div class="row col-12 borda-bottom">
          <div class="col-3 padding-campos">N CONTRATO.: {{numeroContrato}}</div>
          <div class="col-3 padding-campos">N LOTE.: {{lote}}</div>
          <div class="col-6 padding-campos">DESTINO.: {{municipioDestino}}</div>
        </div>
        <div class="row col-12 borda-bottom">
          <div class="col-6 padding-campos">N DO LACRE.: {{numeroLacre}}</div>
        </div>
        <div class="row col-12 borda-bottom">
          <div class="col-4 padding-campos">PLACA CAM.: {{placaCavalo}}</div>
          <div class="col-4 padding-campos">PL. 1 VAGO.: {{placa1}}</div>
          <div class="col-4 padding-campos">PL. 2 VAGO.: {{placa2}}</div>
        </div>
        <div class="row col-12 borda-bottom no-wrap">
          <div class="col-4 padding-campos row">
            <span class="margin-nome-produto">PRODUTO:</span>
            <span v-if="codProduto == 1">SOJA</span>
            <span v-else-if="codProduto == 2">MILHO</span>
            <span v-else-if="codProduto == 3">FARELO</span>
          </div>
          <div class="col-4 padding-campos">
            <span v-if="codProduto == 1">Tipo do produto: {{tipoProduto}}</span>
            <span v-else-if="codProduto == 2">Teste Aflatoxina: {{tipoProduto}}</span>
          </div>
          <div class="col-4 padding-campos">
            <span class="margin-nome-produto">VISTORIA VECULO:</span>
            <span v-if="vistoria==1">SIM</span>
            <span v-else>NO</span>
          </div>
        </div>
      </div>
      <div
        class="col-12 row cor-fundo-divs margin-direita-alinhamento margin-divs"
        v-if="codProduto == 1"
      >
        <div
          class="col-12 centraliza-texto borda-top borda-left borda-right titulo-tabela"
        >CLASSIFICAO DE SOJA</div>
        <table cellspacing="0" class="col-12 fonte-tabela">
          <thead>
            <tr>
              <th rowspan="2" class="padding-th">UMIDADE 14,0%</th>
              <th rowspan="2" class="padding-th">MATRIAIS ESTRANHAS IMPUREZAS %(1,0)</th>
              <th rowspan="2" class="padding-th">PARTIDOS QUEBRADOS AMASSADOS %(30,0)</th>
              <th rowspan="1" colspan="6" class="padding-th">AVARIADOS %</th>
              <th rowspan="2" class="padding-th">TOTAL* (8,0)</th>
            </tr>
            <th class="padding-th">TOTAL DE ARDIDOS (4,0)</th>
            <th class="padding-th">FERMENTADOS CHOCHOS IMATUROS GERMINADOS (8,0)</th>
            <th class="padding-th">PICADOS (4,0)</th>
            <th class="padding-th">MAXIMO QUEIMADOS (1,0)</th>
            <th class="padding-th">MOFADOS (6,0)</th>
            <th class="padding-th">ESVERDEADOS (8,0)</th>
          </thead>
          <tbody class="centraliza-valores-tabela tamanho-fonte-resultados">
            <td>{{umidade}}%</td>
            <td>{{impureza}}%</td>
            <td>{{quebrados}}%</td>
            <td>{{ardidos}}%</td>
            <td>{{fermentados}}%</td>
            <td>{{picados}}%</td>
            <td>{{queimados}}%</td>
            <td>{{mofados}}%</td>
            <td>{{esverdeados}}%</td>
            <td>{{total}}%</td>
          </tbody>
        </table>
      </div>
      <p
        class="aviso-total"
        v-if="codProduto == 1"
      >(*)TOTAL = Soma de queimados, ardidos, mofados, fermentados, germinados, danificados, imaturos e chochos.</p>
      <div
        class="col-12 row cor-fundo-divs margin-direita-alinhamento margin-divs margin-observacoes"
        v-if="codProduto == 2"
      >
        <div
          class="col-12 centraliza-texto borda-top borda-left borda-right titulo-tabela"
        >CLASSIFICAO DE MILHO</div>
        <table cellspacing="0" class="col-12 fonte-tabela">
          <thead>
            <tr>
              <th rowspan="2" class="padding-th">UMIDADE 14,0%</th>
              <th rowspan="2" class="padding-th">MATRIAIS ESTRANHAS IMPUREZAS %(1,0)</th>
              <th rowspan="2" class="padding-th">QUEBRADOS %(30,0)</th>
              <th rowspan="1" colspan="6" class="padding-th">TOTAL AVARIADOS</th>
            </tr>
            <tr>
              <th class="padding-th">ARDIDOS 1,0%</th>
              <th class="padding-th">CARUNCHADOS 1,0%</th>
              <th class="padding-th">FERMENTADOS CHOCHOS IMATUROS GERMINADOS </th>
              <th class="padding-th">MOFADOS %(6,0)</th>
              <th class="padding-th">TOTAL DE AVARIADOS 5,0%</th>
            </tr>
            <tr></tr>
          </thead>
          <tbody class="centraliza-valores-tabela tamanho-fonte-resultados">
            <td>{{umidade}}%</td>
            <td>{{impureza}}%</td>
            <td>{{quebrados}}%</td>
            <td>{{ardidos}}%</td>
            <td>{{carunchados}}%</td>
            <td>{{fermentados}}%</td>
            <td>{{mofados}}%</td>
            <td>{{total}}%</td>
          </tbody>
        </table>
      </div>
      <div
        class="col-12 row cor-fundo-divs margin-direita-alinhamento margin-divs margin-observacoes"
        v-if="codProduto == 3"
      >
        <div
          class="col-12 centraliza-texto borda-top borda-left borda-right titulo-tabela"
        >CLASSIFICAO DE FARELO</div>
        <table cellspacing="0" class="col-12 fonte-tabela">
          <thead>
            <tr>
              <th class="padding-th">Umidade %</th>
              <th class="padding-th">Proteina %</th>
            </tr>
          </thead>
          <tbody class="centraliza-valores-tabela tamanho-fonte-resultados">
            <td>{{umidade}}%</td>
            <td>{{proteinas}}%</td>
          </tbody>
        </table>
      </div>
      <div
        class="col-12 row cor-fundo-divs margin-direita-alinhamento margin-divs borda fonte-tamanho-padrao padding-left"
      >
        <div class="col-12">OBSERVAES:</div>
        <div>{{observacao}}</div>
      </div>
      <div
        class="col-12 row cor-fundo-divs margin-direita-alinhamento margin-divs borda padding-nomes fonte-tamanho-padrao padding-left"
      >
        <div class="col-8">NOME DO MOTORISTA: {{nomeMotorista}}</div>
        <div class="col-4">ORDEM CARGA: {{ordemCarga}}</div>
      </div>
      <div
        class="col-12 row cor-fundo-divs margin-direita-alinhamento margin-divs borda padding-nomes fonte-tamanho-padrao padding-left"
      >
        <div class="col-8">NOME DO CLASSIFICADOR: {{nomeClassificador}}</div>
        <div class="col-4">DATA: {{dataClassificacao}}</div>
      </div>

      <div class="centraliza-texto col-12 distancia-botoes-acao">
        <qrcode-vue :value="urlClassificacao" :size="200" level="H"/>
      </div>
    </div>
    <!-- COPIAR ESTA DIV -->
    <div
      class="text-center distancia-botoes-acao fonte-explicacao-download esconde-botao-impressao"
    >Faa o download em PDF de todos os dados da classificao {{numeroLaudo}} clicando no boto abaixo.</div>
    <div class="row col-12 justify-center">
      <q-btn
        icon="fas fa-file-pdf"
        label="Baixar PDF"
        @click="downloadPDF()"
        class="distancia-botoes esconde-botao-impressao cor-pdf col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
      <q-btn
        icon="fas fa-print"
        color="primary"
        label="Imprimir"
        @click="imprimirPagina()"
        class="distancia-botoes esconde-botao-impressao col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import jsPDF from "jspdf";
// eslint-disable-next-line no-unused-vars
import html2canvas from "html2canvas";
import html2pdf from "js-html2pdf";
import { getComCabecalho } from "../components/js/ConexaoApi.js";
import QrcodeVue from "qrcode.vue";
export default {
  data() {
    return {
      logoGlycine: require("../assets/CabecalhoForm1.jpg"),
      codOrdemServicoClassificacao: "",
      pesoLiquido: "",
      numeroLaudo: "",
      nomeCliente: "",
      municipioOrigem: "",
      nomeLocalEmbarque: "",
      umidade: "",
      impureza: "",
      queimados: "",
      ardidos: "",
      fermentados: "",
      picados: "",
      total: "",
      mofados: "",
      quebrados: "",
      esverdeados: "",
      carunchados: "",
      observacao: "",
      nomeMotorista: "",
      nomeClassificador: "",
      dataClassificacao: "",
      ordemCarga: "",
      codProduto: "",
      chkTransgenico: false,
      chkDeclarado: false,
      chkParticipante: false,
      chkConvencional: false,
      vistoria: "",
      placaCavalo: "",
      placa1: "",
      placa2: "",
      municipioDestino: "",
      numeroContrato: "",
      lote: "",
      numeroLacre: "",
      filial: "",
      layoutCelular: false,
      proteinas: "",
      urlClassificacao: "",
      exibeLayout: true,
      tipoProduto: "",
      tiposProduto: [
        { value: 1, label: "Convencional" },
        { value: 2, label: "Transgenico" },
        { value: 3, label: "Positivo" },
        { value: 4, label: "Negativo" },
        { value: 5, label: "No testado" },
        { value: 7, label: "Intacta Declarada" },
        { value: 8, label: "Participante" },
        { value: 9, label: "Testada Positiva" },
        { value: 10, label: "Testada Negativa" },
        { value: 11, label: "Nao testada" },
        { value: 12, label: "RR2" },
      ],
    };
  },
  methods: {
    carregaDadosClassificacao() {
      let uuid = this.$route.params.u;
      if (uuid == "" || uuid == null || uuid == undefined) {
        this.$router.push("/");
      }
      getComCabecalho("carregadadosqrcode", "uuid", uuid)
        .then((result) => {
          const Resposta = JSON.parse(result);
          this.codOrdemServicoClassificacao =
            Resposta[0].CodOrdemServicoClassificacao;
          this.pesoLiquido = Resposta[0].PesoLiquido;
          this.numeroLaudo = Resposta[0].NumeroLaudo;
          this.nomeCliente = Resposta[0].NomeCliente;
          this.proteinas = Resposta[0].Proteina;
          this.filial = Resposta[0].Filial;
          this.municipioOrigem = Resposta[0].MunicipioOrigem;
          this.nomeLocalEmbarque = Resposta[0].LocalEmbarque;
          this.numeroContrato = Resposta[0].NumeroContrato;
          this.lote = Resposta[0].Lote;
          this.municipioDestino = Resposta[0].MunicipioDestino;
          this.numeroLacre = Resposta[0].NumeroLacre;
          this.placaCavalo = Resposta[0].PlacaCavalo;
          this.placa1 = Resposta[0].Placa1;
          this.placa2 = Resposta[0].Placa2;
          this.codProduto = Resposta[0].CodProduto;
          this.vistoria = Resposta[0].Vistoria;
          this.nomeClassificador = Resposta[0].nome;
          const tipoProduto = parseInt(Resposta[0].TipoProduto);
          this.tipoProduto = this.tiposProduto.find(
            (t) => t.value == tipoProduto
          ).label;
          this.umidade = Resposta[0].Umidade;
          this.impureza = Resposta[0].Impureza;
          this.quebrados = Resposta[0].PartidoQuebradoAmassado;
          if (Resposta[0].Ardidos == 0){
            this.ardidos = Resposta[0].ArdidosEQueimados;
          } else {
            this.ardidos = Resposta[0].Ardidos
          }
          this.fermentados = Resposta[0].FermentadoChochoImaturoGerminado;
          this.picados = Resposta[0].Picados;
          this.queimados = Resposta[0].Queimados;
          this.mofados = Resposta[0].Mofados;
          this.esverdeados = Resposta[0].Esverdeados;
          this.carunchados = Resposta[0].Carunchados;
          this.total = Resposta[0].TotalAvariado;
          this.observacao = Resposta[0].Observacao;
          this.nomeMotorista = Resposta[0].NomeMotorista;
          this.nomeCliente = Resposta[0].NomeCliente;
          this.ordemCarga = Resposta[0].OrdemCarga;
          this.dataClassificacao = Resposta[0].DataClassificacao;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    downloadPDF() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = true;
      }
      setTimeout(() => {
        const elementoASerImprimido = document.getElementById("glycinemax");
        const options = {
          filename: "Classificacao" + this.numeroLaudo + ".pdf",
          margin: 8,
          enableLinks: true,
          image: "jpg",
          useCORS: true,
          html2canvas: {
            scale: 2,
            logging: true,
            dpi: 192,
            letterRendering: true,
            useCORS: true,
          },
          jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
        };
        const exporter = new html2pdf(elementoASerImprimido, options);
        exporter
          .getPdf(true)
          .then(() => {
            if (this.$q.platform.is.mobile) {
              this.exibeLayout = false;
            }
          })
          .catch(() => {
            if (this.$q.platform.is.mobile) {
              this.exibeLayout = false;
            }
          });
      }, 700);
    },
    imprimirPagina() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = true;
      }
      setTimeout(() => {
        window.print();
        if (this.$q.platform.is.mobile) {
          setTimeout(() => {
            this.exibeLayout = false;
          }, 1000);
        }
      }, 1500);
    },
    detectaPlataforma() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = false;
        this.layoutCelular = true;
      } else {
        this.layoutCelular = false;
        this.exibeLayout = true;
      }
    },
  },
  components:{
    QrcodeVue
  },
  mounted() {
    this.carregaDadosClassificacao();

    this.detectaPlataforma();
  },
  beforeMount() {
    this.urlClassificacao = window.location.href;
  },
};
</script>

<style scoped>
