  padding: 2px;
}
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.distancia-botoes-acao {
  margin-top: 35px;
}
.fonte-explicacao-download {
  font-size: 22px;
  margin-bottom: 10px;
}
.cor-pdf {
  background-color: #ff0000;
  color: white;
}
.tamanho-botao {
  height: 100px;
}
