<template>
  <q-layout>
    <div class="responsivo">
      <div class="centraliza-titulo margin-tabela">Minhas OS</div>
      <div>
        <q-table
          :data="ordensServico"
          :columns="colunasTabela"
          row-key="CodOrdemServicoClassificacao"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          :rows-per-page-options="opcoesLinhasPorPagina"
          :selected="linhaSelecionada"
          :pagination.sync="padraoLinhasPorPagina"
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>

          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td
                key="CodOrdemServicoClassificacao"
                :props="props"
              >{{ props.row.CodOrdemServicoClassificacao }}</q-td>
              <q-td key="Data" :props="props">{{ props.row.Data }}</q-td>
              <q-td key="nomelocalarmazenagem" :props="props">{{ props.row.nomelocalarmazenagem }}</q-td>
              <q-td key="NomeMunicipio" :props="props">{{ props.row.NomeMunicipio }}</q-td>
              <q-td key="nomeclassificador" :props="props">{{ props.row.nomeclassificador }}</q-td>
              <q-td key="PesoLiquido" :props="props">{{adicionaMilhar(props.row.PesoLiquido)}} KG</q-td>
              <q-td
                key="PesoSaldoRestante"
                :props="props"
              >{{adicionaMilhar(props.row.PesoSaldoRestante)}} KG</q-td>
              <q-td
                  key="codproduto"
                  :props="props"
                  v-if="props.row.CodProduto == 1"
                  style="maxWidth:20px"
              >Soja</q-td>
              <q-td key="codproduto" :props="props" v-if="props.row.CodProduto == 2">Milho</q-td>
              <q-td key="codproduto" :props="props" v-if="props.row.CodProduto == 3">Farelo</q-td>
              <q-td key="codproduto" :props="props" v-else-if="props.row.CodProduto == 4">Sorgo</q-td>
              <q-td key="codproduto" :props="props" v-else-if="props.row.CodProduto == 5">Milheto</q-td>
              <q-td key="acoes" :props="props">
                <q-btn
                  icon="visibility"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  style="font-size:16px"
                  @click="visualizaOs(props.row)"
                >
                  <q-tooltip>Visualizar dados da OS</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
        </q-table>
      </div>
    </div>
    <ModalVisualizaClassificacao ref="modalVisualizacao" />
  </q-layout>
</template>


<script>
import { getComCabecalho } from "../js/ConexaoApiCliente.js";
import { getCookie } from "../../js/Cookies.js";
import ModalVisualizaClassificacao from "@/components/AreaCliente/components/ModalVisualizacaoDadosOsCliente.vue";

export default {
  name: "gradeOsClientes",
  components: {
    ModalVisualizaClassificacao,
  },
  data() {
    return {
      padraoLinhasPorPagina: { rowsPerPage: 10 },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      exibe: true,
      termoBusca: "",
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "CodOrdemServicoClassificacao",
          label: "Cod. OS",
          field: "CodOrdemServicoClassificacao",
          sortable: true,
          align: "left",
          style: "maxWidth:40px",
        },
        {
          name: "Data",
          label: "Data Abertura",
          field: "Data",
          sortable: true,
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "nomelocalarmazenagem",
          label: "Embarque",
          field: "nomelocalarmazenagem",
          align: "center",
          sortable: true,
          style: "maxWidth:50px",
        },
        {
          name: "NomeMunicipio",
          label: "Destino",
          field: "NomeMunicipio",
          align: "center",
          sortable: true,
          style: "maxWidth:50px",
        },
        {
          name: "nomeclassificador",
          label: "Classificador",
          field: "nomeclassificador",
          sortable: true,
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "PesoLiquido",
          label: "Peso Liquido",
          field: "PesoLiquido",
          sortable: true,
          align: "center",
          style: "maxWidth:80px",
        },
        {
          name: "PesoSaldoRestante",
          label: "Restante",
          field: "PesoSaldoRestante",
          sortable: true,
          align: "center",
          style: "maxWidth:90px",
        },
        {
          name: "codproduto",
          label: "Produto",
          field: "CodProduto",
          sortable: true,
          align: "center",
          style: "maxWidth: 500px",
        },
        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
          align: "center",
          style: "maxWidth:50px",
        },
      ],
      //Array com os dados da tabela
      ordensServico: [],
      linhaSelecionada: [],
    };
  },
  methods: {
    carregaTabela() {
      getComCabecalho("listaoscliente", "CodCliente", getCookie("codcliente"))
        .then((result) => {
          this.ordensServico = JSON.parse(result);
          this.statusCarregamentoTabela = false;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
          this.statusCarregamentoTabela = false;
        });
    },
    visualizaOs(dadosOs) {
      this.$refs.modalVisualizacao.abreModalVisualizacao(dadosOs);
    },
    adicionaMilhar(numero) {
      return Number(numero).toLocaleString("pt-BR");
    },
  },
  beforeMount() {
    this.carregaTabela();
  },
};
</script>
<style scoped>
.margin-tabela {
  margin-top: 10px;
}
.centraliza-titulo {
  text-align: center;
  margin-bottom: 10px;
}
tr:nth-child(even) {
  background-color: lightgray;
}
</style>

