<template>
  <q-dialog prevent-close v-model="exibe">
    <span slot="title">Primeiro login</span>
    <div slot="body">
      <div class="largura-modal">
        <q-input
          v-model="senha"
          type="password"
          float-label="Digite sua nova senha"
          placeholder="Digite sua nova senha"
        />
        <q-input
          v-model="confirmacao"
          type="password"
          @keypress="verificaSenhas()"
          placeholder="Confirme sua senha"
          float-label="Confirme sua senha"
        />
        <div style="margin-top:10px">
          <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
        </div>
      </div>
    </div>
    <template slot="buttons">
      <q-btn color="negative" label="Cancelar" @click="fechaModal()" />
      <q-btn color="primary" label="Salvar" @click="alteraSenha()" />
    </template>
  </q-dialog>
</template>

<script>
import { post as postCliente } from "../components/AreaCliente/js/ConexaoApiCliente.js";
import { post } from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
export default {
  name: "ModalPrimeiroLogin",
  props: ["usuarioOuCliente"],
  data() {
    return {
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      exibe: false,
      senha: "",
      confirmacao: "",
      funcaoBotaoSalvar: Function,
    };
  },
  methods: {
    alteraSenha() {
      if (this.senha != this.confirmacao) {
        this.exibeMensagemRetorno = true;
        this.tipoMensagem = "negative";
        this.mensagemRetorno = "As senhas no correspondem!";
      } else {
        this.exibeMensagemRetorno = false;
        this.tipoMensagem = "positive";
        this.mensagemRetorno = "";
        const novaSenha = {};
        novaSenha.Senha = this.confirmacao;
        if (this.$props.usuarioOuCliente === "cliente") {
          novaSenha.id = getCookie("id");
          this.alteraSenhaAreaCliente(novaSenha);
        } else {
          novaSenha.CodUsuarioNuvem = getCookie("id");
          this.alteraSenhaAreaAdministrativa(novaSenha);
        }
      }
    },
    alteraSenhaAreaCliente(novaSenha) {
      postCliente("primeirologin", novaSenha)
        .then((result) => {
          const Resposta = JSON.parse(result);
          if (Resposta.Status == true) {
            this.exibeMensagemRetorno = true;
            this.tipoMensagem = "positive";
            this.mensagemRetorno = "Senha alterada com sucesso! Entrando...";
            this.$emit("senhaAlterada", this.confirmacao);
            setTimeout(() => {
              this.exibeMensagemRetorno = false;
              this.tipoMensagem = "positive";
              this.mensagemRetorno = "";
              this.funcaoBotaoSalvar();
              this.exibe = false;
            }, 2000);
          } else {
            this.exibeMensagemRetorno = true;
            this.tipoMensagem = "negative";
            this.mensagemRetorno = Resposta.msg;
          }
        })
        .catch((err) => {
          const Erro = JSON.parse(err);
          this.exibeMensagemRetorno = true;
          this.tipoMensagem = "negative";
          this.mensagemRetorno = Erro.msg;
          // eslint-disable-next-line no-console
          console.error(Erro.Erro);
        });
    },
    alteraSenhaAreaAdministrativa(novaSenha) {
      post("auth/primeiroacesso", novaSenha)
        .then((result) => {
          const Resposta = JSON.parse(result);
          if (Resposta.Status == true) {
            this.exibeMensagemRetorno = true;
            this.tipoMensagem = "positive";
            this.mensagemRetorno = "Senha alterada com sucesso! Entrando...";
            this.$emit("senhaAlterada", this.confirmacao);
            setTimeout(() => {
              this.exibeMensagemRetorno = false;
              this.tipoMensagem = "positive";
              this.mensagemRetorno = "";
              this.funcaoBotaoSalvar();
              this.exibe = false;
            }, 2000);
          } else {
            this.exibeMensagemRetorno = true;
            this.tipoMensagem = "negative";
            this.mensagemRetorno = Resposta.msg;
          }
        })
        .catch((err) => {
          const Erro = JSON.parse(err);
          this.exibeMensagemRetorno = true;
          this.tipoMensagem = "negative";
          this.mensagemRetorno = Erro.msg;
          // eslint-disable-next-line no-console
          console.error(Erro.Erro);
        });
    },
    abreModal(callBackFuncaoSalvar) {
      this.funcaoBotaoSalvar = callBackFuncaoSalvar;
      this.exibe = true;
    },
    fechaModal() {
      this.exibe = false;
    },
  },
};
</script>


<style>
.largura-modal {
  width: 350px;
}

.descricao-janela {
  color: black;
}
</style>
