          </template>
          <template slot="footer" class="row"></template>
          <q-tr slot="bottom-row" id="totais">
            <q-td class="totais-fonte">Qtd. O.S: {{totalOs}}</q-td>
            <q-td></q-td>
            <q-td></q-td>
            <q-td></q-td>
            <q-td></q-td>
            <q-td></q-td>
            <q-td></q-td>
            <q-td class="totais-fonte">Total: {{totalizadoPesoLiquido}}</q-td>
            <q-td class="totais-fonte">Total: {{totalizadorClassificado}}</q-td>
            <q-td class="totais-fonte">Total: {{totalizadorRestante}}</q-td>
            <q-td></q-td>
            <q-td></q-td>
          </q-tr>
          <template slot="body" slot-scope="props">
            <q-tr :props="props" class="linhas">
              <q-td
                key="CodOrdemServicoClassificacao"
                :props="props"
              >{{ props.row.CodOrdemServicoClassificacao }}</q-td>
              <q-td key="NumeroContrato" :props="props">{{ props.row.NumeroContrato === null ? 'S/ Contrato' : props.row.NumeroContrato }}</q-td>
              <q-td key="Data" :props="props">{{ props.row.Data }}</q-td>
              <q-td key="nomecliente" :props="props">{{ props.row.nomecliente }}</q-td>
              <q-td
