              >{{ props.row.nomelocalarmazenagem }}</q-td>
              <q-td key="NomeMunicipio" :props="props">{{ props.row.NomeMunicipio }}</q-td>
              <q-td key="nomeclassificador" :props="props">{{ props.row.nomeclassificador }}</q-td>
              <q-td key="PesoLiquido" :props="props">{{adicionaMilhar(props.row.PesoLiquido)}} KG</q-td>
              <q-td
                key="TotalClassificado"
                :props="props"
              >{{adicionaMilhar(props.row.TotalClassificado)}} KG</q-td>
              <q-td
                key="PesoSaldoRestante"
                :props="props"
              >{{adicionaMilhar(props.row.PesoSaldoRestante)}} KG</q-td>
              <q-td
                key="codproduto"
                :props="props"
                v-if="props.row.CodProduto == 1"
