                  color="primary"
                  v-show="administrador"
                  v-if="visualizaFinalizados == false"
                  @click="alteraOs(props.row)"
                >
                  <q-tooltip>Editar OS {{props.row.CodOrdemServicoClassificacao}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="ion-trash"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-show="administrador"
                  @click="desativaOs(props.row.CodOrdemServicoClassificacao)"
                >
                  <q-tooltip>Remover OS {{props.row.CodOrdemServicoClassificacao}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="fas fa-file-alt"
                  dense
                  :props="props"
                  flat
                  v-if="visualizaFinalizados == false"
                  color="positive"
                  @click="abreModalClassificacao(props.row)"
                >
                  <q-tooltip>Classificar</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizaOs(props.row)"
