                  v-show="administrador"
                  @click="reabreOs(props.row.CodOrdemServicoClassificacao)"
                >
                  <q-tooltip>Reabrir OS {{props.row.CodOrdemServicoClassificacao}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="fas fa-check"
