                </q-btn>
              </q-td>
            </q-tr>
          </template>
          <template slot="top-right">
            <q-btn
              v-if="visualizaFinalizados == false"
              color="primary"
              icon="mdi-clipboard-plus"
              label="Nova OS"
              @click="abreModalCadastro()"
              v-show="administrador || supervisor"
            />
          </template>
        </q-table>
        <q-table
          :data="ordensServico"
          :columns="colunasTabelaFinalizados"
          row-key="CodOrdemServicoClassificacao"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          dense
          :rows-per-page-options="opcoesLinhasPorPagina"
          :pagination.sync="serverPagination"
          table-class="css-tabela"
          v-else
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>

          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td
                key="CodOrdemServicoClassificacao"
                :props="props"
              >{{ props.row.CodOrdemServicoClassificacao }}</q-td>
              <q-td key="NumeroContrato" :props="props">{{ props.row.NumeroContrato === null ? 'S/ Contrato' : props.row.NumeroContrato }}</q-td>
              <q-td key="Data" :props="props">{{ props.row.Data }}</q-td>
              <q-td key="nomecliente" :props="props">{{ props.row.nomecliente }}</q-td>
              <q-td
                key="nomelocalarmazenagem"
                :props="props"
                v-if="codClienteAlfameta == 3"
              >{{ props.row.nomelocalarmazenagem }} - {{props.row.MunicipioOrigem}}</q-td>
              <q-td
                key="nomelocalarmazenagem"
                :props="props"
                v-else
              >{{ props.row.nomelocalarmazenagem }}</q-td>
              <q-td key="NomeMunicipio" :props="props">{{ props.row.NomeMunicipio }}</q-td>
              <q-td key="nomeclassificador" :props="props">{{ props.row.nomeclassificador }}</q-td>
              <q-td key="PesoLiquido" :props="props">{{adicionaMilhar(props.row.PesoLiquido)}} KG</q-td>
              <q-td
                key="TotalClassificado"
                :props="props"
              >{{adicionaMilhar(props.row.TotalClassificado)}} KG</q-td>
              <q-td
                key="PesoSaldoRestante"
                :props="props"
              >{{adicionaMilhar(props.row.PesoSaldoRestante)}} KG</q-td>
              <q-td
                key="codproduto"
                :props="props"
                v-if="props.row.CodProduto == 1"
                style="maxWidth:20px"
              >Soja</q-td>
              <q-td key="codproduto" :props="props" v-else-if="props.row.CodProduto == 2">Milho</q-td>
              <q-td key="codproduto" :props="props" v-else-if="props.row.CodProduto == 3">Farelo</q-td>
              <q-td key="codproduto" :props="props" v-else-if="props.row.CodProduto == 4">Sorgo</q-td>
              <q-td key="codproduto" :props="props" v-else-if="props.row.CodProduto == 5">Milheto</q-td>
              <q-td
                key="DataFechamento"
                :props="props"
                v-show="visualizaFinalizados == true"
              >{{ props.row.DataFechamentoFormatada }}</q-td>

              <q-td key="acoes" :props="props">
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  v-show="administrador"
                  v-if="visualizaFinalizados == false"
                  @click="alteraOs(props.row)"
                >
                  <q-tooltip>Editar OS {{props.row.CodOrdemServicoClassificacao}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="ion-trash"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-show="administrador"
                  @click="desativaOs(props.row.CodOrdemServicoClassificacao)"
                >
                  <q-tooltip>Remover OS {{props.row.CodOrdemServicoClassificacao}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="fas fa-file-alt"
                  dense
                  :props="props"
                  flat
                  v-if="visualizaFinalizados == false"
                  color="positive"
                  @click="abreModalClassificacao(props.row)"
                >
                  <q-tooltip>Classificar</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizaOs(props.row)"
                >
                  <q-tooltip>Visualizar dados da OS {{props.row.CodOrdemServicoClassificacao}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="mdi-restart"
                  dense
                  :props="props"
                  flat
                  v-if="visualizaFinalizados"
                  v-show="administrador"
                  @click="reabreOs(props.row.CodOrdemServicoClassificacao)"
                >
                  <q-tooltip>Reabrir OS {{props.row.CodOrdemServicoClassificacao}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="fas fa-check"
                  dense
                  :props="props"
                  flat
                  v-else
                  v-show="administrador"
                  @click="marcaFinalizada(props.row.CodOrdemServicoClassificacao)"
                >
                  <q-tooltip>Marcar OS {{props.row.CodOrdemServicoClassificacao}} como finalizada</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
          <template slot="top-right">
            <q-btn
              v-if="visualizaFinalizados == false"
              color="primary"
              icon="mdi-clipboard-plus"
              label="Nova OS"
              @click="abreModalCadastro()"
              v-show="administrador || supervisor"
            />
          </template>
        </q-table>
      </div>
    </div>
    <ModalOrdemServico
      ref="modalOrdemServico"
      @atualizaTabela="carregaTabela"
      @atualizaPesosOs="carregaTabela"
      :visualizando="visualizaFinalizados"
    />
    <ModalNovaClassificacao
      ref="modalNovaClassificacao"
      :visualizando="visualizaFinalizados"
      @atualizaTabela="carregaTabela"
    />
    <ModalConab
      ref="modalConab"
      :visualizando="visualizaFinalizados"
      @atualizaTabela="carregaTabela"
      v-if="conab"
    />
  </div>
</template>

<script>
import { get, getComCabecalho } from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
import ModalOrdemServico from "@/components/ModalOrdemServico";
import ModalNovaClassificacao from "@/components/ModalNovaClassificacao";
import ModalConab from "./ModalConab";
import { detectaCliente } from "./utils/DetectaCliente.js";

export default {
  props:{'nomeTabela': String, 'visualizaFinalizados' : Boolean, 'exibeConab' : Boolean},
  components: {
    ModalOrdemServico,
    ModalNovaClassificacao,
    ModalConab,
  },
  data() {
    return {
      serverPagination: {
        rowsPerPage: 10,
        // page: 1,
        // rowsNumber: 10
      },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      exibe: true,
      abreModal: false,
      termoBusca: "",
      usuarios: [],
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "CodOrdemServicoClassificacao",
          label: "Cod. OS",
          field: "CodOrdemServicoClassificacao",
          sortable: true,
          align: "center"
        },
        {
          name: "NumeroContrato",
          label: "N Contrato",
          field: "NumeroContrato",
          sortable: false,
          align: "center"
        },
        {
          name: "Data",
          label: "Data Abertura",
          field: "Data",
          sortable: true,
          align: "center"
        },
        {
          name: "nomecliente",
          label: "Cliente",
          sortable: true,
          field: "nomecliente",
          align: "center",
          style: "maxWidth:15px, padding-left:2px",
        },
        {
          name: "nomelocalarmazenagem",
          label: "Embarque",
          field: "nomelocalarmazenagem",
          align: "center",
          sortable: true,
          style: "maxWidth:50px",
        },
        {
          name: "NomeMunicipio",
          label: "Destino",
          field: "NomeMunicipio",
          align: "center",
          sortable: true,
          style: "maxWidth:50px",
        },
        {
          name: "nomeclassificador",
          label: "Classificador",
          field: "nomeclassificador",
          sortable: true,
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "PesoLiquido",
          label: "Peso Liquido",
          field: "PesoLiquido",
          sortable: true,
          align: "center",
          style: "maxWidth:80px",
        },
        {
          name: "TotalClassificado",
          label: "Classificado",
          field: "TotalClassificado",
          sortable: true,
          align: "center",
          style: "maxWidth:90px",
        },
        {
          name: "PesoSaldoRestante",
          label: "Restante",
          field: "PesoSaldoRestante",
          sortable: true,
          align: "center",
          style: "maxWidth:90px",
        },
        {
          name: "codproduto",
          label: "Produto",
          field: "CodProduto",
          sortable: true,
          align: "center",
          style: "maxWidth: 500px",
        },
        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
          align: "center",
          style: "maxWidth:50px",
        },
      ],
      colunasTabelaFinalizados: [
        {
          name: "CodOrdemServicoClassificacao",
          label: "Cod. OS",
          field: "CodOrdemServicoClassificacao",
          sortable: true,
          align: "center"
        },
        {
          name: "NumeroContrato",
          label: "N Contrato",
          field: "NumeroContrato",
          sortable: false,
          align: "center"
        },
        {
          name: "Data",
          label: "Data Abertura",
          field: "Data",
          sortable: true,
          align: "center"
        },
        {
          name: "nomecliente",
          label: "Cliente",
          sortable: true,
          field: "nomecliente",
          align: "center",
          style: "maxWidth:150px, padding:0px",
        },

        {
          name: "nomelocalarmazenagem",
          label: "Embarque",
          field: "nomelocalarmazenagem",
          align: "center",
          sortable: true,
          style: "maxWidth:50px",
        },
        {
          name: "NomeMunicipio",
          label: "Destino",
          field: "NomeMunicipio",
          align: "center",
          sortable: true,
          style: "maxWidth:50px",
        },
        {
          name: "nomeclassificador",
          label: "Classificador",
          field: "nomeclassificador",
          sortable: true,
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "PesoLiquido",
          label: "Peso Liquido",
          field: "PesoLiquido",
          sortable: true,
          align: "center",
          style: "maxWidth:80px",
        },
        {
          name: "TotalClassificado",
          label: "Classificado",
          field: "TotalClassificado",
          sortable: true,
          align: "center",
          style: "maxWidth:90px",
        },
        {
          name: "PesoSaldoRestante",
          label: "Restante",
          field: "PesoSaldoRestante",
          sortable: true,
          align: "center",
          style: "maxWidth:90px",
        },
        {
          name: "codproduto",
          label: "Produto",
          field: "CodProduto",
          sortable: true,
          align: "center",
          style: "maxWidth: 500px",
        },
        {
          name: "DataFechamento",
          label: "Data Fechamento",
          field: "DataFechamento",
          sortable: true,
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
          align: "center",
          style: "maxWidth:50px",
        },
      ],
      //Array com os dados da tabela
      ordensServico: [],
      administrador: false,
      supervisor: false,
      codClienteAlfameta: "",
      conab: false,
      totalizadoPesoLiquido: "",
      totalizadorClassificado: "",
      totalizadorRestante: "",
      totalOs: "",
    };
  },
  methods: {
    carregaTabelaApenasFinalizadas() {
      if (this.administrador == true) {
        if (this.conab == true) {
          getComCabecalho("ordemservicoconab", "Finalizadas", 1)
            .then((result) => {
              this.ordensServico = JSON.parse(result);
              this.statusCarregamentoTabela = false;
              setTimeout(() => {
                this.calculaTotalizadores();
              }, 2000);
            })
            .catch(() => {
              this.statusCarregamentoTabela = false;
              // console.error(err);
            });
        } else {
          get("osfinalizadas")
            .then((result) => {
              this.ordensServico = JSON.parse(result);
              this.statusCarregamentoTabela = false;
              setTimeout(() => {
                this.calculaTotalizadores();
              }, 2000);
            })
            .catch(() => {
              this.statusCarregamentoTabela = false;
              // console.error(err);
            });
        }
      } else if (this.supervisor == true) {
        if (this.conab == true) {
          getComCabecalho("ordemservicoconab", "Finalizadas", 1)
              .then((result) => {
                this.ordensServico = JSON.parse(result);
                this.statusCarregamentoTabela = false;
                setTimeout(() => {
                  this.calculaTotalizadores();
                }, 2000);
              })
              .catch(() => {
                this.statusCarregamentoTabela = false;
                // console.error(err);
              });
        } else {
          getComCabecalho("osfinalizadas", "Supervisor", getCookie("codClassificadorSistema"))
              .then((result) => {
                this.ordensServico = JSON.parse(result);
                this.statusCarregamentoTabela = false;
                setTimeout(() => {
                  this.calculaTotalizadores();
                }, 2000);
              })
              .catch(() => {
                this.statusCarregamentoTabela = false;
                // console.error(err);
              });
        }
      }
    },
    carregaTabela() {
      if (this.administrador == true) {
        if (this.conab == true) {
          getComCabecalho("ordemservicoconab", "administrador", 1)
            .then((result) => {
              this.ordensServico = null;
              this.ordensServico = JSON.parse(result);
              this.statusCarregamentoTabela = false;
              setTimeout(() => {
                this.calculaTotalizadores();
              }, 2000);
            })
            .catch(() => {
              this.statusCarregamentoTabela = false;
              // console.error(err);
            });
        } else {
          getComCabecalho("ordemservico", "administrador", 1)
            .then((result) => {
              this.ordensServico = null;
              this.ordensServico = JSON.parse(result);
              this.statusCarregamentoTabela = false;
              setTimeout(() => {
                this.calculaTotalizadores();
              }, 2000);
            })
            .catch(() => {
              this.statusCarregamentoTabela = false;
              // console.error(err);
            });
        }
      } else if (this.supervisor == true) {
        if (this.conab == true) {
          getComCabecalho("ordemservicoconab", "supervisor", getCookie("codClassificadorSistema"))
              .then((result) => {
                this.ordensServico = null;
                this.ordensServico = JSON.parse(result);
                this.statusCarregamentoTabela = false;
                setTimeout(() => {
                  this.calculaTotalizadores();
                }, 2000);
              })
              .catch(() => {
                this.statusCarregamentoTabela = false;
                // console.error(err);
              });
        } else {
          getComCabecalho("ordemservico", "supervisor", getCookie("codClassificadorSistema"))
              .then((result) => {
                this.ordensServico = null;
                this.ordensServico = JSON.parse(result);
                this.statusCarregamentoTabela = false;
                setTimeout(() => {
                  this.calculaTotalizadores();
                }, 2000);
              })
              .catch(() => {
                this.statusCarregamentoTabela = false;
                // console.error(err);
              });
        }
      }
      else {
        if (this.conab == true) {
          getComCabecalho(
            "ordemservicoconab",
            "CodClassificadorSite",
            getCookie("id")
          )
            .then((result) => {
              this.ordensServico = null;
              this.ordensServico = JSON.parse(result);
              this.statusCarregamentoTabela = false;
              setTimeout(() => {
                this.calculaTotalizadores();
              }, 2000);
            })
            .catch(() => {
              // console.error(err);
              this.statusCarregamentoTabela = false;
            });
        } else {
          getComCabecalho(
            "ordemservico",
            "CodClassificadorSite",
            getCookie("id")
          )
            .then((result) => {
              this.ordensServico = null;
              this.ordensServico = JSON.parse(result);
              this.statusCarregamentoTabela = false;
              setTimeout(() => {
                this.calculaTotalizadores();
              }, 2000);
            })
            .catch(() => {
              // console.error(err);
              this.statusCarregamentoTabela = false;
            });
        }
      }
    },
    visualizaOs(dadosOs) {
      if (this.conab == true) {
        this.$refs.modalConab.abreModalVisualizacao(dadosOs);
      } else {
        this.$refs.modalOrdemServico.abreModalVisualizacao(dadosOs);
      }
    },
    alteraOs(dadosOs) {
      if (this.conab == true) {
        this.$refs.modalConab.abreModalAlteracao(dadosOs);
      } else {
        this.$refs.modalOrdemServico.abreModalAlteracao(dadosOs);
      }
    },
    abreModalClassificacao(dadosOs) {
      if (this.conab == true) {
        this.$refs.modalConab.abreModal(dadosOs);
      } else {
        this.$refs.modalNovaClassificacao.abreModal(dadosOs);
      }
    },
    adicionaMultiploDestino(CodOrdemServicoClassificacao) {
      this.$refs.modalMultiplosDestinos.abreModal(CodOrdemServicoClassificacao);
    },
    calculaTotalizadores() {
      return new Promise((resolve) => {
        setTimeout(() => {
          let os = [];
          //Joga o termo pesquisado para lower case
          const busca = this.termoBusca.toLowerCase();
          //vai varrer todos os registros que esto no array
          for (let i = 0; i < this.ordensServico.length; i++) {
            if (
              this.ordensServico[i].nomecliente.toLowerCase().includes(busca) ||
              this.ordensServico[i].nomelocalarmazenagem
                .toLowerCase()
                .includes(busca) ||
              this.ordensServico[i].NomeMunicipio.toLowerCase().includes(
                busca
              ) ||
              this.ordensServico[i].nomeclassificador
                .toLowerCase()
                .includes(busca) ||
              this.ordensServico[i].Data.includes(busca) ||
              this.ordensServico[i].CodOrdemServicoClassificacao ==
                parseInt(busca)
            ) {
              //Se algum registro estiver de acordo com a condio acima, ele  adicionado em outro array
              os.push(this.ordensServico[i]);
            }
          }
          let pesoLiquidoTotal = 0;
          let pesoClassificadoTotal = 0;
          let pesoRestanteTotal = 0;
          if (os.length > 0) {
            for (let o = 0; o < os.length; o++) {
              pesoLiquidoTotal = pesoLiquidoTotal + parseInt(os[o].PesoLiquido);
              if (os[o].TotalClassificado === null) {
                os[o].TotalClassificado = 0;
              }
              pesoClassificadoTotal =
                pesoClassificadoTotal + parseInt(os[o].TotalClassificado);
              pesoRestanteTotal =
                pesoRestanteTotal + parseInt(os[o].PesoSaldoRestante);
            }
          }
          this.totalizadoPesoLiquido =
            this.adicionaMilhar(pesoLiquidoTotal) + "KG";
          this.totalizadorClassificado =
            this.adicionaMilhar(pesoClassificadoTotal) + "KG";
          this.totalizadorRestante =
            this.adicionaMilhar(pesoRestanteTotal) + " KG";
          this.totalOs = os.length;
          os = [];
          pesoRestanteTotal = null;
          pesoClassificadoTotal = null;
          pesoRestanteTotal = null;
          resolve();
        }, 250);
      });
    },
    desativaOs(CodOrdemServicoClassificacao) {
      this.$q
        .dialog({
          title: "Desativar OS",
          message: "Deseja realmente desativar a OS selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho(
            "desativaos",
            "CodOrdemServicoClassificacao",
            CodOrdemServicoClassificacao
          )
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    marcaFinalizada(CodOs) {
      this.$q
        .dialog({
          title: "Finalizar OS",
          message: "Deseja realmente finalizar a OS selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho("finalizaos", "CodOs", CodOs)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.$emit("atualizaTabelaFinalizada");
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    reabreOs(CodOs) {
      this.$q
        .dialog({
          title: "Reabrir OS",
          message: "Deseja realmente reabrir a OS selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho("reabreos", "CodOs", CodOs)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabelaApenasFinalizadas();
                this.$emit("atualizaTabelaReaberta");
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    abreModalCadastro() {
      if (this.conab == true) {
        this.$refs.modalConab.abreModal();
      } else {
        this.$refs.modalOrdemServico.abreModal();
      }
    },
    adicionaMilhar(numero) {
      return Number(numero).toLocaleString("pt-BR");
    },
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    detectaCliente() {
      detectaCliente()
        .then((result) => {
          this.codClienteAlfameta = result.CodCliente;
        })
        .catch(() => {
          // console.error(err);
        });
    },
  },
  mounted() {
    this.conab = this.$props.exibeConab;
    this.detectaCliente();
    this.verificaAdministrador();
    //this.onRequest({pagination :this.serverPagination});
    if (this.visualizaFinalizados == true) {
      this.carregaTabelaApenasFinalizadas();
    } else {
      this.carregaTabela();
    }
  },
  watch: {
    termoBusca: async function () {
      await this.calculaTotalizadores();
    },
  },
};
</script>

