<template>
  <q-layout>
    <div class="responsivo">
      <div class="centraliza-titulo margin-tabela">{{nomeTabela}}</div>
      <div>
        <q-table
          :data="ordensServico"
          :columns="colunasTabela"
          row-key="CodOrdemServicoClassificacaoConab"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          dense
          ref="table"
          :rows-per-page-options="opcoesLinhasPorPagina"
          :pagination.sync="serverPagination"
          table-class="css-tabela"
          v-if="!visualizaFinalizados"
          id="tabela"
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>
          <template slot="footer" class="row"></template>
          <template slot="body" slot-scope="props">
            <q-tr :props="props" class="linhas">
              <q-td
                key="CodOrdemServicoClassificacaoConab"
                :props="props"
              >{{ props.row.CodOrdemServicoClassificacaoConab }}</q-td>
              <q-td
                key="Data"
                :props="props"
              >{{ new Date(props.row.Data).toLocaleDateString("pt-BR") }}</q-td>
              <q-td key="NomeCliente" :props="props">{{ props.row.NomeCliente }}</q-td>
              <q-td key="NomeLocalArmazenagem" :props="props">{{ props.row.NomeLocalArmazenagem }}</q-td>
              <q-td key="NomeMunicipio" :props="props">{{ props.row.NomeMunicipio }}</q-td>
              <q-td key="NomeClassificador" :props="props">{{ props.row.NomeClassificador }}</q-td>
              <q-td
                key="codproduto"
                :props="props"
                v-if="props.row.CodProduto == 1"
                style="maxWidth:20px"
              >Soja</q-td>
              <q-td key="codproduto" :props="props" v-if="props.row.CodProduto == 2">Milho</q-td>
              <q-td key="codproduto" :props="props" v-if="props.row.CodProduto == 3">Farelo</q-td>
              <q-td key="codproduto" :props="props" v-else-if="props.row.CodProduto == 4">Sorgo</q-td>
              <q-td key="codproduto" :props="props" v-else-if="props.row.CodProduto == 5">Milheto</q-td>
              <q-td key="TotalClassificado" :props="props">{{adicionaMilhar(props.row.TotalClassificado)}}</q-td>
              <q-td key="acoes" :props="props">
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  v-show="administrador"
                  v-if="visualizaFinalizados == false"
                  @click="alterarOrdemServicoConab(props.row)"
                >
                  <q-tooltip>Editar OS {{props.row.CodOrdemServicoClassificacaoConab}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="ion-trash"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-show="administrador"
                  @click="desativarOrdemServicoConab(props.row.CodOrdemServicoClassificacaoConab)"
                >
                  <q-tooltip>Remover OS {{props.row.CodOrdemServicoClassificacaoConab}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="fas fa-file-alt"
                  dense
                  :props="props"
                  flat
                  v-if="visualizaFinalizados == false"
                  color="positive"
                  @click="abrirModalClassificacaoConab(props.row)"
                >
                  <q-tooltip>Classificar</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizarOrdemServicoConab(props.row)"
                >
                  <q-tooltip>Visualizar dados da OS {{props.row.CodOrdemServicoClassificacaoConab}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="mdi-restart"
                  dense
                  :props="props"
                  flat
                  v-if="visualizaFinalizados"
                  v-show="administrador"
                  @click="reabrirOrdemServicoConab(props.row.CodOrdemServicoClassificacaoConab)"
                >
                  <q-tooltip>Reabrir OS {{props.row.CodOrdemServicoClassificacaoConab}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="fas fa-check"
                  dense
                  :props="props"
                  flat
                  v-else
                  v-show="administrador"
                  @click="finalizarOrdemServicoConab(props.row.CodOrdemServicoClassificacaoConab)"
                >
                  <q-tooltip>Marcar OS {{props.row.CodOrdemServicoClassificacaoConab}} como finalizada</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
          <template slot="top-right">
            <q-btn
              v-if="visualizaFinalizados == false"
              color="primary"
              icon="mdi-clipboard-plus"
              label="Nova OS"
              @click="abrirModalCadastroOrdemServicoConab()"
              v-show="administrador"
            />
          </template>
        </q-table>
        <q-table
          :data="ordensServico"
          :columns="colunasTabelaFinalizados"
          row-key="CodOrdemServicoClassificacaoConab"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          dense
          :rows-per-page-options="opcoesLinhasPorPagina"
          :pagination.sync="serverPagination"
          table-class="css-tabela"
          v-else
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>

          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td
                key="CodOrdemServicoClassificacaoConab"
                :props="props"
              >{{ props.row.CodOrdemServicoClassificacaoConab }}</q-td>
              <q-td
                key="Data"
                :props="props"
              >{{ new Date(props.row.Data).toLocaleDateString("pt-BR") }}</q-td>
              <q-td key="NomeCliente" :props="props">{{ props.row.NomeCliente }}</q-td>
              <q-td key="NomeLocalArmazenagem" :props="props">{{ props.row.NomeLocalArmazenagem }}</q-td>
              <q-td key="NomeMunicipio" :props="props">{{ props.row.NomeMunicipio }}</q-td>
              <q-td key="NomeClassificador" :props="props">{{ props.row.NomeClassificador }}</q-td>
              <q-td
                key="CodProduto"
                :props="props"
                v-if="props.row.CodProduto == 1"
                style="maxWidth:20px"
              >Soja</q-td>
              <q-td key="CodProduto" :props="props" v-else-if="props.row.CodProduto == 2">Milho</q-td>
              <q-td key="CodProduto" :props="props" v-else-if="props.row.CodProduto == 3">Farelo</q-td>
              <q-td key="CodProduto" :props="props" v-else-if="props.row.CodProduto == 4">Sorgo</q-td>
              <q-td key="CodProduto" :props="props" v-else-if="props.row.CodProduto == 5">Milheto</q-td>
              <q-td key="TotalClassificado" :props="props">{{ adicionaMilhar(props.row.TotalClassificado)}}</q-td>
              <q-td
                key="FinalizadoEm"
                :props="props"
                v-show="visualizaFinalizados == true"
              >{{ new Date(props.row.FinalizadoEm).toLocaleDateString("pt-BR") }}</q-td>

              <q-td key="acoes" :props="props">
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  v-show="administrador"
                  v-if="visualizaFinalizados == false"
                  @click="alterarOrdemServicoConab(props.row)"
                >
                  <q-tooltip>Editar OS {{props.row.CodOrdemServicoClassificacaoConab}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="ion-trash"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-show="administrador"
                  @click="desativarOrdemServicoConab(props.row.CodOrdemServicoClassificacaoConab)"
                >
                  <q-tooltip>Remover OS {{props.row.CodOrdemServicoClassificacaoConab}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="fas fa-file-alt"
                  dense
                  :props="props"
                  flat
                  v-if="visualizaFinalizados == false"
                  color="positive"
                  @click="abrirModalClassificacaoConab(props.row)"
                >
                  <q-tooltip>Classificar</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizarOrdemServicoConab(props.row)"
                >
                  <q-tooltip>Visualizar dados da OS {{props.row.CodOrdemServicoClassificacaoConab}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="mdi-restart"
                  dense
                  :props="props"
                  flat
                  v-if="visualizaFinalizados"
                  v-show="administrador"
                  @click="reabrirOrdemServicoConab(props.row.CodOrdemServicoClassificacaoConab)"
                >
                  <q-tooltip>Reabrir OS {{props.row.CodOrdemServicoClassificacaoConab}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="fas fa-check"
                  dense
                  :props="props"
                  flat
                  v-else
                  v-show="administrador"
                  @click="finalizarOrdemServicoConab(props.row.CodOrdemServicoClassificacaoConab)"
                >
                  <q-tooltip>Marcar OS {{props.row.CodOrdemServicoClassificacaoConab}} como finalizada</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
          <template slot="top-right">
            <q-btn
              v-if="visualizaFinalizados == false"
              color="primary"
              icon="mdi-clipboard-plus"
              label="Nova OS"
              @click="abrirModalCadastroOrdemServicoConab()"
              v-show="administrador"
            />
          </template>
        </q-table>
      </div>
    </div>
    <modal-classificacao-conab ref="modalClassificacaoConab"/>
    <modal-conab
      ref="modalConab"
      :visualizando="visualizaFinalizados"
      @atualizaTabela="carregaTabela"
    />
  </q-layout>
</template>

<script>
import {
  getComCabecalho,
  patchComCabecalho,
  requestDeleteComCabecalho,
} from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
import ModalConab from "./ModalConab.vue";
import { detectaCliente } from "./utils/DetectaCliente.js";
import ModalClassificacaoConab from './ModalNovaClassificacaoConab.vue';
export default {
  props: { nomeTabela: String, visualizaFinalizados: Boolean },
  components: {
    ModalConab,
    ModalClassificacaoConab
  },
  data() {
    return {
      serverPagination: {
        rowsPerPage: 10,
        // page: 1,
        // rowsNumber: 10
      },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      exibe: true,
      abreModal: false,
      termoBusca: "",
      usuarios: [],
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "CodOrdemServicoClassificacaoConab",
          label: "Cod. OS",
          field: "CodOrdemServicoClassificacaoConab",
          sortable: true,
          align: "center",
          style: "maxWidth:40px",
        },
        {
          name: "Data",
          label: "Data Abertura",
          field: "Data",
          sortable: true,
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "NomeCliente",
          label: "Cliente",
          field: "NomeCliente",
          align: "center",
          style: "maxWidth:15px, padding:0px",
        },
        {
          name: "NomeLocalArmazenagem",
          label: "Embarque",
          field: "NomeLocalArmazenagem",
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "NomeClassificador",
          label: "Classificador",
          field: "NomeClassificador",
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "codproduto",
          label: "Produto",
          field: "CodProduto",
          align: "center",
          style: "maxWidth: 500px",
        },
        {
          name: "TotalClassificado",
          label: "Total Classificado (kg)",
          field: "TotalClassificado",
          align: "center",
          style: "maxWidth: 500px",
        },
        
        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
          align: "center",
          style: "maxWidth:50px",
        },
      ],
      colunasTabelaFinalizados: [
        {
          name: "CodOrdemServicoClassificacaoConab",
          label: "Cod. OS",
          field: "CodOrdemServicoClassificacaoConab",
          sortable: true,
          align: "center",
          style: "maxWidth:40px",
        },
        {
          name: "Data",
          label: "Data Abertura",
          field: "Data",
          sortable: true,
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "NomeCliente",
          label: "Cliente",
          field: "NomeCliente",
          align: "center",
          style: "maxWidth:150px, padding:0px",
        },

        {
          name: "NomeLocalArmazenagem",
          label: "Embarque",
          field: "NomeLocalArmazenagem",
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "NomeClassificador",
          label: "Classificador",
          field: "NomeClassificador",
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "CodProduto",
          label: "Produto",
          field: "CodProduto",
          align: "center",
          style: "maxWidth: 500px",
        },
        {
          name: "TotalClassificado",
          label: "Total Classificado (kg)",
          field: "TotalClassificado",
          align: "center",
          style: "maxWidth: 500px",
        },
        {
          name: "FinalizadoEm",
          label: "Data Fechamento",
          field: "FinalizadoEm",
          sortable: true,
          align: "center",
          style: "maxWidth:50px",
        },
        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
          align: "center",
          style: "maxWidth:50px",
        },
      ],
      //Array com os dados da tabela
      ordensServico: [],
      administrador: false,
      supervisor: false,
      codClienteAlfameta: "",
    };
  },
  methods: {
    carregaTabelaApenasFinalizadas() {
      if (this.administrador == true) {
        getComCabecalho("ordemservicoconab", "Finalizadas", 1)
          .then((result) => {
            this.ordensServico = JSON.parse(result);
            this.statusCarregamentoTabela = false;
          })
          .catch(() => {
            this.statusCarregamentoTabela = false;
            // console.error(err);
          });
      }
    },
    carregaTabela() {
      if (this.administrador == true) {
        getComCabecalho("ordemservicoconab", "administrador", 1)
          .then((result) => {
            this.ordensServico = null;
            this.ordensServico = JSON.parse(result);
            this.statusCarregamentoTabela = false;
          })
          .catch(() => {
            this.statusCarregamentoTabela = false;
            // console.error(err);
          });
      } else {
        getComCabecalho(
          "ordemservicoconab",
          "CodClassificadorSite",
          getCookie("id")
        )
          .then((result) => {
            this.ordensServico = null;
            this.ordensServico = JSON.parse(result);
            this.statusCarregamentoTabela = false;
          })
          .catch(() => {
            // console.error(err);
            this.statusCarregamentoTabela = false;
          });
      }
    },
    visualizarOrdemServicoConab(dadosOs) {
      this.$refs.modalConab.abreModalVisualizacao(dadosOs);
    },
    alterarOrdemServicoConab(dadosOs) {
      this.$refs.modalConab.abreModalAlteracao(dadosOs);
    },
    abrirModalClassificacaoConab(dadosOs) {
      this.$refs.modalClassificacaoConab.abreModal(dadosOs);
    },
    desativarOrdemServicoConab(codOrdemServicoClassificacaoConab) {
      this.$q
        .dialog({
          title: "Desativar OS",
          message: "Deseja realmente desativar a OS selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          requestDeleteComCabecalho(
            `ordemservicoconab?CodOrdemServicoClassificacaoConab=${codOrdemServicoClassificacaoConab}`,
            "CodOrdemServicoClassificacaoConab",
            codOrdemServicoClassificacaoConab
          )
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    finalizarOrdemServicoConab(codOrdemServicoClassificacaoConab) {
      this.$q
        .dialog({
          title: "Finalizar OS",
          message: "Deseja realmente finalizar a OS selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          patchComCabecalho(
            "ordemservicoconab",
            "CodOrdemServicoClassificacaoConab",
            codOrdemServicoClassificacaoConab
          )
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.$emit("atualizaTabelaFinalizada");
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    reabrirOrdemServicoConab(codOrdemServicoClassificacaoConab) {
      this.$q
        .dialog({
          title: "Reabrir OS",
          message: "Deseja realmente reabrir a OS selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          patchComCabecalho(
            "ordemservicoconab",
            "CodOrdemServicoClassificacaoConab",
            codOrdemServicoClassificacaoConab
          )
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabelaApenasFinalizadas();
                this.$emit("atualizaTabelaReaberta");
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    abrirModalCadastroOrdemServicoConab() {
      this.$refs.modalConab.abreModal();
    },
    adicionaMilhar(numero) {
      return Number(numero).toLocaleString("pt-BR");
    },
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    detectaCliente() {
      detectaCliente()
        .then((result) => {
          this.codClienteAlfameta = result.CodCliente;
        })
        .catch(() => {
          // console.error(err);
        });
    },
  },
  mounted() {
    this.detectaCliente();
    this.verificaAdministrador();
    //this.onRequest({pagination :this.serverPagination});
    if (this.$props.visualizaFinalizados == true) {
      this.carregaTabelaApenasFinalizadas();
    } else {
      this.carregaTabela();
    }
  },
};
</script>

<style scoped>
.margin-tabela {
  margin-top: 25px;
}

.centraliza-titulo {
  text-align: center;
}

/*.q-table th, .q-table td {
    padding-left: 3px;
    padding-right: 3px;
    margin: 0px;
}*/
.q-table tbody td {
  height: 15px;
  font-weight: 400;
  font-size: 10px;
}

.q-table th,
.q-table td {
  white-space: nowrap;
  height: 1px;
}

.q-table-dense .q-table th,
.q-table-dense .q-table td {
  padding: 0px 1px;
}

.css-tabela {
  table-layout: fixed;
  width: 100%;
}

.css-tabela tr > *:nth-child(1) {
  width: 65px;
}

.css-tabela tr > *:nth-child(2) {
  width: 120px;
}

.css-tabela tr > *:nth-child(3) {
  width: 200px;
}

.css-tabela tr > *:nth-child(4) {
  width: 220px;
}

.css-tabela tr > *:nth-child(5) {
  width: 130px;
}

.css-tabela tr > *:nth-child(6) {
  width: 150px;
}

.css-tabela tr > *:nth-child(7) {
  width: 115px;
}
.css-tabela tr > *:nth-child(8) {
  width: 115px;
}
.css-tabela tr > *:nth-child(9) {
  width: 115px;
}
.css-tabela tr > *:nth-child(10) {
  width: 115px;
}
.css-tabela tr > *:nth-child(11) {
  width: 115px;
}
.css-tabela tr > *:nth-child(12) {
  width: 115px;
}

.q-layout {
  min-height: auto;
}
tr:nth-child(even) {
  background-color: lightgray;
}
.q-table tbody #totais td {
  font-size: 12px;
