<template>
  <q-layout-header>
    <q-toolbar color="primary">
      <q-toolbar-title>{{nomeClassificadora}}</q-toolbar-title>
      <q-btn @click="drawer = !drawer" flat round icon="menu" />
    </q-toolbar>

    <q-layout-drawer v-model="drawer" side="right" :overlay="true" behavior="mobile">
      <q-list-header class="tamanho-font-menu">Menu</q-list-header>
      <q-item
        v-for="opcao in opcoesMenu"
        :key="opcao.id"
        :inset-separator="true"
        v-show="((opcao.modulo === 'despesa' && clienteAlfameta.ModuloDespesa === true) && opcao.label == 'Despesas') || opcao.label == 'Ordens de servio' || opcao.label == 'Sair' || ((opcao.modulo === 'conab' && clienteAlfameta.ModuloConab === true) && opcao.label == 'Conab') || opcao.show == administrador"
      >
        <q-btn flat @click="opcao.click" dense>
          <q-item-side :icon="opcao.icone" />
          <q-item-main :label="opcao.label" />
        </q-btn>
      </q-item>
    </q-layout-drawer>
  </q-layout-header>
</template>


<script>
import { verificaSeEstaLogado } from "../components/js/VerificaSeEstaLogado.js";
import { deleteCookie } from "../components/js/Cookies.js";
import { verificaAdministrador, verificaSupervisor } from "../components/js/VerificaAdministrador.js";
import { detectaCliente } from "../components/utils/DetectaCliente.js";
export default {
  data() {
    return {
      nome: "",
      codClienteAlfameta: 0,
      administrador: false,
      supervisor: false,
      drawer: true,
      nomeClassificadora: "Alfameta Sistemas",
      clienteAlfameta: {},
      opcoesMenu: [
        {
          icone: "fas fa-users",
          label: "Clientes com acesso",
          click: () => this.abreMenu(3),
          id: 4,
          show: true,
        },
        {
          icone: "mdi-clipboard-text-outline",
          label: "Ordens de servio",
          click: () => this.abreMenu(1),
          id: 1,
        },
        {
          icone: "mdi-clipboard-text-outline",
          label: "Solicitaes",
          click: () => this.abreMenu(5),
          id: 5,
          show: true,
        },
        {
          icone: "mdi-contrast",
          label: "Despesas",
          click: () => this.abreMenu(6),
          id: 6,
          modulo: 'despesa'
        },

        /*{
          icone: "insert_drive_file",
          label: "Layout",
          click: () => this.abreMenu(8),
          id: 8,
          show: true
        },*/

        {
          icone: "account circle",
          label: "Usurios",
          click: () => this.abreMenu(2),
          id: 2,
          show: true,
        },
        {
          icone: "mdi-clipboard-text-outline",
          label: "Conab",
          click: () => this.abreMenu(7),
          modulo: 'conab',
          id: 7
        },
        //{icone:'far fa-question-circle', label:'Ajuda', click:() => this.abreMenu(4), id:5, show:true},
        {
          icone: "mdi-exit-to-app",
          label: "Sair",
          click: () => this.fazLogout(),
          id: 3,
        },
      ],
    };
  },
  methods: {
    fazLogout: function () {
      this.$q
        .dialog({
          title: "Confirmao de saida",
          message: "Deseja realmente sair?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          deleteCookie("tk");
          deleteCookie("id");
          deleteCookie("su");
          deleteCookie("sup");
          deleteCookie("codClassificadorSistema");
          deleteCookie("nome");
          this.$router.push("/");
        });
    },
    verificaSeEstaLogado() {
      verificaSeEstaLogado();
    },
    verificaAdministrador() {
      verificaAdministrador()
        .then((resolve) => {
          this.administrador = resolve;
        })
        .catch((err) => {
          this.administrador = err;
        });
    },
    verificaSupervisor() {
      verificaSupervisor()
          .then((resolve) => {
            this.supervisor = resolve;
          })
          .catch((err) => {
            this.supervisor = err;
          });
    },
    alternaTabs(tabSelecionada) {
      this.$emit("atualizaTabela", tabSelecionada);
    },
    abreMenu(codMenu) {
      switch (codMenu) {
        case 1:
          this.$router.push("/os");
          break;
        case 2:
          this.$router.push("/classificadores");
          break;
        case 3:
          this.$router.push("/clientes");
          break;
        case 4:
          this.$router.push("/ajuda");
          break;
        case 5:
          this.$router.push("/solicitacoes");
          break;
        case 6:
          this.$router.push("/despesa");
          break;
        case 7:
          this.$router.push("/conab");
          break;
      }
    },
    selecionaCliente() {
      detectaCliente()
        .then((result) => {
          this.clienteAlfameta = result
          this.nomeClassificadora = this.clienteAlfameta.Nome;
        })
        .catch(() => {
          this.nomeClassificadora = 'Alfameta Sistemas (Ambiente de Testes)';
        });
    },
  },
  beforeMount() {
    this.selecionaCliente();
    this.verificaSeEstaLogado();
    this.verificaAdministrador();
    this.verificaSupervisor();
  },
};
</script>


<style scoped>
.tamanho-font-menu {
  font-size: 22px;
}
.altura-btn {
  height: 100%;
}
.centralizado {
  justify-content: center;
}
.logo-responsiva {
  width: 190px;
  height: auto;
}
.bg {
  background-color: blue;
}
</style>
