<template> 
  <q-layout>
    <div class="responsivo">
      <div class="centraliza-titulo margin-titulo">Clientes com acesso</div>
      <div>
        <q-table
          :data="clientes"
          :columns="colunasTabela"
          row-key="CodOrdemServicoClassificacao"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          :rows-per-page-options="opcoesLinhasPorPagina"
          :selected="linhaSelecionada"
          :pagination.sync="padraoLinhasPorPagina"
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>

          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td
                key="CodClienteSistema"
                :props="props"
                class="tamanho-coluna-id"
              >{{ props.row.CodClienteSistema }}</q-td>
              <q-td key="Nome" :props="props">{{ props.row.Nome }}</q-td>
              <q-td key="CNPJ" :props="props">{{ props.row.CNPJ }}</q-td>
              <q-td key="Ativo" :props="props" v-if="props.row.Ativo == 'S'">Ativo</q-td>
              <q-td key="Ativo" :props="props" v-else>Desativado</q-td>
              <q-td key="acoes" :props="props" style="width:15px">
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  v-show="administrador"
                  @click="alteraAcessoCliente(props.row)"
                >
                  <q-tooltip>Editar acesso</q-tooltip>
                </q-btn>
                <q-btn
                  icon="lock"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-if="props.row.Ativo == 'S'"
                  @click="desativaAcesso(props.row.id)"
                >
                  <q-tooltip>Desativar acesso</q-tooltip>
                </q-btn>
                <q-btn
                  icon="lock_open"
                  :props="props"
                  flat
                  dense
                  color="positive"
                  v-else
                  @click="reativaAcesso(props.row.id)"
                >
                  <q-tooltip>Reativar acesso</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizaAcesso(props.row)"
                >
                  <q-tooltip>Visualizar acesso</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
          <template slot="top-right">
            <div class="offset-6 col-12">
              <q-btn
                color="primary"
                icon="fas fa-user-plus"
                label="Adicionar novo acesso"
                @click="abreModalNovoAcesso()"
                v-show="administrador"
              />
            </div>
          </template>
        </q-table>
      </div>
    </div>
    <ModalAcessoCliente ref="modalAcessoCliente" @atualizaTabela="carregaTabela" />
  </q-layout>
</template>


<script>
import { get, getComCabecalho } from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
import ModalAcessoCliente from "@/components/ModalAcessoCliente";
export default {
  name: "gradeJustificativas",
  components: {
    ModalAcessoCliente,
  },
  data() {
    return {
      padraoLinhasPorPagina: { rowsPerPage: 10 },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      exibe: true,
      abreModal: false,
      termoBusca: "",
      usuarios: [],
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "CodClienteSistema",
          label: "Cod. Cliente",
          field: "CodClienteSistema",
          sortable: true,
          align: "center",
          style: "width: 75px",
        },
        {
          name: "Nome",
          label: "Nome",
          sortable: true,
          field: "Nome",
          align: "left",
        },
        {
          name: "CNPJ",
          label: "CNPJ",
          field: "CNPJ",
          align: "left",
          sortable: true,
        },
        {
          name: "Ativo",
          label: "Status",
          field: "Ativo",
          sortable: true,
          align: "center",
        },
        { name: "acoes", label: "Aes", field: "acoes" },
      ],
      //Array com os dados da tabela
      clientes: [],
      linhaSelecionada: [],
      administrador: true,
      supervisor: false,
      codOrdemServicoClassificacao: "",
    };
  },
  methods: {
    carregaTabela() {
      get("listaclientesacesso")
        .then((result) => {
          this.clientes = JSON.parse(result);
          this.statusCarregamentoTabela = false;
        })
        .catch(() => {
          this.statusCarregamentoTabela = false;
        });
    },
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    abreModalNovoAcesso() {
      this.$refs.modalAcessoCliente.abreModal();
    },
    fechaTabela() {
      this.exibe = false;
    },
    visualizaAcesso(dadosAcessoCliente) {
      this.$refs.modalAcessoCliente.abreModalVisualizacao(dadosAcessoCliente);
    },
    desativaAcesso(idAcesso) {
      this.$q
        .dialog({
          title: "Desativar acesso",
          message:
            "Deseja realmente desativar o acesso ao cliente selecionado?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho("desativaacesso", "id", idAcesso)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    reativaAcesso(idAcesso) {
      this.$q
        .dialog({
          title: "Reativar acesso",
          message: "Deseja realmente reativar o acesso ao cliente selecionado?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho("reativaacesso", "id", idAcesso)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    alteraAcessoCliente(dadosAcessoCliente) {
      this.$refs.modalAcessoCliente.abreModalAlteracao(dadosAcessoCliente);
    },
  },
  mounted() {
    this.carregaTabela();
  },
};
</script>


<style scoped>
.q-layout {
  min-height: auto;
}
.centraliza-titulo {
  text-align: center;
}
.margin-titulo {
  margin-top: 10px;
  margin-bottom: 10px;
}
</style>
