export function tipoProduto(codTipoProduto) {
    let produto = ''
    switch(codTipoProduto){
        case 1:
            produto =  'Convencional'
            break
        case 2:
            produto = 'Transgnico'
            break
        case 3:
            produto = 'Testado Positivo'
            break
        case 4:
            produto = 'Testado Negativo'
            break
        case 5:
            produto = 'No Testado'
            break
        case 7:
            produto = 'Intacta Declarado'
            break
        case 8:
            produto = 'Participante'
            break
        case 9:
            produto = 'Testada Positiva'
            break
        case 10:
            produto = 'Testada Negativa'
            break
        case 11:
            produto = 'No Testado'
            break
        case 12:
            produto = 'Convencional'
            break
    }

    return produto;
}

export function OGMTeste(codTesteOGM) {
    let teste = ''

    switch (codTesteOGM) {
        case 1:
            teste = 'NO TESTADO' 
            break
        case 2:
            teste = 'POSITIVO' 
            break
        case 3:
            teste = 'NEGATIVO' 
            break
    }

    return teste
}