export function getComVariosCabecalhos(rota, cabecalhos) {
  return new Promise(async(resolve, reject) => {
    if(porta == null || porta == ""){
      await setaPortaCorreta();
    }
    const request = new XMLHttpRequest();
    const token = getCookie("tkCliente");
    if (protocolo == "http:") {
      request.open("GET", url + ":" + porta + "/api/cliente/" + rota, true);
    } else {
      request.open("GET", url + "/api/public/api/cliente/" + rota, true);
    }
    request.setRequestHeader("Authorization", "bearer " + token);
    for(let [key, value] of cabecalhos){
      request.setRequestHeader(key, value);
    }
    try {
      request.send();
      request.onreadystatechange = function() {
        if (request.readyState == request.DONE) {
          if (request.status == 200) {
            resolve(request.response);
          }
          reject(request.response);
        }
      };
    } catch (err) {
      reject(err);
    }
  });
}

export function patchCliente(rota, objeto) {
  return new Promise(async (resolve, reject) => {
    if (porta == null || porta == "") {
      await setaPortaCorreta();
    }

    const request = new XMLHttpRequest();
    const json = JSON.stringify(objeto);
    const token = getCookie("tkCliente");

    if (protocolo == "http:") {
      request.open("PATCH", url + ":" + porta + "/api/cliente/" + rota, true);
    } else {
      request.open("PATCH", url + "/api/public/api/cliente/" + rota, true);
    }

    request.setRequestHeader("Content-Type", "Application/json");
    request.setRequestHeader("Authorization", "bearer " + token);
    try {
      request.send(json);
      request.onreadystatechange = function () {
        if (request.readyState == request.DONE) {
          if (request.status == 200) {
            resolve(request.response);
          }
          reject(request.response);
        }
      };
    }
    catch (err) {
      reject(err);
    }
  });
}
export function postFormDataComCabecalhosCliente(rota, formData, cabecalhos) {
  return new Promise(async (resolve, reject) => {
    if (porta == null || porta == "") {
      await setaPortaCorreta();
    }
    const request = new XMLHttpRequest();
    const token = getCookie("tkCliente");
    
    if (protocolo == "http:") {
      request.open("POST", url + ":" + porta + "/api/cliente/" + rota, true);
    } else {
      request.open("POST", url + "/api/public/api/cliente/" + rota, true);
    }
    for(let [key, value] of cabecalhos){
      request.setRequestHeader(key, value);
    }
    //request.setRequestHeader("Content-Type", "multipart/form-data");
    request.setRequestHeader("Authorization", "bearer " + token);

    try {
      request.send(formData);
      request.onreadystatechange = function () {
        if (request.readyState == request.DONE) {
          if (request.status == 200) {
            resolve(request.response);
          } else {
            reject(request.response);
          }
        }
      };
    }
    catch (err) {
      reject(err);
    }
  });
}