<template>
  <q-layout>
    <div class="col-12">
      <div class="row flex-center">
        <q-table
          :data="dados"
          separator="cell"
          :selected="linhaSelecionada"
          :rows-per-page-options="opcoesLinhasPorPagina"
          :filter="termoBusca"
          class="col-12"
          :columns="colunas"
          row-key="ID"
          table-class="css-tabela"
          :loading="statusCarregamento"
          :pagination.sync="pagination"
          @request="onRequest"
        >
          <template slot="top-left">
            <q-search color="primary" class="col-6" v-model="termoBusca" :debounce="400" />
          </template>

          <template slot="top-right">
            <q-btn label="Lanar nova Despesa" color="primary" @click="incluirNovaDespesa" right />
          </template>
          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td key="ID" :props="props">{{props.row.CodMovContaFinanceiraClassificador}}</q-td>
              <q-td key="Status" :props="props" class="alinha-indicador-centro">
                <div
                  :class="props.row.Status === 'A' ? 'indicador-status aceita' : props.row.Status === 'R' ? 'indicador-status rejeitada' : 'indicador-status aguardando'"
                >
                  <q-tooltip
                    transition-show="flip-right"
                    transition-hide="flip-left"
                  >{{props.row.CodMovContaFinanceiraClassificador}} - {{props.row.Status === 'A' ? 'Despesa aceita' : props.row.Status === 'R' ? 'Despesa rejeitada' : 'Aguardando resposta'}}</q-tooltip>
                </div>
              </q-td>
              <q-td key="TipoDespesa" :props="props">{{props.row.TipoDespesa}}</q-td>
              <q-td key="NomeClassificador" :props="props">{{props.row.NomeClassificador}}</q-td>
              <q-td key="Historico" :props="props">{{props.row.Historico}}</q-td>

              <q-td
                key="Data"
                :props="props"
              >{{ new Date(props.row.DataLancamento).toLocaleDateString('pt-BR') }}</q-td>
              <q-td
                key="ValorDespesa"
                :props="props"
              >{{Number(props.row.Valor).toLocaleString('pt-BR',{minimumFractionDigits: 2, style: 'currency', currency: "BRL"})}}</q-td>

              <q-td key="acoes" :props="props">
                <q-btn
                  icon="remove_red_eye"
                  @click="visualizarDespesa(props.row)"
                  dense
                  color="blue-6"
                  :props="props"
                  size="10px"
                  class="q-mr-sm"
                >
                  <q-tooltip
                    transition-show="flip-right"
                    transition-hide="flip-left"
                  >Visualizar Despesa - {{props.row.CodMovContaFinanceiraClassificador}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="check_circle"
                  @click="aceitarDespesa(props.row.CodMovContaFinanceiraClassificador)"
                  dense
                  color="green"
                  :props="props"
                  size="10px"
                  class="q-mr-sm"
                  v-if="props.row.Status === null || props.row.Status === 'E'"
                >
                  <q-tooltip
                    transition-show="flip-right"
                    transition-hide="flip-left"
                  >Aceitar Despesa - {{props.row.CodMovContaFinanceiraClassificador}}</q-tooltip>
                </q-btn>
                <q-btn
                  icon="clear"
                  dense
                  color="red"
                  :props="props"
                  size="10px"
                  class="q-mr-sm"
                  @click="rejeitaDespesa(props.row.CodMovContaFinanceiraClassificador)"
                  v-if="props.row.Status === null || props.row.Status === 'E'"
                >
                  <q-tooltip
                    transition-show="flip-right"
                    transition-hide="flip-left"
                  >Rejeitar Despesa - {{props.row.CodMovContaFinanceiraClassificador}}</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
        </q-table>
      </div>
    </div>
    <ModalDespesa ref="modalVisualizaDespesa" @atualizaTabela="carregaDados" />
  </q-layout>
</template>

<script>
import ModalDespesa from "@/components/ModalDespesa.vue";
import { get, post } from "../components/js/ConexaoApi.js";

export default {
  name: "TabelaDespesas",
  components: {
    ModalDespesa,
  },
  data() {
    return {
      pagination: {
        page: 1,
        rowsNumber: 10, // specifying this determines pagination is server-side
        rowsPerPage: 5,
      },
      statusCarregamento: false,
      colunas: [
        {
          name: "ID",
          align: "center",
          label: "ID",
          field: "CodMovContaFinanceiraClassificador",
          sortable: false,
        },
        {
          name: "Status",
          align: "center",
          label: "Status",
          headerClasses: "bg-grey-4",
          field: "Status",
          sortable: false,
        },
        {
          name: "TipoDespesa",
          align: "center",
          label: "Despesa",
          field: "TipoDespesa",
          sortable: false,
          style: "text-weight: bold",
        },
        {
          name: "NomeClassificador",
          align: "center",
          label: "Nome do Classificador",
          field: "NomeClassificador",
          sortable: false,
        },
        {
          name: "Historico",
          align: "left",
          label: "Historico",
          field: "Historico",
          sortable: false,
        },
        {
          name: "Data",
          align: "center",
          label: "Data",
          field: "DataLancamento",
          sortable: false,
        },
        {
          name: "ValorDespesa",
          align: "center",
          label: "Valor",
          field: "Valor",
          sortable: false,
        },

        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
          align: "center",
          sortable: false,
        },
      ],
      dados: [],
      linhaSelecionada: [],
      termoBusca: "",
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
    };
  },
  methods: {
    motivoRecusa(despesa) {
      if (despesa.Status == "R") {
        this.$q.notify({
          message: "Essa despesa j foi rejeitada!",
          color: "red",
        });
      } else {
        this.$refs.modalMotivoRecusa.rejeitarDespesa(despesa);
      }
    },
    visualizarDespesa(despesa) {
      this.$refs.modalVisualizaDespesa.visualizaDespesa(despesa);
    },
    onRequest({ pagination, filter }) {
      this.statusCarregamento = true;
      get(`despesas?filter=${filter}&onlyRowCount=true`).then((result) => {
        this.pagination = pagination;
        this.pagination.rowsNumber = JSON.parse(result).rowCount;
      });
      get(
        `despesas?filter=${filter}&pageIndex=${pagination.page}&pageSize=${pagination.rowsPerPage}`
      ).then((result) => {
        this.dados = JSON.parse(result);
      });
      this.statusCarregamento = false;
    },
    rejeitaDespesa(codMovContaFinanceiraClassificador) {
      this.$q
        .dialog({
          title: "Recusar despesa",
          message: "Informe o motivo da recusa",
          prompt: {
            model: "",
            type: "text", // optional
          },
          cancel: true,
          preventClose: true,
          color: "secondary",
        })
        .then((data) => {
          const movContaFinanceiraClassificador = {};
          movContaFinanceiraClassificador.CodMovContaFinanceiraClassificador = codMovContaFinanceiraClassificador;
          movContaFinanceiraClassificador.Status = "R";
          movContaFinanceiraClassificador.MotivoRecusa = data;
          if (data.length === 0) {
            this.$q
              .dialog({
                title: "Aviso",
                message: `Por favor, informe um motivo.`,
              })
              .catch(() => {});
          } else {
            post("aceitarejeitadespesa", movContaFinanceiraClassificador)
              .then((result) => {
                const resposta = JSON.parse(result);
                this.$q.notify({
                  type: "positive",
                  message: resposta.msg,
                });
                this.carregaDados();
              })
              .catch(() => {
                this.$q.notify({
                  type: "negative",
                  message:
                    "Falha ao trasnferir classificao, por favor, contate o suporte",
                });
              });
          }
        })
        .catch(() => {});
    },
    aceitarDespesa(codMovContaFinanceiraClassificador) {
      this.$q
        .dialog({
          title: "Ateno",
          message: "Voc deseja aceitar esta despesa ?",
          ok: true,
          cancel: true,
          preventClose: true,
        })
        .then(() => {
          const MovContaFinanceiraClassificador = {};
          MovContaFinanceiraClassificador.CodMovContaFinanceiraClassificador = codMovContaFinanceiraClassificador;
          MovContaFinanceiraClassificador.Status = "A";
          MovContaFinanceiraClassificador.MotivoRecusa = "";
          post("aceitarejeitadespesa", MovContaFinanceiraClassificador)
            .then(() => {
              this.$q.notify({
                message: "Despesa Aceita com Sucesso!",
                color: "green",
              });
              this.carregaDados();
            })
            .catch((err) => {
              this.$q.notify({
                message: "Despesa no aceita",
                color: "red",
              });
              // eslint-disable-next-line no-console
              console.error(err);
            });
        })
        .catch(() => {});
    },
    incluirNovaDespesa(despesa) {
      this.$refs.modalVisualizaDespesa.insereNovaDespesa(despesa);
    },
    carregaDados() {
      this.onRequest({ pagination: this.pagination, filter: this.termoBusca });
    },
  },
  created() {
    this.carregaDados();
  },
};
</script>

<style scoped>
td {
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.alinha-indicador-centro {
  justify-content: center;
  align-content: center;
}
.indicador-status {
  margin: auto;
  width: 15px;
  height: 15px;
  border-radius: 5px;
  box-shadow: 2px 2px 2px rgba(112, 112, 112, 0.77)
  
}
.aguardando {
  background-color: rgba(241, 211, 2, 1);
}
.rejeitada {
  background-color: rgba(193, 41, 46, 1);
}
.aceita {
  background-color: rgba(77, 139, 49, 1)
}

.css-tabela {
  table-layout: fixed;
  width: 100%;
}

.desativaHover :hover {
  background-color: none;
}

.css-tabela tr > *:nth-child(1) {
  width: 50px;
}

.css-tabela tr > *:nth-child(2) {
  width: 50px;
}

.css-tabela tr > *:nth-child(3) {
  width: 100px;
}

.css-tabela tr > *:nth-child(4) {
  width: 200px;
}

.css-tabela tr > *:nth-child(5) {
  width: 600px;
}

.css-tabela tr > *:nth-child(6) {
  width: 100px;
}

.css-tabela tr > *:nth-child(7) {
  width: 40px;
}
.css-tabela tr > *:nth-child(8) {
  width: 40px;
}
</style>