<script>
import BoxContainer from "../widgets/BoxContainer.vue";
import { getCookie } from "../components/js/Cookies.js";
import { getComCabecalho } from './js/ConexaoApi';
export default {
  components: {
    BoxContainer,
  },
  data() {
    return {
      /* Variaveis com dado dos clientes */
      nomeCliente: "CONAB - COMPAINHA NACIONAL DE ABASTECIMENTO",
      cnpjCliente: "26.461.699/0369-67",
      inscricaoEstadual: "13.185.102-0",
      enderecoCliente: "RUA PADRE JERONIMO BOTELHO 510 - DOM AQUINO",
      cidadeCliente: "CUIAB",
      cepCliente: "78.015-240",
      /* FIM Variaveis com dado dos clientes */
      /* Variaveis com dado do produto */
      descriminacao: "MILHO EM GROS",
      marcaProduto: "",
      pesoLiquidoProduto: "",
      numeroVolumesProduto: "",
      acondicionamentoProduto: "",
      safraProduto: "",
      /* FIM Variaveis com dado do produto */
      /* Variaveis com dado do Armazem */
      nomeArmazem: "",
      cnpjArmazem: "",
      enderecoArmazem: "",
      cidadeArmazem: "",
      cdaOrigem: "",
      /* FIM Variaveis com dado do Armazem */
      /* Variaveis com dado de Amostra de Produto */
      responsavelColeta: "",
      numeroAmostra: "",
      numeroLaudo: "",
      numeroBloco: "",
      naturezaOperacao: "",
      numeroNotaFiscal: "",
      placaCavalo: "",
      regulamentoTecnico: "INSTRUO NORMATIVA N 60 DE 22 DE DEZEMBRO DE 2011 ALT. IN MAPA 18 DO 04/07/2012",
      tempoAtendimento: "",
      /* FIM Variaveis com dado de Amostra de Produto */
      /* Variaveis com dado de Classificao do produto */
      destino: "",
      cdaDestino: "",
      grupo: "",
      classe: "",
      tipo: "",
      atr: "",
      lote: "",
      aparelho: "",
      umidade: "",
      impureza: "",
      impurezaGramas: "",
      quebrados: "",
      quebradoGramas: "",
      carunchados: "",
      carunchadoGramas: "",
      sadios: "",
      sadiosGramas: "",
      ardidos: "",
      ardidosGrama: "",
      mofados: "",
      mofadosGramas: "",
      fermentados: "",
      fermentadosGramas: "",
      germinados: "",
      germinadosGramas: "",
      gessados: "",
      gessadosGramas: "",
      chochos: "",
      chochosGramas: "",
      total: "",
      pesoAmostra: Number(0),
      /* FIM Variaveis com dado de Amostra de Produto */
      /* Variaveis com dado de Classificao do produto */
      observacao: "",
      nomeClassificador: "",
      mapa: "",
      localDataEmissao: "",
      /* FIM Variaveis com dado de Classificao do produto */
      logo: require("../assets/CabecalhoForm1.jpg"),
      assinaturaCarimbo: "",
      classificacaoSalva: false,
      readonly: false,
      camposFaltando: "",
      formularioValido: false,
      certificadoHomogeneizacao: 'N',
      placas: '',
      cssErroUmidade: "padding-input label-negrito tamanho-fonte-valor",
      cssErroQuebrados: "padding-input label-negrito tamanho-fonte-valor",
      cssErroUmidadeLabel:
        "bordas-tabela td-tabela-conab padding-label-classificacao label-negrito tamanho-fonte-label obrigatorio",
      cssErroQuebradosLabel:
        "bordas-tabela td-tabela-conab padding-label-classificacao label-negrito tamanho-fonte-label obrigatorio",
    };
  },
  methods: {
    imprimirPagina() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = true;
      }
      setTimeout(() => {
        window.print();
        if (this.$q.platform.is.mobile) {
          setTimeout(() => {
            this.exibeLayout = false;
          }, 1000);
        }
      }, 1500);
    },
    carregaDadosClassificacao() {
      let numeroLaudo = this.$route.params.u;
      let codOsConab = 0;
      if (
        numeroLaudo !== "" &&
        numeroLaudo !== null &&
        numeroLaudo !== undefined
      ) {
        this.classificacaoSalva = true;
        this.readonly = true;

        getComCabecalho("carregadadosqrcodeconab", "numerolaudo", numeroLaudo)
          .then((result) => {
            const Resposta = JSON.parse(result);
            const classificacao = Resposta[0];
            // console.log(Resposta);
            codOsConab = classificacao.CodOrdemServicoClassificacaoConab;
            this.numeroLaudo = classificacao.NumeroLaudo;
            this.nomeArmazem = classificacao.ArmazemOrigem;
            this.descriminacao = "MILHO EM GROS";
            this.certificadoHomogeneizacao = classificacao.CertificadoHomogeneizacao;
            this.marcaProduto = classificacao.Marca;
            this.pesoLiquidoProduto = Number(classificacao.PesoLiquido).toLocaleString("pt-BR");
            this.numeroVolumesProduto = Number(classificacao.NumeroVolumes).toLocaleString("pt-BR");
            this.acondicionamentoProduto = classificacao.Acondicionamento;
            this.safraProduto = classificacao.Safra;
            this.nomeArmazem = classificacao.ArmazemOrigem;
            this.cnpjArmazem = classificacao.CNPJOrigem;
            this.enderecoArmazem = classificacao.EnderecoOrigem;
            this.cidadeArmazem = classificacao.NomeMunicipioOrigem + "/" + classificacao.UFOrigem;
            this.cdaOrigem = classificacao.CDAOrigem;
            this.responsavelColeta = classificacao.ResponsavelColeta;
            this.numeroAmostra = classificacao.NumeroAmostra;
            this.numeroLaudo = classificacao.NumeroLaudo;
            this.numeroBloco = classificacao.NumeroBloco;
            this.naturezaOperacao = classificacao.NaturezaOperacao;
            this.numeroNotaFiscal = classificacao.NumNF;
            this.placaCavalo = classificacao.PlacaCavalo;
            this.tempoAtendimento = classificacao.TempoAtendimento;
            this.destino = classificacao.Destino;
            this.cdaDestino = classificacao.CDADestino;
            this.destino = classificacao.MunicipioDestino + "/" + classificacao.UFDestino;
            this.grupo = classificacao.NomeGrupoConab;
            this.classe = classificacao.NomeClasseConab;
            this.tipo = classificacao.Tipo;
            this.atr = classificacao.ATR;
            this.lote = classificacao.NumeroLote;
            this.aparelho = classificacao.Aparelho;
            this.pesoAmostra = classificacao.PesoAmostra;
            this.umidade = classificacao.Umidade;
            this.impureza = classificacao.Impureza;
            this.impurezaGramas = classificacao.ImpurezaGramas;
            this.quebrados = classificacao.Quebrados;
            this.quebradoGramas = classificacao.QuebradosGramas;
            this.carunchados = classificacao.Carunchados;
            this.carunchadoGramas = classificacao.CarunchadosGramas;
            this.sadios = classificacao.Sadios;
            this.sadiosGramas = classificacao.SadiosGramas;
            this.ardidos = classificacao.Ardidos;
            this.ardidosGrama = classificacao.ArdidosGramas;
            this.mofados = classificacao.Mofados;
            this.mofadosGramas = classificacao.MofadosGramas;
            this.fermentados = classificacao.Fermentados;
            this.fermentadosGramas = classificacao.FermentadosGramas;
            this.germinados = classificacao.Germinados;
            this.germinadosGramas = classificacao.GerminadosGramas;
            this.gessados = classificacao.Gessados;
            this.gessadosGramas = classificacao.GessadosGramas;
            this.chochos = classificacao.Chochos;
            this.chochosGramas = classificacao.ChochosGramas;
            this.total = classificacao.Total;
            this.observacao = classificacao.Observacao;

            if(this.certificadoHomogeneizacao == 'S'){
              const placas = classificacao.PlacasCertificado;
              for(let i = 0; i < placas.length; i++ ){
                this.placas += `${placas[i].PlacaCavalo} - `;

              }
              this.placas = this.placas.slice(0, -2);
            }

            this.nomeClassificador = classificacao.NomeClassificador;
            this.mapa = classificacao.Mapa;
            this.localDataEmissao = classificacao.LocalDataClassificacao;

            getComCabecalho("ordemservicoconab", "codOrdemServicoClassificacao", codOsConab)
                .then((result) => {
                  const Resposta = JSON.parse(result);
                  const classificacao = Resposta[0];
                  this.nomeCliente = classificacao.NomeCliente;

                  if(classificacao.NomeCliente.trim() == 'H. A. PIMENTA E CIA LTDA'){
                    this.cnpjCliente = "14.732.164/0001-18";
                    this.inscricaoEstadual = "13.441.470-0";
                    this.enderecoCliente = "ROD. MT100 - ZONA RURAL";
                    this.cidadeCliente = "ALTO ARAGUAIA";
                    this.cepCliente = "78.780-000";
                  }
                }).catch((err) => {
              // eslint-disable-next-line no-console
              console.error(err);
            });

          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            console.error(err);
          });
      } else {
        this.nomeClassificador = getCookie("nome");
        this.responsavelColeta = this.nomeClassificador;
        this.classificacaoSalva = false;
        this.readonly = false;
      }
    }
  },
  created() {
    this.carregaDadosClassificacao();
  },
};
</script>

