<template>
  <q-modal
    v-model="exibe"
    minimized
    :content-css="{width: '100vw', minWidth:'95vw', minHeight: '75vh'}"
    no-backdrop-dismis
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn
          style="padding:0px; margin:0"
          flat
          round
          dense
          icon="close"
          @click="confirmarCancelamentoDeCadastroDeOs"
        />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>

      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{tituloModal}}</h6>
      </q-toolbar>

      <div class="layout-padding">
        <div class="row">
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroCliente" error-label="Por favor, informe o cliente">
              <q-search
                v-model="termoBuscaCliente"
                clearable
                placeholder="Escolha um cliente"
                float-label="Escolha um cliente"
                :disable="modoVisualizacao == true"
                :error="erroCliente"
                @focus="todosCamposPreenchidos"
              >
                <q-autocomplete
                  @search="buscaCliente"
                  @selected="clienteSelecionado"
                  :max-results="15"
                />
              </q-search>
            </q-field>
          </div>

          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroDestino" error-label="Por favor, informe o destino">
              <q-search
                v-model="termoBuscaDestino"
                placeholder="Escolha o destino"
                float-label="Escolha o destino"
                :disable="modoVisualizacao == true"
                clearable
                @focus="todosCamposPreenchidos"
              >
                <q-autocomplete
                  @search="buscaDestino"
                  @selected="destinoSelecionado"
                  :max-results="15"
                />
              </q-search>
            </q-field>
          </div>

          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroClassificador" error-label="Por favor, informe um classificador">
              <q-input
                v-model="termoBuscaClassificador"
                placeholder="Escolha um classificador"
                float-label="Escolha um classificador"
                :disable="modoVisualizacao == true"
                clearable
                @focus="todosCamposPreenchidos"
              >
                <q-autocomplete
                  :static-data="{field:'label', list:classificadoresAtivo}"
                  @selected="classificadorSelecionado"
                  :max-results="15"
                />
              </q-input>
            </q-field>
          </div>

          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroProduto"
              error-label="Por favor, informe o produto a ser classificado"
            >
              <q-select
                filled
                v-model="produtoSelecionado"
                float-label="Produto"
                filter
                clearable
                :options="produtos"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>

          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="contrato"
                placeholder="Contrato"
                float-label="Contrato"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>

          <div class="col-md-3 col-sm-6 col-xs-6 margin-form">
            <q-field :error="erroPesoLiquido || erroVirgulaPesoLiquido" :error-label ="erroPesoLiquido ? 'Por favor, informe o peso lquido' : 'Por favor, informe o peso lquido sem vrgula'">
              <q-input
                v-model="pesoLiquido"
                placeholder="Peso Lquido(kg)"
                float-label="Peso Lquido(kg)"
                onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                type="number"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
                @blur="arredondaPesoLiquido()"
                @keyup="CalculaDiasEmbarque(); converteKgParaTon(false)"
              />
            </q-field>
          </div>

          <div class="col-md-3 col-sm-6 col-xs-6 margin-form">
            <q-field :error="erroPesoLiquido" error-label="Por favor, informe o peso lquido" >
              <q-input
                v-model="toneladas"
                placeholder="Peso Lquido(Tons)"
                float-label="Peso Lquido(Tons)"
                onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                type="number"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
                @blur="arredondaPesoLiquido()"
                @keyup="CalculaDiasEmbarque(); converteTonParaKg(false)"
              />
            </q-field>
          </div>

          <div
            class="col-md-6 col-sm-12 col-xs-12 margin-form"
            v-if="clienteAlfameta.CodCliente == 1 || clienteAlfameta.CodCliente == 2"
          >
            <q-field>
              <q-select
                filled
                v-model="cadenciaSelecionada"
                float-label="Possui cadencia"
                :options="opcoesCadencia"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>

          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-else>
            <q-field>
              <q-select
                filled
                v-model="cadenciaSelecionada"
                float-label="Possui cadencia"
                :options="opcoesCadenciaPositivagro"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>

          <div class="col-md-3 col-sm-6 col-xs-6 margin-form">
            <q-field :error="erroPesoCadencia" error-label="Por favor, informe a cadncia">
              <q-input
                v-model="pesoCadencia"
                onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                placeholder="Peso Cadncia (kg)"
                float-label="Peso Cadncia (kg)"
                clear-value
                type="number"
                :disable="cadenciaSelecionada == 'N' || modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
                @keyup="CalculaDiasEmbarque()"
              />
            </q-field>
          </div>

          <div class="col-md-3 col-sm-6 col-xs-6 margin-form">
            <q-field :error="erroPesoCadencia" error-label="Por favor, informe a cadncia">
              <q-input
                v-model="toneladasCadencia"
                onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                placeholder="Peso Cadncia (Tons)"
                float-label="Peso Cadncia (Tons)"
                clear-value
                type="number"
                :disable="cadenciaSelecionada == 'N' || modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
                @keyup="CalculaDiasEmbarque()"
              />
            </q-field>
          </div>

          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-select
                filled
                v-model="statusSelecionado"
                float-label="Status"
                :options="opcoesStatus"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>

          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroLocalEmbarque"
              error-label="Por favor, informe o local de embarque"
            >
              <q-search
                v-model="termoBuscaEmbarque"
                placeholder="Escolha o local de embarque"
                float-label="Escolha o local de embarque"
                :disable="modoVisualizacao == true"
                clearable
                @focus="todosCamposPreenchidos"
              >
                <q-autocomplete
                  @search="buscaLocalEmbarque"
                  @selected="embarqueSelecionado"
                  :max-results="15"
                />
              </q-search>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="filial"
                placeholder="Filial"
                float-label="Filial"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-if="administrador">
            <q-input
              v-model="valorServico"
              placeholder="Valor Servio / Ton"
              float-label="Valor Servio / Ton"
              :disable="modoVisualizacao == true"
              @focus="todosCamposPreenchidos"
            />
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-if="administrador">
            <q-input
              v-model="outrosValores"
              placeholder="Outros Valores / Ton"
              float-label="Outros Valores / Ton"
              :disable="modoVisualizacao == true"
              @focus="todosCamposPreenchidos"
            />
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-input
                v-model="lote"
                placeholder="Lote"
                float-label="Lote"
                :disable="modoVisualizacao == true"
                @focus="todosCamposPreenchidos"
            />
          </div>



          <div v-if="modoVisualizacao == false && (clienteAlfameta.CodCliente == 3 || clienteAlfameta.CodCliente == 0)" class="row col-12 margin-form">
            <div :disable="modoVisualizacao == true">
              <q-checkbox
                v-model="suprimeCabecalho"
                label="No exibir dados da classificadora no laudo de classificao"
              />
            </div>
          </div>

          <div v-if="clienteAlfameta.CodCliente == 3" class="row col-12 margin-form">
            <div class="q-mt-xs">
              <q-checkbox
                :disable="modoVisualizacao == true"
                v-model="despesaLiberada"
                label="Liberar Despesas nesta O.S"
              />
            </div>
          </div>



          <div
            v-if="alteraInsere != 1 && modoVisualizacao == false && (clienteAlfameta.CodCliente === 3 || clienteAlfameta.CodCliente === 0)"
            class="row col-12 margin-form"
          >
            <div :disable="modoVisualizacao == true">
              <q-checkbox
                v-model="transportadorasPredefinidas"
                label="Transportadoras Predefinidas"
              />
            </div>
            <div v-show="transportadorasPredefinidas" class="row col-12 caixa-multiplos-destinos">
              <q-field
                :error="erroDestino"
                error-label="Por favor, informe o destino"
                class="col-md-4"
              >
                <q-search
                  v-model="termoBuscaTransportadora"
                  placeholder="Escolha a Transportadora"
                  float-label="Escolha a Transportadora"
                  :disable="modoVisualizacao == true"
                  @focus="todosCamposPreenchidos"
                >
                  <q-autocomplete
                    @selected="transportadaSelecionada"
                    @search="buscaTransportadora"
                  />
                </q-search>
              </q-field>
              <div class="col-md-4 destinos-selecionados">
                Transportadoras selecionadas:
                <div
                  v-for="(transportadora, index) in transportadorasSelecionadas"
                  :key="index"
                  class="row"
                >
                  <div class="col-8">{{transportadora.Nome}}</div>
                  <div class="col-4">
                    <q-btn
                      icon="clear"
                      dense
                      color="negative"
                      @click="removeTransportadora(index, transportadora);"
                      size="10px"
                      class="q-mr-sm"
                    >
                      <q-tooltip>Remover {{transportadora.Nome}} da lista</q-tooltip>
                    </q-btn>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-if="alteraInsere != 1 && modoVisualizacao == false" class="row col-12 margin-form">
            <div :disable="modoVisualizacao == true">
              <q-checkbox v-model="multiplosDestinos" label="Multiplos destinos" />
            </div>
            <div v-show="multiplosDestinos" class="row col-12 caixa-multiplos-destinos">
              <q-field
                :error="erroDestino"
                error-label="Por favor, informe o destino"
                class="col-md-4"
              >
                <q-search
                  v-model="termoBuscaDestinoMultiplo"
                  placeholder="Escolha o destino"
                  float-label="Escolha o destino"
                  :disable="modoVisualizacao == true"
                  @focus="todosCamposPreenchidos"
                >
                  <q-autocomplete
                    @selected="destinoMultiploSelecionado"
                    @search="buscaDestinoAlternatvo"
                    :max-results="20"
                  />
                </q-search>
              </q-field>
              <div class="col-md-4 destinos-selecionados">
                Destinos selecionados:
                <div v-for="(destino, index) in destinosSelecionados" :key="index" class="row">
                  <div class="col-8">{{destino.NomeMunicipio}}</div>
                  <div class="col-4">
                    <q-btn
                      icon="clear"
                      dense
                      color="negative"
                      @click="removeDestino(index, destino);"
                      size="10px"
                      class="q-mr-sm"
                    >
                      <q-tooltip>Remover {{destino.NomeMunicipio}} da lista</q-tooltip>
                    </q-btn>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-show="abriuAtravesDeSolicitacao">
            Local de destino e embarque da solicitao:
            <br />
            Destino(s): {{destinoSolicitacao}}
            <br />
            Embarque: {{embarqueSolicitacao}}
          </div>
          <div
            class="col-md-4 col-sm-12 col-xs-12 margin-form padding-dias-embarque"
            v-if="clienteAlfameta.CodCliente == 3 || clienteAlfameta.CodCliente == 4 || clienteAlfameta.CodCliente == 0 && cadenciaSelecionada == 'M' && modoVisualizacao == false"
          >
            <div>Dias necessarios para embarque: {{this.diasEmbarque}}</div>
          </div>

          <div class="col-12" style="margin-top:10px;">
            <q-btn
              color="primary"
              label="Salvar OS"
              @click="comportamentoBotaoSalvar()"
              v-show="modoVisualizacao != true"
            />
          </div>
          <div class="col-12">
            <tabela-item-ordem-servico-classificacao
              ref="gradeItemOs"
              @atualizaTabelaOs="atualizaTabelaOs"
            />
          </div>

          <div class="col-12">
            <tabela-justificativa ref="gradeJustificativas" />
          </div>
        </div>
        <div style="margin-top:10px">
          <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
        </div>
      </div>
    </q-modal-layout>
  </q-modal>
</template>

<script>
import { detectaCliente } from "../components/utils/DetectaCliente.js";
import { get, post, getComCabecalho } from "../components/js/ConexaoApi.js";
import TabelaJustificativa from "./TabelaJustificativas.vue";
import TabelaItemOrdemServicoClassificacao from "./TabelaItemOrdemServicoClassificacao.vue";
import {getCookie} from "@/components/js/Cookies";


export default {
  name: "modalVisualizaAbreOS",
  props: ["visualizando"],
  components: {
    TabelaItemOrdemServicoClassificacao,
    TabelaJustificativa,
  },
  data() {
    return {
      //Variaveis de validao
      erroCliente: false,
      erroDestino: false,
      erroClassificador: false,
      erroProduto: false,
      erroPesoLiquido: false,
      erroVirgulaPesoLiquido: false,
      erroPesoCadencia: false,
      erroLocalEmbarque: false,
      despesaLiberada: false,
      modoLeitura: false,
      clienteAlfameta: 1,
      multiplosDestinos: false,
      outrosValores: "",
      lote: "",
      valorServico: "",
      valorCadencia: "",
      tituloModal: "",
      CodOrdemServicoClassificacao: "",
      modoVisualizacao: false,
      todosDadosCarregados: false,
      exibe: false,
      termoBuscaCliente: "",
      clientes: [],
      destinosSelecionados: [],
      destinosSelecionadosString: "",
      termoBuscaDestinoMultiplo: "",
      codClienteSelecionado: "",
      abriuAtravesDeSolicitacao: false,
      destinoSolicitacao: "",
      embarqueSolicitacao: "",
      codsolicitacao: "",
      codEmbarqueSelecionado: "",
      codMunicipioSelecionado: "",
      codClassificadorSelecionado: "",
      termoBuscaEmbarque: "",
      locaisEmbarque: [],
      termoBuscaClassificador: "",
      classificadores: [],
      termoBuscaDestino: "",
      municipios: [],
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      produtos: [],
      produtoSelecionado: "",
      contrato: "",
      pesoLiquido: "",
      filial: "",
      pesoCadencia: "",
      suprimeCabecalho: false,
      opcoesCadencia: [
        {
          value: "S",
          label: "Sim",
        },
        {
          value: "N",
          label: "No",
        },
      ],
      opcoesCadenciaPositivagro: [
        {
          value: "S",
          label: "Sim",
        },
        {
          value: "N",
          label: "No",
        },
        {
          value: "M",
          label: "Mdia",
        },
      ],
      cadenciaSelecionada: "N",
      statusSelecionado: "A",
      opcoesStatus: [
        {
          value: "A",
          label: "Aberto",
        },
        {
          value: "F",
          label: "Finalizado",
        },
      ],
      alteraInsere: "",
      diasEmbarque: 0,
      toneladas: "",
      toneladasCadencia: "",
      maximoResultadoAutoSelectClientes: 20,
      transportadorasPredefinidas: false,
      transportadorasSelecionadas: [],
      transportadorasSelecionadasString: "",
      termoBuscaTransportadora: "",
      transportadoras: [],
      administrador: false,
      supervisor: false,
    };
  },
  methods: {
    abreModal() {
      this.limpaErrosCadastro();
      this.exibe = true;
      this.tituloModal = "Nova OS";
      this.modoVisualizacao = false;
      this.$refs.gradeItemOs.fechaTabela();
      this.$refs.gradeJustificativas.fechaTabela();
      this.alteraInsere = 1;
      this.acaoBotaoSalvar = () => this.salvaOS();
    },
    abreModalAberturaApartirSolicitacao(dadosSolicitacao) {
      const solicitacao = dadosSolicitacao
      this.abriuAtravesDeSolicitacao = true;
      this.limpaErrosCadastro();
      this.todosDadosCarregados = false;
      this.exibe = true;
      this.tituloModal = "Nova OS";
      this.modoVisualizacao = false;
      this.alteraInsere = 1;
      this.acaoBotaoSalvar = () => this.salvaOS();
      this.produtoSelecionado = solicitacao.CodProduto;
      this.selecionaClienteApartirSolicitacao(solicitacao.CodCliente);
      this.contrato = solicitacao.NumeroContrato;
      this.lote = solicitacao.Lote;
      this.filial = solicitacao.Filial;
      this.pesoLiquido = solicitacao.PesoLiquido;
      this.destinoSolicitacao = solicitacao.Destino;
      this.embarqueSolicitacao = solicitacao.LocalEmbarque;
      this.codsolicitacao = solicitacao.CodSolicitacao;
    },
    carregaDados() {
      if (this.$props.visualizando == false) {
        let classificadoresCarregados = false;
        let produtosCarregados = false;
        let clientesCarregados = false;
        if (this.todosDadosCarregados != true) {
          //Carrega os clientes
          //Carrega os classificadores
          get("classificadores")
            .then((result) => {
              result = result.replace(/"id":/g, '"value":');
              result = result.replace(/"nome":/g, '"label":');
              this.classificadores = JSON.parse(result);
              classificadoresCarregados = true;
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              console.error("Falha ao carregar classificadores" + err);
            });
          //Carrega os municipios de destino

          get("produto")
            .then((result) => {
              let jsonProdutos = result;
              jsonProdutos = jsonProdutos.replace(/"CodProduto":/g, '"value":');
              jsonProdutos = jsonProdutos.replace(/"Nome":/g, '"label":');
              this.produtos = JSON.parse(jsonProdutos)
              produtosCarregados = true;
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              console.error(err);
            });
          get("clientes")
              .then((result) => {
                let jsonClientes = result;
                jsonClientes = jsonClientes.replace(/"CodCliente":/g, '"value":');
                jsonClientes = jsonClientes.replace(/"Nome":/g, '"label":');
                jsonClientes = jsonClientes.replace(/"CnpjCpf":/g, '"sublabel":');
                jsonClientes = jsonClientes.replace(/"NomeMunicipio":/g, '"stamp":');
                this.clientes = JSON.parse(jsonClientes);
                clientesCarregados = true;
                // eslint-disable-next-line no-console
              })
              .catch(() => {
                // eslint-disable-next-line no-console
                console.error("Falha ao carregar clientes");
              });
          if (classificadoresCarregados == true && produtosCarregados == true && clientesCarregados == true) {
            this.todosDadosCarregados = true;
          } else {
            this.todosDadosCarregados = false;
          }
        }
      }
    },
    carregaCampos(dadosOs, funcaoBotaoSalvar) {
      this.limpaErrosCadastro();
      const multiplosDestinos = dadosOs.MultiplosDestinos;
      if (multiplosDestinos == "S") {
        this.multiplosDestinos = true;
      } else {
        this.multiplosDestinos = false;
      }
      const transportadorasPredefinidas = dadosOs.TransportadorasPredefinidas;
      if (transportadorasPredefinidas == "S") {
        this.transportadorasPredefinidas = true;
      } else {
        this.transportadorasPredefinidas = false;
      }
      const despesaOSLiberada = dadosOs.DespesaLiberada
      if (despesaOSLiberada == "S") {
        this.despesaLiberada = true
      } else {
        this.despesaLiberada = false
      }
      this.alteraInsere = funcaoBotaoSalvar;
      this.produtoSelecionado = dadosOs.CodProduto;
      this.termoBuscaCliente = dadosOs.nomecliente;
      this.termoBuscaDestino = dadosOs.NomeMunicipio;
      this.termoBuscaEmbarque = dadosOs.nomelocalarmazenagem;
      this.termoBuscaClassificador = dadosOs.nomeclassificador;
      this.codClienteSelecionado = dadosOs.CodCliente;
      this.codEmbarqueSelecionado = dadosOs.CodLocalArmazenagem;
      this.codMunicipioSelecionado = dadosOs.CodMunicipio;
      this.codClassificadorSelecionado = dadosOs.CodClassificador;
      this.contrato = dadosOs.NumeroContrato;
      this.lote = dadosOs.Lote;
      this.pesoLiquido = dadosOs.PesoLiquido;
      this.pesoCadencia = dadosOs.PesoCadencia;
      this.statusSelecionado = dadosOs.Status;
      this.cadenciaSelecionada = dadosOs.Cadencia;
      this.filial = dadosOs.Filial;
      this.outrosValores = dadosOs.OutrosValores;
      this.CodOrdemServicoClassificacao = dadosOs.CodOrdemServicoClassificacao;
      this.valorServico = dadosOs.ValorServico;
      this.valorCadencia = dadosOs.valorCadencia;
      if (this.cadenciaSelecionada == "M") {
        this.diasEmbarque = dadosOs.diasEmbarque;
      }
      if (dadosOs.SuprimeCabecalho == "S") {
        this.suprimeCabecalho = true;
      } else {
        this.suprimeCabecalho = false;
      }
    },
    abreModalVisualizacao(dadosOs) {
      this.limpaErrosCadastro();
      this.tituloModal = "Visualizando OS";
      this.modoVisualizacao = true;
      this.modoLeitura = true;
      this.exibe = true;
      this.carregaCampos(dadosOs);
      this.$refs.gradeItemOs.pegaCodOs(this.CodOrdemServicoClassificacao);
      this.$refs.gradeJustificativas.pegaCodOs(
        this.CodOrdemServicoClassificacao
      );
    },
    abreModalAlteracao(dadosOs) {
      this.limpaErrosCadastro();
      this.alteraInsere = 2;
      this.acaoBotaoSalvar = () => this.alteraOs();
      this.tituloModal = "Alterao OS";
      this.modoVisualizacao = false;
      this.exibe = true;
      this.carregaCampos(dadosOs);
      this.$refs.gradeItemOs.fechaTabela();
      this.$refs.gradeJustificativas.fechaTabela();
      getComCabecalho(
        "destinosalternativosos",
        "CodOrdemServicoClassificacao",
        this.CodOrdemServicoClassificacao
      )
        .then((result) => {
          this.destinosSelecionados = JSON.parse(result);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
      getComCabecalho(
        "tranportadorasosporos",
        "CodOrdemServicoClassificacao",
        this.CodOrdemServicoClassificacao
      )
        .then((result) => {
          this.transportadorasSelecionadas = JSON.parse(result);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    fechaModal() {
      this.limpaErrosCadastro();
      this.abriuAtravesDeSolicitacao = false;
      this.exibe = false;
      this.termoBuscaClassificador = "";
      this.suprimeCabecalho = false;
      this.contrato = "";
      this.statusSelecionado = "A";
      this.cadenciaSelecionada = "N";
      this.termoBuscaDestino = "";
      (this.termoBuscaCliente = ""), (this.termoBuscaEmbarque = "");
      this.produtoSelecionado = "";
      this.filial = "";
      this.pesoLiquido = "";
      this.pesoCadencia = "";
      this.cadenciaSelecionada = "";
      this.codClienteSelecionado = "";
      this.codEmbarqueSelecionado = "";
      this.codMunicipioSelecionado = "";
      this.codClassificadorSelecionado = "";
      this.valorServico = "";
      this.outrosValores = "";
      this.diasEmbarque = 0;
      this.toneladas = "";
      this.toneladasCadencia = "";
      this.destinosSelecionados = [];
      this.destinosSelecionadosString = "";
      this.multiplosDestinos = false;
      this.codsolicitacao = "";
      this.despesaLiberada = false;
    },
    removeTransportadora(index, transportadora) {
      this.transportadorasSelecionadas.splice(index, 1);
      post("deletatransportadoraos", transportadora)
        .then((result) => {
          const Resposta = JSON.parse(result);
          if (Resposta.Status == true) {
            this.$q.notify({
              message: Resposta.msg,
              timeout: 3000,
              type: "positive",
              color: "positive",
            });
          } else {
            this.$q.notify({
              message: Resposta.msg,
              timeout: 3000,
              type: "negative",
              color: "negative",
            });
          }
        })
        .catch((err) => {
          const Erro = JSON.parse(err);
          this.$q.notify({
            message: Erro.msg,
            timeout: 3000,
            type: "positve",
            color: "positive",
          });
        });
    },
    removeDestino(index, destino) {
      this.destinosSelecionados.splice(index, 1);
      post("deletadestinoalternativo", destino)
        .then((result) => {
          const Resposta = JSON.parse(result);
          if (Resposta.Status == true) {
            this.$q.notify({
              message: Resposta.msg,
              timeout: 3000,
              type: "positive",
              color: "positive",
            });
          } else {
            this.$q.notify({
              message: Resposta.msg,
              timeout: 3000,
              type: "negative",
              color: "negative",
            });
          }
        })
        .catch((err) => {
          const Erro = JSON.parse(err);
          this.$q.notify({
            message: Erro.msg,
            timeout: 3000,
            type: "positve",
            color: "positive",
          });
        });
    },
    salvaTransportadoras() {
      return new Promise((resolve, reject) => {
        const Transportadoras = JSON.parse(
          JSON.stringify(this.transportadorasSelecionadas, [
            "CodOrdemServicoClassificacao",
            "CodTransportadora",
          ])
        );
        post("adicionatransportadoraos", Transportadoras)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.$q.notify({
                message: Resposta.msg,
                timeout: 3000,
                type: "positive",
                color: "positive",
              });
              resolve();
            } else {
              this.$q.notify({
                message: Resposta.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
              });
              reject();
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            // eslint-disable-next-line no-console
            console.error(Erro);
            this.$q.notify({
              message: Erro.msg,
              timeout: 3000,
              type: "negative",
              color: "negative",
            });
            reject();
          });
      });
    },
    salvaDestinosAlternativos() {
      return new Promise((resolve, reject) => {
        const Destinos = JSON.parse(
          JSON.stringify(this.destinosSelecionados, [
            "CodOrdemServicoClassificacao",
            "CodMunicipio",
            "created_at",
          ])
        );
        post("adicionadestinoalternativo", Destinos)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.$q.notify({
                message: Resposta.msg,
                timeout: 3000,
                type: "positive",
                color: "positive",
              });
              resolve();
            } else {
              this.$q.notify({
                message: Resposta.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
              });
              reject();
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            // eslint-disable-next-line no-console
            console.error(Erro);
            this.$q.notify({
              message: Erro.msg,
              timeout: 3000,
              type: "negative",
              color: "negative",
            });
            reject();
          });
      });
    },
    todosCamposPreenchidos() {
      return new Promise((resolve) => {
        let erro = false
        if (this.codClienteSelecionado == "" || this.termoBuscaCliente == "") {
          this.erroCliente = true;
          this.termoBuscaCliente = "";
          erro = true;
        } else {
          this.erroCliente = false;
        }
        if (
          this.codMunicipioSelecionado == "" ||
          this.termoBuscaDestino == ""
        ) {
          this.termoBuscaDestino = "";
          this.erroDestino = true;
          erro = true;
        } else {
          this.erroDestino = false;
        }
        if (
          this.codClassificadorSelecionado == "" ||
          this.termoBuscaClassificador == ""
        ) {
          this.erroClassificador = true;
          this.termoBuscaClassificador = "";
          erro = true;
        } else {
          this.erroClassificador = false;
        }
        if (this.produtoSelecionado == "") {
          this.erroProduto = true;
          erro = true;
        } else {
          this.erroProduto = false;
        }
        if (
          this.pesoLiquido == "" ||
          this.pesoLiquido == 0 ||
          this.pesoLiquido == null
        ) {
          this.erroPesoLiquido = true;
          erro = true;
        } else {
          this.erroPesoLiquido = false;
        }
        if (
          this.cadenciaSelecionada == "S" &&
          (this.pesoCadencia == "" || this.pesoCadencia == null)
        ) {
          this.erroPesoCadencia = true;
          erro = true;
        } else {
          this.erroPesoCadencia = false;
        }
        if (
          this.codEmbarqueSelecionado == "" ||
          this.termoBuscaEmbarque == ""
        ) {
          this.erroLocalEmbarque = true;
          erro = true;
          this.termoBuscaEmbarque = "";
        } else {
          this.erroLocalEmbarque = false;
        }
        if (String(this.pesoLiquido).includes('.') || String(this.pesoLiquido).includes(',')){
          this.erroVirgulaPesoLiquido = true;
          erro = true;
        }else {
          this.erroVirgulaPesoLiquido = false;
        }
        if (!erro) {
          resolve(true);
        } else {
          resolve(false);
        }
      });
    },
    confirmarCancelamentoDeCadastroDeOs() {
      if (this.modoVisualizacao != true) {
        this.$q
          .dialog({
            title: "Cancelar lanamento de OS",
            message: "Deseja realmente cancelar o lanamento da OS?",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => {
            this.fechaModal();
          })
          .catch(() => {});
      } else {
        this.fechaModal();
      }
    },
    limpaErrosCadastro() {
      this.erroCliente = false;
      this.erroDestino = false;
      this.erroClassificador = false;
      this.erroProduto = false;
      this.erroPesoLiquido = false;
      this.erroPesoCadencia = false;
      this.erroLocalEmbarque = false;
    },
    CalculaDiasEmbarque() {
      if (
        this.cadenciaSelecionada == "M" &&
        this.cadenciaSelecionada !== null
      ) {
        this.diasEmbarque = Math.floor(this.pesoLiquido / this.pesoCadencia);
      }
    },
    async salvaOS() {
      const todosCamposValidados = await this.todosCamposPreenchidos();
      if (todosCamposValidados) {
        const OS = {}
        OS.CodEmpresa = 1;
        OS.CodCliente = this.codClienteSelecionado;
        OS.CodMunicipio = this.codMunicipioSelecionado;
        OS.CodClassificador = this.codClassificadorSelecionado;
        OS.CodProduto = this.produtoSelecionado;
        OS.NumeroContrato = this.contrato;
        OS.Lote = this.lote;
        OS.PesoLiquido = this.pesoLiquido;
        OS.Cadencia = this.cadenciaSelecionada;
        OS.PesoCadencia = this.pesoCadencia;
        OS.Status = this.statusSelecionado;
        OS.CodLocalArmazenagem = this.codEmbarqueSelecionado;
        OS.Filial = this.filial;
        if (this.suprimeCabecalho == true) {
          OS.SuprimeCabecalho = "S";
        } else {
          OS.SuprimeCabecalho = "N";
        }
        if (OS.CodProduto == 1) {
          OS.IconeClassificacao = "soja26.png";
        } else if (OS.CodProduto == 2) {
          OS.IconeClassificacao = "milho26.png";
        } else if (OS.CodProduto == 3) {
          OS.IconeClassificacao = "farelo26.png";
        } else if (OS.CodProduto == 4) {
          OS.IconeClassificacao = "sorgo26.png";
        } else if (OS.CodProduto == 5) {
          OS.IconeClassificacao = "milheto26.png";
        }
        if (this.outrosValores !== null && this.outrosValores !== "") {
          OS.OutrosValores = this.outrosValores //.replace(/,/g, ".");
        } else {
          OS.OutrosValores = null;
        }
        if (this.valorServico !== null && this.valorServico !== "") {
          OS.ValorServico = this.valorServico //.replace(/,/g, ".");
        } else {
          OS.ValorServico = null;
        }
        OS.PesoSaldoRestante = this.pesoLiquido;
        if (this.cadenciaSelecionada == "M") {
          OS.DiasCadenciaMedia = this.diasEmbarque;
        }
        if (this.despesaLiberada === true) {
          OS.DespesaLiberada = 'S'
        } else {
          OS.DespesaLiberada = 'N'
        }
        // eslint-disable-next-line no-console
        console.log(JSON.stringify(OS));
        // eslint-disable-next-line no-console
        post("novaos", OS)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              if (this.abriuAtravesDeSolicitacao == true) {
                this.informaOsAberta(Resposta.CodOS);
              }
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
      }
    },
    atualizaTabelaOs() {
      this.$emit("atualizaTabela");
    },
    async informaOsAberta(CodOs) {
      const OS = {}
      OS.CodSolicitacao = this.codsolicitacao;
      OS.CodOrdemServicoClassificacao = CodOs;
      post("osabertasolicitacao", OS)
        .then(() => {
          this.$emit("atualizaSolicitacao");
        })
        .catch(() => {});
    },
    async alteraOs() {
      const todosCamposPreenchidos = await this.todosCamposPreenchidos();
      if (todosCamposPreenchidos) {
        this.exibeMensagemRetorno = false;
        const OS = {}
        OS.NumeroContrato = this.contrato;
        OS.Lote = this.lote;
        OS.CodOrdemServicoClassificacao = this.CodOrdemServicoClassificacao;
        OS.CodEmpresa = 1;
        if (
          this.transportadorasPredefinidas == true &&
          this.transportadorasSelecionadas.length > 0
        ) {
          await this.salvaTransportadoras();
          OS.TransportadorasPredefinidas = "S";
        } else {
          OS.TransportadorasPredefinidas = "N";
        }
        if (this.multiplosDestinos && this.destinosSelecionados.length > 0) {
          await this.salvaDestinosAlternativos();
          OS.MultiplosDestinos = "S";
        } else {
          OS.MultiplosDestinos = "N";
        }
        OS.CodCliente = this.codClienteSelecionado;
        OS.CodMunicipio = this.codMunicipioSelecionado;
        OS.CodClassificador = this.codClassificadorSelecionado;
        OS.CodProduto = this.produtoSelecionado;
        if (this.suprimeCabecalho == true) {
          OS.SuprimeCabecalho = "S";
        } else {
          OS.SuprimeCabecalho = "N";
        }
        OS.PesoLiquido = this.pesoLiquido;
        OS.Cadencia = this.cadenciaSelecionada;
        OS.PesoCadencia = this.pesoCadencia;
        OS.Status = this.statusSelecionado;
        OS.CodLocalArmazenagem = this.codEmbarqueSelecionado;
        OS.Filial = this.filial;
        if (this.outrosValores !== null && this.outrosValores !== "") {
          OS.OutrosValores = this.outrosValores //.replace(/,/g, ".");
        } else {
          OS.OutrosValores = null;
        }
        if (OS.CodProduto == 1) {
          OS.IconeClassificacao = "soja26.png";
        } else if (OS.CodProduto == 2) {
          OS.IconeClassificacao = "milho26.png";
        } else if (OS.CodProduto == 3) {
          OS.IconeClassificacao = "farelo26.png";
        }
        if (this.valorServico !== null && this.valorServico !== "") {
          OS.ValorServico = this.valorServico //.replace(/,/g, ".");
        } else {
          OS.ValorServico = null;
        }
        if (this.cadenciaSelecionada == "M") {
          OS.DiasCadenciaMedia = this.diasEmbarque;
        }
        if (this.despesaLiberada === true) {
          OS.DespesaLiberada = 'S'
        } else {
          OS.DespesaLiberada = 'N'
        }
        post("alteraos", OS)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
      }
    },
    buscaCliente(terms, done) {
      get(`clientessite?filter=${this.termoBuscaCliente}`)
        .then((result) => {
          let jsonClientes = result;
          jsonClientes = jsonClientes.replace(/"CodCliente":/g, '"value":');
          jsonClientes = jsonClientes.replace(/"Nome":/g, '"label":');
          jsonClientes = jsonClientes.replace(/"CnpjCpf":/g, '"sublabel":');
          jsonClientes = jsonClientes.replace(/"NomeMunicipio":/g, '"stamp":');
          const clientes = JSON.parse(jsonClientes);
          done(clientes);
        })
        .catch(() => {
          done([]);
        });
    },
    buscaTransportadora(terms, done) {
      get(`transportadorassites?filter=${this.termoBuscaTransportadora}`)
        .then((result) => {
          let jsonTransportadoras = result;
          jsonTransportadoras = jsonTransportadoras.replace(
            /"CodTransportadora":/g,
            '"value":'
          );
          jsonTransportadoras = jsonTransportadoras.replace(
            /"Nome":/g,
            '"label":'
          );
          jsonTransportadoras = jsonTransportadoras.replace(
            /"UF":/g,
            '"stamp":'
          );
          this.transportadoras = JSON.parse(jsonTransportadoras);
          done(this.transportadoras);
        })
        .catch(() => {
          done([]);
        });
    },
    buscaLocalEmbarque(terms, done) {
      let rota = `locaissite?filter=${this.termoBuscaEmbarque}`;
      if (this.supervisor) {
        rota = rota + `&supervisor=${getCookie("codClassificadorSistema")}`;
      }
      get(rota)
        .then((result) => {
          let jsonLocais = result;
          jsonLocais = jsonLocais.replace(
            /"CodLocalArmazenagem":/g,
            '"value":'
          );
          jsonLocais = jsonLocais.replace(/"Nome":/g, '"label":');
          jsonLocais = jsonLocais.replace(/"cnpjcpf":/g, '"sublabel":');
          jsonLocais = jsonLocais.replace(/"NomeMunicipio":/g, '"stamp":');
          // jsonLocais = jsonLocais.replace(/\"RazaoSocial\":/g, '"a":')
          this.locaisEmbarque = JSON.parse(jsonLocais);
          this.locaisEmbarque.forEach((local) => {
            local.label =
              local.RazaoSocial !== null
                ? local.label + " - " + local.RazaoSocial
                : local.label;
          });
          done(this.locaisEmbarque);
        })
        .catch(() => {
          done([]);
        });
    },
    buscaDestino(terms, done) {
      get(`municipios?filter=${this.termoBuscaDestino}`)
        .then((result) => {
          let jsonMunicipios = result;
          jsonMunicipios = jsonMunicipios.replace(
            /"CodMunicipio":/g,
            '"value":'
          );
          jsonMunicipios = jsonMunicipios.replace(
            /"NomeMunicipio":/g,
            '"label":'
          );
          jsonMunicipios = jsonMunicipios.replace(/"UF":/g, '"stamp":');
          const municipios = JSON.parse(jsonMunicipios);
          done(municipios);
        })
        .catch(() => {
          done([]);
        });
    },
    buscaDestinoAlternatvo(terms, done) {
      get(`municipios?filter=${this.termoBuscaDestinoMultiplo}`)
        .then((result) => {
          let jsonMunicipios = result;
          jsonMunicipios = jsonMunicipios.replace(
            /"CodMunicipio":/g,
            '"value":'
          );
          jsonMunicipios = jsonMunicipios.replace(
            /"NomeMunicipio":/g,
            '"label":'
          );
          jsonMunicipios = jsonMunicipios.replace(/"UF":/g, '"stamp":');
          const municipios = JSON.parse(jsonMunicipios);
          done(municipios);
        })
        .catch(() => {
          done([]);
        });
    },
    selecionaClienteApartirSolicitacao(codCliente) {
      this.codClienteSelecionado = codCliente;
      this.termoBuscaCliente = this.clientes.find(
        (x) => x.value == codCliente
      ).label;
    },
    clienteSelecionado(cliente) {
      this.termoBuscaCliente = cliente.label;
      this.codClienteSelecionado = cliente.value;
      if(cliente.DataValidadeValorTonelada !== null){
        let dt = Date.parse(cliente.DataValidadeValorTonelada)
        if(Date.now() <= dt){
          if(cliente.ValorTonelada !== null){
            let valorServico = Number(cliente.ValorTonelada).toFixed(2)
            this.valorServico = valorServico
          }
        }
      }else{
        if(cliente.ValorTonelada !== null){
          let valorServico = Number(cliente.ValorTonelada).toFixed(2)
          this.valorServico = valorServico
        }
      }
      this.cadenciaSelecionada = cliente.PossuiCadencia;
    },
    embarqueSelecionado(embarque) {
      this.termoBuscaEmbarque = embarque.label;
      this.codEmbarqueSelecionado = embarque.value;
    },
    destinoSelecionado(destino) {
      this.termoBuscaDestino = destino.label;
      this.codMunicipioSelecionado = destino.value;
    },
    transportadaSelecionada(transportadora) {
      const trans = {
        CodOrdemServicoClassificacao: this.CodOrdemServicoClassificacao,
        CodTransportadora: transportadora.value,
        Nome: transportadora.label,
        created_at: new Date(),
      };
      this.transportadorasSelecionadas.push(trans);
      this.termoBuscaTransportadora = "";
      if (this.transportadorasSelecionadasString.length > 0) {
        this.transportadorasSelecionadasString += " ," + transportadora.label;
      } else {
        this.transportadorasSelecionadasString += transportadora.label;
      }
    },
    destinoMultiploSelecionado(destino) {
      const dest = {
        CodOrdemServicoClassificacao: this.CodOrdemServicoClassificacao,
        CodMunicipio: destino.value,
        NomeMunicipio: destino.label,
        created_at: new Date(),
      };
      this.destinosSelecionados.push(dest);
      this.termoBuscaDestinoMultiplo = "";
      if (this.destinosSelecionadosString.length > 0) {
        this.destinosSelecionadosString += " ," + destino.label;
      } else {
        this.destinosSelecionadosString += destino.label;
      }
    },
    classificadorSelecionado(classificador) {
      this.termoBuscaClassificador = classificador.label;
      this.codClassificadorSelecionado = classificador.value;
    },
    comportamentoBotaoSalvar() {
      if (this.alteraInsere == 1) {
        return this.salvaOS();
      } else {
        return this.alteraOs();
      }
    },
    selecionaCliente() {
      detectaCliente()
        .then((result) => {
          this.clienteAlfameta = result;
        })
        .catch(() => {
          this.clienteAlfameta = 0;
        });
    },
    arredondaPesoLiquido(){
      //if(String(this.toneladas).includes('.') || String(this.toneladas).includes(','))
       // this.toneladas = Math.round(this.toneladas);
      /*if(String(this.toneladas).includes('.') || String(this.toneladas).includes(','))
        this.$q.notify({
          message: `Por favor, informe o peso lquido sem vrgula`,
          timeout: 3000,
          type: "negative",
          color: "orange",
        });
      else
        this.pesoLiquido();*/
      if(String(this.pesoLiquido).includes('.') || String(this.pesoLiquido).includes(',')){
        let pesoAnterior = this.pesoLiquido;
        this.pesoLiquido = pesoAnterior;
        this.pesoLiquido = Math.round(this.pesoLiquido);
        this.$q.notify({
          message: `Peso lquido foi arredondando de ${pesoAnterior} para ${this.pesoLiquido} `,
          timeout: 3000,
          type: "info",
          color: "info",
        });
      }
      // if(String(this.pesoCadencia).includes('.') || String(this.pesoCadencia).includes(','))
     ///   this.pesoCadencia = Math.round(this.pesoCadencia);
     // if(String(this.toneladasCadencia).includes('.') || String(this.toneladasCadencia).includes(','))
     //   this.toneladasCadencia = Math.round(this.toneladasCadencia);
    },
    converteKgParaTon(calculandoCadencia) {
      if (calculandoCadencia) {
        this.toneladasCadencia = this.pesoCadencia / 1000;
      } else {
        this.toneladas = this.pesoLiquido / 1000;
      }
    },
    converteTonParaKg(calculandoCadencia) {
      if (calculandoCadencia) {
        this.pesoCadencia = this.toneladasCadencia * 1000;
      } else {
        this.pesoLiquido = this.toneladas * 1000;
      }
    },
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
  },
  computed: {
    classificadoresAtivo() {
      return this.classificadores.filter(i => i.ativo !== 'N')
    }
  },
  mounted() {
    this.verificaAdministrador();
    this.selecionaCliente();
    this.carregaDados();
  },
  watch: {
    toneladasCadencia: function () {
      this.converteTonParaKg(true);
    },
    pesoCadencia: function () {
      this.converteKgParaTon(true);
    },
    toneladas: function () {
      this.converteTonParaKg(false);
    },
    pesoLiquido: function () {
      this.converteKgParaTon(false);
    },
    multiplosDestinos: function () {
      if (this.multiplosDestinos != true) {
        this.destinosSelecionados = [];
        this.destinosSelecionadosString = "";
      }
    },
    transportadorasPredefinidas: function () {
      if (this.transportadorasPredefinidas != true) {
        this.transportadorasSelecionadas = [];
        this.transportadorasSelecionadasString = "";
      }
    },
    exibe: function () {
      if (this.exibe == false) {
        this.fechaModal();
      }
    },
    cadenciaSelecionada: function () {
      if (this.cadenciaSelecionada == "M") {
        this.CalculaDiasEmbarque();
      } else {
        this.diasEmbarque = 0;
      }
    },
  },
};
</script>

