.tira-margin-pading {
  padding: 0;
  margin: 0;
}
.padding-dias-embarque {
  padding-top: 150px;
}
.margin-form {
  padding: 5px;
}
.caixa-multiplos-destinos {
  border: 1px solid black;
  border-radius: 5px;
}
.destinos-selecionados {
  margin-top: 15px;
  margin-left: 15px;
}

@media screen and (max-width: 768px) {
  .margin-form {
    padding: 0px;
  }

  .lagura-modal {
    width: 70px;
  }
}
</style>
