<template>
  <div>
    <div>
      <div
        class="alinha-texto-centro margin-distancia-caixa-arquivos tamanho-fonte-arquivos-enviados"
      >
        Arquivos enviados pelo Classificador
      </div>
      <div class="row">
        <div v-for="arquivo in arquivosClassificador" :key="arquivo.id">
          <box-arquivos :arquivoProp="arquivo" />
        </div>
      </div>
    </div>
    <div>
      <div
        class="alinha-texto-centro  margin-distancia-caixa-arquivos tamanho-fonte-arquivos-enviados"
      >
        Arquivos enviados pelo Cliente
      </div>
      <div class="row">
        <div v-for="arquivo in arquivosCliente" :key="arquivo.id">
          <box-arquivos :arquivoProp="arquivo" @arquivoLiberado="listaArquivos"/>
        </div>
      </div>
    </div>
    <div class="container-upload-arquivos">
      <div
        class="alinha-texto-centro margin-distancia-caixa-arquivos tamanho-fonte-arquivos-enviados"
      >
        Enviar Arquivos
      </div>
      <box-envia-arquivo
        ref="boxEnviaArquivos"
        @arquivoEnviado="listaArquivos"
      />
    </div>
  </div>
</template>
<script>
import * as conexaoCliente from "@/components/AreaCliente/js/ConexaoApiCliente.js";
import * as conexao from "@/components/js/ConexaoApi.js";
import { getCookie } from "../../js/Cookies.js";
import { detectaCliente } from "../../utils/DetectaCliente";
import BoxArquivos from "../../../widgets/BoxArquivo.vue";
import BoxEnviaArquivo from "../../../widgets/BoxEnviaArquivo.vue";
export default {
  components: {
    BoxArquivos,
    BoxEnviaArquivo,
  },
  name: "VisualizaArquivos",
  data() {
    return {
      exibe: false,
      codCliente: "",
      codItemOrdemServicoClassificacao: "",
      codOrdemServicoClassificacao: "",
      codClassificador: "",
      numeroLaudo: "",
      arquivosClassificador: [],
      arquivosCliente: [],
      carregouArquivos: false,
      enviadoPor: "",
      urlUpload: "",
      urlDownload: "",
      urlApi: "",
      token: "",
      porta: "8081",
      iconePdf: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/pdf.png",
      iconeJpg: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/jpg.png",
      iconeFile: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/file.png",
      iconeDoc: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/doc.png",
      iconeTxt: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/txt.png",
      iconePng: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/png.png",
      arquivoCarregado: false,
      itemOrdemServicoClassificacao: null,
    };
  },
  methods: {
    uploadAutomatico() {
      this.$refs.uploader.upload();
    },
    filtroArquivos(files) {
      const MAX_FILE_SIZE = 5 * 1024 * 1024; //5MB
      return files.filter((file) => {
        return file.size <= MAX_FILE_SIZE;
      });
    },
    setaInformacaoOs(dadosItemOs) {
      if (getCookie("codcliente") !== "") {
        this.codCliente = getCookie("codcliente");
      } else {
        this.codClassificador = getCookie("id");
      }
      this.codItemOrdemServicoClassificacao =
        dadosItemOs.CodItemOrdemServicoClassificacao;
      this.codOrdemServicoClassificacao =
        dadosItemOs.CodOrdemServicoClassificacao;
      this.numeroLaudo = dadosItemOs.NumeroLaudo;
      this.enviadoPor = dadosItemOs.EnviadoPor;
      this.$refs.boxEnviaArquivos.setaInformacaoOs(dadosItemOs);
      this.detectaUrlEPorta(dadosItemOs.EnviadoPor);
    },
    listaArquivos() {
      //Se a listagem de arquivos for aberta pela area do cliente, utiliza o metodo abaixo
      if (this.enviadoPor == "CLI") {
        //Carrega arquivos do cliente
        let cabecalhos = new Map();
        cabecalhos.set("CodCliente", this.codCliente);
        cabecalhos.set("VisualizadoPor", 'CLI');
        cabecalhos.set("CodItemOrdemServicoClassificacao", this.codItemOrdemServicoClassificacao);
        cabecalhos.set("CodOrdemServicoClassificacao", this.codOrdemServicoClassificacao);
        cabecalhos.set("NumeroLaudo", this.numeroLaudo);
        cabecalhos.set("EnviadoPor", this.enviadoPor);
        conexaoCliente.getComVariosCabecalhos("listaarquivos", cabecalhos)
          .then((result) => {
            this.arquivosCliente = null;
            this.arquivosCliente = JSON.parse(result);
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            console.error(err);
          });
        // Carrega arquivos enviados pelo classificador
        cabecalhos.set("EnviadoPor", "CLA");
        conexaoCliente.getComVariosCabecalhos("listaarquivos", cabecalhos)
          .then((result) => {
            this.arquivosClassificador = null;
            this.arquivosClassificador = JSON.parse(result);
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            console.error(err);
          });


      // Se a listagem de arquivos tiver sido aberta pelo um usurio atravs da rea administrativa
      } else {
        let cabecalhos = new Map();
        cabecalhos.set("CodCliente", this.codCliente);
        cabecalhos.set("CodItemOrdemServicoClassificacao", this.codItemOrdemServicoClassificacao);
        cabecalhos.set("CodOrdemServicoClassificacao", this.codOrdemServicoClassificacao);
        cabecalhos.set("NumeroLaudo", this.numeroLaudo);
        cabecalhos.set("EnviadoPor", 'CLI');
        cabecalhos.set("VisualizadoPor", 'CLA');
        conexao.getComVariosCabecalhos("listaarquivos", cabecalhos)
          .then((result) => {
            this.arquivosCliente = null;
            this.arquivosCliente = JSON.parse(result);
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            console.error(err);
          });

        cabecalhos.set("EnviadoPor", this.enviadoPor);
        conexao.getComVariosCabecalhos("listaarquivos", cabecalhos)
          .then((result) => {
            this.arquivosClassificador = null;
            this.arquivosClassificador = JSON.parse(result);
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            console.error(err);
          });
      }
    },
    detectaUrlEPorta(enviadoPor) {
      const protocolo = location.protocol;
      const url = protocolo + "//alfameta.ddns.net";
      if (enviadoPor == "CLI") {
        this.urlUpload = url + ":" + this.porta + "/api/cliente/enviaarquivos3";
        this.token = getCookie("tkCliente");
      } else {
        this.urlUpload = url + ":" + this.porta + "/api/enviaarquivos3";
        this.token = getCookie("tk");
      }
      this.urlApi = url + ":" + this.porta + "/arquivos/";
    },
  },
  mounted() {
    detectaCliente().then((result) => {
      this.porta = result.PortaAPI;
    });
  },
};
</script>


<style scoped>

.alinha-texto-centro {
  text-align: center;
}
.tamanho-fonte-arquivos-enviados {
  font-size: 22px;
}
.tamanho-fonte-arquivos {
  font-size: 14px;
}
.margin-distancia-caixa-arquivos {
  margin-top: 15px;
  margin-bottom: 15px;
}
.container-upload-arquivos {
  border: 1px solid black;
  border-radius: 5px;
  margin: 1px;
  padding: 10px;
}
</style>