<template>
  <q-layout>
    <div class="responsivo">
      <div>
        <q-table
          :data="classificadores"
          :columns="colunasTabela"
          row-key="CodOrdemServicoClassificacao"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          :rows-per-page-options="opcoesLinhasPorPagina"
          :selected="linhaSelecionada"
          :pagination.sync="padraoLinhasPorPagina"
        >
          <template slot="top-left">
            <form autocomplete="off">
             <q-search color="secondary" v-model="termoBusca" class="col-6"  />
            </form>
          </template>

          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td key="id" :props="props" class="tamanho-coluna-id">{{ props.row.id }}</q-td>
              <q-td key="usuario" :props="props">{{ props.row.usuario }}</q-td>
              <q-td key="nome" :props="props">{{ props.row.nome }}</q-td>
              <q-td key="acoes" :props="props" style="width:15px">
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  v-show="administrador"
                  @click="alteraUsuario(props.row)"
                >
                  <q-tooltip>Editar usurio</q-tooltip>
                </q-btn>
                <q-btn
                  icon="lock"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  v-show="props.row.ativo == 'S'"
                  @click="desativaUsuario(props.row.id)"
                >
                  <q-tooltip>Desativar acesso</q-tooltip>
                </q-btn>
                <q-btn
                  icon="lock_open"
                  :props="props"
                  flat
                  dense
                  color="positive"
                  v-show="props.row.ativo == 'N'"
                  @click="reativaUsuario(props.row.id)"
                >
                  <q-tooltip>Reativar acesso</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizaUsuario(props.row)"
                >
                  <q-tooltip>Visualizar dados do usario</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
          <template slot="top-right">
            <div class="offset-6 col-12">
              <q-btn
                color="primary"
                icon="mdi-account-plus"
                label="Novo Usurio"
                @click="abreModalCadastro()"
                v-if="administrador"
              />
            </div>
          </template>
        </q-table>
      </div>
    </div>
    <ModalClassificadores ref="modalClassificadores" @atualizaTabela="carregaTabela" />
  </q-layout>
</template>


<script>
import { get, getComCabecalho } from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
import ModalClassificadores from "@/components/ModalClassificadores.vue";
export default {
  name: "gradeClassificadores",
  components: {
    ModalClassificadores,
  },
  data() {
    return {
      padraoLinhasPorPagina: { rowsPerPage: 10 },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      exibe: true,
      abreModal: false,
      termoBusca: "",
      usuarios: [],
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "id",
          label: "ID",
          field: "id",
          sortable: true,
          align: "center",
          style: "width: 75px",
        },
        {
          name: "usuario",
          label: "Usurio",
          sortable: true,
          field: "usuario",
          align: "left",
        },
        {
          name: "nome",
          label: "Nome",
          field: "nome",
          align: "left",
          sortable: true,
        },
        { name: "acoes", label: "Aes", field: "acoes" },
      ],
      //Array com os dados da tabela
      classificadores: [],
      linhaSelecionada: [],
      administrador: false,
      supervisor: false,
    };
  },
  methods: {
    carregaTabela() {
      get("classificadores?listaTodos=true")
        .then((result) => {
          this.classificadores = JSON.parse(result);
          this.statusCarregamentoTabela = false;
        })
        .catch((err) => {
          // eslint-disable-next-line
          console.error(err);
          this.statusCarregamentoTabela = false;
        });
    },
    visualizaUsuario(dadosUsuario) {
      this.$refs.modalClassificadores.abreModalVisualizacao(dadosUsuario);
    },
    alteraUsuario(dadosUsuario) {
      this.$refs.modalClassificadores.abreModalAlteracao(dadosUsuario);
    },
    abreModalNovoUsuario() {
      this.$refs.modalClassificadores.abreModal();
    },
    abreModalCadastro() {
      this.$refs.modalClassificadores.abreModal();
    },
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    desativaUsuario(id) {
      this.$q
        .dialog({
          title: "Desativar acesso",
          message:
            "Deseja realmente desativar o acesso ao cliente selecionado?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho("desativausuario", "id", id)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
    reativaUsuario(id) {
      this.$q
        .dialog({
          title: "Reativar acesso",
          message: "Deseja realmente reativar o acesso ao cliente selecionado?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
        })
        .then(() => {
          getComCabecalho("reativausuario", "id", id)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.$q.notify({
                message: Erro.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
        })
        .catch(() => {});
    },
  },

  beforeMount() {
    this.verificaAdministrador();
    this.carregaTabela();
  },
};
</script>

