<template>
  <q-layout>
    <Cabecalho />
    <q-page-container>
      <div class="container">
        <TabelaOrdensServico
          nomeTabela="OS em Aberto"
          :visualizaFinalizados="false"
          ref="gradeOs"
          @atualizaTabelaFinalizada="atualizaGradeOsFinalizadas"
          :exibeConab="false"
        />
        <TabelaOrdensServico
          nomeTabela="OS Finalizadas"
          :visualizaFinalizados="true"
          ref="gradeOsFinalizadas"
          @atualizaTabelaReaberta="atualizaGradeOs"
          v-if="administrador || supervisor"
          :exibeConab="false"
        />
      </div>
    </q-page-container>
  </q-layout>
</template>

<script>
import TabelaOrdensServico from "@/components/TabelaOrdensServico.vue";
import Cabecalho from "@/components/Cabecalho.vue";
import { getCookie } from "../components/js/Cookies.js";
export default {
  components: {
    TabelaOrdensServico,
    Cabecalho,
  },
  data() {
    return {
      administrador: false,
      supervisor: false,
    };
  },
  methods: {
    verificaAdministrador() {
      const admin = getCookie("su");
      const supervisor = getCookie("sup");

      this.administrador = false;
      this.supervisor = false;

      if (admin !== '0') {
        this.administrador = true;
      } else if (supervisor !== '0') {
        this.supervisor = true;
      }
    },
    atualizaGradeOs() {
      this.$refs.gradeOs.carregaTabela();
    },
    atualizaGradeOsFinalizadas() {
      this.$refs.gradeOsFinalizadas.carregaTabelaApenasFinalizadas();
    },
  },
  beforeMount() {
    this.verificaAdministrador();
  },
};
</script>

