<template>
  <q-modal
    v-model="exibe"
    minimized
    :content-css="{ width: '55vw', minHeight: '45vh' }"
    no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn
          style="padding: 0px; margin: 0"
          flat
          round
          dense
          icon="close"
          @click="cancelarCadastroDeLote"
        />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>
      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{ tituloModal }}</h6>
      </q-toolbar>
      <div class="layout-padding">
        <div class="row">
          <div class="col-md-12 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroMunicipio"
              error-label="Por favor, informe um destino"
            >
              <q-input
                v-model="termoBuscaDestino"
                placeholder="Escolha um destino"
                float-label="Escolha um destino"
                :disable="modoVisualizacao == true"
                clearable
              >
                <q-autocomplete
                  @search="buscaDestino"
                  @selected="destinoSelecionado"
                  :max-results="30"
                />
              </q-input>
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroCDADestino"
              error-label="Por favor, informe o C.D.A do destino"
            >
              <q-input
                v-model="cdaDestino"
                placeholder="C.D.A do Destino"
                float-label="C.D.A do Destino"
                :disable="modoVisualizacao == true"
                upper-case
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroATR" error-label="Por favor, informe o A.T.R">
              <q-input
                v-model="atr"
                placeholder="A.T.R"
                float-label="A.T.R"
                :disable="modoVisualizacao == true"
                upper-case
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroNumeroLote"
              error-label="Por favor, informe o numero do lote"
            >
              <q-input
                v-model="numeroLote"
                placeholder="Numero do Lote"
                float-label="Numero do Lote"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroPesoLiquido"
              error-label="Por favor, informe o peso liquido do lote"
            >
              <q-input
                v-model="pesoLiquido"
                placeholder="Peso Liquido(kg)"
                float-label="Peso Liquido(kg)"
                onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                :disable="modoVisualizacao == true"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-input
              v-model="acondicionamento"
              placeholder="Acondicionamento"
              float-label="Acondicionamento"
              :disable="modoVisualizacao == true"
              upper-case
            />
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroSafra"
              error-label="Por favor, informe a safra"
            >
              <q-input
                v-model="safra"
                placeholder="Safra"
                float-label="Safra"
                :disable="modoVisualizacao == true"
                upper-case
              />
            </q-field>
          </div>
          <div class="col-12" style="margin-top: 10px">
            <q-btn
              color="primary"
              label="Salvar"
              @click="comportamentoBotaoSalvar()"
              v-show="modoVisualizacao != true"
            />
          </div>
        </div>
        <div style="margin-top: 10px">
          <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{
            this.mensagemRetorno
          }}</q-alert>
        </div>
      </div>
    </q-modal-layout>
  </q-modal>
</template>

<script>
import { get, post, putComCabecalho } from "../components/js/ConexaoApi.js";
export default {
  name: "modalLoteConab",
  components: {},
  data() {
    return {
      codLote: "",
      tituloModal: "",
      numeroLote: "",
      pesoLiquido: "",
      acondicionamento: "",
      cdaDestino: "",
      safra: "",
      atr: "",
      termoBuscaDestino: "",
      codMunicipioSelecionado: "",
      modoVisualizacao: false,
      exibe: false,
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      alteraInsere: "",
      codOrdemServicoClassificacaoConab: "",
      erroMunicipio: false,
      erroCDADestino: false,
      erroATR: false,
      erroNumeroLote: false,
      erroPesoLiquido: false,
      erroSafra: false,
    };
  },
  methods: {
    abreModal(codOs) {
      this.exibe = true;
      this.tituloModal = "Cadastrar novo lote";
      this.modoVisualizacao = false;
      this.alteraInsere = 1;
      this.codOrdemServicoClassificacaoConab = codOs;
    },
    carregaCampos(dadosLote) {
      this.termoBuscaDestino = dadosLote.NomeMunicipio;
      this.codMunicipioSelecionado = dadosLote.CodMunicipio;
      this.cdaDestino = dadosLote.CDADestino;
      this.atr = dadosLote.ATR;
      this.numeroLote = dadosLote.NumeroLote;
      this.pesoLiquido = Number(dadosLote.PesoLiquido).toLocaleString("pt-BR");
      this.acondicionamento = dadosLote.Acondicionamento;
      this.safra = dadosLote.Safra;
      this.codOrdemServicoClassificacaoConab =
        dadosLote.CodOrdemServicoClassificacaoConab;
      this.codLote = dadosLote.CodLote;
    },
    abreModalVisualizacao(dadosLote) {
      this.tituloModal = "Visualizando Lote";
      this.modoVisualizacao = true;
      this.exibe = true;
      this.carregaCampos(dadosLote);
    },
    abreModalAlteracao(dadosLote) {
      this.alteraInsere = 2;
      this.tituloModal = "Alterao de lote";
      this.modoVisualizacao = false;
      this.exibe = true;
      this.carregaCampos(dadosLote);
    },
    fechaModal() {
      this.termoBuscaDestino = "";
      this.codMunicipioSelecionado = "";
      this.cdaDestino = "";
      this.atr = "";
      this.numeroLote = "";
      this.pesoLiquido = "";
      this.acondicionamento = "";
      this.safra = "";
      this.codOrdemServicoClassificacaoConab = "";
      this.exibe = false;
    },
    cancelarCadastroDeLote() {
      if (this.modoVisualizacao != true) {
        this.$q
          .dialog({
            title: "Confirmao",
            message: "Deseja realmente cancelar a abertura do lote?",
            color: "negative",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => {
            this.fechaModal();
          })
          .catch(() => {});
      } else {
        this.fechaModal();
      }
    },
    classificadorSelecionado(classificador) {
      this.termoBuscaDestino = classificador.label;
      this.codClassificadorSelecionado = classificador.value;
    },
    async cadastrarLote() {
      const fomularioValido = await this.validaFormulario();
      if (fomularioValido) {
        const lote = {};
        lote.CodOrdemServicoClassificacaoConab = this.codOrdemServicoClassificacaoConab;
        lote.CodMunicipio = this.codMunicipioSelecionado;
        lote.NumeroLote = this.numeroLote;
        if (this.pesoLiquido !== undefined && this.pesoLiquido !== "") {
          let pesoLiquido = this.pesoLiquido.replace(/\./g, "");
          pesoLiquido = pesoLiquido.replace(/,/g, "");
          lote.PesoLiquido = pesoLiquido;
        }
        lote.Acondicionamento = this.acondicionamento;
        lote.Safra = this.safra;
        lote.ATR = this.atr;
        lote.CDADestino = this.cdaDestino;
        // eslint-disable-next-line no-console
        // console.log(JSON.stringify(lote));
        post("loteconab", lote)
          .then((result) => {
            const resposta = JSON.parse(result);
            if (resposta.Status == true) {
              this.mensagemRetorno = resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.exibe = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const erro = JSON.parse(err);
            this.mensagemRetorno = erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(erro.Erro);
          });
      }
    },
    async alterarLoteConab() {
      const fomularioValido = await this.validaFormulario();
      if (fomularioValido) {
        this.exibeMensagemRetorno = false;
        const lote = {};
        lote.CodOrdemServicoClassificacaoConab = this.codOrdemServicoClassificacaoConab;
        lote.CodMunicipio = this.codMunicipioSelecionado;
        lote.NumeroLote = this.numeroLote;
        if (this.pesoLiquido !== undefined && this.pesoLiquido !== "") {
          let pesoLiquido = this.pesoLiquido.replace(/\./g, "");
          pesoLiquido = pesoLiquido.replace(/,/g, "");
          lote.PesoLiquido = pesoLiquido;
        }
        lote.Acondicionamento = this.acondicionamento;
        lote.Safra = this.safra;
        lote.ATR = this.atr;
        lote.CDADestino = this.cdaDestino;
        lote.CodLote = this.codLote;
        // eslint-disable-next-line no-console
        // console.log(JSON.stringify(lote));
        putComCabecalho("loteconab", "CodLote", this.codLote, lote)
          .then((result) => {
            const resposta = JSON.parse(result);
            if (resposta.Status == true) {
              this.mensagemRetorno = resposta.msg;
              this.tipoMensagem = "positive";
              this.exibeMensagemRetorno = true;
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.exibe = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = resposta.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
            }
          })
          .catch((err) => {
            const erro = JSON.parse(err);
            this.mensagemRetorno = erro.msg;
            this.tipoMensagem = "negative";
            this.exibeMensagemRetorno = true;
            // eslint-disable-next-line no-console
            console.error(erro.Erro);
          });
      }
    },
    comportamentoBotaoSalvar() {
      if (this.alteraInsere == 1) {
        return this.cadastrarLote();
      } else {
        return this.alterarLoteConab();
      }
    },
    buscaClassificador(terms, done) {
      setTimeout(() => {
        done(
          // eslint-disable-next-line no-undef
          filter(this.termoBuscaCliente, {
            field: "label",
            list: this.clientes,
          })
        );
      }, 100);
    },
    buscaDestino(terms, done) {
      get(`municipios?filter=${this.termoBuscaDestino}`)
        .then((result) => {
          let jsonMunicipios = result;
          jsonMunicipios = jsonMunicipios.replace(
            /"CodMunicipio":/g,
            '"value":'
          );
          jsonMunicipios = jsonMunicipios.replace(
            /"NomeMunicipio":/g,
            '"label":'
          );
          jsonMunicipios = jsonMunicipios.replace(/"UF":/g, '"stamp":');
          const municipios = JSON.parse(jsonMunicipios);
          done(municipios);
        })
        .catch(() => {
          done([]);
        });
    },
    destinoSelecionado(destino) {
      this.termoBuscaDestino = destino.label;
      this.codMunicipioSelecionado = destino.value;
    },
    validaFormulario() {
      return new Promise((resolve) => {
        this.erroMunicipio = false;
        this.erroCDADestino = false;
        this.erroATR = false;
        this.erroNumeroLote = false;
        this.erroPesoLiquido = false;
        this.erroSafra = false;
        if (
          this.codMunicipioSelecionado === undefined ||
          this.codMunicipioSelecionado === ""
        ) {
          this.termoBuscaDestino = "";
          this.erroMunicipio = true;
        }
        if (this.cdaDestino === undefined || this.cdaDestino === "") {
          this.erroCDADestino = true;
        }
        if (this.atr === undefined || this.atr === "") {
          this.erroATR = true;
        }
        if (this.numeroLote === undefined || this.numeroLote === "") {
          this.erroNumeroLote = true;
        }
        if (this.pesoLiquido === undefined || this.pesoLiquido === "") {
          this.erroPesoLiquido = true;
        }
        if (this.safra === undefined || this.safra === "") {
          this.erroSafra = true;
        }
        if (
          this.erroMunicipio === false &&
          this.erroCDADestino === false &&
          this.erroATR === false &&
          this.erroNumeroLote === false &&
          this.erroPesoLiquido === false &&
          this.erroSafra === false
        ) {
          resolve(true);
        } else {
          resolve(false);
        }
      });
    },
  },
};
</script>

<style>
.tira-margin-pading {
  padding: 0;
  margin: 0;
}
.margin-form {
  padding: 5px;
}
@media screen and (max-width: 768px) {
  .margin-form {
    padding: 0px;
  }
  .lagura-modal {
    width: 70px;
  }
}
</style>
