<template>
  <q-dialog
    v-model="exibe"
    @ok="onOk"
    @cancel="onCancel"
    @show="onShow"
    @hide="onHide"
    style="minHeight:15vw"
  >
    <!-- This or use "title" prop on <q-dialog> -->
    <span slot="title">Alterar classificador</span>

    <!-- This or use "message" prop on <q-dialog> -->
    <span slot="message">Informe o classificador</span>

    <div slot="body">
      <q-field>
        <q-input
          v-model="termoBuscaClassificador"
          placeholder="Escolha um classificador"
          float-label="Escolha um classificador"
          :label-width="3"
        >
          <q-autocomplete
            :static-data="{field:'label', list:classificadores}"
            @selected="classificadorSelecionado"
          />
        </q-input>
      </q-field>
    </div>

    <template slot="buttons" slot-scope="props">
      <q-btn color="positive" label="Alterar" @click="choose(props.ok)" />
      <q-btn flat label="Cancelar" @click="props.cancel" />
    </template>
  </q-dialog>
</template>

<script>
import { get, post } from "./js/ConexaoApi";
export default {
  data() {
    return {
      // model for Dialog example
      exibe: false,
      codItemOrdemServicoClassificacao: "",
      classificadores: [],
      termoBuscaClassificador: "",
      codClassificadorSelecionado: "",
    };
  },
  methods: {
    // when props.ok() gets called
    onOk() {
      
    },

    // when props.cancel() gets called
    onCancel() {
      this.codClassificadorSelecionado = "";
      this.codItemOrdemServicoClassificacao = "";
      this.termoBuscaClassificador = "";
      this.exibe = false;
    },
    abreDialog(codItemOrdemServicoClassificacao) {
      this.codItemOrdemServicoClassificacao = codItemOrdemServicoClassificacao;
      this.onShow();
    },
    // when we show it to the user
    onShow() {
      this.exibe = true;
      get("classificadores")
        .then((result) => {
          result = result.replace(/"id":/g, '"value":');
          result = result.replace(/"nome":/g, '"label":');
          this.classificadores = JSON.parse(result);
        })
        .catch(() => {
          
        });
    },
    classificadorSelecionado(classificador) {
      this.termoBuscaClassificador = classificador.label;
      this.codClassificadorSelecionado = classificador.value;
    },
    // when it gets hidden
    onHide() {
      this.codClassificadorSelecionado = "";
      this.codItemOrdemServicoClassificacao = "";
      this.termoBuscaClassificador = "";
      this.exibe = false;
    },

    // custom handler
    async choose(okFn) {
      if (this.codClassificadorSelecionado == "") {
        this.$q.dialog({
          title: "Erro",
          message: `Por favor, informe um classificador`,
        });
      } else {
        const objeto = {}
        objeto.CodItemOrdemServicoClassificacao = this.codItemOrdemServicoClassificacao;
        objeto.CodClassificador = this.codClassificadorSelecionado;
        post("alteraclassificador", objeto)
          .then(async (result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.$emit("atualizaTabela");
              this.$q.notify({
                message: Resposta.msg,
                timeout: 3000,
                type: "positive",
                color: "positive",
              });
              await okFn();
            } else {
              this.$q.notify({
                message: Resposta.msg,
                timeout: 3000,
                type: "negative",
                color: "negative",
              });
            }
          })
          .catch(() => {
            this.$q.notify({
              message:
                "Falha ao alterar classificador, por favor, contate o suporte",
              timeout: 3000,
              type: "negative",
              color: "negative",
            });
          });
      }
    },
  },
};
</script>