<template>
  <q-layout v-show="exibe" class="teste">
    <div class="responsivo">
      <div class="centraliza-titulo margin-titulo">
        Classificaes realizadas
      </div>
      <div class="tamanho-tabela teste">
        <q-table
          :data="itensOrdemServico"
          :columns="colunasTabela"
          row-key="CodItemOrdemServicoClassificacao"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          :selected="linhaSelecionada"
          :pagination.sync="padraoLinhasPorPagina"
          :rows-per-page-options="opcoesLinhasPorPagina"
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>
          <template slot="top-right">
            <q-btn
              color="primary"
              icon="description"
              label="Relatrio de embarue"
              @click="abreRelatorio()"
            />
          </template>
          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td key="DataClassificacao" :props="props">
                {{ props.row.DataClassificacao }}
              </q-td>
              <q-td key="NumeroLaudo" :props="props">
                {{ props.row.NumeroLaudo }}
              </q-td>
              <q-td key="Placa" :props="props">
                {{ props.row.PlacaCavalo }}
              </q-td>
              <q-td key="PesoLiquido" :props="props">
                {{ props.row.PesoLiquido }}
              </q-td>
              <q-td key="NomeClassificador" :props="props">
                {{ props.row.NomeClassificador }}
              </q-td>
              <q-td key="Transportadora" :props="props">
                {{ props.row.Transportadora }}
              </q-td>
              <q-td key="Enviados" :props="props">
                <div :class="props.row.ArquivosEnviadosPeloCliente > 0 ? 'tem-arquivo' : 'nao-tem-arquivo'">
                  {{ props.row.ArquivosEnviadosPeloCliente }}
                  <q-tooltip>
                    Voc enviou {{props.row.ArquivosEnviadosPeloCliente}} arquivos
                  </q-tooltip>
                </div>
              </q-td>
              <q-td key="Recebidos" :props="props">
                <div :class="props.row.ArquivosEnviadosPeloClassificador > 0 ? 'tem-arquivo' : 'nao-tem-arquivo'">
                  {{ props.row.ArquivosEnviadosPeloClassificador }}
                  <q-tooltip>
                    O classificador enviou {{props.row.ArquivosEnviadosPeloClassificador}} arquivos
                  </q-tooltip>
                </div>
              </q-td>
              <q-td key="acoes" :props="props" style="width: 15px">
                <q-btn
                  icon="fas fa-file-pdf"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  @click="abrePdf(props.row.UUID)"
                  v-if="codClienteAlfameta != 6 && codClienteAlfameta !== 7"
                >
                  <q-tooltip
                    >Exibir PDF da classificao
                    {{ props.row.NumeroLaudo }}</q-tooltip
                  >
                </q-btn>
                <q-btn
                  icon="visibility"
                  dense
                  :props="props"
                  flat
                  color="info"
                  @click="visualizaClassificacao(props.row)"
                >
                  <q-tooltip>Visualizar dados da classificao</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
        </q-table>
      </div>
    </div>
    <ModalNovaClassificacao
      ref="modalNovaClassificacao"
      @atualizaTabela="carregaTabela"
    />
  </q-layout>
</template>


<script>
import { getComCabecalho } from "../../AreaCliente/js/ConexaoApiCliente.js";
import ModalNovaClassificacao from "@/components/AreaCliente/components/ModalVisualizacaoDadosClassificacaoCliente.vue";
import { detectaCliente } from "../../utils/DetectaCliente.js";
export default {
  name: "gradeItensOS",
  components: {
    ModalNovaClassificacao,
  },
  data() {
    return {
      termoBusca: "",
      padraoLinhasPorPagina: { rowsPerPage: 5 },
      opcoesLinhasPorPagina: [5],
      codOrdemServicoClassificacao: "",
      umidade: "",
      impureza: "",
      ardidosEQueimados: "",
      queimados: "",
      fermentados: "",
      ardidos: "",
      partidos: "",
      esverdeados: "",
      picados: "",
      carunchados: "",
      total: "",
      transportadora: "",
      exibe: true,
      tituloModal: "",
      modoVisualizacao: false,
      tipoProdutoSoja: [
        { value: 1, label: "Convencional" },
        { value: 2, label: "Transgnico" },
      ],
      tipoProdutoSojaSelecionado: "",
      tipoProdutoMilho: [
        { value: 5, label: "Intacta Declarada" },
        { value: 6, label: "Participante" },
      ],
      tipoProdutoMilhoSelecionado: "",
      testesMilho: [
        { value: 1, label: "Teste Aflatoxina - Positivo" },
        { value: 2, label: "Teste Aflatoxina - Negativo" },
        { value: 3, label: "No testado" },
      ],
      testeMilhoSelecinado: "",
      testesSoja: [
        { value: 4, label: "Testada Positiva" },
        { value: 5, label: "Testada Negativa" },
        { value: 4, label: "No Testada" },
      ],
      testeSojaSelecionado: "",
      todosDadosCarregados: false,
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      pesoLiquido: "",
      placaCavalo: "",
      placa1: "",
      placa2: "",
      codProduto: "",
      codMunicipio: "",
      codClassificador: "",
      statusCarregamentoTabela: true,
      colunasTabela: [
        {
          name: "DataClassificacao",
          label: "Data",
          field: "DataClassificacao",
          align: "center",
          sortable: true,
        },
        {
          name: "NumeroLaudo",
          label: "N Laudo",
          field: "NumeroLaudo",
          align: "center",
          sortable: true,
        },
        {
          name: "Placa",
          label: "Placa",
          field: "PlacaCavalo",
          align: "center",
          sortable: true,
        },
        {
          name: "PesoLiquido",
          label: "Peso Liquido(Kg)",
          field: "PesoLiquido",
          align: "center",
          sortable: true,
        },
        {
          name: "NomeClassificador",
          label: "Classificador",
          field: "NomeClassificador",
          align: "center",
          sortable: true,
        },
        {
          name: "Transportadora",
          label: "Transportadora",
          field: "Transportadora",
          align: "center",
          sortable: true,
        },
        {
          name: "Enviados",
          label: "",
          field: "ArquivosEnviadosPeloCliente",
          align: "center",
          sortable: true,
        },
        {
          name: "Recebidos",
          label: "",
          field: "ArquivosEnviadosPeloClassificador",
          align: "center",
          sortable: true,
        },
        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
          align: "center",
          sortable: false,
        },
      ],
      //Array com os dados da tabela
      itensOrdemServico: [],
      linhaSelecionada: [],
      administrador: false,
      codClienteAlfameta: "",
    };
  },
  methods: {
    carregaTabela() {
      getComCabecalho(
        "listaitensoscliente",
        "CodOs",
        this.codOrdemServicoClassificacao
      )
        .then((result) => {
          this.itensOrdemServico = JSON.parse(result);
          this.statusCarregamentoTabela = false;
        })
        .catch(() => {
          this.statusCarregamentoTabela = false;
        });
    },
    visualizaClassificacao(dadosOs) {
      this.$refs.modalNovaClassificacao.abreModalVisualizacao(dadosOs);
    },
    fechaTabela() {
      this.exibe = false;
    },
    abreRelatorio() {
      this.$router.push({
        name: "RelatorioEmbarque",
        params: { itens: this.itensOrdemServico },
      });
    },
    pegaCodOs(codOS) {
      this.exibe = true;
      this.codOrdemServicoClassificacao = codOS;
      this.carregaTabela();
    },
    pegaUrlQrCode() {
      detectaCliente()
        .then((result) => {
          this.urlQrCode = result.UrlQRCode;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    abrePdf(uuid) {
      window.open(this.urlQrCode + uuid, "_blank");
    },
  },
  created() {
    this.pegaUrlQrCode();
  },
};
</script>

<style scoped>
.tem-arquivo{
  text-align: center;
  background-color: green;
  color: white;
  width: 20px;
  height: 20px;
  padding-top: 2px;
  padding-right: 1px;
  border-radius: 50px;
  box-shadow: 2px 2px 2px rgba(112, 112, 112, 0.77)
}
.nao-tem-arquivo{
  text-align: center;
  background-color: grey;
  color: white;
  width: 20px;
  height: 20px;
  padding-top: 2px;
  padding-right: 1px;
  border-radius: 50px;
  box-shadow: 2px 2px 2px rgba(112, 112, 112, 0.77)
}
.q-layout {
  min-height: 0;
  margin-bottom: 95px;
}
.centraliza-titulo {
  text-align: center;
}
.margin-titulo {
  margin-top: 10px;
  margin-bottom: 10px;
}
.titulo-dependentes {
  font-size: 14px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.tamanho-coluna-id {
  width: 5px;
}
.responsivo {
  width: 98%;
  margin: 10px;
}
.distancia-topo {
  margin-top: 15px;
}
.nome-usuario {
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
</style>

