        <div class="col-12 margin-interna-campos">Transportadora: {{transportadora}}</div>
      </div>
      <div class="col-12 row distancia-divs-1">
        <div class="col-4 divPesoAmostra centraliza-texto">PESO LQUIDO</div>
        <div class="col-4 divPesoAmostra centraliza-texto">PESO AMOSTRA TRABALHADA</div>
        <div class="col-4 divProduto centraliza-texto">PRODUTO</div>
        <div class="col-4 divQuantidadePesoAmostra centraliza-texto remove-borda-superior">{{pesoLiquido}}</div>
        <div class="col-4 divQuantidadePesoAmostra centraliza-texto remove-borda-superior">{{pesoTrabalhado}}</div>
        <div class="col-4 divProdutoEspecificado centraliza-texto remove-borda-superior" v-if="codProduto == 1">Soja (Em Gros)</div>
        <div class="col-4 divProdutoEspecificado centraliza-texto remove-borda-superior" v-else>Milho (Em Gros)</div>
      </div>
      <div class="col-12 row distancia-divs-1" v-if="codProduto == 1">
        <div
          class="col-12 borda-cabecalho-classificacao centraliza-texto titulo-qualidades"
        >QUALIDADE DO PRODUTO SOJA</div>
        <table cellspacing="0" class="col-12">
          <thead>
            <tr>
              <th rowspan="2">14% UMIDADE</th>
              <th rowspan="2">1% MATERIAIS ESTRANHOS IMPUREZAS</th>
              <th rowspan="1" colspan="5">AVARIADOS</th>
              <th rowspan="2">8% ESVERDEADOS</th>
              <th rowspan="2">30% PARTIDOS QUEBRADOS AMASSADOS</th>
            </tr>
            <th>1% QUEIMADOS</th>
            <th>4% ARDIDOS</th>
            <th>FERMENTADOS CHOCHOS IMATUROS GERMINADOS</th>
            <th>PICADOS</th>
            <th>8% TOTAL(*)</th>
            <tr></tr>
          </thead>
          <tbody>
            <td class="borda-rodape-esquerdo-tabela">{{umidade}}</td>
            <td>{{impureza}}</td>
            <td>{{queimados}}</td>
            <td>{{ardidos}}</td>
            <td>{{fermentados}}</td>
            <td>{{picados}}</td>
            <td>{{total}}</td>
            <td>{{esverdeados}}</td>
            <td class="borda-rodape-direito-tabela">{{quebrados}}</td>
          </tbody>
        </table>
        <div class="col-12 borda distancia-divs-2">
          <p
            class="aviso-total"
          >(*)TOTAL = Soma de queimados, ardidos, mofados, fermentados, germinados, danificados, imaturos e chochos.</p>
        </div>
      </div>
      <div class="col-12 row distancia-divs-0" v-else>
        <div
          class="col-12 borda-cabecalho-classificacao centraliza-texto titulo-qualidades"
        >QUALIDADE DO PRODUTO MILHO</div>
        <table cellspacing="0" class="col-12">
          <thead>
            <tr>
              <th rowspan="2">14% UMIDADE</th>
              <th rowspan="2">1% MATERIAIS ESTRANHOS IMPUREZAS</th>
              <th rowspan="1" colspan="3">AVARIADOS</th>
              <th rowspan="2">2% CARUNCHADOS</th>
              <th rowspan="2">3% PARTIDOS QUEBRADOS</th>
            </tr>
            <th>1% ARDIDOS E QUEIMADOS</th>
            <th>FERMENTADOS CHOCHOS IMATUROS GERMINADOS</th>
            <th>5% TOTAL(*)</th>
            <tr></tr>
          </thead>
          <tbody>
            <td class="borda-rodape-esquerdo-tabela">{{umidade}}</td>
            <td>{{impureza}}</td>
            <td>{{ardidosEQueimados}}</td>
            <td>{{fermentados}}</td>
            <td>{{total}}</td>
            <td>{{carunchados}}</td>
            <td class="borda-rodape-direito-tabela">{{quebrados}}</td>
          </tbody>
        </table>
      </div>
      <div class="col-12 row borda distancia distancia-interna-bordas distancia-divs-1">
        <div class="col-5 testes-aflatoxina" v-if="codProduto == 2">
          MILHO:
          <q-checkbox v-model="aflatoxinaNegativo" val="true" label="Negativo" :disable="true" />
          <q-checkbox v-model="aflatoxinaPositivo" val="true" label="Positivo" :disable="true" />
        </div>
        <div class="col-6 testes-aflatoxina" v-if="codProduto == 1">
          SOJA INTACTA:
          <q-checkbox v-model="sojaNegativo" val="true" label="Negativo" :disable="true" />
          <q-checkbox v-model="sojaPositivo" val="true" label="Positivo" :disable="true" />
          <q-checkbox v-model="sojaDeclarado" val="true" label="Declarado" :disable="true" />
        </div>
        <div class="col-12 margin-interna-campos">OBS: {{observacao}}</div>
        <div class="col-4 margin-interna-campos">Data: {{data}}</div>
        <div class="col-6 margin-interna-campos">Nome do classificador: {{nomeClassificador}}</div>
      </div>
      <div class="col-12 margin-interna-campos fonte-url-classificacao">
        Classificao disponivel online em:
        <a :href="urlClassificacao">{{urlClassificacao}}</a>
      </div>
      <div class="centraliza-texto col-12 distancia-botoes-acao">
        <qrcode-vue :value="urlClassificacao" :size="200" level="H"/>
      </div>
    </div>
    <div
      class="text-center distancia-botoes-acao fonte-explicacao-download esconde-botao-impressao"
    >Faa o download em PDF de todos os dados da classificao {{numeroLaudo}} clicando no boto abaixo.</div>
    <div class="row col-12 justify-center distancia-botoes-acao">
      <q-btn
        icon="fas fa-file-pdf"
        label="Baixar PDF"
        @click="downloadPDF()"
        class="distancia-botoes esconde-botao-impressao cor-pdf col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
      <q-btn
        icon="fas fa-print"
        color="primary"
        label="Imprimir"
        @click="imprimirPagina()"
        class="distancia-botoes esconde-botao-impressao col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
    </div>
  </div>
</template>


<script>
// eslint-disable-next-line no-unused-vars
import jsPDF from "jspdf";
// eslint-disable-next-line no-unused-vars
import html2canvas from "html2canvas";
import html2pdf from "js-html2pdf";
import QrcodeVue from 'qrcode.vue';
export default {
  data() {
    return {
      codProduto: 1,
      logo: require("../assets/logo-exata.png"),
      nomeCliente: "",
      municipioOrigem: "",
      localEmbarque: "",
      placaCavalo: "",
      placa1: "",
      placa2: "",
      transportadora: "",
      pesoTrabalhado: "",
      pesoLiquido: "",
      nomeProduto: "",
      umidade: "",
      impureza: "",
      queimados: "",
      ardidos: "",
      fermentados: "",
      picados: "",
      total: "",
      esverdeados: "",
      quebrados: "",
      ardidosEQueimados: "",
      carunchados: "",
      aflatoxinaNegativo: false,
      aflatoxinaPositivo: false,
      sojaNegativo: false,
      sojaPositivo: false,
      sojaDeclarado: false,
      observacao: "",
      data: "",
      nomeClassificador: "",
      numeroLaudo: "",
      ufOrigem: "",
      ufDestino: "",
      tipoProduto: "",
      municipioDestino: "",
      urlClassificacao: "",
      numeroContrato: "",
      exibeLayout: false,
      layoutCelular: false,
      cnpjParticipante: "",
      codOrdemServicoClassificacao: "",
    };
  },
  methods: {

    carregaDados(dadosClassificacao) {
      this.codProduto = dadosClassificacao.CodProduto;
      this.nomeCliente = dadosClassificacao.NomeCliente;
      this.numeroLaudo = dadosClassificacao.NumeroLaudo;
      this.localEmbarque = dadosClassificacao.LocalEmbarque;
      this.municipioDestino = dadosClassificacao.MunicipioDestino;
      this.pesoTrabalhado = dadosClassificacao.PesoAmostraTrabalhada;
      this.pesoLiquido = dadosClassificacao.PesoLiquido;
      this.ufDestino = dadosClassificacao.UFDestino;
      this.ufOrigem = dadosClassificacao.UFOrigem;
      this.umidade = dadosClassificacao.Umidade;
      this.impureza = dadosClassificacao.Impureza;
      this.fermentados = dadosClassificacao.FermentadoChochoImaturoGerminado;
      this.total = dadosClassificacao.TotalAvariado;
      this.transportadora = dadosClassificacao.Transportadora;
      this.placaCavalo = dadosClassificacao.PlacaCavalo;
      this.placa1 = dadosClassificacao.Placa1;
      this.placa2 = dadosClassificacao.Placa2;
      this.observacao = dadosClassificacao.Observacao;
      this.data = dadosClassificacao.DataClassificacao;
      this.nomeClassificador = dadosClassificacao.nome;
      this.tipoProduto = dadosClassificacao.TipoProduto;
      this.municipioOrigem = dadosClassificacao.MunicipioOrigem;
      this.numeroContrato = dadosClassificacao.NumeroContrato;
      this.cnpjParticipante = dadosClassificacao.CNPJ;
      this.codOrdemServicoClassificacao = dadosClassificacao.CodOrdemServicoClassificacao;
      if (this.codProduto == 1) {
        this.esverdeados = dadosClassificacao.Esverdeados;
        this.queimados = dadosClassificacao.Queimados;
        this.ardidos = dadosClassificacao.Ardidos;
        this.picados = dadosClassificacao.Picados;
        if (this.tipoProduto == 9) {
          this.sojaPositivo = true;
        } else {
          this.sojaPositivo = false;
        }
        if (this.tipoProduto == 10) {
          this.sojaNegativo = true;
        } else {
          this.sojaNegativo = false;
        }
        if (this.tipoProduto == 7) {
          this.sojaDeclarado = true;
        } else {
          this.sojaDeclarado = false;
        }
      } else {
        this.ardidosEQueimados = dadosClassificacao.ArdidosEQueimados;
        this.carunchados = dadosClassificacao.Carunchados;
        if (this.tipoProduto == 4) {
          this.aflatoxinaNegativo = true;
        } else {
          this.aflatoxinaNegativo = false;
        }
        if (this.tipoProduto == 3) {
          this.aflatoxinaPositivo = true;
        } else {
          this.aflatoxinaPositivo = false;
        }
      }
      this.quebrados = dadosClassificacao.PartidoQuebradoAmassado;
    },
    //Gera o PDF e inicia o Download
    downloadPDF() {
      this.exibeLayout = true;
      setTimeout(() => {
        const elementoASerImprimido = document.getElementById("content");
        const options = {
          filename: "Classificacao" + this.numeroLaudo + ".pdf",
          margin: 0,
          enableLinks: true,
          image: "png",
        };
        const exporter = new html2pdf(elementoASerImprimido, options);
        exporter
          .getPdf(true)
          .then(() => {
            this.exibeLayout = false;
          })
          .catch(() => {
            this.exibeLayout = false;
          });
        this.exibeLayout = false;
      }, 500);
    },
    imprimirPagina() {
      this.exibeLayout = true;
      setTimeout(() => {
        window.print();
      }, 1500);

      setTimeout(() => {
        this.exibeLayout = false;
      }, 3000);
    },
    detectaPlataforma() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = false;
        this.layoutCelular = true;
      } else {
        this.layoutCelular = false;
        this.exibeLayout = true;
      }
    },
    formataCnpj(cnpj){
      if(typeof (cnpj) == "undefined" || cnpj == null){
        return "";
      }
      //Remove espaos em branco
      cnpj = cnpj.trim();
      //Remove caracteres invlidos do CNPJ
      cnpj = cnpj.replace("/[!\"#$%&'()*+,-./:;<=>?@[\\]^_`{|}~]/g", "");
      if(cnpj.length === 14){
        return cnpj.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g,"$1.$2.$3/$4-$5");
      }else{
        return cnpj.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})/,"$1.$2.$3-$4");
      }
    },
    getSojaIntacta(tipoProduto){
      // eslint-disable-next-line no-console
      console.log(this.codProduto);
      // eslint-disable-next-line no-console
      console.log(this.tipoProduto);
      if (this.codProduto == 1){ //SOJA
        switch (tipoProduto) {
          case 1:
            return "Convencional";
          case 2:
            return "Transgnico";
          case 7:
            return "Intacta Declarada";
          case 8:
            return "Participante";
          case 9:
            return "Testada Positiva";
          case 10:
            return "Testada Negativa";
          case 11:
            return "No testada";
          case 12:
            return "RR2";
          default:
            return "";
        }
      }
      else if(this.codProduto == 2){ //MILHO
        switch (tipoProduto) {
          case 1:
            return "Convencional";
          case 2:
            return "Transgnico";
          case 3:
            return "Aflatoxina - Positiva";
          case 4:
            return "Aflatoxina - Negativa";
          case 5:
            return "No testado";
          case 12:
            return "RR2";
          default:
            return "";
        }
      } else{
        return ""
      }
    },
  },
  components:{
    QrcodeVue
  },
  beforeMount() {
    this.urlClassificacao = window.location.href;
    this.detectaPlataforma();
  },
};
</script>

