<template>
<div>
  <div
    @click="abreArquivo(arquivo.CaminhoArquivo)"
    :class="arquivo.Liberado === 'S' ? 'borda-btn-arquivo-aprovado cursor tamanho-fonte-arquivos tamanho-btn-arquivo margin-btn-arquivos alinha-texto-centro borda-botao div-arquivos' : 'cursor tamanho-fonte-arquivos tamanho-btn-arquivo margin-btn-arquivos alinha-texto-centro borda-botao div-arquivos'"
    :v-if="arquivo.EnviadoPor == 'CLI'"
  >
    <img :src="icone" class="centraliza-icone-meio" />
    <div class="tipo-arquivo">
      <span>{{arquivo.TipoDocumento}}</span>
    </div>
  </div>
  <div v-if="arquivo.EnviadoPor === 'CLI' && arquivo.Liberado === 'A'" class="row aceita-rejeita-arquivo borda-div-libera-rejeita-arquivo">
    <div class="col-6 cursor" style="background-color: green; text-align:center;" @click="alteraStatusArquivo('S')">
      <q-tooltip>Aprovar {{arquivo.TipoDocumento}}</q-tooltip>
      <q-icon name="check" color="white"/>
    </div>
    <div class="col-6 cursor" style="background-color: red; text-align:center;" @click="alteraStatusArquivo('N')">
      <q-tooltip>Remover {{arquivo.TipoDocumento}}</q-tooltip>
      <q-icon name="delete" color="white"/>
    </div>
  </div>
</div>
  
</template>

<script>
import { patchCliente } from '../components/AreaCliente/js/ConexaoApiCliente';
export default {
  props: {'arquivoProp' : {}},
  data(){
    return{
      arquivo: {},
      iconePdf: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/pdf.png",
      iconeJpg: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/jpg.png",
      iconeFile: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/file.png",
      iconeDoc: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/doc.png",
      iconeTxt: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/txt.png",
      iconePng: "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/png.png",
      icone: '',
    }
  },
  methods:{
    abreArquivo(url){
      window.open(url, "_blank");
    },
    alteraStatusArquivo(status){
      const arquivo = {};
      arquivo.id = this.arquivo.id;
      arquivo.Liberado = status;
      patchCliente('alterastatusarquivo', arquivo).then((result) => {
        this.$emit("arquivoLiberado");
        const resposta = JSON.parse(result);
        this.arquivo.Liberado = status;
        this.$q.notify({
          message: resposta.msg,
          timeout: 3000,
          type: "positive",
          color: "postive",
          textColor: "white",
          position: "top",
        });
      }).catch(err => {
        this.$emit("arquivoLiberado");
        // eslint-disable-next-line no-console
        console.error(err);
        this.$q.notify({
          message: `Falha ao remover ${this.arquivo.TipoDocumento}, por favor, contate o suporte!`,
          timeout: 3000,
          type: "negative",
          color: "negative",
          textColor: "white",
          position: "top",
        });
      })
    }
  },
  created(){
    this.arquivo = this.$props.arquivoProp;
    if(this.arquivo.NomeOriginal.toLowerCase().includes('.txt')){
      this.icone = this.iconeTxt;
    }else if (this.arquivo.NomeOriginal.toLowerCase().includes('.jpg') || this.arquivo.NomeOriginal.toLowerCase().includes('.jpeg')){
      this.icone = this.iconeJpg;
    }else if (this.arquivo.NomeOriginal.toLowerCase().includes('.png') || this.arquivo.NomeOriginal.toLowerCase().includes('.PNG')){
      this.icone = this.iconePng
    }else if (this.arquivo.NomeOriginal.toLowerCase().includes('.pdf') || this.arquivo.NomeOriginal.toLowerCase().includes('.PDF')){
      this.icone = this.iconePdf
    }else if (this.arquivo.NomeOriginal.toLowerCase().includes('.doc') || this.arquivo.NomeOriginal.toLowerCase().includes('.docx')){
      this.icone = this.iconeDoc;
    }else{
      this.icone = this.iconeFile;
    }
  }
}
</script>

<style scoped>
.overflow-arquivos {
  overflow-x: auto;
  white-space: nowrap;
}
.tamanho-btn-arquivo {
  width: 150px;
  height: 100px;
}
.margin-btn-arquivos {
  margin: 2px;
}
.alinha-texto-centro {
  text-align: center;
}
.borda-botao {
  border-top: 1px solid black;
  border-right: 1px solid black;
  border-left: 1px solid black;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.borda-div-libera-rejeita-arquivo{
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  border-left: 1px solid black;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.borda-btn-arquivo-aprovado{
  border: 1px solid black;
  border-radius: 5px;
}
.cursor {
  cursor: pointer;
}
.centraliza-icone-meio{
  margin-top: 15px;
}
.aceita-rejeita-arquivo{
  height: auto;
  margin: 2px;
  margin-top: -2px;
}
.tipo-arquivo {
  position: absolute;
  height: auto;
  padding-top: 4px;
  width: 100%;
  background-color: #1b1f5f;
  color: #fff;
  bottom: 0;
  font-size: 14px;
}
.div-arquivos {
  position: relative;
}
.container-upload-arquivos {
  border: 1px solid black;
  border-radius: 5px;
  margin: 1px;
  padding: 10px;
}
.margin-campos-upload {
  margin-top: 15px;
}
</style>