<template>
  <q-layout style="margin-top:55px;">
    <div>
      <div class="centraliza-titulo margin-titulo">Solicitaces</div>
      <div>
        <q-table
          :data="solicitacoes"
          :columns="colunasTabela"
          row-key="CodOrdemServicoClassificacao"
          :loading="statusCarregamentoTabela"
          separator="cell"
          :filter="termoBusca"
          :rows-per-page-options="opcoesLinhasPorPagina"
          :selected="linhaSelecionada"
          :pagination.sync="padraoLinhasPorPagina"
        >
          <template slot="top-left">
            <q-search color="secondary" v-model="termoBusca" class="col-6" />
          </template>

          <template slot="body" slot-scope="props">
            <q-tr :props="props">
              <q-td
                key="CodSolicitacao"
                :props="props"
                class="tamanho-coluna-id"
              >{{ props.row.CodSolicitacao }}</q-td>
              <q-td key="Destino" :props="props">{{ props.row.Destino }}</q-td>
              <q-td key="LocalEmbarque" :props="props">{{ props.row.LocalEmbarque }}</q-td>
              <q-td key="NumeroContrato" :props="props">{{ props.row.NumeroContrato }}</q-td>
              <q-td key="DataSolicitacao" :props="props">{{ props.row.DataSolicitacaoFormatada }}</q-td>
              <q-td key="Status" :props="props" v-if="props.row.Status == 'A'">Aguardando</q-td>
              <q-td
                key="Status"
                :props="props"
                v-if="props.row.Status == 'O'"
              >O.S N{{props.row.CodOrdemServicoClassificacao}} Aberta</q-td>
              <q-td key="acoes" :props="props" style="width:15px">
                <q-btn
                  icon="add"
                  :props="props"
                  flat
                  dense
                  color="positive"
                  @click="abreOsApartirSolicitacao(props.row)"
                  v-if="props.row.Status != 'O'"
                >
                  <q-tooltip>Abrir OS para esta solicitao</q-tooltip>
                </q-btn>
                <q-btn
                  icon="visibility"
                  :props="props"
                  flat
                  dense
                  color="secondary"
                  @click="visualizaSolicitacao(props.row)"
                >
                  <q-tooltip>Visualizar solicitao</q-tooltip>
                </q-btn>
                <q-btn
                  icon="edit"
                  :props="props"
                  flat
                  dense
                  color="primary"
                  @click="alteraSolicitacao(props.row)"
                  v-if="props.row.Status != 'O'"
                >
                  <q-tooltip>Editar solicitao</q-tooltip>
                </q-btn>
                <q-btn
                  icon="ion-trash"
                  :props="props"
                  flat
                  dense
                  color="negative"
                  @click="excluiSolicitacao(props.row.CodSolicitacao)"
                  v-if="props.row.Status != 'O'"
                >
                  <q-tooltip>Excluir solicitao</q-tooltip>
                </q-btn>
              </q-td>
            </q-tr>
          </template>
        </q-table>
      </div>
    </div>
    <ModalAbreSolicitacao ref="modalSolicitacao" @atualizou="carregaTabela()" />
    <ModalAbreOs
      ref="modalAbreOs"
      :visualizando="sempreFalso"
      @atualizaSolicitacao="carregaTabela()"
    />
  </q-layout>
</template>

<script>
import { getComCabecalho } from "./js/ConexaoApi";
import { getCookie } from "./js/Cookies";
import ModalAbreSolicitacao from "./ModalSolicitacaoOs.vue";
import ModalAbreOs from "./ModalOrdemServico";
export default {
  components: {
    ModalAbreSolicitacao,
    ModalAbreOs,
  },
  data() {
    return {
      padraoLinhasPorPagina: { rowsPerPage: 10 },
      opcoesLinhasPorPagina: [5, 10, 15, 30, 50],
      exibe: false,
      termoBusca: "",
      sempreFalso: false,
      usuarios: [],
      alteraInsere: 1,
      statusCarregamentoTabela: true,
      solicitacoes: [],
      linhaSelecionada: [],
      codOrdemServicoClassificacao: "",
      codCliente: "",
      colunasTabela: [
        {
          name: "CodSolicitacao",
          label: "ID",
          field: "CodSolicitacao",
          align: "center",
          sortable: true,
        },
        {
          name: "Destino",
          label: "Destino",
          field: "Destino",
          align: "center",
          sortable: true,
        },
        {
          name: "LocalEmbarque",
          label: "Embarque",
          field: "LocalEmbarque",
          align: "center",
          sortable: true,
        },
        {
          name: "NumeroContrato",
          label: "Contrato",
          field: "NumeroContrato",
          align: "center",
          sortable: true,
        },
        {
          name: "DataSolicitacao",
          label: "Solicitado em",
          field: "DataSolicitacao",
          sortable: true,
          align: "center",
        },
        {
          name: "Status",
          label: "Status",
          field: "Status",
          sortable: true,
          align: "center",
        },
        {
          name: "acoes",
          label: "Aes",
          field: "acoes",
        },
      ],
    };
  },
  methods: {
    carregaTabela() {
      getComCabecalho("solicitacao")
        .then((result) => {
          this.solicitacoes = JSON.parse(result);
          this.statusCarregamentoTabela = false;
        })
        .catch(() => {
          this.statusCarregamentoTabela = false;
        });
    },
    abreOsApartirSolicitacao(dadosSolicitacao) {
      this.$refs.modalAbreOs.abreModalAberturaApartirSolicitacao(
        dadosSolicitacao
      );
    },
    excluiSolicitacao(CodSolicitacao) {
      this.$q
        .dialog({
          title: "Excluir solicitao",
          message: "Deseja realmente excluir a solicitao selecionada?",
          color: "negative",
          ok: "Sim",
          cancel: "No",
          position: "top",
          // eslint-disable-next-line
        })
        .then(() => {
          getComCabecalho("deletasolicitacao", "CodSolicitacao", CodSolicitacao)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "positive",
                  color: "positive",
                  textColor: "white",
                  position: "top",
                });
                this.carregaTabela();
              } else {
                this.$q.notify({
                  message: Resposta.msg,
                  timeout: 3000,
                  type: "negative",
                  color: "negative",
                  textColor: "white",
                  position: "top",
                });
              }
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              console.error(err);
              this.$q.notify({
                message:
                  "Falha ao excluir solicitao, por favor, contate o suporte",
                timeout: 3000,
                type: "negative",
                color: "negative",
                textColor: "white",
                position: "top",
              });
            });
          // eslint-disable-next-line
        })
        .catch(() => {});
    },
    abreModalSolicitacao() {
      this.$refs.modalSolicitacao.abreFechaModal();
    },
    alteraSolicitacao(solicitacao) {
      this.$refs.modalSolicitacao.abreModalAlteracao(solicitacao);
    },
    visualizaSolicitacao(solicitacao) {
      this.$refs.modalSolicitacao.abreModalVisualizacao(solicitacao);
    },
  },
  beforeMount() {
    this.codCliente = getCookie("codcliente");
  },
  mounted() {
    this.carregaTabela();
  },
};
</script>

