/* eslint-disable no-console */
<template>
  <q-modal
    v-model="exibe"
    minimized
    :content-css="{width: '100vw', minHeight: '95vh'}"
    no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn
          style="padding:0px; margin:0"
          flat
          round
          dense
          icon="close"
          @click="confirmarCancelamentoDeCadastroDeClassificacao"
        />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>
      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{tituloModal}}</h6>
      </q-toolbar>
      <div class="layout-padding">
        <div class="row">
          <div
            class="col-12 centraliza-titulo margin-top-laudo-classificacao-numero"
          >Laudo de classificao {{numeroLaudo}}</div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroPesoLiquido" error-label="Por favor, informe o peso lquido">
              <q-input
                v-model="pesoLiquido"
                placeholder="Peso Lquido"
                float-label="Peso Lquido"
                onkeypress="return event.charCode >= 48 && event.charCode <= 57"
                type="number"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroPlacaCavalo" error-label="Por favor, informe uma placa">
              <q-input
                v-model="placaCavalo"
                placeholder="Placa Cavalo"
                float-label="Placa Cavalo"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="placa1"
                placeholder="Placa 01"
                float-label="Placa 01"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="placa2"
                placeholder="Placa 02"
                float-label="Placa 02"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroTransportadora" error-label="Por favor, informe a transportadora">
              <q-input
                v-model="transportadora"
                placeholder="Transportadora"
                float-label="Transportadora"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroPesoAmostra" error-label="Por favor, informe o peso da amostra">
              <q-input
                v-model="pesoAmostraTrabalhada"

                placeholder="Peso Amostra"
                float-label="Peso Amostra"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div
            class="col-md-6 col-sm-12 col-xs-12 margin-form"
            v-if="codProduto != 5 && codProduto != 4 && codProduto != 3"
          >
            <q-field
              v-if="codProduto == 2"
              :error="erroTesteAflatoxina"
              error-label="Por favor, preencha corretamente esse campo"
            >
              <q-select
                filled
                v-model="tipoProdutoMilhoSelecionado"
                float-label="Teste Aflatoxina"
                filter
                :options="tipoProdutoMilho"
                :disable="modoVisualizacao == true"
                clearable
                @focus="validaCamposClassificacao"
              />
            </q-field>
            <q-field
              v-else-if="codProduto == 1"
              :error="erroSojaIntacta"
              error-label="Por favor, preencha corretamente esse campo"
            >
              <q-select
                filled
                v-model="tipoProdutoSojaSelecionado"
                float-label="Soja Intacta"
                filter
                :options="tipoProdutoSoja"
                :disable="modoVisualizacao == true"
                clearable
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div
            v-if="codProduto == 2 && clienteAlfameta.CodCliente == 3"
            class="col-md-6 col-sm-12 col-xs-12 margin-form"
          >
            <q-select
              filled
              v-model="tipoTesteOGMSelecionado"
              float-label="Teste OGM"
              filter
              :options="tipoTesteOGM"
              :disable="modoVisualizacao == true"
              clearable
            />
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroUmidade" error-label="Por favor, informe a umidade">
              <q-input
                v-model="umidade"
                placeholder="Umidade (14%)"
                float-label="Umidade (14%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
                v-if="codProduto != 5 && codProduto != 4"
              />
              <q-input
                v-model="umidade"
                placeholder="Umidade (13%)"
                float-label="Umidade (13%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
                v-else
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-if="codProduto == 3">
            <q-field :error="erroProteina" error-label="Por favor, informe a proteina">
              <q-input
                v-model="proteina"
                placeholder="Proteinas"
                float-label="Proteinas"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" v-else>
            <q-field :error="erroImpureza" error-label="Por favor, informe a impureza">
              <q-input
                v-model="impureza"
                placeholder="Materiais estranhos e impurezas (1%)"
                float-label="Materiais estranhos e impurezas (1%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div
            class="col-md-6 col-sm-12 col-xs-12 margin-form"
            v-if="codProduto != 5 && codProduto != 4 && codProduto != 3"
          >
            <q-field
              v-if="codProduto == 2"
              :error="erroArdidosEQueimados"
              error-label="Por favor, informe a porcentagem de Ardidos e Queimados"
            >
              <q-input
                v-model="ardidosEQueimados"
                placeholder="Ardidos e Queimados (1%)"
                float-label="Ardidos e Queimados (1%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
            <q-field v-else :error="erroQueimados" error-label="Por favor, informe os Queimados">
              <q-input
                v-model="queimados"
                placeholder="Queimados (1%)"
                float-label="Queimados (1%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>

          <div
            class="col-md-6 col-sm-12 col-xs-12 margin-form"
            v-if="codProduto != 5 && codProduto != 4 && codProduto != 3"
          >
            <q-field
              v-if="codProduto == 2"
              :error="erroFermentados"
              error-label="Por favor, informe os Fermentados"
            >
              <q-input
                v-model="fermentados"
                placeholder="Fermentados, chochos, imaturos, germinados"
                float-label="Ferment/Chocho/Imat/Germinado"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
            <q-field v-else :error="erroArdidos" error-label="Por favor, informe os Ardidos">
              <q-input
                v-model="ardidos"
                placeholder="Ardidos (4%)"
                float-label="Ardidos (4%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div
            class="col-md-6 col-sm-12 col-xs-12 margin-form"
            v-if="codProduto != 5 && codProduto != 4 && codProduto != 3"
          >
            <q-field
              v-if="codProduto == 2"
              :error="erroTotal"
              error-label="Por favor, informe o total"
            >
              <q-input
                v-model="total"
                placeholder="Total (5%)"
                float-label="Total (5%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
            <q-field
              v-else
              :error="erroFermentados"
              error-label="Por favor, informe os Fermentados"
            >
              <q-input
                v-model="fermentados"
                placeholder="Fermentados, chochos, imaturos, germinados"
                float-label="Ferment/Chocho/Imat/Germinado"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div
            class="col-md-6 col-sm-12 col-xs-12 margin-form"
            v-if="codProduto != 5 && codProduto != 4 && codProduto != 3"
          >
            <q-field
              v-if="codProduto == 2"
              :error="erroCarunchados"
              error-label="Por favor, informe os Carunchados"
            >
              <q-input
                v-model="carunchados"
                placeholder="Carunchados (2%)"
                float-label="Carunchados (2%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
            <q-field v-else :error="erroPicados" error-label="Por favor, informe os Picados">
              <q-input
                v-model="picados"
                placeholder="Picados (8%)"
                float-label="Picados (8%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 2" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroPartidos" error-label="Por favor, informe os Partidos">
              <q-input
                v-model="partidos"
                placeholder="Partidos/quebrados (3%)"
                float-label="Partidos/quebrados (3%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div
            v-else-if="codProduto == 1 || codProduto == 2"
            class="col-md-6 col-sm-12 col-xs-12 margin-form"
          >
            <q-field :error="erroTotal" error-label="Por favor, informe o Total">
              <q-input
                v-model="total"
                placeholder="Total (8%)"
                float-label="Total (8%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 1" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroPartidos" error-label="Por favor, informe os Partidos">
              <q-input
                v-model="partidos"
                placeholder="Partidos, Quebrados, Amassados (30%)"
                float-label="Partidos, Quebrados, Amassados (30%)"

                :disable="modoVisualizacao == true"
                type="number"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 1" class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroEsverdeados" error-label="Por favor, informe os Esverdeados">
              <q-input
                v-model="esverdeados"
                placeholder="Esverdeados (8%)"
                float-label="Esverdeados (8%)"

                :disable="modoVisualizacao == true"
                type="tel"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div v-if="codProduto == 1 || codProduto == 2 " class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroMofados" error-label="Por favor, informe os Mofados">
              <q-input
                  v-model="mofados"
                  placeholder="Mofados (6%)"
                  float-label="Mofados (6%)"

                  :disable="modoVisualizacao == true"
                  type="tel"
                  @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="observacao"
                placeholder="Observao"
                float-label="Observao"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="observacaoInterna"
                placeholder="Observao Interna"
                float-label="Observao Interna"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field
              :error="erroNomeMotorista"
              error-label="Por favor, informe o nome do motorista"
            >
              <q-input
                v-model="nomeMotorista"
                placeholder="Nome do Motorista"
                float-label="Nome do Motorista"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="ordemCarga"
                placeholder="Ordem de Carga"
                float-label="Ordem de Carga"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field>
              <q-input
                v-model="numNF"
                placeholder="Num. NF"
                float-label="Numero nota fiscal"
                :disable="modoVisualizacao == true"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-datetime
              v-model="dataClassificacao"
              placeholder="Data"
              float-label="Data"
              type="datetime"
              format-model="string"
              format="DD/MM/YYYY HH:mm"
              :disable="modoVisualizacao == true"
            />
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroCnpj" error-label="Por favor, informe o CNPJ">
              <q-input
                v-model="cnpj"
                placeholder="CNPJ Participante"
                float-label="CNPJ Participante"
                :disable="modoVisualizacao == true || (codProduto == 1 && tipoProdutoSojaSelecionado != 8)"
                v-show="codProduto == 1"
                @focus="validaCamposClassificacao"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form" style="margin-top:20px" v-if="clienteAlfameta.CodCliente === 2">
            <q-checkbox v-model="cobraTrocaNota" label="Cobrar troca de nota" />
          </div>
          <div class="col-12" style="margin-top:10px;">
            <div style="margin-top:10px; margin-bottom:10px;">
              <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
            </div>
            <q-btn
              color="primary"
              label="Salvar Classificao"
              @click="comportamentoBotaoSalvar()"
              v-show="modoVisualizacao != true"
            />
          </div>
          <ContainerArquivos
            ref="containerArquivos"
            v-show="modoVisualizacao && clienteAlfameta.ModuloArquivos === true"
            class="col-12"
          />
        </div>
      </div>
    </q-modal-layout>
  </q-modal>
</template>

<script>
import { post, getComVariosCabecalhos } from "../components/js/ConexaoApi.js";
import { getCookie } from "../components/js/Cookies.js";
import { criaUUID } from "../components/js/GeraUUID.js";
import { ConverteDataHorarioFormatoBancoDeDados } from "../components/utils/DataUtils.js";
import ContainerArquivos from "@/components/AreaCliente/components/ContainerArquivos";
import { detectaCliente } from "../components/utils/DetectaCliente.js";
export default {
  components: {
    ContainerArquivos,
  },
  name: "modalClassificacao",
  data() {
    return {
      //Variaveis de validao
      erroPesoLiquido: false,
      erroPlacaCavalo: false,
      erroTransportadora: false,
      erroPesoAmostra: false,
      erroSojaIntacta: false,
      erroTesteAflatoxina: false,
      erroUmidade: false,
      erroImpureza: false,
      erroQueimados: false,
      erroProteina: false,
      erroArdidos: false,
      erroArdidosEQueimados: false,
      erroFermentados: false,
      erroPicados: false,
      erroCarunchados: false,
      erroTotal: false,
      erroPartidos: false,
      erroEsverdeados: false,
      erroMofados: false,
      erroNomeMotorista: false,
      erroOrdemCarga: false,
      erroCnpj: false,
      //*********************/
      numeroLaudo: "",
      cnpj: "",
      dataClassificacao: "",
      ordemCarga: "",
      codOrdemServicoClassificacao: "",
      observacao: "",
      observacaoInterna: "",
      proteina: "",
      nomeMotorista: "",
      umidade: "",
      impureza: "",
      ardidosEQueimados: "",
      queimados: "",
      fermentados: "",
      ardidos: "",
      partidos: "",
      esverdeados: "",
      mofados: "",
      picados: "",
      carunchados: "",
      total: "",
      transportadora: "",
      tituloModal: "",
      pesoAmostraTrabalhada: "",
      modoVisualizacao: false,
      tipoProdutoMilho: [
        { value: 1, label: "Convencional" },
        { value: 2, label: "Transgnico" },
        { value: 3, label: "Aflatoxina - Positivo" },
        { value: 4, label: "Aflatoxina - Negativo" },
        { value: 5, label: "No testado" },
        { value: 12, label: "2RR2" },
      ],
      tipoProdutoSojaSelecionado: "",
      tipoProdutoSoja: [
        { value: 7, label: "Intacta Declarada" },
        { value: 8, label: "Participante" },
        { value: 9, label: "Testada Positiva" },
        { value: 10, label: "Testada Negativa" },
        { value: 11, label: "No Testada" },
        { value: 1, label: "Convencional" },
        { value: 2, label: "Transgnico" },
        { value: 12, label: "RR2" },
      ],
      tipoTesteOGMSelecionado: "",
      tipoTesteOGM: [
        { value: 1, label: "No testado" },
        { value: 2, label: "Positivo" },
        { value: 3, label: "Negativo" },
      ],
      tipoProdutoMilhoSelecionado: "",
      codItemServicoClassificacao: "",
      todosDadosCarregados: false,
      exibe: false,
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      pesoLiquido: "",
      placaCavalo: "",
      placa1: "",
      placa2: "",
      codProduto: "",
      codMunicipio: "",
      codClassificador: "",
      alteraInsere: "",
      numNF: "",
      cobraTrocaNota: false,
      clienteAlfameta : {},
    };
  },
  methods: {
    abreModal(dadosOs) {
      this.alteraInsere = 1;
      this.exibe = true;
      const admin = getCookie("su");
      const supervisor = getCookie("sup");
      if (admin != 0 || supervisor != 0) {
        this.codClassificador = dadosOs.CodClassificador;
      } else {
        this.codClassificador = getCookie("id");
      }
      this.codProduto = dadosOs.CodProduto;
      this.codOrdemServicoClassificacao = dadosOs.CodOrdemServicoClassificacao;
      this.codMunicipio = dadosOs.CodMunicipio;
      this.tituloModal = "Nova classificao";
      this.modoVisualizacao = false;
      this.limpaMensagensErro();
    },
    carregaCampos(dadosOs) {
      this.limpaMensagensErro();
      this.codOrdemServicoClassificacao = dadosOs.CodOrdemServicoClassificacao;
      this.codItemServicoClassificacao =
        dadosOs.CodItemOrdemServicoClassificacao;
      this.codProduto = dadosOs.CodProduto;
      this.pesoLiquido = dadosOs.PesoLiquido;
      this.placaCavalo = dadosOs.PlacaCavalo;
      this.placa1 = dadosOs.Placa1;
      this.proteina = dadosOs.Proteina;
      this.numeroLaudo = dadosOs.NumeroLaudo;
      this.placa2 = dadosOs.Placa2;
      const dia = dadosOs.DataClassificacao.substring(0, 2);
      const mes = dadosOs.DataClassificacao.substring(3, 5);
      const ano = dadosOs.DataClassificacao.substring(6, 10);
      const hora = dadosOs.DataClassificacao.substring(11, 13);
      const minuto = dadosOs.DataClassificacao.substring(14, 16);
      const data =
        ano + "-" + mes + "-" + dia + " " + hora + ":" + minuto + ":00";
      this.dataClassificacao = data;
      this.ordemCarga = dadosOs.OrdemCarga;
      this.observacao = dadosOs.Observacao;
      this.codMunicipio = dadosOs.CodMunicipio;
      this.observacaoInterna = dadosOs.ObservacaoInterna;
      this.nomeMotorista = dadosOs.NomeMotorista;
      this.transportadora = dadosOs.Transportadora;
      this.impureza = dadosOs.Impureza;
      this.numNF = dadosOs.NumNF;
      this.partidos = dadosOs.PartidoQuebradoAmassado;
      this.fermentados = dadosOs.FermentadoChochoImaturoGerminado;
      this.mofados = dadosOs.Mofados;
      this.umidade = dadosOs.Umidade;
      this.observacaoInterna = dadosOs.ObservacaoInterna;
      this.pesoAmostraTrabalhada = dadosOs.PesoAmostraTrabalhada;
      this.tipoTesteOGMSelecionado = dadosOs.CodTesteOGM;
      this.cnpj = dadosOs.CNPJ;
      if (this.codProduto == 1) {
        this.tipoProdutoSojaSelecionado = dadosOs.TipoProduto;
        this.ardidos = dadosOs.Ardidos;
        this.queimados = dadosOs.Queimados;
        this.picados = dadosOs.Picados;
        this.esverdeados = dadosOs.Esverdeados;
      } else {
        this.tipoProdutoMilhoSelecionado = dadosOs.TipoProduto;
        this.ardidosEQueimados = dadosOs.ArdidosEQueimados;
        this.carunchados = dadosOs.Carunchados;
      }
      if(dadosOs.CobraTrocaNota === 'S'){
        this.cobraTrocaNota = true;
      }
      this.total = dadosOs.TotalAvariado;
      const admin = getCookie("su");
      const supervisor = getCookie("sup");
      if (admin != 0 && supervisor != 0) {
        this.codClassificador = dadosOs.CodClassificador;
      } else {
        this.codClassificador = getCookie("id");
      }
      this.formataNumeros();
    },
    limpaCampos() {
      this.pesoLiquido = "";
      this.placaCavalo = "";
      this.placa1 = "";
      this.placa2 = "";
      this.codProduto = "";
      this.codMunicipio = "";
      this.codClassificador = "";
      this.tipoProdutoSojaSelecionado = "";
      this.cnpj = "";
      this.tipoProdutoMilhoSelecionado = "";
      this.tipoTesteOGMSelecionado = "";
      this.dataClassificacao = "";
      this.ordemCarga = "";
      this.codOrdemServicoClassificacao = "";
      this.observacao = "";
      this.observacaoInterna = "";
      this.nomeMotorista = "";
      this.umidade = "";
      this.impureza = "";
      this.numNF = "";
      this.ardidosEQueimados = "";
      this.queimados = "";
      this.fermentados = "";
      this.mofados = "";
      this.ardidos = "";
      this.partidos = "";
      this.esverdeados = "";
      this.proteina = "";
      this.picados = "";
      this.carunchados = "";
      this.total = "";
      this.transportadora = "";
      this.tituloModal = "";
      this.pesoAmostraTrabalhada = "";
      this.exibe = false;
      this.termoBuscaClassificador = "";
      this.contrato = "";
      this.statusSelecionado = "A";
      this.cadenciaSelecionada = "N";
      this.termoBuscaDestino = "";
      (this.termoBuscaCliente = ""), (this.termoBuscaEmbarque = "");
      this.produtoSelecionado = "";
      this.pesoLiquido = "";
      this.filial = "";
      this.pesoCadencia = "";
      this.cadenciaSelecionada = "";
      this.codClienteSelecionado = "";
      this.codEmbarqueSelecionado = "";
      this.codMunicipioSelecionado = "";
      this.codClassificadorSelecionado = "";
      this.pesoLiquido = "";
      this.pesoAmostraTrabalhada = "";
      this.nomeMotorista = "";
      this.observacao = "";
      this.observacaoInterna = "";
      this.ordemCarga = "";
      this.cnpj = "";
      this.cobraTrocaNota = false;
    },
    //Continuar a validao aqui
    validaCamposClassificacao() {
      return new Promise((resolve) => {

        let erro = false
        if (this.pesoLiquido == null || this.pesoLiquido === "") {
          this.erroPesoLiquido = true;
          erro = true;
        } else {
          this.erroPesoLiquido = false;
        }
        if (this.transportadora == null || this.transportadora === "") {
          this.erroTransportadora = true;
          erro = true;
        } else {
          this.erroTransportadora = false;
        }
        if (this.placaCavalo == null || this.placaCavalo === "") {
          this.erroPlacaCavalo = true;
          erro = true;
        } else {
          this.erroPlacaCavalo = false;
        }
        if (
          this.pesoAmostraTrabalhada == null ||
          this.pesoAmostraTrabalhada === ""
        ) {
          this.erroPesoAmostra = true;
          erro = true;
        } else {
          this.erroPesoAmostra = false;
        }
        if (this.umidade == null || this.umidade === "") {
          this.erroUmidade = true;
          erro = true;
        } else {
          this.erroUmidade = false;
        }
        if ((this.impureza === "" || this.impureza == null) && this.codProduto != 3) {
          this.erroImpureza = true;
          erro = true;
        } else {
          this.erroImpureza = false;
        }
        if (
          (this.fermentados === "" || this.fermentados == null) &&
          this.codProduto != 4 && this.codProduto != 3
        ) {
          this.erroFermentados = true;
          erro = true;
        } else {
          this.erroFermentados = false;
        }
        if ((this.total === "" || this.total == null) && this.codProduto != 4 && this.codProduto != 3) {
          this.erroTotal = true;
          erro = true;
        } else {
          this.erroTotal = false;
        }
        if (
          (this.partidos === "" || this.partidos == null) &&
          this.codProduto != 4 && this.codProduto != 3
        ) {
          this.erroPartidos = true;
          erro = true;
        } else {
          this.erroPartidos = false;
        }
        if (this.nomeMotorista === "" || this.nomeMotorista == null) {
          this.erroNomeMotorista = true;
          erro = true;
        } else {
          this.erroNomeMotorista = false;
        }
        if (this.codProduto == 1) {
          if (
            this.tipoProdutoSojaSelecionado === "" ||
            this.tipoProdutoSojaSelecionado == null
          ) {
            this.erroSojaIntacta = true;
            erro = true;
          } else {
            this.erroSojaIntacta = false;
          }
          if (this.queimados === "" || this.queimados == null) {
            this.erroQueimados = true;
            erro = true;
          } else {
            this.erroQueimados = false;
          }
          if (this.ardidos === "" || this.ardidos == null) {
            this.erroArdidos = true;
            erro = true;
          } else {
            this.erroArdidos = false;
          }
          if (this.picados === "" || this.picados == null) {
            this.erroPicados = true;
            erro = true;
          } else {
            this.erroPicados = false;
          }
          if (this.esverdeados === "" || this.esverdeados == null) {
            this.erroEsverdeados = true;
            erro = true;
          } else {
            this.erroEsverdeados = false;
          }
          if (this.mofados === "" || this.mofados == null) {
            this.erroMofados = true;
            erro = true;
          } else {
            this.erroMofados = false;
          }
          if (
            this.tipoProdutoSojaSelecionado == 8 &&
            (this.cnpj === "" || this.cnpj == null)
          ) {
            this.erroCnpj = true;
            erro = true;
          } else {
            this.erroCnpj = false;
          }
          if (this.ardidos === "" || this.ardidos == null) {
            this.erroArdidos = true;
            erro = true;
          } else {
            this.erroArdidos = false;
          }
          if (erro) {
            resolve(false);
          } else {
            resolve(true);
          }
        } else if (this.codProduto == 2) {
          if (
            this.tipoProdutoMilhoSelecionado == null ||
            this.tipoProdutoMilhoSelecionado === ""
          ) {
            this.erroTesteAflatoxina = true;
            erro = true;
          } else {
            this.erroTesteAflatoxina = false;
          }
          if (this.ardidosEQueimados === "" || this.ardidosEQueimados == null) {
            this.erroArdidosEQueimados = true;
            erro = true;
          } else {
            this.erroArdidosEQueimados = false;
          }
          if (this.mofados === "" || this.mofados == null) {
            this.erroMofados = true;
            erro = true;
          } else {
            this.erroMofados = false;
          }
          if (this.carunchados === "" || this.carunchados == null) {
            this.erroCarunchados = true;
            erro = true;
          } else {
            this.erroCarunchados = false;
          }
          if (erro) {
            resolve(false);
          } else {
            resolve(true);
          }
        } else if (this.codProduto == 3) {
          if (this.proteina === "" || this.proteina == null) {
            this.erroProteina = true;
            erro = true;
          } else {
            this.erroProteina = false;
          }
          if (erro) {
            resolve(false);
          } else {
            resolve(true);
          }
        } else if (this.codProduto == 4) {
          if (erro) {
            resolve(false);
          } else {
            resolve(true);
          }
        }
      });
    },
    limpaMensagensErro() {
      //Limpa as variaveis de erro
      this.erroPesoLiquido = false;
      this.erroPlacaCavalo = false;
      this.erroArdidosEQueimados = false;
      this.erroFermentados = false;
      this.erroPicados = false;
      this.erroMofados = false;
      this.erroCarunchados = false;
      this.erroTotal = false;
      this.erroPartidos = false;
      this.erroEsverdeados = false;
      this.erroNomeMotorista = false;
      this.erroOrdemCarga = false;
      this.erroCnpj = false;
      this.erroTransportadora = false;
      this.erroPesoAmostra = false;
      this.erroTesteAflatoxina = false;
      this.erroUmidade = false;
      this.erroImpureza = false;
      this.erroQueimados = false;
      this.erroArdidos = false;
      this.erroSojaIntacta = false;
      this.erroProteina = false;
    },
    abreModalVisualizacao(dadosOs) {
      dadosOs.EnviadoPor = "CLA";
      //Descomentar as linhas abaixo quando implementar o container de arquivos
      this.$refs.containerArquivos.setaInformacaoOs(dadosOs);
      this.$refs.containerArquivos.listaArquivos();
      this.tituloModal = "Visualizando classificao";
      this.exibe = true;
      this.carregaCampos(dadosOs);
      this.modoVisualizacao = true;
    },
    abreModalAlteracao(dadosOs) {
      this.alteraInsere = 2;
      this.tituloModal = "Alterao de classificao";
      this.modoVisualizacao = false;
      this.exibe = true;
      this.carregaCampos(dadosOs);
    },
    fechaModal() {
      this.limpaCampos();
      this.limpaMensagensErro();
    },
    async formataNumeros() {
      this.proteina = await this.removeVirgulaNumero(this.proteina);
      this.umidade = await this.removeVirgulaNumero(this.umidade);
      this.impureza = await this.removeVirgulaNumero(this.impureza);
      this.ardidosEQueimados = await this.removeVirgulaNumero(this.ardidosEQueimados);
      this.queimados = await this.removeVirgulaNumero(this.queimados);
      this.partidos = await this.removeVirgulaNumero(this.partidos);
      this.esverdeados = await this.removeVirgulaNumero(this.esverdeados);
      this.picados = await this.removeVirgulaNumero(this.picados);
      this.mofados = await this.removeVirgulaNumero(this.mofados);
      this.carunchados = await this.removeVirgulaNumero(this.carunchados);
      this.total = await this.removeVirgulaNumero(this.total);
    },
    async removeVirgulaNumero(numero) {
      return new Promise((resolve) => {
        try {
          if (numero !== undefined) {
            numero = numero.toString();
            if (numero !== "") {
              numero = numero.replace(/,/g, ".");
            }
          }
          resolve(numero);
        } catch (e) {
          resolve(numero);
        }
      });
    },
    async verificaLancamentoDuplicado() {
      return new Promise(async (resolve) => {
        const cabecalhos = new Map();
        cabecalhos.set("PlacaCavalo", this.placaCavalo);
        cabecalhos.set("Data", await ConverteDataHorarioFormatoBancoDeDados(this.dataClassificacao))
        getComVariosCabecalhos("verificalancamentoduplicado", cabecalhos)
          .then((result) => {
            const resposta = JSON.parse(result);
            if (resposta.Status == true) {
              resolve(true);
            } else {
              this.$q
                .dialog({
                  title: "Confirmao de lanamento",
                  message: resposta.msg,
                  color: "negative",
                  ok: "Sim",
                  cancel: "No",
                  position: "top",
                })
                .then(() => {
                  resolve(true);
                })
                .catch(() => {
                  resolve(false);
                });
            }
          })
          .catch((err) => {
            // eslint-disable-next-line no-console
            console.error(err);
            resolve(false);
          });
      });
    },
    async salvarItemOrdemClassificacao() {
      await this.formataNumeros();
      if (await this.validaCamposClassificacao()) {
        if (await this.verificaLancamentoDuplicado()) {
          const itemOrdemServicoClassificacao = {};
          itemOrdemServicoClassificacao.UUID = await criaUUID();
          itemOrdemServicoClassificacao.NumNF = this.numNF;
          itemOrdemServicoClassificacao.PesoAmostraTrabalhada = this.pesoAmostraTrabalhada;
          itemOrdemServicoClassificacao.CodOrdemServicoClassificacao = this.codOrdemServicoClassificacao;
          itemOrdemServicoClassificacao.CodProduto = this.codProduto;
          itemOrdemServicoClassificacao.CodMunicipio = this.codMunicipio;
          itemOrdemServicoClassificacao.CodClassificador = this.codClassificador;
          itemOrdemServicoClassificacao.PesoLiquido = this.pesoLiquido;
          itemOrdemServicoClassificacao.PlacaCavalo = this.placaCavalo;
          itemOrdemServicoClassificacao.DataClassificacao = await ConverteDataHorarioFormatoBancoDeDados(
            this.dataClassificacao
          );
          itemOrdemServicoClassificacao.Placa1 = this.placa1;
          itemOrdemServicoClassificacao.Placa2 = this.placa2;
          itemOrdemServicoClassificacao.Proteina = this.proteina;
          itemOrdemServicoClassificacao.OrdemCarga = this.ordemCarga;
          itemOrdemServicoClassificacao.Observacao = this.observacao;
          itemOrdemServicoClassificacao.ObservacaoInterna = this.observacaoInterna;
          itemOrdemServicoClassificacao.NomeMotorista = this.nomeMotorista;
          itemOrdemServicoClassificacao.Transportadora = this.transportadora;
          itemOrdemServicoClassificacao.Umidade = this.umidade;
          itemOrdemServicoClassificacao.Impureza = this.impureza;
          itemOrdemServicoClassificacao.FermentadoChochoImaturoGerminado = this.fermentados;
          itemOrdemServicoClassificacao.Mofados = this.mofados;
          itemOrdemServicoClassificacao.PartidoQuebradoAmassado = this.partidos;
          itemOrdemServicoClassificacao.TotalAvariado = this.total;
          itemOrdemServicoClassificacao.CodTesteOGM = this.tipoTesteOGMSelecionado;
          itemOrdemServicoClassificacao.CodEmpresa = 1;
          itemOrdemServicoClassificacao.Id = 999999;
          if (this.codProduto == 1) {
            if (this.tipoProdutoSojaSelecionado == 8) {
              itemOrdemServicoClassificacao.CNPJ = this.cnpj;
            }
            itemOrdemServicoClassificacao.TipoProduto = this.tipoProdutoSojaSelecionado;
            itemOrdemServicoClassificacao.Ardidos = this.ardidos;
            itemOrdemServicoClassificacao.Queimados = this.queimados;
            itemOrdemServicoClassificacao.Picados = this.picados;
            itemOrdemServicoClassificacao.Esverdeados = this.esverdeados;
          } else {
            itemOrdemServicoClassificacao.TipoProduto = this.tipoProdutoMilhoSelecionado;
            itemOrdemServicoClassificacao.ArdidosEQueimados = this.ardidosEQueimados;
            itemOrdemServicoClassificacao.Carunchados = this.carunchados;
          }
          if(this.cobraTrocaNota){
            itemOrdemServicoClassificacao.CobraTrocaNota = 'S'
          }
          // console.log(JSON.stringify(itemOrdemServicoClassificacao));
          post("classificacao", itemOrdemServicoClassificacao)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.mensagemRetorno = Resposta.msg;
                this.exibeMensagemRetorno = true;
                this.tipoMensagem = "positive";
                setTimeout(() => {
                  this.mensagemRetorno = "";
                  this.tipoMensagem = "positive";
                  this.exibeMensagemRetorno = false;
                  this.fechaModal();
                }, 2000);
              } else {
                this.mensagemRetorno = Resposta.msg;
                this.exibeMensagemRetorno = true;
                this.tipoMensagem = "negative";
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.mensagemRetorno = Erro.msg;
              this.exibeMensagemRetorno = true;
              this.tipoMensagem = "negative";
              // eslint-disable-next-line no-console
              console.error(Erro.Erro);
            });
        }
      }
    },
    confirmarCancelamentoDeCadastroDeClassificacao() {
      if (this.modoVisualizacao != true) {
        this.$q
          .dialog({
            title: "Cancelar lanamento de classificao",
            message: "Deseja realmente cancelar o lanamento da classificao?",
            color: "negative",
            ok: "Sim",
            cancel: "No",
          })
          .then(() => {
            this.fechaModal();
          })
          .catch(() => {});
      } else {
        this.fechaModal();
      }
    },
    async alteraItemOrdemClassificacao() {
      await this.formataNumeros();
      if (await this.validaCamposClassificacao()) {
        const itemOrdemServicoClassificacao = {}
        //Coloca a hora no momento em que a OS  criada
        itemOrdemServicoClassificacao.DataClassificacao = await ConverteDataHorarioFormatoBancoDeDados(
          this.dataClassificacao
        );
        itemOrdemServicoClassificacao.NumNF = this.numNF;
        itemOrdemServicoClassificacao.CodOrdemServicoClassificacao = this.codOrdemServicoClassificacao;
        itemOrdemServicoClassificacao.CodItemOrdemServicoClassificacao = this.codItemServicoClassificacao;
        itemOrdemServicoClassificacao.CodProduto = this.codProduto;
        itemOrdemServicoClassificacao.CodMunicipio = this.codMunicipio;
        itemOrdemServicoClassificacao.CodClassificador = this.codClassificador;
        itemOrdemServicoClassificacao.PesoLiquido = this.pesoLiquido;
        itemOrdemServicoClassificacao.PlacaCavalo = this.placaCavalo;
        itemOrdemServicoClassificacao.Placa1 = this.placa1;
        itemOrdemServicoClassificacao.Placa2 = this.placa2;
        itemOrdemServicoClassificacao.Transportadora = this.transportadora;
        itemOrdemServicoClassificacao.Umidade = this.umidade;
        itemOrdemServicoClassificacao.Impureza = this.impureza;
        itemOrdemServicoClassificacao.FermentadoChochoImaturoGerminado = this.fermentados;
        itemOrdemServicoClassificacao.Mofados = this.mofados;
        itemOrdemServicoClassificacao.PartidoQuebradoAmassado = this.partidos;
        itemOrdemServicoClassificacao.TotalAvariado = this.total;
        itemOrdemServicoClassificacao.CodEmpresa = 1;
        itemOrdemServicoClassificacao.PesoAmostraTrabalhada = this.pesoAmostraTrabalhada;
        itemOrdemServicoClassificacao.idAplicativo = 999999;
        itemOrdemServicoClassificacao.Proteina = this.proteina;
        itemOrdemServicoClassificacao.OrdemCarga = this.ordemCarga;
        itemOrdemServicoClassificacao.Observacao = this.observacao;
        itemOrdemServicoClassificacao.ObservacaoInterna = this.observacaoInterna;
        itemOrdemServicoClassificacao.NomeMotorista = this.nomeMotorista;
        itemOrdemServicoClassificacao.CodTesteOGM = this.tipoTesteOGMSelecionado;
        if (this.codProduto == 1) {
          if (this.tipoProdutoSojaSelecionado == 8) {
            itemOrdemServicoClassificacao.CNPJ = this.cnpj;
          }
          itemOrdemServicoClassificacao.TipoProduto = this.tipoProdutoSojaSelecionado;
          itemOrdemServicoClassificacao.Ardidos = this.ardidos;
          itemOrdemServicoClassificacao.Queimados = this.queimados;
          itemOrdemServicoClassificacao.Picados = this.picados;
          itemOrdemServicoClassificacao.Esverdeados = this.esverdeados;
        } else {
          itemOrdemServicoClassificacao.TipoProduto = this.tipoProdutoMilhoSelecionado;
          itemOrdemServicoClassificacao.ArdidosEQueimados = this.ardidosEQueimados;
          itemOrdemServicoClassificacao.Carunchados = this.carunchados;
        }
        if(this.cobraTrocaNota){
          itemOrdemServicoClassificacao.CobraTrocaNota = 'S'
        }
        post("alteraitem", itemOrdemServicoClassificacao)
          .then((result) => {
            const Resposta = JSON.parse(result);
            if (Resposta.Status == true) {
              this.mensagemRetorno = Resposta.msg;
              this.exibeMensagemRetorno = true;
              this.tipoMensagem = "positive";
              this.$emit("atualizaTabela");
              setTimeout(() => {
                this.mensagemRetorno = "";
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = false;
                this.fechaModal();
              }, 2000);
            } else {
              this.mensagemRetorno = Resposta.msg;
              this.exibeMensagemRetorno = true;
              this.tipoMensagem = "negative";
            }
          })
          .catch((err) => {
            const Erro = JSON.parse(err);
            this.mensagemRetorno = Erro.msg;
            this.exibeMensagemRetorno = true;
            this.tipoMensagem = "negative";
            // eslint-disable-next-line no-console
            console.error(Erro.Erro);
          });
      }
    },
    comportamentoBotaoSalvar() {
      if (this.alteraInsere == 1) {
        return this.salvarItemOrdemClassificacao();
      } else {
        return this.alteraItemOrdemClassificacao();
      }
    },
    detectaCliente() {
      detectaCliente()
        .then((result) => {
          this.clienteAlfameta = result
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
  },
  beforeMount() {
    this.detectaCliente();
  },
};
</script>

