<template>
  <div class="caixa-login">
    <div>
      <div class="centraliza-titulo">
        <img :src="logo" class="logo-responsiva" />
      </div>
      <q-field icon="fas fa-user">
        <q-input
          v-model="cnpjUsuario"
          type="text"
          placeholder="Informe seu CNPJ"
          float-label="CNPJ (Apenas numeros)"
          @keyup.enter="$refs.campoSenha.focus"
        />
      </q-field>
      <q-field icon="fas fa-key">
        <q-input
          ref="campoSenha"
          v-model="senha" 
          type="password"
          float-label="Senha"
          placeholder="Informe sua senha"
          @keyup.enter="fazLogin()"
        />
      </q-field>
      <q-btn class="logo-responsiva btn-login" color="primary" label="Entrar" @click="fazLogin" />
    </div>
    <div style="margin-top:5px">
      <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
    </div>
    <ModalPrimeiroLogin ref="modalPrimeiraSenha" @senhaAlterada="senhaAlterada" usuarioOuCliente="cliente"/>
  </div>
</template>

<script>
import { setCookie } from "../components/js/Cookies.js";
import { post } from "../components/js/ConexaoApi.js";
import { detectaCliente } from "../components/utils/DetectaCliente.js";
import ModalPrimeiroLogin from "@/components/ModalPrimeiroLogin.vue";
export default {
  components: {
    ModalPrimeiroLogin,
  },
  name: "Login",
  data() {
    return {
      erroUsuario: false,
      erroSenha: false,
      logo: "",
      cnpjUsuario: "",
      senha: "",
      tipoMensagem: "positive",
      mensagemRetorno: "",
      exibeMensagemRetorno: "",
    };
  },
  methods: {
    fazLogin() {
      const Cliente = {}
      Cliente.CNPJ = this.cnpjUsuario.toLowerCase();
      Cliente.Senha = this.senha;
      post("logincliente", Cliente)
        .then((result) => {
          const Resposta = JSON.parse(result);
          if (Resposta.Status) {
            if (Resposta.PrimeiroAcesso == "S") {
              setCookie("id", Resposta.id);
              setCookie("tkCliente", Resposta.token_acesso);
              this.$refs.modalPrimeiraSenha.abreModal(this.fazLogin);
            } else {
              this.exibeMensagemRetorno = true;
              this.tipoMensagem = "positive";
              this.mensagemRetorno = "Entrando";
              setCookie("tkCliente", Resposta.token_acesso, 7);
              setCookie("codcliente", Resposta.CodClienteSistema);
              setCookie("nomeCliente", Resposta.Nome);
              setCookie("email", Resposta.Email);
              setCookie("cnpj", Resposta.CNPJ);
              setCookie("layout", Resposta.LayoutEmail);
              this.$router.push("/areacliente/principal");
            }
          }
        })
        .catch((err) => {
          const Erro = JSON.parse(err);
          this.exibeMensagemRetorno = true;
          this.tipoMensagem = "negative";
          this.mensagemRetorno = Erro.Erro;
        });
    },
    senhaAlterada(novaSenha) {
      this.senha = novaSenha;
    },
    detectaCliente() {
      detectaCliente()
        .then((result) => {
          this.logo = result.LogoEmpresa;
        })
        .catch(() => {
          this.logo =
            "https://alfameta-cdn.s3-sa-east-1.amazonaws.com/logotransparente.png";
        });
    },
  },
  beforeMount() {
    this.detectaCliente();
  },
};
</script>

<style scoped>
.caixa-login {
  border: 1px solid black;
  position: relative;
  width: 315px;
  height: auto;
  display: table;
  margin: 0 auto;
  margin-top: 90px;
  border-radius: 4px;
  padding: 8px;
}

.logo-responsiva {
  width: 100%;
  height: auto;
}

.centraliza-titulo {
  text-align: center;
}

.distancia-inputs {
  margin-top: 5px;
  border-radius: 4px;
}
.btn-login {
  margin-top: 15px;
  height: 50px;
}
html,
body {
  width: 100%;
  height: 100%;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  background-color: white;
}
</style>

