<template>
  <q-modal
      v-model="exibe"
      :content-css="{width: '50vw', minHeight: '60vh'}"
      minimized
      no-backdrop-dismiss
  >
    <q-modal-layout>
      <q-toolbar slot="header">
        <q-btn
            dense
            flat
            icon="close"
            round
            style="padding:0px; margin:0"
            @click="confirmarCancelamentoDeCadastroDeUsuario"
        />
        <q-toolbar-title>Fechar</q-toolbar-title>
      </q-toolbar>
      <q-toolbar slot="header">
        <h6 class="tira-margin-pading">{{ tituloModal }}</h6>
      </q-toolbar>
      <div class="layout-padding">
        <div class="row">
          <div class="col-md-12 col-sm-12 col-xs-12 margin-form">
            <q-input
                v-model="termoBuscaClassificador"
                :disable="modoVisualizacao == true"
                float-label="Escolha um classificador"
                placeholder="Escolha um classificador"
                @focus="validaCamposForm"
            >
              <q-autocomplete
                  :static-data="{field:'label', list:classificadores}"
                  @selected="classificadorSelecionado"
              />
            </q-input>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-field :error="erroFieldUsuario" :error-label="descricaoErroFieldUsuario">
              <q-input
                  v-model="usuario"
                  :disable="modoVisualizacao == true"
                  float-label="Usurio"
                  placeholder="Usurio"
                  @focus="validaCamposForm"
              />
            </q-field>
          </div>
          <div class="col-md-6 col-sm-12 col-xs-12 margin-form">
            <q-input
                v-model="senha"
                :disable="modoVisualizacao == true"
                float-label="Senha"
                placeholder="Senha"
                type="password"
                @focus="validaCamposForm"
            />
          </div>
          <div class="col-12 margin-form">
            <q-input
                v-model="nome"
                :disable="modoVisualizacao == true"
                float-label="Nome completo"
                placeholder="Nome completo"
            />
          </div>
          <div class="col-12 margin-form">
            <q-checkbox v-model="primeiroLogin" label="Alterar senha no proximo login" val="true">
              <q-tooltip>O usurio deve alterar sua senha no proximo login</q-tooltip>
            </q-checkbox>
          </div>
          <div class="col-12 margin-form">
            <q-select :options="opcoesTipoUsuario"
                      v-model="tipoUsuarioSelecionado"
                      float-label="Tipo de usurio"
                      @input="onChangeTipoUsuario"/>
<!--            <q-checkbox v-model="administrador" label="Administrador/Gestor(*)" val="true">-->
<!--              <q-tooltip>O usurio pode lanar/alterar/remover classificaes e OS</q-tooltip>-->
<!--            </q-checkbox>-->
          </div>
          <div class="col-12" style="margin-top:10px;">
            <q-btn
                v-show="modoVisualizacao != true"
                color="primary"
                label="Salvar"
                @click="comportamentoBotaoSalvar()"
            />
          </div>
        </div>
        <div style="margin-top:10px">
          <q-alert v-show="exibeMensagemRetorno" :type="tipoMensagem">{{ this.mensagemRetorno }}</q-alert>
        </div>
      </div>
    </q-modal-layout>
  </q-modal>
</template>

<script>
import {post, get} from "../components/js/ConexaoApi.js";

export default {
  name: "modalClassificadores",
  components: {},
  data() {
    return {
      tituloModal: "",
      termoBuscaClassificador: "",
      codClassificadorSelecionado: "",
      modoVisualizacao: false,
      exibe: false,
      exibeMensagemRetorno: false,
      tipoMensagem: "positive",
      mensagemRetorno: "",
      alteraInsere: "",
      usuario: "",
      senha: "",
      nome: "",
      administrador: false,
      supervisor: false,
      opcoesTipoUsuario: [
        {label: "Usurio", value: 0},
        {label: "Administrador/Gestor", value: 1},
        {label: "Supervisor", value: 2}
      ],
      tipoUsuarioSelecionado: 0,
      idUsuario: "",
      classificadores: [],
      primeiroLogin: false,
      erroFieldUsuario: false,
      descricaoErroFieldUsuario: "",
    };
  },
  methods: {
    abreModal() {
      this.exibe = true;
      this.tituloModal = "Novo usurio";
      this.modoVisualizacao = false;
      this.alteraInsere = 1;
    },
    carregaCampos(dadosUsuario) {
      this.usuario = dadosUsuario.usuario;
      this.nome = dadosUsuario.nome;
      this.idUsuario = dadosUsuario.id;
      this.codClassificadorSelecionado = dadosUsuario.codclassificador;
      this.termoBuscaClassificador = dadosUsuario.nomeclassificador;
      this.administrador = false;
      this.supervisor = false;
      this.tipoUsuarioSelecionado = 0;
      if (dadosUsuario.superuser === 1) {
        this.administrador = true;
        this.tipoUsuarioSelecionado = 1;
      } else if (dadosUsuario.supervisor === 1) {
        this.supervisor = true;
        this.tipoUsuarioSelecionado = 2;
      }
    },
    abreModalVisualizacao(dadosUsuario) {
      this.tituloModal = "Visualizando usurio";
      this.modoVisualizacao = true;
      this.exibe = true;
      this.carregaCampos(dadosUsuario);
    },
    abreModalAlteracao(dadosUsuario) {
      this.alteraInsere = 2;
      this.tituloModal = "Alterao de usurio";
      this.modoVisualizacao = false;
      this.exibe = true;
      this.carregaCampos(dadosUsuario);
    },
    fechaModal() {
      this.exibe = false;
      this.senha = "";
      this.nome = "";
      this.usuario = "";
      this.codClassificadorSelecionado = "";
      this.termoBuscaClassificador = "";
      this.administrador = false;
      this.primeiroLogin = false;
    },
    confirmarCancelamentoDeCadastroDeUsuario() {
      if (this.modoVisualizacao != true) {
        this.$q
            .dialog({
              title: "Cancelar cadastro de usurio",
              message: "Deseja realmente cancelar o cadastro desse usurio?",
              color: "negative",
              ok: "Sim",
              cancel: "No",
            })
            .then(() => {
              this.fechaModal();
            })
            .catch(() => {
            });
      } else {
        this.fechaModal();
      }
    },
    validaCamposForm() {
      if (this.codClassificadorSelecionado === "") {
        this.termoBuscaClassificador = "";
      }
    },
    salvaUsuario() {

      // eslint-disable-next-line no-console
      // console.log(this.codClassificadorSelecionado)
      // eslint-disable-next-line no-console
      // console.log(this.administrador)
      if (this.usuario.toLowerCase().indexOf(" ") > -1) {
        this.erroFieldUsuario = true;
        this.descricaoErroFieldUsuario =
            "O usurio no pode conter espaos, por favor, corrija!";
      } else if (this.codClassificadorSelecionado === '' && this.administrador === false) {
        this.$q
            .dialog({
              title: "Erro",
              message: "Por favor, vincule um classificador ao usurio antes de continuar",
              color: "negative",
            })
            .then(() => {
            })
            .catch(() => {
            });
      } else {
        this.alteraInsere = 1;
        const Usuario = {}
        Usuario.usuario = this.usuario.toLowerCase();
        Usuario.senha = this.senha;
        Usuario.nome = this.nome.toUpperCase();
        Usuario.superuser = this.administrador;
        Usuario.supervisor = this.supervisor;
        if (this.primeiroLogin == true) {
          Usuario.PrimeiroLogin = "S";
        } else {
          Usuario.PrimeiroLogin = "N";
        }
        Usuario.codclassificador = this.codClassificadorSelecionado;
        // eslint-disable-next-line no-console
        post("novousuario", Usuario)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.mensagemRetorno = Resposta.msg;
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = true;
                this.$emit("atualizaTabela");
                setTimeout(() => {
                  this.mensagemRetorno = "";
                  this.tipoMensagem = "positive";
                  this.exibeMensagemRetorno = false;
                  this.exibe = false;
                  this.fechaModal();
                }, 2000);
              } else {
                this.mensagemRetorno = Resposta.msg;
                this.tipoMensagem = "negative";
                this.exibeMensagemRetorno = true;
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.mensagemRetorno = Erro.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
              // eslint-disable-next-line no-console
              console.error(Erro.Erro);
            });
      }
    },
    alteraUsuario() {
      if (this.usuario.toLowerCase().indexOf(" ") > -1) {
        this.erroFieldUsuario = true;
        this.descricaoErroFieldUsuario =
            "O usurio no pode conter espaos, por favor, corrija!";
      } else {
        this.erroFieldUsuario = false;
        this.descricaoErroFieldUsuario = "";
        this.exibeMensagemRetorno = false;
        const Usuario = {}
        Usuario.usuario = this.usuario.toLowerCase();
        Usuario.nome = this.nome.toUpperCase();
        Usuario.codclassificador = this.codClassificadorSelecionado;
        if (this.primeiroLogin == true) {
          Usuario.PrimeiroLogin = "S";
        } else {
          Usuario.PrimeiroLogin = "N";
        }
        if (this.senha == "" || this.senha == null) {
          Usuario.senha = undefined;
        } else {
          Usuario.senha = this.senha;
        }
        Usuario.superuser = this.administrador;
        Usuario.supervisor = this.supervisor;
        Usuario.id = this.idUsuario;
        post("alterausuario", Usuario)
            .then((result) => {
              const Resposta = JSON.parse(result);
              if (Resposta.Status == true) {
                this.mensagemRetorno = Resposta.msg;
                this.tipoMensagem = "positive";
                this.exibeMensagemRetorno = true;
                this.$emit("atualizaTabela");
                setTimeout(() => {
                  this.mensagemRetorno = "";
                  this.tipoMensagem = "positive";
                  this.exibeMensagemRetorno = false;
                  this.exibe = false;
                  this.fechaModal();
                }, 2000);
              } else {
                this.mensagemRetorno = Resposta.msg;
                this.tipoMensagem = "negative";
                this.exibeMensagemRetorno = true;
              }
            })
            .catch((err) => {
              const Erro = JSON.parse(err);
              this.mensagemRetorno = Erro.msg;
              this.tipoMensagem = "negative";
              this.exibeMensagemRetorno = true;
              // eslint-disable-next-line no-console
              console.error(Erro.Erro);
            });
      }
    },
    comportamentoBotaoSalvar() {
      if (this.alteraInsere == 1) {
        return this.salvaUsuario();
      } else {
        return this.alteraUsuario();
      }
    },
    buscaClassificador(terms, done) {
      setTimeout(() => {
        done(
            // eslint-disable-next-line no-undef
            filter(this.termoBuscaCliente, {
              field: "label",
              list: this.clientes,
            })
        );
      }, 100);
    },
    classificadorSelecionado(classificador) {
      this.termoBuscaClassificador = classificador.label;
      this.codClassificadorSelecionado = classificador.value;
      this.nome = classificador.label;
    },
    carregaClassificadores() {
      get("classificadoressistema")
          .then((result) => {
            let jsonClassificadores = result;
            jsonClassificadores = jsonClassificadores.replace(
                /"codclassificador":/g,
                '"value":'
            );
            jsonClassificadores = jsonClassificadores.replace(
                /"nome":/g,
                '"label":'
            );
            this.classificadores = JSON.parse(jsonClassificadores);
          })
          .catch(() => {
          });
    },
    onChangeTipoUsuario(value) {
      this.administrador = false;
      this.supervisor = false;
      if(value === 1) {
        this.administrador = true;
      } else if(value === 2) {
        this.supervisor = true;
      }
    },
  },
  mounted() {
    this.carregaClassificadores();
  },
};
</script>

<style scoped>
.tira-margin-pading {
  padding: 0;
  margin: 0;
}

.margin-form {
  padding: 5px;
}

@media screen and (max-width: 768px) {
  .margin-form {
    padding: 0px;
  }

  .lagura-modal {
    width: 70px;
  }
}
</style>
