import { getCookie } from "./Cookies.js";
import { post } from "./ConexaoApi.js";
export function verificaSeEstaLogado() {
  const cliente = window.location.pathname;
  if (getCookie("tk") != null || getCookie("tk") != "") {
    const Token = {}
    Token.token = getCookie("tk");
    post("auth", Token)
      .then(result => {
        const Resposta = JSON.parse(result);
        if (Resposta.Autenticado != true) {
          window.location.href = cliente;
        }
      })
      .catch(() => {
        window.location.href = cliente;
      });
  } else {
    window.location.href = cliente;
  }
}
