<template>
  <div>
    <div class="container cor-fundo no-wrap" id="content">
      <div class="logo justifica-centro">
        <img :src="logo" class="logo" />
      </div>
      <div class="texto-centro margin-top-padrao fonte-padrao tamanho-fonte-cabecalho">
        <div>FGL AGRO SERVIOS AGRCOLAS (42.200.362/0001-76)</div>
        <div>AV. PRESIDENTE MEDICI N 4101 - CIDADE SALMEN - CEP 78.705-164</div>
        <div>RONDONPOLIS, MT - FONE: (66) 99674-1588</div>
      </div>
      <hr size="15" />
      <div class="texto-centro tamanho-fonte-cabecalho">LAUDO DE CLASSIFICAO</div>
      <hr size="15" />
      <div class="row col-12 tamanho-fonte-padrao">
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Ordem de Servio.:</b>
          {{codOrdemServicoClassificacao}}
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Data Classificao.:</b>
          {{data}}
        </div>
        <div class="col-12">
          <b>Empresa.:</b>
          {{nomeCliente}}
        </div>
        <div class="col-12">
          <b>Numero Contrato.:</b>
          {{numeroContrato}}
        </div>
        <div class="col-12">
          <b>Lacre.:</b>
          {{numeroLacre}}
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Origem.:</b>
          {{municipioOrigem}}
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Local Embarque.:</b>
          {{localEmbarque}}
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Destino.:</b>
          {{municipioDestino}}
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Placas.:</b>
          {{placaCavalo}} {{placa1}} {{placa2}}
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Peso Liquido.:</b>
          {{pesoLiquido}} Kg
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Nota Fiscal.:</b>
          {{numNf}}
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6" v-if="codProduto == 1">
          <b>Produto.:</b> SOJA
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6" v-else-if="codProduto == 2">
          <b>Produto.:</b> MILHO
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6" v-else-if="codProduto == 4">
          <b>Produto.:</b> SORGO
        </div>
        <!-- Verificar como esta vindo a marcao da vistoria -->
        <div class="col-xs-6 col-sm-6 col-md-6" v-if="vistoriado == 1">
          <b>Veiculo Vistoriado.:</b> SIM
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6" v-else>
          <b>Veiculo Vistoriado.:</b> NO
        </div>
        <div class="col-xs-6 col-sm-6 col-md-6">
          <b>Motorista.:</b>
          {{nomeMotorista}}
        </div>
        <!-- Verificar como vai ser feito o tipo do produto -->
        <div class="col-12" v-if="codProduto == 1 || codProduto == 2">
          <b>{{tipoDoProduto}}</b>
          {{tipoProduto}}
        </div>
      </div>
      <hr />
      <div class="texto-centro tamanho-fonte-cabecalho">ITENS DA CLASSIFICAO</div>
      <hr />
      <div>
        <div>
          <b>Umidade.:</b>
          {{umidade}}%
        </div>
        <div>
          <b>Materias estranhas e Impurezas.:</b>
          {{impureza}}%
        </div>
        <div v-if="codProduto == 1">
          <div>
            <b>Ardidos.:</b>
            {{ardidos}}%
          </div>
          <div>
            <b>Queimados.:</b>
            {{queimados}}%
          </div>
          <div>
            <b>Picados.:</b>
            {{picados}}%
          </div>
          <div>
            <b>Esverdeados.:</b>
            {{esverdeados}}%
          </div>
        </div>
        <div v-else-if="codProduto == 2">
          <div>
            <b>Ardidos/Queimados.:</b>
            {{ardidosEQueimados}}%
          </div>
          <div>
            <b>Carunchados.:</b>
            {{carunchados}}%
          </div>
        </div>
        <div v-if="codProduto != 4">
          <b>Fermentados.:</b>
          {{fermentados}}%
        </div>
        <div v-if="codProduto != 4">
          <b>Part./ Queb./ Amassados.:</b>
          {{quebrados}}%
        </div>
        <div v-if="codProduto != 4">
          <b>Total.:</b>
          {{total}}%
        </div>
      </div>
      <hr />
      <div>
        <b>Observao.:</b>
      </div>
      <div>{{observacao}}</div>
      <hr />
      <div>
        <b>Laudo Classificao.:</b>
        {{numeroLaudo}}
      </div>
      <div>
        <b>Classificador Responsavel.:</b>
        {{nomeClassificador}}
      </div>
      <div class="row col-12 justifica-centro" style="paddingTop:15px">
        <QrCodeVue :value="urlClassificacao" :size="size" level="H"></QrCodeVue>
      </div>
    </div>
    <div
      class="text-center distancia-botoes-acao fonte-explicacao-download esconde-botao-impressao"
    >Faa o download em PDF de todos os dados da classificao {{numeroLaudo}} clicando no boto abaixo.</div>
    <div class="row col-12 justify-center distancia-botoes-acao">
      <q-btn
        icon="fas fa-file-pdf"
        label="Baixar PDF"
        @click="downloadPDF()"
        class="distancia-botoes esconde-botao-impressao cor-pdf col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
      <q-btn
        icon="fas fa-print"
        color="primary"
        label="Imprimir"
        @click="imprimirPagina()"
        class="distancia-botoes esconde-botao-impressao col-md-6 col-xs-12 col-sm-12 tamanho-botao fonte-botao"
      >
        <q-tooltip anchor="bottom middle" self="top middle">Clique aqui para baixar o PDF</q-tooltip>
      </q-btn>
    </div>
  </div>
</template>

<script>
import QrCodeVue from "qrcode.vue";
// eslint-disable-next-line no-unused-vars
import jsPDF from "jspdf";
// eslint-disable-next-line no-unused-vars
import html2canvas from "html2canvas";
import html2pdf from "js-html2pdf";
import { getComCabecalho } from "../components/js/ConexaoApi.js";
import { tipoProduto } from "./utils/TipoProduto";
export default {
  components: {
    QrCodeVue,
  },
  data() {
    return {
      logo: require("../assets/logo-fglagro.png"),
      codProduto: 1,
      nomeCliente: "",
      municipioOrigem: "",
      localEmbarque: "",
      placaCavalo: "",
      placa1: "",
      placa2: "",
      transportadora: "",
      pesoTrabalhado: "",
      pesoLiquido: "",
      nomeProduto: "",
      umidade: "",
      impureza: "",
      queimados: "",
      ardidos: "",
      fermentados: "",
      picados: "",
      total: "",
      esverdeados: "",
      quebrados: "",
      ardidosEQueimados: "",
      numeroContrato: "",
      carunchados: "",
      aflatoxinaNegativo: false,
      aflatoxinaPositivo: false,
      sojaNegativo: false,
      sojaPositivo: false,
      sojaDeclarado: false,
      observacao: "",
      data: "",
      nomeClassificador: "",
      numeroLaudo: "",
      ufOrigem: "",
      numeroLacre: "",
      ufDestino: "",
      tipoDoProduto: "",
      tipoProduto: "",
      municipioDestino: "",
      urlClassificacao: "",
      exibeLayout: false,
      layoutCelular: false,
      vistoriado: 0,
      nomeMotorista: "",
      proteinas: "",
      numNf: "",
      codOrdemServicoClassificacao: "",
      size: 200,
    };
  },
  methods: {
    carregaDadosClassificacao() {
      let uuid = this.$route.params.u;
      if (uuid == "" || uuid == null || uuid == undefined) {
        this.$router.push("/");
      }
      getComCabecalho("carregadadosqrcode", "uuid", uuid)
        .then((result) => {
          const Resposta = JSON.parse(result);
          this.nomeCliente = Resposta[0].NomeCliente;
          this.numeroLaudo = Resposta[0].NumeroLaudo;
          this.localEmbarque = Resposta[0].LocalEmbarque;
          this.municipioDestino = Resposta[0].MunicipioDestino;
          this.pesoTrabalhado = Resposta[0].PesoAmostraTrabalhada;
          this.pesoLiquido = Resposta[0].PesoLiquido;
          this.codOrdemServicoClassificacao =
            Resposta[0].CodOrdemServicoClassificacao;
          this.ufDestino = Resposta[0].UFDestino;
          this.vistoriado = Resposta[0].Vistoria;
          this.ufOrigem = Resposta[0].UFOrigem;
          this.umidade = Resposta[0].Umidade;
          this.numeroLacre = Resposta[0].NumeroLacre;
          this.codProduto = Resposta[0].CodProduto;
          this.impureza = Resposta[0].Impureza;
          this.fermentados = Resposta[0].FermentadoChochoImaturoGerminado;
          this.total = Resposta[0].TotalAvariado;
          this.transportadora = Resposta[0].Transportadora;
          this.placaCavalo = Resposta[0].PlacaCavalo;
          this.placa1 = Resposta[0].Placa1;
          this.placa2 = Resposta[0].Placa2;
          this.observacao = Resposta[0].Observacao;
          this.data = Resposta[0].DataClassificacao;
          this.nomeClassificador = Resposta[0].nome;
          this.tipoProduto = Resposta[0].TipoProduto;
          this.numeroContrato = Resposta[0].NumeroContrato;
          this.municipioOrigem = Resposta[0].MunicipioOrigem;
          this.nomeMotorista = Resposta[0].NomeMotorista;
          this.numNf = Resposta[0].NumNF;

          if (this.codProduto == 1) {
            this.esverdeados = Resposta[0].Esverdeados;
            this.queimados = Resposta[0].Queimados;
            this.ardidos = Resposta[0].Ardidos;
            this.picados = Resposta[0].Picados;
          } else {
            this.ardidosEQueimados = Resposta[0].ArdidosEQueimados;
            this.carunchados = Resposta[0].Carunchados;
          }

          this.quebrados = Resposta[0].PartidoQuebradoAmassado;
          this.tipoProduto = tipoProduto(this.tipoProduto);

          if (this.codProduto == 1) {
            this.tipoDoProduto = "Tipo do Produto: ";
          } else {
            this.tipoDoProduto = "Aflatoxina: ";
          }
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.error(err);
        });
    },
    //Gera o PDF e inicia o Download
    downloadPDF() {
      this.exibeLayout = true;
      setTimeout(() => {
        const elementoASerImprimido = document.getElementById("content");
        const options = {
          filename: "Classificacao" + this.numeroLaudo + ".pdf",
          margin: 0,
          enableLinks: true,
          image: "png",
        };
        const exporter = new html2pdf(elementoASerImprimido, options);
        exporter
          .getPdf(true)
          .then(() => {})
          .catch(() => {});
      }, 500);
    },
    imprimirPagina() {
      this.exibeLayout = true;
      setTimeout(() => {
        window.print();
      }, 1500);

      setTimeout(() => {
        this.exibeLayout = false;
      }, 3000);
    },
    detectaPlataforma() {
      if (this.$q.platform.is.mobile) {
        this.exibeLayout = false;
        this.layoutCelular = true;
      } else {
        this.layoutCelular = false;
        this.exibeLayout = true;
      }
    },
  },
  mounted() {
    this.urlClassificacao = window.location.href;
    this.carregaDadosClassificacao();
  },
};
</script>
<style scoped>
.container {
  margin-left: 15%;
  margin-right: 15%;
  margin-top: 25px;
  padding-top: 25px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
}
@media only print {
  .esconde-botao-impressao {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .container {
    margin-left: 1%;
    margin-right: 1%;
    margin-top: 25px;
    padding-top: 25px;
    padding-bottom: 20px;
  }
}
.logo-download {
  width: 35vw;
  height: auto;
}
.tamanho-fonte-cabecalho {
  font-size: 15px;
}

.fonte-padrao {
  font-family: Roboto, Open-sans;
}
.margin-top-padrao {
  margin-top: 25px;
}
.justifica-centro {
  justify-content: center;
}
.alinha-itens-centro {
  align-items: center;
}
.texto-centro {
  text-align: center;
}
.alinha-content-centro {
  align-content: center;
}
.tamanho-fonte-padrao {
  font-size: 14px;
}
.logo {
  margin-left: auto;
  margin-right: auto;
  display: block;
  width: 60%;
  height: auto;
}
.distancia-botoes-acao {
  margin-top: 35px;
}
.distancia-botoes {
  margin: 5px;
  border-radius: 5px;
}
.tamanho-botao {
  height: 100px;
}
.fonte-botao {
  font-size: 28px;
}
.cor-fundo {
  background-color: #ecffea;
}
.margin-bottom {
  margin-bottom: 15px;
}
hr {
  border: 1px solid black;
}
.qrcode {
  width: 0 auto;
}
.cor-pdf {
  background-color: #ff0000;
  color: white;
}
</style>
